﻿import { PatientEvent } from "idun-common/models/patient-event"
import { ModelTestData } from "./model-test-data"
import { TimeInterval } from "idun-common/models/time-interval"
import { DateHelper } from "utility/date-helper"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { resourceTestData } from "./resource-test-data"
import { staffTestData } from "./staff-test-data"
import { procedureTestData } from "./procedure-test-data"
import { BookingTimeService } from "utility/booking-time-finder"
const baseUrl = "api/patientevent/"

export const patientEventTestData: PatientEvent[] = [
	new PatientEvent({
		DBID: 1,
		PatientId: 2,
		ProcedureEvents: [
			new ProcedureEvent({
				DBID: 5,
				PatientEventId: 1,
				Times: {
					StartTime: DateHelper.newDate(new Date(), { hours: 10 }),
					EndTime: DateHelper.newDate(new Date(), { hours: 10, minutes: 10 })
				},
				Placements: [],
				TreatmentCodeIds: [9, 16],
				PaymentCodeIds: [],
				OccupiedStaff: [1],
				OccupiedResources: [1],
				ProcedureId: 5,
				Variation: "Dobbeltsidig",
				Price: 30
			}),
			new ProcedureEvent({
				DBID: 6,
				PatientEventId: 1,
				Times: {
					StartTime: DateHelper.newDate(new Date(), { hours: 10, minutes: 10 }),
					EndTime: DateHelper.newDate(new Date(), { hours: 10, minutes: 20 })
				},
				Placements: ["areas/left hand"],
				TreatmentCodeIds: [7, 15],
				PaymentCodeIds: [1],
				OccupiedStaff: [1],
				OccupiedResources: [1],
				ProcedureId: 6,
				Variation: "Venstre",
				Price: 24,
				PartnerId: 3
			})
		]
	})
]

const otherResponses = {}

otherResponses[baseUrl + "getavailabletimes"] = (parameters: { patientEvent: PatientEvent, start: Date, end: Date }) => {
	const procedureEvents = parameters.patientEvent.ProcedureEvents
	if (procedureEvents.length === 0 || !procedureEvents[0].ProcedureId)
		return "[]"
	const procedure = procedureTestData
		.find(({ DBID }) => DBID === procedureEvents[0].ProcedureId)
	const times = new BookingTimeService(staffTestData, resourceTestData)
		.findAvailableTimesFor(procedure, TimeInterval.fromInterval(parameters))
	return JSON.stringify(times)
}

otherResponses[baseUrl + "getevents"] = (parameters: { start: Date, end: Date }) => {
	const interval = TimeInterval.fromInterval(parameters)
	return JSON.stringify(patientEventTestData.filter(e => interval.contains(e.ProcedureEvents[0].Times)))
}

otherResponses[baseUrl + "geteventsfor"] = (parameters: { id: number }) => {
	return JSON.stringify(patientEventTestData.filter(e => e.PatientId === parameters.id))
}

export function PatientEventResponses() {
	return {
		...ModelTestData.commonResponses(PatientEvent, baseUrl, patientEventTestData, { getpage: () => "", getlist: () => "" }),
		...otherResponses
	}
}
