﻿import { Component, ElementRef, Input } from "@angular/core"
import { Router } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LayoutService } from "idun-layout/layout.service"

@Component({
	selector: "idun-menu-bar-entry",
	templateUrl: "menu-bar-entry.component.html",
	styleUrls: ["menu-bar-entry.component.scss"]
})
export class MenuBarEntryComponent {
	navigater: NavigationHandler
	@Input() menuInfo: any

	constructor(
		myElement: ElementRef,
		router: Router,
		public layoutService: LayoutService
	) {
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	mouseover(side: string) {
		this.layoutService.showShadowOverlays(side)
	}

	mouseout(side: string) {
		this.layoutService.hideShadowOverlays(side)
	}
}

export class MenuEntry {
	constructor(
		public _title: () => string,
		public image: string,
		public links: MenuLink[]
	) { }

	get title() {
		return this._title()
	}
}

export interface MenuLink {
	title: string
	image: string
	destination: string
	show: boolean
	normalLink?: boolean
}

export class StandardMenuLink implements MenuLink {
	public show = true

	constructor(
		private _title: () => string,
		public image: string,
		public destination: string,
	) { }

	get title() {
		return this._title()
	}
}
