import { Component, ElementRef } from "@angular/core"
import { Router } from "@angular/router"
import { LanguageService } from "idun-layout/language.service"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LayoutService } from "idun-layout/layout.service"

@Component({
	selector: "idun-help-bar",
	templateUrl: "help-bar.component.html",
	styleUrls: ["help-bar.component.scss"]
})
export class HelpBarComponent {
	navigater: NavigationHandler

	constructor(
		myElement: ElementRef,
		router: Router,
		layoutService: LayoutService,
		public l: LanguageService
	) {
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	modeOptions = [
		{ text: () => this.l.sidebar.general, selected: () => this.mode === "general", click: () => this.mode = "general" },
		{ text: () => this.l.sidebar.guide, selected: () => this.mode === "guide", click: () => this.mode = "guide" },
	]

	mode = "general"

	helpbarInfo = [
		{
			question: "Access to Refhost",
			answer: `Refhost isn't integrated into IdunHealth, so you have to go to their site to get access:
		<a href='https://www.refhost.dk/wcfLogin.aspx' target='blank'>Refhost.dk</a>'`
		},
		{
			question: "Booking a time",
			answer: `At the home page, go to Calendar. Navigate to the desired date with the button at the top
		 of the calendar, and then press on the desired time in the calendar. Fill out the information in the box that appears and press submit.`
		},
		{
			question: "Add new patient",
			answer: "At the home page, go to Patients. The press the button 'Add' to add a patient. Fill out the information in the box and press submit."
		},
		{
			question: "Access a patient",
			answer: `In the right sidebar, navigate to the patient tab and then write the name or cpr on the patient in the search field.
		 When the desired patient is found, press 'Select' to add the patient to the sidebar so the system knows that you are working
		 with that patient, and/or press 'Details' to see all details about that patient.`
		},
		{
			question: "Access patients from yesterday, today and tomorrow",
			answer: "In the right sidebar, navigate to 'Patients yesterday, today and tomorrow' and you can see and access current patients."
		}
	]

	guideInfo = [
		{
			title: "Secretary workflow",
			info: `When a patient arrives or calls the clinic, you can either: <br/>
		Check if they have a referral by going to <a href="https://www.refhost.dk/wcfLogin.aspx" target="_blank">refhost.dk</a> <br/>
		Or go to "Patients" and search after them in the system. If they don't exist you can add them from here by pressing "Add". <br/><br/>
		If you used Refhost, an edifact will be recieved and add the patient to the list of patients if they had a referral. <br/><br/>
		To make a booking for the patient it can be preferred to add the patient to the right sidebar by going to "Patients",
		and search after them when they are in the system, and press "Select", this makes the system aware that you are working with that patient.
		<br/><br/>
		You can then open the booking calendar, find a free or desired date and and add a booking request for the patient.`}
	]
}
