﻿import { Component, forwardRef, Input } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { FormRow } from "./form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormTextComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormTextComponent),
	multi: true
}

@Component({
	selector: "idun-form-text",
	template: `
<idun-form-input-container [hint]="hint" [label]="label" [display]="display" [error]="errorToShow()" [showAsterisk]="showAsterisk()">
	<div class="flex-row">
		<ng-container *ngIf="!options">
			<input type="text" [(ngModel)]="value" (blur)="onBlur()" [disabled]="display" autocomplete="off">
		</ng-container>
		<ng-container *ngIf="options">
			<input list="text-options" type="text" [(ngModel)]="value" (blur)="onBlur()" [disabled]="display" autocomplete="off">
			<datalist id="text-options">
				<option *ngFor="let option of options">{{option}}</option>
			</datalist>
		</ng-container>
		<ng-content></ng-content>
	</div>
	<div class="flex-row">
		<ng-content select="[data-below]"></ng-content>
	</div>
</idun-form-input-container>`,
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormTextComponent extends FormBasicComponent<string> {
	rowClass = FormTextRow
	@Input() options: string[]
}

export class FormTextRow extends FormRow { }
