﻿import { Component, ElementRef } from "@angular/core"
import { Router } from "@angular/router"
import { BaseModelSubscriber } from "idun-common/base-model-subscriber"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LayoutService } from "idun-layout/layout.service"
import { LanguageService } from "idun-layout/language.service"
import { MenuEntry, StandardMenuLink, MenuLink } from "../menu-bar-entry/menu-bar-entry.component"

import { UserService } from "idun-main/user.service"
import { StaffNotificationService } from "idun-main/model-services/staff-notification.service"
import { Permission } from "idun-common/models/staff-group"

@Component({
	selector: "idun-nav",
	templateUrl: "idun-nav.component.html",
	styleUrls: ["idun-nav.component.scss"]
})
export class IdunNavComponent extends BaseModelSubscriber {
	navigater: NavigationHandler

	menus = [
		new MenuEntry(
			() => this.l.common.user,
			"SettingsRound",
			[
				this.settingsLink(),
				this.logoutLink(),
			]
		),
		new MenuEntry(
			() => this.l.common.production,
			"ProductionRound",
			[
				this.patientsLink(),
				this.bookingLink(),
				this.personalCalendarLink(),
			]
		),
		new MenuEntry(
			() => this.l.common.planning,
			"CalendarRound",
			[
				this.overviewCalendarLink(),
				this.requestCalendarLink(),
				this.planningCalendarLink(),
			]
		),
		new MenuEntry(
			() => this.l.common.foundation,
			"FundamentalRound",
			[
				this.setupLink(),
				this.clinicSettingsLink(),
				this.userSettingsLink(),
			]
		),
		new MenuEntry(
			() => this.l.common.analytics,
			"EconomyRound",
			[
				this.economyLink(),
				this.statisticsLink()
			]
		)
	]

	private settingsLink() {
		return new StandardMenuLink(
			() => this.l.common.settings,
			"Settings",
			"user-overview"
		)
	}

	private logoutLink(): MenuLink {
		const _this = this
		return {
			get title() { return _this.l.common.logOut },
			image: "Settings",
			destination: "api/Authentication/Logout",
			show: true,
			normalLink: true
		}
	}

	private patientsLink() {
		return new StandardMenuLink(
			() => this.l.patient.namePlural,
			"Patient",
			"patient-overview"
		)
	}

	private bookingLink() {
		return new StandardMenuLink(
			() => this.l.common.booking,
			"CalendarBooking",
			"booking",
		)
	}

	private personalCalendarLink() {
		const _this = this
		return {
			get title() { return _this.l.common.personalCalendar },
			image: "Calendar",
			get destination() { return "calendar/-/" + _this.userService.user.StaffID },
			show: true,
		}
	}

	private overviewCalendarLink() {
		return new StandardMenuLink(
			() => this.l.calendar.overviewCalendar,
			"CalendarDoctor",
			"calendar-overview"
		)
	}

	private requestCalendarLink() {
		return new StandardMenuLink(
			() => this.l.calendar.requestCalendar,
			"CalendarRoom",
			"calendar-request"
		)
	}

	private planningCalendarLink(): MenuLink {
		const _this = this
		return {
			get title() { return _this.l.periodPlan.planning },
			image: "Calendar",
			destination: "period-plan/list",
			get show() { return _this.userService.hasPermission(Permission.EditStatus) }
		}
	}

	private setupLink() {
		return new StandardMenuLink(
			() => this.l.common.setup,
			"Clinic",
			"clinic-overview"
		)
	}

	private clinicSettingsLink(): MenuLink {
		const _this = this
		return {
			get title() { return _this.l.clinic.clinicSettings },
			image: "ClinicSettings",
			destination: "clinic-settings",
			get show() { return _this.userService.hasPermission(Permission.Admin) }
		}
	}

	private userSettingsLink() {
		return new StandardMenuLink(
			() => this.l.common.userSettings,
			"Settings",
			"settings"
		)
	}

	private economyLink() {
		return new StandardMenuLink(
			() => this.l.economic.economics,
			"Economy",
			"economic-overview"
		)
	}
	private statisticsLink() {
		return new StandardMenuLink(
			() => this.l.statistics.namePlural,
			"Statistics",
			"statistics-overview"
		)
	}

	constructor(
		myElement: ElementRef,
		router: Router,
		public layoutService: LayoutService,
		private userService: UserService,
		private notificationService: StaffNotificationService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	hoverNotificationDelete(element) {
		element.setAttribute("src", "./assets/images/Icons/Delete_icon2.png")
	}

	unhoverNotificationDelete(element) {
		element.setAttribute("src", "./assets/images/Icons/Delete_icon3.png")
	}

	deleteNotification(element) {
		this.notificationService.delete(element.DBID)
	}

	mouseover(side) {
		this.layoutService.showShadowOverlays(side)
	}

	mouseout(side) {
		this.layoutService.hideShadowOverlays(side)
	}
}
