﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { LanguageService } from "idun-layout/language.service"

import { Staff } from "idun-common/models/staff"
import { StaffGroupService } from "./staff-group.service"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { FormSelectModelRow } from "idun-common/form/form-select-model.component"
import { FormTelephoneRow } from "idun-common/form/form-telephone.component"
import { FormExtraRow } from "idun-common/form/form-extra-row.component"
import { ButtonThing, LabelThing, LinkThing } from "idun-common/form/form-dynamic-extra.component"
import { CompetenceSet } from "idun-common/models/competence-set"
import { abbreviationFor } from "utility/abbreviations"
import { UserService } from "idun-main/user.service"
import { Subscriber } from "utility/subscriber"
import { Permission } from "idun-common/models/staff-group"
import { FormTextareaRow } from "idun-layout/form/form-textarea.component"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"
import { FormListFileRow } from "idun-common/form/form-list-file.component"
import { DatabaseFileReference } from "idun-common/models/database-file"
import { environment } from "environments/environment"
import { AutoFormInfo } from "idun-common/auto-form-info"
import { AutoFormComponent } from "idun-common/auto-form.component"

@Injectable()
export class StaffService extends ExtendedService<Staff> {
	subscriber = new Subscriber()
	constructor(
		http: Http,
		staffGroupService: StaffGroupService,
		userService: UserService,
		l: LanguageService
	) {
		super(http, l,
			Staff,
			"api/Staff",
			"staff",
			() => l.staff
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.Admin)
		this.icon = "Doctor"
		this.color = "var(--color-staff)"
		this.showListLink = false
		this.subscriber.subscribe(userService.user$, user => {
			if (userService.staffGroup || !user)
				return
			this.subscriber.subscribe(this.getModel$(user.StaffID), (userStaff) => {
				if (userService.staffGroup && userService.staffGroup.DBID === userStaff.StaffRoleID)
					return
				this.subscriber.subscribe(staffGroupService.getModel$(userStaff.StaffRoleID), (group) => {
					userService.staffGroup = group
				}, "group")
			}, "staff")
		})

		userService.loadUser()
		this.loadModels()
		this.formInfo = new AutoFormInfo(
			(model: Staff) => {
				if (model.Name)
					return model.Name
				return l.staff.namePlural
			},
			[{
				rows: [
					new FormTextRow("ID", {
						label: () => l.common.id,
						hint: () => l.staff.idHint,
						requirements: [
							ValidationRequirement.ValueRequired(l),
							new ValidationRequirement((value, model) => {
								if (this.models.find(x => x.ID === value && x.DBID !== model.DBID))
									return () => l.staff.idMustBeUnique
							})
						],
					}),
					new FormTextRow("Name", {
						label: () => l.common.name,
						hint: () => l.staff.nameHint,
						requirements: [ValidationRequirement.ValueRequired(l)],
						width: 60,
						change: (model: Staff, value: string) => {
							if (value)
								model.Abbreviation = abbreviationFor(value,
									this.models.filter(e => e.DBID !== model.DBID).map(e => e.Abbreviation))
							else if (!model.Name)
								model.Abbreviation = ""
						}
					}),
					new FormTextRow("Abbreviation", {
						label: () => l.common.abbreviation,
						hint: () => l.staff.abbreviationHint,
						requirements: [
							ValidationRequirement.ValueRequired(l),
							new ValidationRequirement((value, model) => {
								if (this.models.find(x => x.Abbreviation === value && x.DBID !== model.DBID))
									return () => l.commonForm.abbreviationMustBeUnique
							})
						],
						width: 40,
						cssClasses: "padding-left"
					})]
			},
			{
				getHeader: (model) => this.l.common.contact +
					": " + (model.getPrimaryPhone().Number || "") +
					" - " + (model.Email || ""),
				rows: [
					new FormTextRow("Email", {
						label: () => l.common.email,
						requirements: [
							ValidationRequirement.ValueRequired(l),
							ValidationRequirement.MustBeEmail(l)
						],
					}),
					new FormTelephoneRow("TelephoneNumbers", {
						label: () => l.common.telephone,
						requirements: [ValidationRequirement.ValueRequired(l)],
					}),
					new FormTextareaRow("Address", {
						label: () => l.common.address,
						requirements: [ValidationRequirement.ValueRequired(l)],
					})
				]
			},
			{
				getHeader: () => this.l.staff.hiringConditions,
				rows: [
					new FormTextRow("SalaryBankAccount", {
						label: () => l.staff.salaryBankAccount,
						hint: () => l.staff.salaryBankAccountHint
					}),
					new FormTextRow("PensionInfo", {
						label: () => l.staff.pensionInfo,
						hint: () => l.staff.pensionInfoHint
					}),
					new FormListFileRow("Files", {
						label: () => l.patientNote.files
					})]
			},
			{
				getHeader: () => this.l.staff.function,
				rows: [
					new FormSelectModelRow("StaffRoleID", {
						label: () => l.staffGroup.nameSingular,
						hint: () => l.staff.staffGroupHint,
						requirements: [ValidationRequirement.ValueRequired(l)],
						service: staffGroupService,
						change: (staff, value) => {
							const role = staffGroupService.getModel(value)
							if (role)
								staff.Competences = new CompetenceSet(role.Competences)
						},
						things: [
							new LinkThing<Staff>(() => l.common.add, () => "staff-group/add", (m, d) => !d, "positive")
						]
					}),
					new FormExtraRow({
						label: () => l.staff.workHours,
						hint: () => l.staff.workHoursHint,
						cssClasses: "padding-right",
						things: [
							new LabelThing<Staff>(staff =>
								l.format(l.staff.xWeeklyWorkHours, staff.getWorkHours().toLocaleString(undefined, { maximumFractionDigits: 1 }))
							),
							new ButtonThing<Staff>(
								() => this.l.staff.workHoursSetup,
								(_, c) => c.currentPopup = "work-hours")
						]
					}),
					new FormExtraRow({
						label: () => l.staff.competences,
						hint: () => l.staff.competencesHint,
						things: [
							new ButtonThing<Staff>(
								(_, display) => display ? this.l.staff.viewCompetences : this.l.staff.competenceSetup,
								(_, c) => c.currentPopup = "competences"
							)
						]
					})
				]
			}],
			{
				onSubmit: async (model: Staff, component: AutoFormComponent<Staff>) => {
					component.isSaving = true
					await this.uploadUnsavedFiles(model, component)
					component._onSubmit()
				}
			}
		)
		this.setupList({
			entryHeader: (model) => model.Name,
			contentList: [(model) => {
				const role = staffGroupService.getModel(model.StaffRoleID)
				if (role)
					return model.Abbreviation + " - " + role.Title
				return model.Abbreviation
			}]
		})
		this.listInfo.searchTypes.push("Name")
		this.listInfo.searchTypes.push("Abbreviation")
		this.listInfo.searchTypes.push("Title")
	}

	async uploadUnsavedFiles(
		entry: Staff,
		component: any
	) {
		for (let i = 0; i < entry.Files.length; i++) {
			const file = entry.Files[i]
			if (file instanceof DatabaseFileReference)
				continue

			component.saveText = this.l.format(this.l.patientNote.uploadingX, file.Name)
			entry.Files[i] = await this.uploadFile(file.Name, file.Data)
		}
		component.saveText = undefined
	}

	uploadFile(fileName: string, fileData: File): Promise<DatabaseFileReference> {
		// test
		if (!environment.production)
			return new Promise((resolve) => {
				setTimeout(() => {
					resolve(new DatabaseFileReference({ DBID: Math.floor(Math.random() * 100000), Name: fileName }))
				}, 1000)
			})
		// endtest

		return new Promise((resolve) => {
			const formData = new FormData()
			formData.append("files", fileData, fileName)

			const request = new XMLHttpRequest()
			request.onload = (e) => {
				resolve(new DatabaseFileReference(JSON.parse((<any>e.target).response)))
			}
			// TODO: handle errors
			request.open("POST", "/api/PatientNote/UploadFile")
			request.send(formData)
		})
	}

	isStaffAvailable(
		staffId: number,
		starttime: Date,
		endtime: Date
	): Promise<boolean> {
		return this.baseRequest(
			"IsStaffAvailable",
			{ start: starttime, end: endtime, id: staffId })
	}
}
