﻿import { TimeInterval } from "idun-common/models/time-interval"
import { WorkRequest, WorkRequestInterval, WorkRequestIntervalType, WorkRequestType } from "idun-common/models/work-request"
import { ModelTestData } from "./model-test-data"
import { newResetDateFrom } from "utility/date-helper"
import { workIntervalTestData, getWorkIntervalTestDataFor } from "./work-interval-test-data"
import { WorkInterval } from "idun-common/models/work-interval"

const baseUrl = "api/workrequest/"

export const workRequestTestData = [
	new WorkRequest({
		DBID: 1,
		StaffID: 1,
		Type: WorkRequestType.Vacation,
		Changes: [
			new WorkRequestInterval({
				Type: WorkRequestIntervalType.Free,
				Times: TimeInterval.from(new Date(2000, 1, 1, 8), new Date(2000, 1, 1, 18))
			})
		]
	}),
	new WorkRequest({
		DBID: 2,
		StaffID: 2,
		Type: WorkRequestType.Sick,
		Changes: [
			new WorkRequestInterval({
				Type: WorkRequestIntervalType.Free,
				Times: TimeInterval.from(newResetDateFrom({ hours: 7 }), newResetDateFrom({ hours: 17 }))
			})
		]
	}),
	new WorkRequest({
		DBID: 3,
		StaffID: 3,
		Type: WorkRequestType.Vacation,
		Changes: [
			new WorkRequestInterval({
				Type: WorkRequestIntervalType.Free,
				Times: TimeInterval.from(newResetDateFrom({ days: 7 }), newResetDateFrom({ days: 14 }))
			})
		]
	}),
]

const otherResponses = {}

otherResponses[baseUrl + "acceptrequest"] = (parameters: { id: number }) => {
	const request = workRequestTestData.find(x => x.DBID === parameters.id)
	for (const change of request.Changes)
		implementChange(change, request.StaffID)
	const index = workRequestTestData.findIndex(x => x.DBID === parameters.id)
	workRequestTestData.splice(index, 1)
	return JSON.stringify(true)
}

otherResponses[baseUrl + "rejectrequest"] = (parameters: { id: number }) => {
	const index = workRequestTestData.findIndex(x => x.DBID === parameters.id)
	workRequestTestData.splice(index, 1)
	return JSON.stringify(true)
}

export function WorkRequestResponses() {
	return {
		...ModelTestData.commonResponses(WorkRequest, baseUrl, workRequestTestData),
		...otherResponses
	}
}

function implementChange(change: WorkRequestInterval, staffId: number) {
	if (change.Type === WorkRequestIntervalType.Free)
		removeWorkIntervals(change.Times, staffId)
	else
		addWorkInterval(change, staffId)
}

function removeWorkIntervals(freeInterval: TimeInterval, staffId: number) {
	removeContainedIntervals(freeInterval, staffId)
	adjustIntersectingIntervals(freeInterval, staffId)
}

function removeContainedIntervals(freeInterval: TimeInterval, staffId: number) {
	const containedIntervals = getWorkIntervalTestDataFor(staffId, freeInterval)
		.filter(x => freeInterval.contains(x.Times))
	for (const interval of containedIntervals)
		removeWorkInterval(interval)
}

function removeWorkInterval(interval: WorkInterval) {
	const index = workIntervalTestData.indexOf(interval)
	workIntervalTestData.splice(index, 1)
}

function adjustIntersectingIntervals(freeInterval: TimeInterval, staffId: number) {
	const intersectingIntervals = getWorkIntervalTestDataFor(staffId, freeInterval)
	for (const interval of intersectingIntervals)
		adjustIntersectingInterval(interval, freeInterval)
}

function adjustIntersectingInterval(interval: WorkInterval, freeInterval: TimeInterval) {
	if (interval.Times.contains(freeInterval))
		splitInterval(interval, freeInterval)
	else if (interval.Times.contains(freeInterval.StartTime))
		interval.Times.EndTime = new Date(freeInterval.StartTime)
	else
		interval.Times.StartTime = new Date(freeInterval.EndTime)
}

function splitInterval(interval: WorkInterval, freeInterval: TimeInterval) {
	const endTime = interval.Times.EndTime
	interval.Times.EndTime = new Date(freeInterval.StartTime)
	const newWorkInterval = new WorkInterval({
		DBID: workIntervalTestData.length,
		StaffID: interval.StaffID,
		Times: TimeInterval.from(freeInterval.EndTime, endTime)
	})
	workIntervalTestData.push(newWorkInterval)
}

function addWorkInterval(change: WorkRequestInterval, staffId: number) {
	const intersectingIntervals = getWorkIntervalTestDataFor(staffId, change.Times)
	const earliestTime = findEarliestTime(intersectingIntervals, change.Times.StartTime)
	const latestTime = findLatestTime(intersectingIntervals, change.Times.EndTime)
	for (const interval of intersectingIntervals)
		removeWorkInterval(interval)
	const newWorkInterval = new WorkInterval({
		DBID: workIntervalTestData.length,
		StaffID: staffId,
		Times: TimeInterval.from(earliestTime, latestTime)
	})
	workIntervalTestData.push(newWorkInterval)
}

function findEarliestTime(intervals: WorkInterval[], base: Date) {
	return intervals.reduce((previous, current) => {
		if (current.Times.StartTime.getTime() < previous.getTime())
			return current.Times.StartTime
		return previous
	}, base)
}

function findLatestTime(intervals: WorkInterval[], base: Date) {
	return intervals.reduce((previous, current) => {
		if (previous.getTime() < current.Times.EndTime.getTime())
			return current.Times.EndTime
		return previous
	}, base)
}
