﻿import { SearchableIdunModel } from "./idun-model"

export class ProcedureType extends SearchableIdunModel {
	DBID: number
	Title: string
	Description: string
	Hue: number

	constructor(source?: any) {
		super()
		this.Hue = Math.random()
		for (const prop in source) this[prop] = source[prop]
	}

	getTitle(): string {
		return this.Title
	}

	searchAttributes() {
		return [this.Title]
	}
}
