﻿import { IdunModel } from "./idun-model"

export class StaffNotification extends IdunModel {
	DBID: number
	Title: string
	Message: string
	IdunRoutePath: string
	TargetedStaffGroupIds: number[]
	TargetedStaffIds: number[]
	ReadByStaffIds: number[]
	Created: Date
	Type: StaffNotificationType

	constructor(source?: any) {
		super()
		for (const prop in source) this[prop] = source[prop]
		if (source.Created)
			this.Created = new Date(source.Created)
	}

	getTitle(): string {
		return this.Message.split("\n")[0].substring(0, 50)
	}
}

export enum StaffNotificationType { PatientArrival, EdifactReferral }
