import { Component, Input, Output, EventEmitter } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { CalendarEntryProvider } from "./calendar-entry-provider"
import { CalendarEntry } from "./calendar-entry"
import { DateHelper } from "utility/date-helper"

@Component({
	selector: "idun-multi-calendar-day-rows",
	templateUrl: "multi-calendar-day-rows.component.html",
	styleUrls: ["multi-calendar-day-rows.component.scss", "multi-calendar-rows.component.scss"]
})
export class MultiCalendarDayRowsComponent {
	@Input() date: Date
	@Input() showDates = true

	_entryProviders: CalendarEntryProvider[]
	@Input() set entryProviders(value: CalendarEntryProvider[]) {
		this._entryProviders = value
	}
	get entryProviders(): CalendarEntryProvider[] {
		return this._entryProviders.filter(e => e)
	}

	@Output() addEntry = new EventEmitter<{ provider: CalendarEntryProvider, time: Date }>()
	@Output() selectEntry = new EventEmitter<{ provider: CalendarEntryProvider, entry: CalendarEntry }>()

	shownHours: number[] = []
	firstHour = 6
	lastHour = 18

	constructor(
		public l: LanguageService
	) {
		this.updateShownHours()
	}

	private updateShownHours() {
		this.shownHours = []
		for (let i = this.firstHour; i < this.lastHour; i++)
			this.shownHours.push(i)
	}

	clickProvider(provider: CalendarEntryProvider) {
		if (provider.onTitleClick)
			provider.onTitleClick(this.date)
	}

	getDateString() {
		if (this.showDates)
			return this.l.dayName(this.date) + ` - ${this.date.getDate()}/${this.date.getMonth() + 1}`
		return this.l.dayName(this.date) + " - " + this.l.date.week + " " + DateHelper.getWeekNumber(this.date)
	}

	toggleFirstHour() {
		this.firstHour = this.firstHour === 0
			? 6
			: 0
		this.updateShownHours()
	}

	toggleLastHour() {
		this.lastHour = this.lastHour === 24
			? 18
			: 24
		this.updateShownHours()
	}

	shouldShowMinutes() {
		return this.firstHour !== 0 && this.lastHour !== 24
	}

	newEntry(hour: number, provider: CalendarEntryProvider) {
		return { provider: provider, time: new Date(this.date.getTime() + hour * 60 * 60 * 1000) }
	}
}
