import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { LanguageService } from "idun-layout/language.service"
import { StaffGroup, Permission } from "idun-common/models/staff-group"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { FormListStringRow } from "idun-layout/form/form-list-string.component"
import { OptionInfo } from "idun-layout/form/form-row"
import { FormCompetencesRow } from "idun-common/form/form-competences.component"
import { UserService } from "idun-main/user.service"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"

const permissions = {
	"Admin": "adminPrivileges",
	"ReadPatients": "readpatients",
	"EditPatients": "editpatients",
	"EditJournalEntries": "editjournalEntries",
	"EditJournalTemplates": "editjournalTemplates",
	"EditResources": "editResources",
	"EditProcedures": "editProcedures",
	"EditTreatmentCodes": "editTreatmentCodes",
	"EditPartners": "editPartners",
	"EditStatus": "editStatus",
	"EditStatusType": "editStatusType",
	"ViewStatistics": "viewStatistics",
	"EditEconomics": "editEconomics",
}

@Injectable()
export class StaffGroupService extends ExtendedService<StaffGroup> {
	constructor(
		http: Http,
		userService: UserService,
		l: LanguageService
	) {
		super(http, l,
			StaffGroup,
			"api/StaffGroup",
			"staff-group",
			() => l.staffGroup
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.Admin)
		this.icon = "Doctor"
		this.color = "var(--color-fundamental)"

		this.setupForm({
			rows: [
				new FormTextRow("Title", {
					label: () => l.common.title,
					requirements: [ValidationRequirement.ValueRequired(l)],
				}),
				(model) => { return this.l.staffGroup.permissions },
				new FormListStringRow("Permissions", {
					checkboxMode: true,
					options: Object.keys(permissions).map(key =>
						new OptionInfo(key, () => l.staffGroup[permissions[key]], () => l.staffGroup["hintPermission" + key]))
				}),
				(model) => { return this.l.staffGroup.competences },
				new FormCompetencesRow("Competences")
			]
		})
		this.setupList({
			entryHeader: (model) => model.Title,
			contentList: [(model) => ""]
		})
	}
}
