﻿import { StaffGroup } from "idun-common/models/staff-group"
import { ModelTestData } from "./model-test-data"

const baseUrl = "api/staffgroup/"

const list = [
	new StaffGroup({
		DBID: 1,
		Title: "Admin",
		Permissions: [
			"ReadPatients",
			"EditPatients",
			"Admin",
			"EditProcedures",
			"EditStatus",
			"EditStatusType",
			"EditResources",
			"EditPartners",
			"EditJournalEntries",
			"EditJournalTemplates",
			"EditTreatmentCodes",
			"ViewStatistics"
		]
	}),
	new StaffGroup({
		DBID: 2,
		Title: "Doktor",
		Competences: { AllowedProcedureTypes: [1, 2, 3] },
		Permissions: [
			"ReadPatients",
			"EditPatients"
		]
	}),
	new StaffGroup({
		DBID: 3,
		Title: "Sekretær",
		Permissions: [
			"ReadPatients"
		]
	})
]

ModelTestData.loadFromLocalStorage(baseUrl, list, StaffGroup)

export function StaffGroupsResponses() {
	return {
		...ModelTestData.commonLocalStorageResponses(StaffGroup, baseUrl, list)
	}
}
