﻿import { Component, OnInit, Input, OnDestroy } from "@angular/core"
import { search } from "utility/search-helper"
import { IdunModel } from "./models/idun-model"
import { ExtendedService } from "idun-common/extended-service"
import { LanguageService } from "idun-layout/language.service"
import { Subscriber } from "utility/subscriber"
import { ExtraThing } from "./form/form-dynamic-extra.component"
import { DisplaySetup } from "./filter-list.component"

@Component({
	selector: "idun-nested-list",
	templateUrl: "nested-list.html"
})
export class NestedListComponent<Entry extends IdunModel, Group extends IdunModel> implements OnInit, OnDestroy {
	subscriber = new Subscriber()
	@Input() entryService: ExtendedService<Entry>
	@Input() groupService: ExtendedService<Group>
	@Input() groupVariable: string
	@Input() alternateThings: ExtraThing<any>[]

	@Input() showHeader = true
	@Input() showSearch = true
	@Input() pageSize = 20
	page = 0
	scrollTimestamp: number

	entries: Entry[]
	entryCount = 0

	listDisplaySetup = new DisplaySetup(
		() => this.groupService.translations().namePlural,
		() => this.entryService.translations().namePlural,
		() => this.groupService.ngUrl + "/add",
		() => this.entryService.ngUrl + "/add",
	)

	listGroups: any[] = []

	constructor(
		public l: LanguageService
	) { }

	ngOnInit(): void {
		if (!this.entryService)
			return console.error("Missing entry service for nested list")
		if (!this.groupService)
			return console.error("Missing group service for nested list")
		if (!this.groupVariable)
			return console.error("Missing group variable for nested list")
		if (!this.entryService.hasEditPermission())
			this.listDisplaySetup.entryAddLink = undefined
		if (!this.groupService.hasEditPermission())
			this.listDisplaySetup.groupAddLink = undefined
		this.subscriber.subscribe(this.groupService.getModels$(), list => {
			this.listGroups = list.map(this.mapGroup)
		}, "groups")
		this.subscriber.subscribe(this.entryService.count$(), count => {
			this.entryCount = count
		}, "count")
		this.updateSubscription()
	}

	ngOnDestroy() {
		this.subscriber.unsubscribe()
	}

	mapGroup = (group: Group) => {
		return {
			title: () => group.getTitle(),
			contains: (entry) => entry[this.groupVariable] === group.DBID,
			link: () => {
				return {
					title: this.l.common.groupDetails,
					destination: this.groupService.ngUrl + "/" + group.DBID
				}
			}
		}
	}

	updateSubscription(query: string = "") {
		const pageSize = this.entryService.loadAll ? 0 : this.pageSize
		const observable = pageSize > 0
			? this.entryService.loadPage(this.page, pageSize, query)
			: this.entryService.getModels$()
		this.subscriber.subscribe(observable,
			(list: Entry[]) => {
				this.entries = list
			}, "models")
	}

	loadPageFromScroll(event) {
		if (!this.scrollReachedEnd(event))
			return
		if (Date.now() - this.scrollTimestamp < 1000)
			return

		this.scrollTimestamp = Date.now()
		this.pageSize += 20
		this.updateSubscription()
	}

	scrollReachedEnd(event: any) {
		const limit = event.target.scrollHeight - event.target.clientHeight - 50
		return event.target.scrollTop > limit
	}
}
