﻿import { Component, ElementRef, OnInit } from "@angular/core"
import { Router } from "@angular/router"
import { BaseModelSubscriber } from "idun-common/base-model-subscriber"
import { NavigationHandler } from "../../../idun-layout/navigation-handler"

import { Bookmark } from "idun-common/models/bookmark"
import { BookmarkService } from "idun-main/model-services/bookmark.service"
import { LayoutService } from "idun-layout/layout.service"
import { LanguageService } from "idun-layout/language.service"

@Component({
	selector: "idun-bookmark-bar",
	templateUrl: "bookmark-bar.component.html",
	styleUrls: ["bookmark-bar.component.scss"]
})
export class BookmarkBarComponent extends BaseModelSubscriber implements OnInit {
	navigater: NavigationHandler
	bookmarks: Bookmark[] = []

	constructor(
		private myElement: ElementRef,
		private router: Router,
		private layoutService: LayoutService,
		private bookmarkService: BookmarkService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit() {
		this.subscribeToList(this.bookmarkService, (bookmarks: Bookmark[]) => {
			this.bookmarks = bookmarks
		})
	}

	showDoubleBookmarkButton() {
		return this.layoutService.navigationController.twoPagesOpen
			&& !this.layoutService.sizeController.isSinglePageLayout
	}

	bookmarkCurrentPages() {
		const bookmark = new Bookmark()
		bookmark.IdunRoutePath = this.bookmarkService.generaliseUrl(this.router.url.substring(1))
		bookmark.IconPath = "Settings"
		bookmark.MouseoverText = "New bookmark"
		this.bookmarkService.add(bookmark)
	}

	expandededit = false

	expandEdit() {
		if (!this.layoutService.sizeController.isSinglePageLayout)
			this.expandededit = true
	}

	collapseEdit() {
		this.expandededit = false
	}

	mouseover(side: string) {
		this.layoutService.showShadowOverlays(side)
	}

	mouseout(side: string) {
		this.layoutService.hideShadowOverlays(side)
	}
}
