import { Injectable } from "@angular/core"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { PartnerService } from "idun-main/model-services/partner.service"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { StaffService } from "idun-main/model-services/staff.service"
import { PatientEvent } from "idun-common/models/patient-event"
import { Warning } from "idun-layout/components/warning-box.component"
import { LanguageService } from "idun-layout/language.service"
import { ResourceService } from "idun-main/model-services/resource.service"

@Injectable()
export class ProcedureEventAnalysisService {

	constructor(
		private partnerService: PartnerService,
		private procedureService: ProcedureService,
		private resourceService: ResourceService,
		private staffService: StaffService,
		private l: LanguageService,
	) { }

	analyseIn(patientEvent: PatientEvent) {
		const analysises = new Map<ProcedureEvent, ProcedureEventAnalysis>()
		for (const procedureEvent of patientEvent.ProcedureEvents)
			analysises.set(procedureEvent, this.analyse(procedureEvent))
		return analysises
	}

	analyse(procedureEvent: ProcedureEvent): ProcedureEventAnalysis {
		const analysis = new ProcedureEventAnalysis(this.l)
		if (!procedureEvent)
			return analysis
		this.procedureIsSupported(procedureEvent, analysis)
		this.isMissingStaff(procedureEvent, analysis)
		this.isMissingResource(procedureEvent, analysis)
		this.isMissingCompetence(procedureEvent, analysis)
		this.isMissingRightResource(procedureEvent, analysis)
		if (!procedureEvent.DBID) {
			this.isStaffAvailable(procedureEvent, analysis)
			this.isResourceAvailable(procedureEvent, analysis)
		}
		return analysis
	}

	private async procedureIsSupported(
		procedureEvent: ProcedureEvent,
		analysis: ProcedureEventAnalysis
	) {
		if (!procedureEvent.PartnerId || !procedureEvent.ProcedureId)
			return
		const partner = await this.partnerService.model(procedureEvent.PartnerId)
		const changes = partner.changesFor(procedureEvent.ProcedureId)
		analysis.isSupported = !changes || changes.Supported
	}

	private isMissingStaff(
		procedureEvent: ProcedureEvent,
		analysis: ProcedureEventAnalysis
	) {
		analysis.staffWarnings.missingStaff.show = procedureEvent.OccupiedStaff.length === 0
	}

	private isMissingResource(
		procedureEvent: ProcedureEvent,
		analysis: ProcedureEventAnalysis
	) {
		analysis.resourceWarnings.missingResource.show = procedureEvent.OccupiedResources.length === 0
	}

	private async isMissingCompetence(
		procedureEvent: ProcedureEvent,
		analysis: ProcedureEventAnalysis
	) {
		if (!procedureEvent.ProcedureId)
			return
		const procedure = await this.procedureService.model(procedureEvent.ProcedureId)
		analysis.staffWarnings.missingRightCompetence.show = !procedureEvent.OccupiedStaff
			.map(id => this.staffService.getModel(id))
			.filter(x => x)
			.some(x => x.Competences.canPerform(procedure))
	}

	private async isMissingRightResource(
		procedureEvent: ProcedureEvent,
		analysis: ProcedureEventAnalysis
	) {
		if (!procedureEvent.ProcedureId)
			return
		const procedure = await this.procedureService.model(procedureEvent.ProcedureId)
		analysis.resourceWarnings.missingRightResource.show = !procedure.ResourceRequirements
			.includesAny(procedureEvent.OccupiedResources)
	}

	private async isStaffAvailable(
		procedureEvent: ProcedureEvent,
		analysis: ProcedureEventAnalysis
	) {
		for (const staffId of procedureEvent.OccupiedStaff)
			this.staffService.isStaffAvailable(staffId, procedureEvent.Times.StartTime, procedureEvent.Times.EndTime)
				.then(result => {
					if (!result)
						analysis.staffWarnings.selectedStaffUnavailable.show = true
				})
	}

	private async isResourceAvailable(
		procedureEvent: ProcedureEvent,
		analysis: ProcedureEventAnalysis
	) {
		for (const resourceId of procedureEvent.OccupiedResources)
			this.resourceService.isResourceAvailable(resourceId, procedureEvent.Times.StartTime, procedureEvent.Times.EndTime)
				.then(result => {
					if (!result)
						analysis.resourceWarnings.selectedResourceUnavailable.show = true
				})
	}
}

export class ProcedureEventAnalysis {
	constructor(
		l: LanguageService,
		public isSupported = true,
		public staffWarnings = {
			missingStaff: new Warning(() => l.booking.missingStaffWarning),
			missingRightCompetence: new Warning(() => l.booking.missingRightCompetenceWarning),
			selectedStaffUnavailable: new Warning(() => l.booking.selectedStaffIsNotAvailableWarning),
		},
		public resourceWarnings = {
			missingResource: new Warning(() => l.booking.missingResourceWarning),
			missingRightResource: new Warning(() => l.booking.missingRightResourceWarning),
			selectedResourceUnavailable: new Warning(() => l.booking.selectedResourceIsNotAvailableWarning),
		}
	) { }

	hasProblems() {
		let hasStaffWarning = false
		for (const key in this.staffWarnings)
			hasStaffWarning = hasStaffWarning || this.staffWarnings[key].show
		let hasResourceWarning = false
		for (const key in this.resourceWarnings)
			hasResourceWarning = hasResourceWarning || this.resourceWarnings[key].show
		return !this.isSupported || hasStaffWarning || hasResourceWarning
	}
}
