import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { Observable, BehaviorSubject } from "rxjs"
import { RequestHelper } from "utility/request-helper"

import { Patient } from "idun-common/models/patient"
import { PatientService } from "idun-main/model-services/patient.service"

import { IdunUser } from "idun-common/models/idun-user"
import { StaffGroup, Permission } from "idun-common/models/staff-group"
import { PatientEvent } from "idun-common/models/patient-event"

export class ClinicSettings {
	ClinicName: string
	Location: string
	Website: string
	Timezone: string
	Currency = "DKK"

	constructor(source?) {
		for (const key in source) this[key] = source[key]
	}
}
export class Settings {
	website: string
	backgroundImage: string
}
export class FrontendState {
	currentPatient: number
	previousPatients: { DBID: number, Name: string }[] = []
}

@Injectable()
export class UserService {
	private lastUserUpdate: Date
	user: IdunUser
	staffGroup: StaffGroup
	private _user$: BehaviorSubject<IdunUser>
	get user$(): Observable<IdunUser> {
		return this._user$.asObservable()
	}
	private settings: Settings
	private _settings$: BehaviorSubject<Settings>
	get settings$(): Observable<Settings> {
		return this._settings$.asObservable()
	}
	clinicSettings: ClinicSettings
	private _clinicSettings$: BehaviorSubject<ClinicSettings>
	get clinicSettings$(): Observable<ClinicSettings> {
		return this._clinicSettings$.asObservable()
	}
	private frontendState: FrontendState
	private _frontendState$: BehaviorSubject<FrontendState>
	get frontendState$(): Observable<FrontendState> {
		return this._frontendState$.asObservable()
	}
	private clinicName: string
	private _clinicName$: BehaviorSubject<string>
	get clinicName$(): Observable<string> {
		return this._clinicName$.asObservable()
	}

	private patientSubscription
	private currentId
	public currentPatient: Patient
	private _currentPatient$: BehaviorSubject<Patient>
	get currentPatient$(): Observable<Patient> {
		return this._currentPatient$.asObservable()
	}
	public patientService: PatientService

	constructor(
		public http: Http
	) {
		this._user$ = <BehaviorSubject<IdunUser>>new BehaviorSubject(undefined)
		this._settings$ = <BehaviorSubject<Settings>>new BehaviorSubject(undefined)
		this._clinicSettings$ = <BehaviorSubject<ClinicSettings>>new BehaviorSubject(new ClinicSettings())
		this._frontendState$ = <BehaviorSubject<FrontendState>>new BehaviorSubject(undefined)
		this._currentPatient$ = <BehaviorSubject<Patient>>new BehaviorSubject(undefined)
		this._clinicName$ = <BehaviorSubject<string>>new BehaviorSubject("")
	}

	loadClinicSettings(): Promise<string> {
		return RequestHelper.postRequest(this.http, "api/Clinic/GetSettings", null).then((settings) => {
			this.clinicSettings = settings
			this._clinicSettings$.next(settings)
			return settings
		})
	}

	updateClinicSettings(newSettings) {
		RequestHelper.postRequest(this.http, "api/Clinic/UpdateSettings", newSettings).then((settings) => {
			this.clinicSettings = settings
			this._clinicSettings$.next(settings)
			return settings
		})
	}

	loadClinicName(): Promise<string> {
		return RequestHelper.postRequest(this.http, "api/Clinic/GetCurrentClinic", null).then((clinic) => {
			this.clinicName = clinic
			this._clinicName$.next(this.clinicName)
			return clinic
		})
	}

	loadUser(): Promise<IdunUser> {
		return RequestHelper.postRequest(this.http, "api/User/Get", null).then((user) => {
			this.updateData(user)
			return user
		})
	}

	updateUser() {
		RequestHelper.postRequest(this.http, "api/User/Get", null).then((user) => {
			this.updateData(user)
		})
	}

	updateData(user: IdunUser) {
		this.user = user
		this._user$.next(this.user)
		this.settings = JSON.parse(this.user.Settings)
		this._settings$.next(this.settings)
		this.frontendState = JSON.parse(this.user.FrontendState || `{"previousPatients":[]}`)
		this._frontendState$.next(this.frontendState)
		if (this.frontendState.currentPatient)
			this.selectPatient(this.frontendState.currentPatient)
	}

	hasPermission(permission: Permission) {
		if (!this.staffGroup)
			return true
		return this.staffGroup.hasPermission(permission)
	}

	updateSettings(key, value) {
		const settings = JSON.parse(this.user.Settings) || {}
		settings[key] = value
		this.user.Settings = JSON.stringify(settings)
		RequestHelper.postRequest(this.http, "api/User/SetSettings", { state: this.user.Settings })
		this.updateData(this.user)
	}

	updateFrontendState(key, value) {
		const frontendState = JSON.parse(this.user.FrontendState || `{"previousPatients":[]}`)
		frontendState[key] = value
		this.user.FrontendState = JSON.stringify(frontendState)
		RequestHelper.postRequest(this.http, "api/User/SetFrontendState", { state: this.user.FrontendState })
		this.updateData(this.user)
	}

	getCurrentPatientDBID() {
		if (this.currentPatient)
			return this.currentPatient.DBID
		return undefined
	}

	selectPatient(id: number) {
		if (!this.patientService)
			return
		id = +id
		if (this.currentPatient && (+this.currentPatient.DBID === id))
			return // don't do anything if the patient is already selected
		if (this.currentId === id)
			return
		if (this.patientSubscription)
			this.patientSubscription.unsubscribe() // unsubscribe from the previous patient if any
		this.currentId = id
		this.frontendState.currentPatient = id
		this.patientSubscription = this.patientService.getModel$(id).subscribe((patient: Patient) => {
			if (this.currentPatient && this.currentPatient.DBID !== patient.DBID)
				this.frontendState.previousPatients.unshift({ DBID: this.currentPatient.DBID, Name: this.currentPatient.Name })
			this.currentPatient = patient
			this._currentPatient$.next(this.currentPatient)
			this.currentId = patient.DBID
			this.frontendState.currentPatient = patient.DBID

			for (let i = 0; i < this.frontendState.previousPatients.length; i++)
				if (this.currentPatient.DBID === this.frontendState.previousPatients[i].DBID)
					this.frontendState.previousPatients.splice(i, 1)

			if (this.frontendState.previousPatients.length > 5)
				this.frontendState.previousPatients.shift()

			this.user.FrontendState = JSON.stringify(this.frontendState)
			RequestHelper.postRequest(this.http, "api/User/SetFrontendState", { state: this.user.FrontendState })
			this.updateData(this.user)
		})
	}

	changePasswords(
		oldPassword: string,
		newPassword: string
	): Promise<"success" | "failure"> {
		return RequestHelper.postRequest(
			this.http,
			"api/User/UpdatePassword",
			{ oldPassword: oldPassword, newPassword: newPassword })
	}
}
