export class EdifactHelper {
	static segmentTerminator = "'"
	static dataElementSeperator = "+"
	static dataComponentSeperator = ":"
	static releaseCharacter = "?"

	static getSegments(message: string): EdifactSegment[] {
		const rawSegments = this.getRawSegments(message)
		const segments: EdifactSegment[] = []
		let currentSequence = 0
		let currentSegmentGroup = 0
		let currentSegmentGroupRepetition = 0
		let currentSegmentGroupTagCounts = {}
		for (let i = 0; i < rawSegments.length; i++) {
			const rawSegment = rawSegments[i].trim()
			const segment = new EdifactSegment()
			segment.tag = rawSegment.substr(0, 3)
			segment.elements = this.getRawElements(rawSegment)

			if (currentSegmentGroupTagCounts[segment.tag])
				currentSegmentGroupTagCounts[segment.tag]++
			else
				currentSegmentGroupTagCounts[segment.tag] = 1

			if (segment.tag === "SEQ") {
				currentSequence = +segment.elements[2]
				currentSegmentGroup = 0
				currentSegmentGroupRepetition = 0
				currentSegmentGroupTagCounts = {}
			} else if (segment.tag.substr(0, 1) === "S" && +segment.tag.substr(1, 3)) {
				currentSegmentGroupTagCounts = {}
				if (currentSegmentGroup === +segment.tag.substr(1, 3))
					currentSegmentGroupRepetition++
				else
					currentSegmentGroupRepetition = 0
				currentSegmentGroup = +segment.tag.substr(1, 3)
			}
			segment.sequence = currentSequence
			segment.segmentGroup = currentSegmentGroup
			segment.segmentGroupRepetition = currentSegmentGroupRepetition
			segment.repetition = currentSegmentGroupTagCounts[segment.tag]
			segments.push(segment)
		}
		return segments
	}

	static getRawElements(rawSegment: string): string[] {
		const rc = rawSegment.split(this.releaseCharacter + this.dataElementSeperator)
		const allRawElements = [""]
		for (let i = 0; i < rc.length; i++) {
			const rawElements = rc[i].split(this.dataElementSeperator)
			allRawElements.push(allRawElements.pop() + (i ? this.dataElementSeperator : "") + rawElements.shift())
			allRawElements.push(...rawElements)
		}
		if (allRawElements === [""])
			return []
		return allRawElements
	}

	static getRawSegments(message: string): string[] {
		const rc = message.split(this.releaseCharacter + this.segmentTerminator)
		const allRawSegments = [""]
		for (let i = 0; i < rc.length; i++) {
			const rawSegments = rc[i].split(this.segmentTerminator)
			allRawSegments.push(allRawSegments.pop() + (i ? this.segmentTerminator : "") + rawSegments.shift())
			allRawSegments.push(...rawSegments)
		}
		if (allRawSegments === [""])
			return []
		return allRawSegments
	}

	static getRawComponents(message: string): string[] {
		const rc = message.split(this.releaseCharacter + this.dataComponentSeperator)
		const allRawComponents = [""]
		for (let i = 0; i < rc.length; i++) {
			const rawCompnents = rc[i].split(this.dataComponentSeperator)
			allRawComponents.push(allRawComponents.pop() + (i ? this.dataComponentSeperator : "") + rawCompnents.shift())
			allRawComponents.push(...rawCompnents)
		}
		if (allRawComponents === [""])
			return []
		return allRawComponents
	}

	static getComponentString(segment: EdifactSegment, element: number, component: number): string {
		const elem = segment.elements[element]
		const clist = this.getRawComponents(elem)
		return clist[component]
	}
}

export class EdifactSegment {
	tag: string
	elements: string[]
	sequence: number
	segmentGroup: number
	segmentGroupRepetition: number
	repetition: number
}
