﻿import { Component, ElementRef, Input, ViewChild, OnInit } from "@angular/core"
import { Router, NavigationEnd } from "@angular/router"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { NavigationHandler } from "./navigation-handler"
import { LayoutService } from "./layout.service"
import { ScreenSpace } from "./layout-size"

@Component({
	selector: "idun-layout",
	templateUrl: "./idun-layout.component.html",
	styleUrls: ["idun-layout.component.scss"]
})
export class IdunLayoutComponent extends BaseSubscriber implements OnInit {
	@Input() disableSidebars = false

	@Input() backgroundImage = "assets/images/Backgrounds/main_background.png"
	get backgroundImageUrl() {
		return "url(" + this.backgroundImage + ")"
	}

	_leftbarOpen = true
	get leftbarOpen() { return !this.disableSidebars && this._leftbarOpen }
	set leftbarOpen(value) { this._leftbarOpen = value }
	_rightbarOpen = true
	get rightbarOpen() { return !this.disableSidebars && this._rightbarOpen }
	set rightbarOpen(value) { this._rightbarOpen = value }
	hideLeft = true

	@ViewChild("contentPosition") contentPosition
	@ViewChild("contentholder") contentholder
	@ViewChild("leftholder") leftholder
	@ViewChild("rightholder") rightholder
	@ViewChild("outerleftshadow") outerleftshadow
	@ViewChild("outerrightshadow") outerrightshadow

	navigater: NavigationHandler

	constructor(
		private myElement: ElementRef,
		private router: Router,
		public layoutService: LayoutService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
		layoutService.layoutComponent = this
	}

	ngOnInit(): void {
		(<any>window).onresize = () => {
			this.layoutService.sizeController.update()
			this.autocollapse(this.layoutService.navigationController.twoPagesOpen, this.layoutService.sizeController.screenSpace)
		}
		this.subscribe(this.router.events,
			(evt: any) => {
				if (evt instanceof NavigationEnd)
					this.layoutService.navigationController.updatePages(evt.urlAfterRedirects)
			})
		this.subscribe(this.layoutService.navigationController.twoPagesOpen$, (twoPagesOpen: boolean) => {
			if (twoPagesOpen) {
				this.contentholder.nativeElement.style.marginLeft = "calc((100% - 100Rem)/2)"
				this.leftholder.nativeElement.style.display = "block"
			}
			this.hideLeft = !twoPagesOpen
			this.autocollapse(twoPagesOpen, this.layoutService.sizeController.screenSpace)
		})

		this.layoutService.sizeController.update()
		this.autocollapse(this.layoutService.navigationController.twoPagesOpen, this.layoutService.sizeController.screenSpace)
	}

	zoomIn() {
		const zoom = this.layoutService.sizeController.zoomIn()
		this.updateLayoutService()
		return zoom
	}

	zoomOut() {
		const zoom = this.layoutService.sizeController.zoomOut()
		this.updateLayoutService()
		return zoom
	}

	resetZoom() {
		const zoom = this.layoutService.sizeController.resetZoom()
		this.updateLayoutService()
		return zoom
	}

	private updateLayoutService(): void {
		this.layoutService.sizeController.update()
		this.autocollapse(this.layoutService.navigationController.twoPagesOpen, this.layoutService.sizeController.screenSpace)
		this.layoutService.updateMaximisation()
	}

	displayBar(isOpen: boolean) {
		if (this.disableSidebars) return "none"
		return isOpen ? "block" : "none"
	}
	displayBarExpand(isOpen: boolean) {
		if (this.disableSidebars) return "none"
		return isOpen ? "none" : "block"
	}

	collapseRightsidebar() {
		this.rightbarOpen = false
		this.updateLayout()
	}

	expandRightsidebar() {
		this.rightbarOpen = true
		this.updateLayout()
	}

	collapseLeftsidebar() {
		this.leftbarOpen = false
		this.updateLayout()
	}

	expandLeftsidebar() {
		this.leftbarOpen = true
		this.updateLayout()
	}

	updateLayout() {
		const screenSpace = this.layoutService.sizeController.screenSpace
		const twoPagesOpen = this.layoutService.navigationController.twoPagesOpen
		if (this.layoutService.sizeController.hasSpaceForSidebars) {
			let sidebarSize = 0
			if (this.leftbarOpen) {
				sidebarSize = 3.5
			}
			if (this.rightbarOpen) {
				sidebarSize += 20
			}
			this.contentPosition.nativeElement.style.width = sidebarSize ? "calc(100% - " + sidebarSize + "rem)" : "100%"
			this.contentPosition.nativeElement.style.marginLeft = this.leftbarOpen ? "3.5rem" : "0"
		} else {
			this.contentPosition.nativeElement.style.width = "100%"
			this.contentPosition.nativeElement.style.marginLeft = "0"
		}
		if (this.layoutService.maximised) {
			this.contentholder.nativeElement.style.marginLeft = "0"
			this.contentholder.nativeElement.style.width = "100%"
			this.contentholder.nativeElement.style.display = "flex"
			this.outerleftshadow.nativeElement.style.width = "calc((100% - 100rem)/2)"
			this.outerrightshadow.nativeElement.style.width = "calc((100% - 100rem)/2)"
		}
		else {
			this.contentholder.nativeElement.style.width = ""
			this.contentholder.nativeElement.style.display = ""
			if (twoPagesOpen && (screenSpace === ScreenSpace.TwoPages || screenSpace === ScreenSpace.Full))
				this.contentholder.nativeElement.style.marginLeft = "calc((100% - 100rem)/2)"
			else {
				this.contentholder.nativeElement.style.marginLeft = "0"
				this.outerleftshadow.nativeElement.style.width = "calc((100% - 50rem)/2)"
				this.outerrightshadow.nativeElement.style.width = "calc((100% - 50rem)/2)"
			}
		}
		if (twoPagesOpen && (screenSpace === ScreenSpace.TwoPages || screenSpace === ScreenSpace.Full)) {
			this.leftholder.nativeElement.style.display = "block"
			this.outerleftshadow.nativeElement.style.display = "none"
			this.outerrightshadow.nativeElement.style.display = "none"
		}
		else {
			this.leftholder.nativeElement.style.display = "none"
			this.outerleftshadow.nativeElement.style.display = ""
			this.outerrightshadow.nativeElement.style.display = ""
		}
	}

	// Autocollapse functionality
	autocollapse(twoPagesOpen: boolean, screenSpace: ScreenSpace) {
		if (this.contentholder === undefined)
			return

		if (screenSpace === ScreenSpace.Mobile) {
			this.layoutService.sizeController.setFontSize(Math.floor(window.innerWidth / 52))
			this.collapseLeftsidebar()
			this.collapseRightsidebar()
		}
		else if (screenSpace === ScreenSpace.OnePage) {
			this.collapseLeftsidebar()
			this.collapseRightsidebar()
			this.layoutService.sizeController.setFontSize(Math.min(Math.floor(window.innerWidth / 52), 14))
		}
		else if (screenSpace === ScreenSpace.PageAndBars) {
			this.expandLeftsidebar()
			this.expandRightsidebar()
		}
		else if (screenSpace === ScreenSpace.TwoPages) {
			if (twoPagesOpen) {
				this.collapseLeftsidebar()
				this.collapseRightsidebar()
			} else {
				this.expandLeftsidebar()
				this.expandRightsidebar()
			}
		}
		else if (screenSpace === ScreenSpace.Full) {
			this.expandLeftsidebar()
			this.expandRightsidebar()
		}
		this.updateLayout()
	}

	showPageTopBar(element) {
		element.children[0].className = "page-top-bar shown-top-bar"
	}

	hidePageTopBar(element) {
		element.children[0].className = "page-top-bar hidden-top-bar"
	}

	overlayStatus(overlay: string): string {
		return this.layoutService.showOverlay(overlay)
			&& (this.layoutService.sizeController.screenSpace === ScreenSpace.TwoPages
				|| this.layoutService.sizeController.screenSpace === ScreenSpace.Full)
			? "on" : "off"
	}

	scrollbarStatus(): string {
		return this.layoutService.showOverlay("outer-left")
			|| this.layoutService.showOverlay("left")
			|| this.layoutService.showOverlay("outer-right")
			|| this.layoutService.showOverlay("right")
			|| this.layoutService.showOverlay("both")
			? "hidden"
			: "overlay"
	}
}
