﻿import { Component, forwardRef, Input, OnInit, OnDestroy } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor, FormControl } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { FormRow } from "idun-layout/form/form-row"
import { ProcedureVariation } from "../models/procedure"
import { LanguageService } from "idun-layout/language.service"
import { TreatmentCodeService } from "idun-main/model-services/treatment-code.service"
import { TreatmentCode } from "idun-common/models/treatment-code"
import { Subscription } from "rxjs"
import { UserService } from "idun-main/user.service"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormProcedureVariationsComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormProcedureVariationsComponent),
	multi: true
}

@Component({
	selector: "idun-form-procedure-variations",
	templateUrl: "form-procedure-variations.component.html",
	styleUrls: ["form-procedure-variations.component.scss"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormProcedureVariationsComponent extends FormBasicComponent<ProcedureVariation[]> implements ControlValueAccessor, OnInit, OnDestroy {
	rowClass = FormProcedureVariationsRow

	currentPlacementSelector = ""
	treatmentCodeSubscription: Subscription
	currency = "DKK"

	variationControl = new FormControl()
	options = []

	constructor(
		public l: LanguageService,
		public userService: UserService,
		public treatmentCodeService: TreatmentCodeService
	) {
		super()
	}

	get variationKeys() {
		if (this.value)
			return Object.keys(this.value)
		else return []
	}

	ngOnInit(): void {
		this.getTreatmentCodes()
		this.userService.clinicSettings$.subscribe((settings) => {
			this.currency = settings.Currency
		})
	}

	ngOnDestroy(): void {
		this.treatmentCodeSubscription.unsubscribe()
	}

	shouldShowVariationName() {
		return this.variationKeys.length > 1
	}

	public getTreatmentCodes(searchString?: string): void {
		this.treatmentCodeService.loadPage(0, 50, searchString).then((observable) => {
			if (this.treatmentCodeSubscription)
				this.treatmentCodeSubscription.unsubscribe()
			this.treatmentCodeSubscription = observable.subscribe((data) => {
				this.setupOptionsForTreatmentCodes(data)
			})
		})
	}

	private setupOptionsForTreatmentCodes(data: TreatmentCode[]): void {
		this.options = data.map(e => {
			return { title: e.Code + " - " + e.Title, id: e.DBID }
		})
	}

	titleFromID(id: number) {
		const model = this.treatmentCodeService.getModel(id)
		if (model)
			return model.getTitle()
		return ""
	}

	shortTitleFromID(id: number) {
		const model = this.treatmentCodeService.getModel(id)
		if (model)
			return model.getShortTitle()
		return ""
	}

	addSet(): void {
		const newEntry = new ProcedureVariation({ Title: this.l.common.variation + " " + (this.value.length + 1) })
		newEntry.Placements = []
		newEntry.ModelIDs = []
		this.value.push(newEntry)
		this.onChangeCallback(this.value)
	}

	addModel(key: string | number, id: number): void {
		this.value[key].ModelIDs.push(+id)
		this.onChangeCallback(this.value)
	}

	findCodeOptionByTitle(title: string) {
		for (const option of this.options)
			if (title === option.title)
				return option
		return null
	}

	public addCode(key: string | number, title: string) {
		const code = this.findCodeOptionByTitle(title)
		if (code)
			this.addModel(key, code.id)
		this.variationControl.setValue("")
	}

	remove(index: string, idIndex?: string): void {
		if (+idIndex > -1) {
			this.value[index].ModelIDs.splice(+idIndex, 1)
			this.onChangeCallback(this.value)
			return
		}
		this.value.splice(+index, 1)
		this.onChangeCallback(this.value)
	}

	showPlacementSelector(index: string): void {
		this.currentPlacementSelector = index
	}

	removePlacement(index: string, idIndex: string): void {
		this.value[index].Placements.splice(+idIndex, 1)
		this.onChangeCallback(this.value)
	}

	addPlacements(parts: string[]) {
		for (const part of parts)
			this.value[this.currentPlacementSelector].Placements.push(part)
		this.currentPlacementSelector = ""
		this.onChangeCallback(this.value)
	}
}

export class FormProcedureVariationsRow extends FormRow { }
