﻿import { Component, OnInit } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"

import { StaffNotification, StaffNotificationType } from "idun-common/models/staff-notification"
import { StaffNotificationService } from "idun-main/model-services/staff-notification.service"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { StaffService } from "idun-main/model-services/staff.service"

@Component({
	selector: "idun-staff-notification-list",
	templateUrl: "staff-notification-list.component.html"
})
export class StaffNotificationListComponent extends BaseSubscriber implements OnInit {
	notifications = []

	constructor(
		private staffNotificationService: StaffNotificationService,
		private staffService: StaffService,
		public l: LanguageService
	) { super() }



	ngOnInit(): void {
		this.subscribe(this.staffNotificationService.getModels$(), (notifications) => {
			this.notifications = notifications
		})
	}

	readBy(notification: StaffNotification) {
		const names = []
		notification.ReadByStaffIds.forEach((staffID) => {
			const staff = this.staffService.getModel(staffID)
			if (staff) {
				names.push(this.staffService.getModel(staffID).Name)
			}
		})
		if (names.length > 0)
			return this.l.staffNotification.seenBy + " " + names.join(", ")
		else
			return this.l.staffNotification.notSeen
	}
}
