import { DateHelper, TimeChange, newDateFrom } from "utility/date-helper"

export class TimeInterval {
	StartTime: Date
	EndTime: Date

	constructor(source?: any) {
		if (source) {
			if (typeof (source.StartTime) === "string")
				this.StartTime = DateHelper.parseDate(source.StartTime)
			else
				this.StartTime = new Date(source.StartTime)

			if (typeof (source.EndTime) === "string")
				this.EndTime = DateHelper.parseDate(source.EndTime)
			else
				this.EndTime = new Date(source.EndTime)
		}
		else {
			this.StartTime = new Date()
			this.EndTime = new Date()
		}
	}

	getStartMinute(base: Date): number {
		return (this.StartTime.getTime() - base.getTime()) / (1000 * 60)
	}

	getEndMinute(base: Date): number {
		return (this.EndTime.getTime() - base.getTime()) / (1000 * 60)
	}

	getLengthInMinutes(): number {
		return (this.EndTime.getTime() - this.StartTime.getTime()) / (1000 * 60)
	}

	static from(start: Date, end: Date) {
		return new TimeInterval({ StartTime: start, EndTime: end })
	}

	static fromInterval(interval: { start: Date, end: Date }) {
		return this.from(interval.start, interval.end)
	}

	static fromDuration(start: Date, duration: TimeChange) {
		return this.from(start, newDateFrom(duration, start))
	}

	offset(offset: number) {
		return TimeInterval.from(new Date(this.StartTime.getTime() + offset), new Date(this.EndTime.getTime() + offset))
	}

	intersection(other: TimeInterval): TimeInterval | null {
		if (this.intersects(other))
			return TimeInterval.from(
				other.StartTime < this.StartTime ? this.StartTime : other.StartTime,
				other.EndTime < this.EndTime ? other.EndTime : this.EndTime)
		else return null
	}

	intersects(other: TimeInterval): boolean {
		if (this.containsDateExclusive(other.StartTime))
			return true
		if (other.containsDateExclusive(this.StartTime))
			return true
		if (this.StartTime.getTime() === other.StartTime.getTime())
			return true
		return false
	}

	intersectsInclusive(other: TimeInterval): boolean {
		if (this.containsDate(other.StartTime))
			return true
		if (other.containsDate(this.StartTime))
			return true
		if (this.StartTime.getTime() === other.StartTime.getTime())
			return true
		return false
	}

	contains(other: TimeInterval | Date) {
		if (other instanceof TimeInterval)
			return this.containsInterval(other)
		return this.containsDate(other)
	}

	containsDate(time: Date): boolean {
		return this.StartTime.getTime() <= time.getTime() && time.getTime() <= this.EndTime.getTime()
	}

	containsDateExclusive(time: Date): boolean {
		return this.StartTime.getTime() < time.getTime() && time.getTime() < this.EndTime.getTime()
	}

	containsInterval(interval: TimeInterval): boolean {
		return this.StartTime.getTime() <= interval.StartTime.getTime() && interval.EndTime.getTime() <= this.EndTime.getTime()
	}

	equals(other: TimeInterval): boolean {
		return this.StartTime.getTime() === other.StartTime.getTime() && this.EndTime.getTime() === other.EndTime.getTime()
	}

	days(): TimeInterval[] {
		const result = []
		for (
			const day = new Date(this.StartTime);
			day.getTime() < this.EndTime.getTime();
			day.setDate(day.getDate() + 1)
		)
			result.push(TimeInterval.fromInterval(DateHelper.dayInterval(day)))
		return result
	}

	toShortString() {
		return DateHelper.timeToString(this.StartTime) + " - " + DateHelper.timeToString(this.EndTime)
	}
}
