﻿import { Component } from "@angular/core"
import { RouteParameterComponent } from "idun-common/route-parameter-component"
import { Patient } from "idun-common/models/patient"
import { UserService } from "idun-main/user.service"
import { LanguageService } from "idun-layout/language.service"

@Component({
	selector: "idun-patient-page",
	templateUrl: "patient-page.component.html",
	styleUrls: ["patient-page.component.scss"]
})
export class PatientPageComponent extends RouteParameterComponent {
	patient: Patient
	previousId: number

	constructor(
		private userService: UserService,
		public l: LanguageService
	) {
		super()
	}

	onUpdate(patient: Patient) {
		this.patient = patient
		if (!patient.DBID)
			return
		if (this.previousId !== patient.DBID && this.userService.getCurrentPatientDBID() !== this.patient.DBID)
			this.userService.selectPatient(this.patient.DBID)
		this.previousId = patient.DBID
	}
}
