import { createTestData } from "./test-data-creation"
import { WorkRequestResponses } from "./test-data/work-request-test-data"


export class TestHelper {
	static useDelay = true

	static paths: { [index: string]: ((parameters) => string) } = {
		...AdminResponses(),
		...BookingRequestResponses(),
		...BookingServiceResponses(),
		...BookmarkResponses(),
		...CalendarEventResponses(),
		...PatientEventResponses(),
		...ProcedureEventResponses(),
		...EdifactResponses(),
		...CardReaderTokenResponses(),
		...JournalEntryResponses(),
		...StaffNotificationResponses(),
		...PatientResponses(),
		...PatientSummaryResponses(),
		...PatientNoteResponses(),
		...StaffGroupsResponses(),
		...ProcedureResponses(),
		...ProcedureTypeResponses(),
		...TreatmentCodeTypeResponses(),
		...PartnerResponses(),
		...PeriodPlanResponses(),
		...InvoiceResponses(),
		...JournalTemplateResponses(),
		...LanguageResponses(),
		...ResourceResponses(),
		...StatusResponses(),
		...StatusRequestResponses(),
		...StatusTypeResponses(),
		...StaffResponses(),
		...TreatmentCodeResponses(),
		...UserResponses(),
		...WorkIntervalResponses(),
		...WorkRequestResponses(),
		...ClinicResponses(),
		...StaffNotificationReceiversResponses(),
	}

	public static request(http: Http, path: string, parameters: any, onResponse?: any): Promise<any> {
		const responseFunction = TestHelper.paths[path]
		if (!responseFunction)
			return Promise.reject("No testdata for the path: " + path)

		const waittime = 50
		return new Promise((resolve, reject) => {
			if (TestHelper.useDelay) {
				setTimeout(() => {
					const response = TestHelper.paths[path](parameters)
					console.debug("Request: " + path + " " + JSON.stringify(parameters))
					// console.log("Response: " + response)
					resolve(response)
				}, waittime)
			}
			else {
				const response = TestHelper.paths[path](parameters)
				console.debug("Request: " + path + " " + JSON.stringify(parameters))
				// console.log("Response: " + response)
				resolve(response)
			}
		}).then(
			onResponse ? onResponse : function (r: string) {
				return JSON.parse(r)
			})
	}
}

createTestData()
