import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { BaseService } from "idun-common/base-service"
import { WorkRequest } from "idun-common/models/work-request"

@Injectable()
export class WorkRequestService extends BaseService<WorkRequest> {
	constructor(http: Http) {
		super(http, WorkRequest, "api/WorkRequest")
	}

	acceptRequest(requestId: number): Promise<boolean> {
		return this.baseRequest("AcceptRequest", { id: requestId })
	}

	rejectRequest(requestId: number): Promise<boolean> {
		return this.baseRequest("RejectRequest", { id: requestId })
	}
}
