import { CalendarEntry } from "idun-calendar/calendar-entry"
import { CalendarEvent } from "idun-common/models/calendar-event"

export class CalendarEventEntry implements CalendarEntry {
	constructor(
		public source: CalendarEvent) {
	}

	isSelected(selection: any): boolean {
		return this.source === selection
	}

	boxSaturation = 0
	borderHue = null
	boxHue = null
	statusIcon = null
	clickable = true

	get length(): number {
		return this.source.Times.getLengthInMinutes()
	}

	getStartMinute(baseDate: Date): number {
		return this.source.Times.getStartMinute(baseDate)
	}

	moveStartMinute(minuteChange: number) {
		const minutes = this.source.Times.StartTime.getMinutes()
		this.source.Times.StartTime.setMinutes(minutes + minuteChange)
	}

	moveEndMinute(minuteChange: number) {
		const minutes = this.source.Times.EndTime.getMinutes()
		this.source.Times.EndTime.setMinutes(minutes + minuteChange)
	}

	get description(): string {
		return this.source.Title
	}

	get hoverText(): string {
		return this.description
	}
}
