import { Component, OnInit, ElementRef } from "@angular/core"
import { Router, ActivatedRoute } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { RouteParameterComponent } from "idun-common/route-parameter-component"
import { DateHelper, changeDate } from "utility/date-helper"
import { LanguageService } from "idun-layout/language.service"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { Staff } from "idun-common/models/staff"
import { StaffService } from "idun-main/model-services/staff.service"
import { Resource } from "idun-common/models/resource"
import { ResourceService } from "idun-main/model-services/resource.service"
import { LayoutService, Maximisable } from "idun-layout/layout.service"
import { ColorThing } from "./color-thing.component"
import { ProcedureTypeService } from "../../model-services/procedure-type.service"
import { ProcedureType } from "idun-common/models/procedure-type"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { ProcedureEventService } from "idun-main/model-services/procedure-event.service"
import { StaffEventProvider } from "./staff-event-provider"
import { ResourceEventProvider } from "./resource-event-provider"
import { ProcedureEventEntry } from "./procedure-event-entry"
import { combineLatest } from "rxjs"
import { CalendarEventService } from "idun-main/model-services/calendar-event.service"
import { CalendarEvent } from "idun-common/models/calendar-event"
import { TimeInterval } from "idun-common/models/time-interval"
import { AutoFormComponent } from "idun-common/auto-form.component"

@Maximisable
@Component({
	selector: "idun-calendar-page",
	templateUrl: "calendar-page.component.html",
	styleUrls: ["calendar-page.component.scss"]
})
export class CalendarPageComponent extends RouteParameterComponent implements OnInit {
	navigater: NavigationHandler

	currentEvent: ProcedureEvent | CalendarEvent
	date: Date
	entryProviders: (StaffEventProvider | ResourceEventProvider)[] = []
	loading = true
	subtitle = ""

	procedureTypes: ProcedureType[]
	staff: Staff[]
	staffIds: number[]
	resources: Resource[]
	resourceIds: number[]

	colorThing = new ColorThing()

	constructor(
		myElement: ElementRef,
		router: Router,
		public layoutService: LayoutService,
		private route: ActivatedRoute,
		private calendarEventService: CalendarEventService,
		private procedureService: ProcedureService,
		private procedureTypeService: ProcedureTypeService,
		private procedureEventService: ProcedureEventService,
		private staffService: StaffService,
		private resourceService: ResourceService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
		this.parameterHandlers["date"] = (date: any) => { this.handleDateParameter(date) }
		this.parameterHandlers["staff"] = (ids: any) => {
			this.staffIds = this.parseUrlIds(ids)
		}
		this.parameterHandlers["resources"] = (ids: any) => {
			this.resourceIds = this.parseUrlIds(ids)
		}
	}

	private parseUrlIds(ids: string) {
		if (!ids)
			return []
		return ids.split("-")
			.map(e => +e)
			.filter(e => e)
	}

	ngOnInit(): void {
		this.handleRouteParameters(this.route, () => {
			this.currentEvent = undefined
			this.updateEvents()
			this.updateSubtitle()
		})
		this.subscribeToList(this.procedureTypeService, (procedureTypes) => {
			this.procedureTypes = procedureTypes
			this.addProcedureColorMode()
			this.colorThing.setMode("procedures")
		})
		this.subscribeToList(this.staffService, (staff: Staff[]) => {
			this.staff = staff
			this.addStaffColorMode()
		})
		this.subscribeToList(this.resourceService, (resources: Resource[]) => {
			this.resources = resources
			this.addResourceColorMode()
		})
		this.subscribe(this.layoutService.maximised$, _ => {
			this.updateEvents()
		})
	}

	private addProcedureColorMode() {
		this.colorThing.addModeFromList(
			"procedures",
			() => this.l.procedure.namePlural,
			this.procedureTypes,
			(procedureType) => {
				return {
					title: () => procedureType.getTitle(),
					hue: procedureType.Hue * 360,
					hasColor: (e: ProcedureEvent) => this.eventBelongsToProcedureType(e, procedureType)
				}
			})
	}

	private updateSubtitle() {
		if (this.staffIds.length === 1 && this.resourceIds.length === 0)
			this.setSubtitleFromStaff(this.staffIds[0])
		else if (this.resourceIds.length === 1 && this.staffIds.length === 0)
			this.setSubtitleFromResource(this.resourceIds[0])
		else
			this.subtitle = ""
	}

	private async setSubtitleFromStaff(id: number) {
		const staff = await this.staffService.model(id)
		this.subtitle = staff.Name
	}

	private async setSubtitleFromResource(id: number) {
		const resource = await this.resourceService.model(id)
		this.subtitle = resource.Title
	}

	private addStaffColorMode() {
		this.colorThing.addModeFromList(
			"staff",
			() => this.l.staff.namePlural,
			this.staff,
			(staffMember) => {
				return {
					title: () => staffMember.getTitle(),
					hue: ColorThing.integerToZeroToOneMap(staffMember.DBID - 1) * 360,
					hasColor: (e: ProcedureEvent) => e.OccupiedResources.includes(staffMember.DBID)
				}
			})
	}

	private addResourceColorMode() {
		this.colorThing.addModeFromList(
			"resources",
			() => this.l.resource.namePlural,
			this.resources,
			(resource) => {
				return {
					title: () => resource.getTitle(),
					hue: ColorThing.integerToZeroToOneMap(resource.DBID - 1) * 360,
					hasColor: (e: ProcedureEvent) => e.OccupiedResources.includes(resource.DBID)
				}
			})
	}

	private eventBelongsToProcedureType = (event: ProcedureEvent, procedureType: ProcedureType) => {
		const procedure = this.procedureService.getModel(event.ProcedureId)
		return procedure
			? procedure.ProcedureTypeID === procedureType.DBID
			: false
	}

	private handleDateParameter(date: any) {
		if (date === undefined || date === "-")
			date = new Date()
		else
			date = new Date(date)
		this.date = date
	}

	updateEvents() {
		this.entryProviders = []
		for (const key in this.subscriber.namedSubs)
			if (key.startsWith("column"))
				this.subscriber.namedSubs[key].unsubscribe()
		for (const staffId of this.staffIds)
			this.addProviderForStaff(staffId)
		for (const resourceId of this.resourceIds)
			this.addProviderForResource(resourceId)
	}

	private async addProviderForStaff(staffId: number) {
		const interval = this.getInterval()
		const events = []
		const provider = new StaffEventProvider(
			events,
			this.l,
			this.colorThing,
			this.procedureService,
			() => this.staffService.getModel(staffId))

		this.entryProviders.push(provider)
		const observables = await Promise.all([
			this.procedureEventService.getEventsForStaff(staffId, interval.start, interval.end),
			this.calendarEventService.getEventsForStaff(staffId, interval.start, interval.end)])
		this.subscribe(
			combineLatest(...observables),
			(values) => {
				const procedureEvents = values[0]
				const calendarEvents = values[1]
				events.splice(0, events.length, ...procedureEvents, ...calendarEvents)
				provider.updateEntries()
				this.loading = false
			},
			"columnstaff" + staffId)
	}

	private async addProviderForResource(resourceId: number) {
		const interval = this.getInterval()
		const events: (ProcedureEvent | CalendarEvent)[] = []
		const provider = new ResourceEventProvider(
			events,
			this.l,
			this.colorThing,
			this.procedureService,
			() => this.resourceService.getModel(resourceId))

		this.entryProviders.push(provider)
		const observables = await Promise.all([
			this.procedureEventService.getEventsForResource(resourceId, interval.start, interval.end),
			this.calendarEventService.getEventsForResource(resourceId, interval.start, interval.end)])
		this.subscribe(
			combineLatest(...observables),
			(values) => {
				const procedureEvents: ProcedureEvent[] = values[0]
				const calendarEvents: CalendarEvent[] = values[1]
				events.splice(0, events.length, ...procedureEvents, ...calendarEvents)
				provider.updateEntries()
				this.loading = false
			},
			"columnresource" + resourceId)
	}

	private getInterval() {
		const interval = DateHelper.weekInterval(this.date)
		if (this.layoutService.maximised)
			changeDate(interval.end, { days: 7 })
		return interval
	}

	addEntry(params: { provider: StaffEventProvider | ResourceEventProvider, time: Date }) {
		const start = new Date(params.time)
		const end = new Date(params.time)
		end.setMinutes(end.getMinutes() + 30)
		this.currentEvent = new CalendarEvent({ Times: TimeInterval.from(start, end) })
		if (params.provider instanceof StaffEventProvider)
			this.currentEvent.OccupiedStaff = [this.staffIds[0]]
		else
			this.currentEvent.OccupiedResources = [this.resourceIds[0]]
	}

	onSubmitNewEvent = (_: CalendarEvent, component: AutoFormComponent<CalendarEvent>) => {
		component._onSubmit()
			.then(e => {
				this.updateEvents()
			})
	}

	selectEntry(params: { provider: StaffEventProvider | ResourceEventProvider, entry: ProcedureEventEntry }) {
		this.currentEvent = params.entry.source
	}

	showPatientEvent() {
		return this.currentEvent instanceof ProcedureEvent
	}

	showCalendarEvent() {
		return this.currentEvent instanceof CalendarEvent
	}

	deselectEvent = () => {
		this.currentEvent = null
	}

	chooseStaff(event) {
		this.staffIds = [event.target.value]
		this.resourceIds = []
		this.navigate(this.date)
		event.target.value = ""
	}

	chooseResource(event) {
		this.staffIds = []
		this.resourceIds = [event.target.value]
		this.navigate(this.date)
		event.target.value = ""
	}

	navigate(date: Date) {
		this.navigater.current(this.getUrl(date))
	}

	private getUrl(date: Date) {
		return "calendar" +
			"/" + date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate() +
			"/" + (this.staffIds.join("-") || "0") +
			"/" + (this.resourceIds.join("-") || "0")
	}
}
