﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { LanguageService } from "idun-layout/language.service"
import { Procedure } from "idun-common/models/procedure"
import { ResourceService } from "./resource.service"
import { ProcedureTypeService } from "./procedure-type.service"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { FormSelectModelRow } from "idun-common/form/form-select-model.component"
import { FormTextareaRow } from "idun-layout/form/form-textarea.component"
import { FormNumberRow } from "idun-layout/form/form-number.component"
import { FormListSelectModelRow } from "idun-common/form/form-list-select-model.component"
import { LinkThing } from "idun-common/form/form-dynamic-extra.component"
import { abbreviationFor } from "utility/abbreviations"
import { UserService } from "../user.service"
import { Permission } from "idun-common/models/staff-group"
import { FormProcedureVariationsRow } from "idun-common/form/form-procedure-variations.component"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"
import { ProcedureEvent } from "idun-common/models/procedure-event"

@Injectable()
export class ProcedureService extends ExtendedService<Procedure> {
	constructor(
		http: Http,
		resourceService: ResourceService,
		procedureTypeService: ProcedureTypeService,
		userService: UserService,
		public l: LanguageService
	) {
		super(http, l,
			Procedure,
			"api/Procedure",
			"procedure",
			() => l.procedure
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.EditProcedures)
		this.icon = "Fundamental"
		this.color = "var(--color-fundamental)"
		userService.loadClinicSettings()

		this.setupForm({
			getHeader: (model: Procedure) => this.l.procedure.nameSingular + (model.Title ? ": " + model.Title : ""),
			rows: [
				new FormTextRow("Title", {
					label: () => l.common.title,
					requirements: [ValidationRequirement.ValueRequired(l)],
					width: 60,
					cssClasses: "padding-right",
					change: (model, value) => {
						if (value)
							model.Abbreviation = abbreviationFor(value,
								this.models.filter(e => e.DBID !== model.DBID).map(e => e.Abbreviation))
						else if (!model.Title)
							model.Abbreviation = ""
					}
				}),
				new FormTextRow("Abbreviation", {
					label: () => l.common.abbreviation,
					hint: () => l.procedure.abbreviationHint,
					requirements: [
						ValidationRequirement.ValueRequired(l),
						new ValidationRequirement((value, model) => {
							if (this.models.find(x => x.Abbreviation === value && x.DBID !== model.DBID))
								return () => l.commonForm.abbreviationMustBeUnique
						})
					],
					width: 40
				}),
				new FormSelectModelRow("ProcedureTypeID", {
					label: () => l.procedureType.nameSingular,
					hint: () => l.procedure.procedureTypeHint,
					service: procedureTypeService,
					things: [
						new LinkThing<Procedure>(() => l.common.add, () => "procedure-type/add", (m, d) => !d, "positive")
					]
				}),
				new FormTextareaRow("Description", {
					label: () => l.common.description,
					hint: () => l.procedure.descriptionHint
				}),
				{ header: () => l.procedure.process, headerHelp: () => l.procedure.processHelp },
				new FormNumberRow("Duration", {
					label: () => l.common.duration,
					hint: () => l.procedure.durationHint,
					endLabel: () => l.date.minutes,
					requirements: [new ValidationRequirement(value => {
						if (!this.isPositiveNumber(value))
							return () => l.commonForm.fieldMustBePositiveNumber
					})]
				}),
				new FormListSelectModelRow("ResourceRequirements", {
					label: () => l.procedure.usableResources,
					hint: () => l.procedure.usableResourcesHint,
					service: resourceService
				}),
				{ header: () => l.procedure.placementAndProcedureCodes, headerHelp: () => l.procedure.placementAndProcedureCodesHelp },
				new FormProcedureVariationsRow("Variations", {
					label: () => l.procedure.variationName,
					requirements: [new ValidationRequirement(value => {
						for (const variation of value)
							if (!this.isPositiveNumber(variation.Price))
								return () => l.procedure.variationMustHavePrice
					})]
				})
			]
		})
		this.setupList({
			entryHeader: (model: Procedure) => model.Abbreviation,
			contentList: [(model: Procedure) => model.Title]
		})
		this.listInfo.searchTypes.push("Title")
		this.listInfo.searchTypes.push("Description")
	}

	isPositiveNumber(value: any) {
		if (value === "" || value === null || value === undefined)
			return false
		return +value >= 0
	}

	getNewModel(source?: any): Procedure {
		if (!source)
			source = { Variations: [{ Title: this.l.common.variation + " 1" }] }
		return new this.modelClass(source)
	}

	getAvailableStaffFor(starttime: Date, endtime: Date, procedureid: number): Promise<number[]> {
		return this.baseRequest("GetAvailableStaffFor", { start: starttime, end: endtime, id: procedureid })
	}

	getAvailableResourcesFor(starttime: Date, endtime: Date, procedureid: number): Promise<number[]> {
		return this.baseRequest("GetAvailableResourcesFor", { start: starttime, end: endtime, id: procedureid })
	}

	availableStaffFor(procedureEvent: ProcedureEvent) {
		return this.getAvailableStaffFor(
			procedureEvent.Times.StartTime,
			procedureEvent.Times.EndTime,
			procedureEvent.ProcedureId)
	}

	availableResourcesFor(procedureEvent: ProcedureEvent) {
		return this.getAvailableResourcesFor(
			procedureEvent.Times.StartTime,
			procedureEvent.Times.EndTime,
			procedureEvent.ProcedureId)
	}
}
