﻿import { IdunModel } from "./idun-model"
import { DateHelper } from "utility/date-helper"
import { UnsavedFile, DatabaseFileReference } from "./database-file"

export class PatientNote extends IdunModel {
	DBID: number
	Date: Date
	PatientID: number
	StaffID: number
	Content: string
	Tags: string[]
	Files: (UnsavedFile | DatabaseFileReference)[]

	constructor(source?: any) {
		super()
		this.Tags = []
		this.Files = []
		this.Date = new Date()
		if (source) {
			for (const prop in source) this[prop] = source[prop]
			if (typeof (source.Date) === "string")
				this.Date = DateHelper.parseDate(source.Date)
			else
				this.Date = new Date(source.Date)
			if (source.Tags)
				this.Tags = source.Tags.map(e => e)
			if (source.Files)
				this.Files = source.Files.map(e => new DatabaseFileReference(e))
		}
	}

	getTitle(): string {
		if (this.Content)
			return this.Content.split("\n")[0].substring(0, 50)
		else if (this.Files.length > 0)
			return this.Files[0].Name
		else
			return ""
	}

	getTime() {
		return this.Date.getTime()
	}
}
