import { CalendarEntryProvider } from "idun-calendar/calendar-entry-provider"
import { CalendarEntry } from "idun-calendar/calendar-entry"
import { Staff } from "idun-common/models/staff"
import { TimeInterval } from "idun-common/models/time-interval"
import { DateHelper } from "utility/date-helper"
import { BasicCalendarEntry } from "idun-calendar/basic-calendar-entry"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { WorkInterval } from "idun-common/models/work-interval"
import { Resource } from "idun-common/models/resource"
import { ProcedureType } from "idun-common/models/procedure-type"

export class WorkIntervalProvider implements CalendarEntryProvider {
	entries: CalendarEntry[]

	constructor(
		public staff: Staff,
		private navigater: NavigationHandler,
		public changeMode: boolean
	) { }

	getEntries(date?: Date): CalendarEntry[] {
		if (!date)
			return this.entries
		const interval = TimeInterval.fromInterval(DateHelper.dayInterval(date))
		return this.entries.filter(e => (<any>e).source.Interval.intersects(interval))
	}
	getTitle(): string {
		return this.staff.Name
	}
	getSubtitle(): string {
		return ""
	}
	getHovertitle(): string {
		return this.staff.Name
	}

	withIntervals(intervals: TimeInterval[]) {
		this.entries = intervals.map(e => this.entryFromInterval(e))
		return this
	}

	withRequests(intervals: TimeInterval[]) {
		this.entries = intervals.map(e => this.entryFromInterval(e, true))
		return this
	}

	private entryFromInterval(interval: TimeInterval, request = false) {
		return new BasicCalendarEntry({Interval: interval }, {
			boxHue: 100,
			ghost: request ? !this.changeMode : this.changeMode,
			clickable: request ? this.changeMode : !this.changeMode
		})
	}

	onTitleClick(date: Date) {
		this.navigater.current(`calendar/${DateHelper.dateToString(date)}/${this.staff.DBID}`)
	}
}

export class ResourceEntryProvider implements CalendarEntryProvider {
	entries: CalendarEntry[] = []

	constructor(
		public resource: Resource,
		private navigater: NavigationHandler
	) { }

	getEntries(date?: Date): CalendarEntry[] {
		if (!date)
			return this.entries
		const interval = TimeInterval.fromInterval(DateHelper.dayInterval(date))
		return this.entries.filter(e => (<any>e).source.Interval.intersects(interval))
	}

	getTitle(): string {
		return this.resource.Title
	}

	getSubtitle(): string {
		return ""
	}

	getHovertitle(): string {
		return this.resource.Title
	}

	onTitleClick(date: Date) {
		this.navigater.current(`calendar/${DateHelper.dateToString(date)}/0/${this.resource.DBID}`)
	}
}

