﻿import { Component, OnInit, ElementRef, ViewChild } from "@angular/core"
import { Router } from "@angular/router"
import { DateHelper } from "utility/date-helper"
import { LanguageService } from "idun-layout/language.service"

import { StatisticsService } from "./statistics.service"
import { PatientService } from "idun-main/model-services/patient.service"
import { LayoutService } from "idun-layout/layout.service"

@Component({
	selector: "idun-statistics-patients",
	templateUrl: "statistics-patients.component.html"
})
export class StatisticsPatientsComponent implements OnInit {
	ageDistributionChartHeight = 0
	@ViewChild("genderDistributionChart") genderDistributionChartElement: any
	@ViewChild("ageDistributionChart") ageDistributionChartElement: any

	constructor(
		private statisticsService: StatisticsService,
		private patientService: PatientService,
		public l: LanguageService
	) { }

	ngOnInit(): void {
		this.patientService.getGenderDistribution().then((distribution: any) => {
			this.statisticsService.distributionPieChart(this.genderDistributionChartElement.nativeElement, distribution, "Gender")
		})
		this.patientService.getBirthdates().then((response: any[]) => {
			const distribution: any[] = this.getAgeDistribution(response)
			this.ageDistributionChartHeight = 12 + (3 * distribution.length)
			setTimeout(() => {
				this.statisticsService.distributionBarChart(this.ageDistributionChartElement.nativeElement, distribution, "Age")
			}, 50)
		})
	}

	getAgeDistribution(birthdates: string[]): any[] {
		let oldest = 0

		const intervalSize = 10

		const ageRoundedToIntervalList: number[] = []

		for (let i = 0; i < birthdates.length; i++) {
			const age: number = DateHelper.ageFromDate(birthdates[i])
			if (age > -1) {
				if (oldest < age) {
					oldest = age
				}
				ageRoundedToIntervalList.push(age - (age % intervalSize))
			}
		}

		const numberOfIntervals: number = ((oldest - (oldest % intervalSize)) / intervalSize) + 1

		const amountPerInterval: number[] = []

		for (let i = 0; i < numberOfIntervals; i++) {
			amountPerInterval.push(0)
		}

		for (let i = 0; i < ageRoundedToIntervalList.length; i++) {
			amountPerInterval[(ageRoundedToIntervalList[i] / intervalSize)]++
		}

		const distribution: any[] = []

		for (let i: number = amountPerInterval.length - 1; i > -1; i--) {
			distribution.push([(i * intervalSize) + " - " + ((i + 1) * intervalSize), amountPerInterval[i]])
		}

		return distribution
	}
}
