﻿import { LayoutService } from "./layout.service"

export class NavigationHandler {
	constructor(
		private router: any,
		private element: any,
		private layoutService: LayoutService
	) { }

	enableNavigation() {
		this.layoutService.navigationController.allowNavigation = () => true
	}

	disableNavigation() {
		this.layoutService.navigationController.allowNavigation = () => false
	}

	allowNavigation(allow: (addresses: { left: string, right: string }) => boolean) {
		this.layoutService.navigationController.allowNavigation = allow
	}

	get currentSide(): "left" | "right" | "main" {
		const holderElement = document.getElementById("leftcontent")
		const inLeft = holderElement ? holderElement.contains(this.element.nativeElement.parentNode) : false
		if (inLeft)
			return "left"
		else if (this.layoutService.navigationController.leftUrl === "")
			return "main"
		else
			return "right"
	}

	get currentAddress(): string {
		const addresses = this.layoutService.navigationController.addressesFromUrl(this.router.url)
		return addresses[this.currentSide === "main" ? "right" : this.currentSide]
	}

	get leftAddress(): string {
		return this.layoutService.navigationController.leftUrl
	}

	get rightAddress(): string {
		return this.layoutService.navigationController.rightUrl
	}

	public current(url: string) {
		switch (this.currentSide) {
			case "left": return this.left(url)
			case "right": return this.right(url)
			case "main": return this.main(url)
		}
	}

	public left(url: string) {
		const addresses = this.layoutService.navigationController.addressesFromUrl(this.router.url)
		addresses.left = url
		this.layoutService.navigationController.navigateByAddresses(addresses)
	}

	public right(url: string) {
		const addresses = this.layoutService.navigationController.addressesFromUrl(this.router.url)
		if (!addresses.left)
			addresses.left = addresses.right
		addresses.right = url
		this.layoutService.navigationController.navigateByAddresses(addresses)
	}

	public main(url: string) {
		this.layoutService.navigationController.navigate(url)
	}

	public both(left: string, right: string) {
		this.layoutService.navigationController.navigateByAddresses({ left: left, right: right })
	}

	public back() {
		if (this.currentSide === "main" || this.currentSide === "right")
			this.layoutService.navigationController.navigateBackRight()
		else
			this.layoutService.navigationController.navigateBackLeft()
	}

	public hasNavigationHistory() {
		if (this.currentSide === "main" || this.currentSide === "right")
			return this.layoutService.navigationController.rightHistory.length > 0
		else
			return this.layoutService.navigationController.leftHistory.length > 0
	}
}
