﻿import { Component, forwardRef } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { LanguageService } from "idun-layout/language.service"
import { FormRow, FieldInput, FormRowOptions } from "./form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormTimeIntervalComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormTimeIntervalComponent),
	multi: true
}

@Component({
	selector: "idun-form-time-interval",
	template: `
			<div class="idun-form-row">
				<idun-label *ngIf="showLabel" [hint]="hint" [display]="display">
					<ng-content select="label"></ng-content>
				</idun-label>

				<label class="{{display?'display-label':''}}"
						style="width: 4rem; text-align: center; min-width: initial;">
					{{l.common.from}}
				</label>

				<input type="text" [(ngModel)]="_startTime"
						(change)="updateStartTime()" (blur)="onBlur()" autocomplete="off"
						[disabled]="display" style="text-align: center;" class="{{(error && isBlurred)?'invalid':''}}"/>

				<label class="{{display?'display-label':''}}"
						style="width: 3rem; text-align: center; min-width: initial;">
					{{l.common.to}}
				</label>

				<input type="text" [(ngModel)]="_endTime"
						(change)="updateEndTime()" (blur)="onBlur()" autocomplete="off"
						[disabled]="display" style="text-align: center;" class="{{(error && isBlurred)?'invalid':''}}"/>

				<ng-content></ng-content>
			</div>`,
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormTimeIntervalComponent extends FormBasicComponent<any> {
	rowClass = FormTimeIntervalRow
	_startTime = "10:00"
	_endTime = "10:00"

	constructor(public l: LanguageService) {
		super()
	}

	updateStartTime() {
		this.value.startFromString(this._startTime)
		this._startTime = this.value.startToString()
	}

	updateEndTime() {
		this.value.endFromString(this._endTime)
		this._endTime = this.value.endToString()
	}

	onValueChange() {
		if (!this.value || typeof this.value === "string")
			return
		if (this.value) {
			this._startTime = this.value.startToString()
			this._endTime = this.value.endToString()
		}
	}
}

export class FormTimeIntervalRow extends FormRow {
	constructor(
		field: FieldInput,
		private l: LanguageService,
		options: FormRowOptions = {}
	) {
		super(field, options)
	}

	basicValidityErrors(value) {
		if (!value)
			return
		if (typeof value === "string")
			return () => "unknown error"
		if (value.End < value.Start)
			return () => this.l.commonForm.endTimeMustBeAfterStartTime
	}}
