﻿import { SearchableIdunModel } from "./idun-model"
import { Procedure } from "./procedure"

export class Partner extends SearchableIdunModel {
	DBID: number
	Name: string
	PercentageDiscount: number
	ProcedureChanges: ProcedureSetup[]
	Notes: string

	constructor(source?: any) {
		super()
		this.PercentageDiscount = 0
		this.ProcedureChanges = []
		if (source) {
			for (const prop in source) this[prop] = source[prop]
			if (source.ProcedureChanges)
				this.ProcedureChanges = source.ProcedureChanges.map(e => new ProcedureSetup(e))
		}
	}

	getTitle(): string {
		return this.Name
	}

	changesFor(procedureId: number) {
		return this.ProcedureChanges.find(e => e.ProcedureID === procedureId)
	}

	priceFor(procedure: Procedure, variationTitle: string) {
		return this.ProcedureChanges
			.find(e => e.ProcedureID === procedure.DBID)
			.priceFor(procedure, variationTitle)
	}

	searchAttributes() {
		return [this.Name]
	}
}

export class ProcedureSetup {
	ProcedureID: number
	Variations: PriceVariation[]
	Supported: boolean
	ChangedTime: string

	constructor(source?: any) {
		this.Supported = true
		this.Variations = []
		if (!source)
			return

		for (const prop in source) this[prop] = source[prop]
		if (source.Variations)
			this.Variations = source.Variations.map(e => new PriceVariation(e))
	}

	priceFor(procedure: Procedure, variationTitle: string) {
		const variation = this.Variations.find(e => e.Title === variationTitle)
		if (!this.Supported || !variation || !variation.hasPrice())
			return procedure.Variations.find(e => e.Title === variationTitle).Price
		return variation.Price
	}
}

export class PriceVariation {
	Title: string
	Price?: number
	TreatmentCodeIDs: number[]

	constructor(source?: any) {
		this.TreatmentCodeIDs = []
		if (!source)
			return

		for (const prop in source) this[prop] = source[prop]
		if (source.TreatmentCodeIDs)
			this.TreatmentCodeIDs = source.TreatmentCodeIDs.map(e => e)
	}

	hasPrice() {
		return this.Price !== null && this.Price !== undefined
	}
}
