﻿import { SearchableIdunModel } from "./idun-model"
import { TimeInterval } from "./time-interval"

export class BookingRequest extends SearchableIdunModel {
	DBID: number
	Requester: string
	Clinic: string
	ProcedureID: number
	Times: TimeInterval
	PatientData: string
	Status: RequestStatus
	CalendarEventID: number

	constructor(source?: any) {
		super()
		this.Times = new TimeInterval()

		if (source) {
			for (const prop in source) this[prop] = source[prop]
			this.Times = new TimeInterval(source.Times)
		}
	}

	getTitle(): string {
		return "TODO" // TODO
	}

	searchAttributes() {
		return [this.Requester]
	}
}

export enum RequestStatus { Waiting, Accepted, Denied }
