export function abbreviationFor(name: string, otherAbbreviations: string[], ) {
	const names = name.split(" ")
	const initialCounts = names.map(e => 1)
	let nextToIncrease = 0
	let number = 2
	let abbreviation = names.map((n, i) => n.substr(0, initialCounts[i])).join("")
	while (otherAbbreviations.includes(abbreviation)) {
		if (noMoreInitialsLeft(names, initialCounts)) {
			abbreviation = names.map((n, i) => n.substr(0, initialCounts[i])).join("") + number
			number++
		} else {
			nextToIncrease = increaseInitialsUsed(initialCounts, nextToIncrease)
			abbreviation = names.map((n, i) => n.substr(0, initialCounts[i])).join("")
		}
	}
	return abbreviation
}

function increaseInitialsUsed(initialCounts, nextToIncrease) {
	initialCounts[nextToIncrease]++
	nextToIncrease++
	if (initialCounts.length <= nextToIncrease)
		nextToIncrease = 0
	return nextToIncrease
}

function noMoreInitialsLeft(names, initialCounts) {
	const countIsTooLong = names.map((n, i) => n.length < initialCounts[i])
	for (const isTooLong of countIsTooLong)
		if (!isTooLong)
			return false
	return true
}
