import { Component, OnInit, ViewChild, ElementRef } from "@angular/core"
import { Edifact } from "idun-common/models/edifact"
import { ActivatedRoute } from "@angular/router"
import { FormComponent } from "idun-common/form-component"
import { EdifactService } from "idun-main/model-services/edifact.service"
import { EdifactHelper, EdifactSegment } from "utility/edifact/edifact-helper"
import { XREF01Letter, XREF01Envelope } from "utility/edifact/XREF01-structures"

@Component({
	selector: "idun-edifact",
	templateUrl: "./edifact.component.html",
	styleUrls: ["edifact.component.scss"]
})
export class EdifactComponent extends FormComponent<Edifact> implements OnInit {
	edifact: Edifact
	rawSegments = []
	segments: EdifactSegment[] = []

	edifactEnvelope: XREF01Envelope
	currentEdifactLetter: XREF01Letter

	@ViewChild("S1") s1: ElementRef
	@ViewChild("S2") s2: ElementRef
	@ViewChild("S3") s3: ElementRef
	@ViewChild("S4") s4: ElementRef
	@ViewChild("S5") s5: ElementRef
	@ViewChild("S6") s6: ElementRef

	constructor(
		private route: ActivatedRoute,
		private edifactService: EdifactService
	) {
		super(edifactService)
	}

	ngOnInit() {
		this.handleRouteParameters(this.route)
	}

	handleHeaderClick(element: ElementRef) {
		element.nativeElement.style.display = element.nativeElement.style.display === "none" ? "" : "none"
	}

	onModelUpdate(edifact: Edifact) {
		this.edifact = edifact
		this.segments = EdifactHelper.getSegments(this.edifact.RawMessage)
		this.edifactEnvelope = new XREF01Envelope()
		for (let i = 0; i < this.segments.length; i++)
			this.handleSegment(this.segments[i])
	}

	handleSegment(segment: EdifactSegment) {
		switch (segment.tag) {
			case "UNB": return this.handleUNBSegment(segment)
			case "UNH": return this.handleUNHSegment(segment)
			case "DTM": return this.handleDTMSegment(segment)
			case "NAD": return this.handleNADSegment(segment)
			case "ADR": return this.handleADRSegment(segment)
			case "SPR": return this.handleSPRSegment(segment)
			case "RFF": return this.handleRFFSegment(segment)
			case "CIN": return this.handleCINSegment(segment)
			case "FTX": return this.handleFTXSegment(segment)
			case "PNA": return this.handlePNASegment(segment)
			case "UNT": return this.handleUNTSegment(segment)
			case "UNZ": return this.handleUNZSegment(segment)
		}
	}

	handleUNBSegment(segment: EdifactSegment) {
		// "Kuvertens afsendelsesdato", element: 4, component: 0
		// "Kuvertens afsendelsesklokkeslet", element: 4, component: 1
		this.edifactEnvelope.ID = +this.getDataString(segment, 5, 0)
		this.edifactEnvelope.senderLocation = +this.getDataString(segment, 2, 0)
		this.edifactEnvelope.recieverLocation = +this.getDataString(segment, 3, 0)
	}

	handleUNHSegment(segment: EdifactSegment) {
		this.currentEdifactLetter = new XREF01Letter
		this.currentEdifactLetter.ID = +this.getDataString(segment, 1, 0)
		this.currentEdifactLetter.MEDREF = this.getDataString(segment, 2, 0)
		this.currentEdifactLetter.version = this.getDataString(segment, 2, 4)
	}

	handleDTMSegment(segment: EdifactSegment) {
		if (segment.sequence === 0 && segment.segmentGroup === 0 && segment.repetition === 1) {
			// "Brevets godkendelsesdato", element: 1, component: 1
		}
		else if (segment.sequence === 4 && segment.segmentGroup === 2 && segment.repetition === 1) {
			// "Brevets godkendelsesdato", element: 1, component: 1
		}
	}

	handleNADSegment(segment: EdifactSegment) {
		if (segment.sequence === 0 && segment.segmentGroup === 1 && segment.repetition === 1) {
			// "Afsenders afdeling", element: 4, component: 1
			this.currentEdifactLetter.senderId = +this.getDataString(segment, 2, 0)
			this.currentEdifactLetter.senderOrganisation = this.getDataString(segment, 4, 0)
			this.currentEdifactLetter.senderCodeSystem = this.getDataString(segment, 2, 1)
			this.currentEdifactLetter.senderCodeOrganisation = this.getDataString(segment, 2, 2)
		}
		else if (segment.sequence === 1 && segment.segmentGroup === 1 && segment.repetition === 1) {
			this.currentEdifactLetter.recieverId = +this.getDataString(segment, 2, 0)
			this.currentEdifactLetter.recieverOrganisation = this.getDataString(segment, 4, 0)
			this.currentEdifactLetter.recieverCodeSystem = this.getDataString(segment, 2, 1)
			this.currentEdifactLetter.recieverCodeOrganisation = this.getDataString(segment, 2, 2)
		}
		else if (segment.sequence === 2 && segment.segmentGroup === 1 && segment.repetition === 1) {
			this.currentEdifactLetter.referrerTitle = this.getDataString(segment, 4, 0)
			this.currentEdifactLetter.referrerName = this.getDataString(segment, 4, 1)
		}
	}

	handleADRSegment(segment: EdifactSegment) {
		if (segment.sequence === 0 && segment.segmentGroup === 1 && segment.repetition === 1) {
			// "Brevafsenders adresse", element: 2, component: 1
			// "Brevafsenders stednavn", element: 2, component: 2
			// "Brevafsenders by", element: 3, component: 0
			// "Brevafsenders postnummer", element: 4, component: 0
		}
		else if (segment.sequence === 1 && segment.segmentGroup === 1 && segment.repetition === 1) {
			// "Brevmodtagers adresse", element: 2, component: 1
			// "Brevmodtagers stednavn", element: 2, component: 2
			// "Brevmodtagers by", element: 3, component: 0
			// "Brevmodtagers postnummer", element: 4, component: 0
		}
		else if (segment.sequence === 3 && segment.segmentGroup === 1 && segment.repetition === 1) {
			// "Brevhenvisers adresse", element: 2, component: 1
			// "Brevhenvisers stednavn", element: 2, component: 2
			// "Brevhenvisers by", element: 3, component: 0
			// "Brevhenvisers postnummer", element: 4, component: 0
		}
	}

	handleSPRSegment(segment: EdifactSegment) {
		this.currentEdifactLetter.letterType = this.getDataString(segment, 3, 0)
	}

	handleRFFSegment(segment: EdifactSegment) {
		if (segment.sequence === 2 && segment.segmentGroup === 1 && segment.repetition === 1) {
			this.currentEdifactLetter.PartnerId = +this.getDataString(segment, 1, 1)
		}
		else if (segment.sequence === 4 && segment.segmentGroup === 2 && segment.repetition === 1) {
			this.currentEdifactLetter.ForloebsNr = this.getDataString(segment, 1, 1)
		}
	}

	handleCINSegment(segment: EdifactSegment) {
		if (segment.sequence === 4 && segment.segmentGroup === 2 && segment.repetition === 1) {
			// "Henvisningstype", element: 1, component: 0
			// "Kodenummer", element: 2, component: 0
			// "Kodetype", element: 2, component: 1
			// "Kodebetydning", element: 2, component: 3
		}
		else if (segment.sequence === 4 && segment.segmentGroup === 2 && segment.repetition >= 2 && segment.repetition <= 6) {
			// "Henvisningstype", element: 1, component: 0
			// "Kodenummer", element: 2, component: 0
			// "Kodetype", element: 2, component: 1
			// "Kodebetydning", element: 2, component: 3
		}
	}

	handleFTXSegment(segment: EdifactSegment) {
		if (segment.sequence === 4 && segment.segmentGroup === 2 && segment.repetition === 1) {
			// "Henvisers angivelse af hevisningsårsagen/ønsket behandling", element: 4, component: 0
		}
		else if (segment.sequence === 4 && segment.segmentGroup === 12 && segment.repetition >= 1 && segment.repetition <= 9) {
			const currentAnam = this.getAnam(segment)
			if (this.currentEdifactLetter.anamAll.find((value) => {
				return value.format === currentAnam.format && value.text === currentAnam.text
			}))
				return
			this.currentEdifactLetter.anamAll.push({ format: currentAnam.format, text: currentAnam.text })
			const currentAnamValue = this.getDataString(segment, 1, 0)
			switch (currentAnamValue) {
				case "IN":
					if (this.currentEdifactLetter.previousAnamValue === currentAnamValue) {
						const anam = this.currentEdifactLetter.anamIN.pop()
						anam.text += currentAnam.text
						this.currentEdifactLetter.anamIN.push(anam)
					}
					else {
						this.currentEdifactLetter.anamIN.push(currentAnam)
					}
					break
				case "NC":
					if (this.currentEdifactLetter.previousAnamValue === currentAnamValue) {
						const anam = this.currentEdifactLetter.anamNC.pop()
						anam.text += currentAnam.text
						this.currentEdifactLetter.anamNC.push(anam)
					}
					else {
						this.currentEdifactLetter.anamNC.push(currentAnam)
					}
					break
				case "AL":
					if (this.currentEdifactLetter.previousAnamValue === currentAnamValue) {
						const anam = this.currentEdifactLetter.anamAL.pop()
						anam.text += currentAnam.text
						this.currentEdifactLetter.anamAL.push(anam)
					}
					else {
						this.currentEdifactLetter.anamAL.push(currentAnam)
					}
					break
				case "MT":
					if (this.currentEdifactLetter.previousAnamValue === currentAnamValue) {
						const anam = this.currentEdifactLetter.anamMT.pop()
						anam.text += currentAnam.text
						this.currentEdifactLetter.anamMT.push(anam)
					}
					else {
						this.currentEdifactLetter.anamMT.push(currentAnam)
					}
					break
				case "CF":
					if (this.currentEdifactLetter.previousAnamValue === currentAnamValue) {
						const anam = this.currentEdifactLetter.anamCF.pop()
						anam.text += currentAnam.text
						this.currentEdifactLetter.anamCF.push(anam)
					}
					else {
						this.currentEdifactLetter.anamCF.push(currentAnam)
					}
					break
			}
			this.currentEdifactLetter.previousAnamValue = currentAnamValue
		}
		else if (segment.sequence === 4 && segment.segmentGroup === 18 && segment.repetition >= 1 && segment.repetition <= 9) {
			this.currentEdifactLetter.ForbeholdtSygehuset += (this.getDataString(segment, 4, 0) ? this.getDataString(segment, 4, 0) + "\n" : "")
				+ (this.getDataString(segment, 4, 1) ? this.getDataString(segment, 4, 1) + "\n" : "")
				+ (this.getDataString(segment, 4, 2) ? this.getDataString(segment, 4, 2) + "\n" : "")
				+ (this.getDataString(segment, 4, 3) ? this.getDataString(segment, 4, 3) + "\n" : "")
				+ (this.getDataString(segment, 4, 4) ? this.getDataString(segment, 4, 4) + "\n" : "")
		}
	}

	getAnam(segment: EdifactSegment): { format: string; text: string; } {
		return {
			format: this.getDataString(segment, 2, 0),
			text: this.getAnamString(segment)
		}
	}

	getAnamString(segment: EdifactSegment): string {
		return this.getAnamComponentString(segment, 4, 0)
			+ this.getAnamComponentString(segment, 4, 1)
			+ this.getAnamComponentString(segment, 4, 2)
			+ this.getAnamComponentString(segment, 4, 3)
			+ this.getAnamComponentString(segment, 4, 4)
	}

	getAnamComponentString(segment: EdifactSegment, element: number, component: number): string {
		let componentString = this.getDataString(segment, element, component)
		if (!componentString)
			return ""
		const firstLetter = componentString.substr(0, 1)
		if (firstLetter === firstLetter.toUpperCase() && firstLetter !== firstLetter.toLowerCase())
			componentString = "\n" + componentString
		return componentString + "\n"
	}

	handlePNASegment(segment: EdifactSegment) {
		if (segment.sequence === 4 && segment.segmentGroup === 7 && segment.repetition === 1) {
			this.currentEdifactLetter.patientCPR = +this.getDataString(segment, 2, 0)
			this.currentEdifactLetter.patientLastName = this.getDataString(segment, 5, 1)
			this.currentEdifactLetter.patientFirstName = this.getDataString(segment, 6, 1)
		}
	}

	handleUNTSegment(segment: EdifactSegment) {
		// "Brev ID", element: 2, component: 0
		this.currentEdifactLetter.AntSeg = +this.getDataString(segment, 1, 0)
	}

	handleUNZSegment(segment: EdifactSegment) {
		// "Kuvert ID", element: 2, component: 0
		this.edifactEnvelope.AntUNH = +this.getDataString(segment, 1, 0)
	}

	getDataString(segment: EdifactSegment, element: number, component: number): string {
		return EdifactHelper.getComponentString(segment, element, component)
	}
}
