export abstract class Colour {
	static HSVtoRGB(h, s, v) {
		let r, g, b, i, f, p, q, t
		i = Math.floor(h * 6)
		f = h * 6 - i
		p = v * (1 - s)
		q = v * (1 - f * s)
		t = v * (1 - (1 - f) * s)
		switch (i % 6) {
			case 0:
				r = v
				g = t
				b = p
				break
			case 1:
				r = q
				g = v
				b = p
				break
			case 2:
				r = p
				g = v
				b = t
				break
			case 3:
				r = p
				g = q
				b = v
				break
			case 4:
				r = t
				g = p
				b = v
				break
			case 5:
				r = v
				g = p
				b = q
				break
		}
		return {
			r: Math.round(r * 255),
			g: Math.round(g * 255),
			b: Math.round(b * 255)
		}
	}

	static HSLtoRGB(h, s, l) {
		let r, g, b

		if (s === 0) {
			r = g = b = l // achromatic
		} else {
			const hue2rgb = (_p, _q, _h) => {
				if (_h < 0) _h += 1
				if (_h > 1) _h -= 1
				if (_h < 1 / 6) return _p + (_q - _p) * 6 * _h
				if (_h < 1 / 2) return _q
				if (_h < 2 / 3) return _p + (_q - _p) * (2 / 3 - _h) * 6
				return p
			}

			const q = l < 0.5 ? l * (1 + s) : l + s - l * s
			const p = 2 * l - q
			r = hue2rgb(p, q, h + 1 / 3)
			g = hue2rgb(p, q, h)
			b = hue2rgb(p, q, h - 1 / 3)
		}

		return { r: Math.round(r * 255), g: Math.round(g * 255), b: Math.round(b * 255)}
	}

	static ToRgbaString(r, g, b?, a?) {
		if (r.r !== undefined) {
			a = g
			g = r.g
			b = r.b
			r = r.r
		}
		return "rgba(" + r + ", " + g + ", " + b + ", " + a + ")"
	}

	static ToRgbString(r, g?, b?) {
		if (r.r !== undefined) {
			g = r.g
			b = r.b
			r = r.r
		}
		return "rgba(" + r + ", " + g + ", " + b + ")"
	}

	static weirdRoundToPowerOfTwo(n) {
		return Math.pow(2, Math.floor(Math.log2(n * 2)))
	}

	static mapIntegerToZeroToOne(n) {
		if (n < 1) return 0
		const m = this.weirdRoundToPowerOfTwo(n)
		return (1 + 2 * (n - m / 2)) / m
	}
}
