﻿import { OnInit } from "@angular/core"
import { BaseModelSubscriber } from "./base-model-subscriber"
import { BaseService } from "./base-service"
import { IdunModel } from "./models/idun-model"
import { LanguageService } from "idun-layout/language.service"

export class ListComponent<Model extends IdunModel> extends BaseModelSubscriber implements OnInit {
	list: Model[]
	service: BaseService<Model>

	constructor(
		service: BaseService<Model>,
		public l: LanguageService
		) {
		super()
		this.service = service
	}

	ngOnInit(): void {
		this.subscribeToList(this.service,
			(list: Model[]) => {
				this.list = list
			})
	}
}
