﻿import { IdunModel } from "./idun-model"

export class Bookmark extends IdunModel {
	DBID: number
	IconPath: string
	IdunRoutePath: string
	MouseoverText: string

	constructor(source?: any) {
		super()
		for (const prop in source) this[prop] = source[prop]
	}

	getTitle(): string {
		return this.IdunRoutePath
	}

	isSinglePageBookmark() {
		if (!this.IdunRoutePath) {
			console.warn("Bookmark without path: " + this.DBID + " - " + this.MouseoverText)
			return true
		}
		return this.IdunRoutePath.indexOf("(left-content:") === -1
	}
}
