﻿import { SearchableIdunModel } from "./idun-model"
import { CompetenceSet } from "./competence-set"

export class StaffGroup extends SearchableIdunModel {
	DBID: number
	Title: string
	Competences: CompetenceSet
	Permissions: string[]

	constructor(source?: any) {
		super()
		this.Competences = new CompetenceSet()
		this.Permissions = []
		for (const prop in source) this[prop] = source[prop]
		if (source) {
			this.Competences = new CompetenceSet(source.Competences)
			this.Permissions = source.Permissions.map(e => e)
		}
	}

	getTitle(): string {
		return this.Title
	}

	searchAttributes() {
		return [this.Title]
	}

	hasPermission(permission: Permission) {
		switch (permission) {
			case Permission.Open: return true
			case Permission.Admin: return this.Permissions.includes("Admin")
			case Permission.EditProcedures: return this.Permissions.includes("EditProcedures")
			case Permission.ReadPatients: return this.Permissions.includes("ReadPatients")
			case Permission.EditPatients: return this.Permissions.includes("EditPatients")
			case Permission.EditJournalEntries: return this.Permissions.includes("EditJournalEntries")
			case Permission.EditJournalTemplates: return this.Permissions.includes("EditJournalTemplates")
			case Permission.EditTreatmentCodes: return this.Permissions.includes("EditTreatmentCodes")
			case Permission.EditPartners: return this.Permissions.includes("EditPartners")
			case Permission.EditResources: return this.Permissions.includes("EditResources")
			case Permission.EditStatus: return this.Permissions.includes("EditStatus")
			case Permission.EditStatusType: return this.Permissions.includes("EditStatusType")
		}
		return false
	}
}

export enum Permission {
	Open,
	Admin,
	EditProcedures,
	ReadPatients,
	EditPatients,
	EditTreatmentCodes,
	EditJournalEntries,
	EditJournalTemplates,
	EditPartners,
	EditResources,
	EditStatus,
	EditStatusType
}
