import { Component, ElementRef, ViewChild, Input } from "@angular/core"

@Component({
	selector: "idun-checkbox-grid",
	templateUrl: "checkbox-grid.component.html",
	styleUrls: ["checkbox-grid.component.scss"]
})
export class CheckboxGridComponent<T> {
	@Input() title = ""
	@Input() rowGroups: {
		getTitle: () => string,
		getHint?: () => string,
		toggle: (target: T) => void,
		get: (target: T) => boolean,
		expanded: boolean,
		rows: { getTitle: () => string, toggle: (target: T) => void, get: (target: T) => boolean }[]
	}[] = []
	@Input() columns: { getTitle: () => string, getHoverTitle: () => string, source: T, disabled?: () => boolean }[] = []

	@Input() display = false
	@Input() width = ""

	@ViewChild("container") containerElement: ElementRef
	@ViewChild("content") contentElement: ElementRef

	onArrowClick(event, change: number) {
		event.stopPropagation()
		const container = this.containerElement.nativeElement
		const content = this.contentElement.nativeElement
		const blub = content.style.left ? content.style.left.substr(0, content.style.left.length - 2) : 0
		content.style.left = (+blub + change) + "px"
		if (content.offsetLeft > container.offsetLeft) {
			content.style.left = 0 + "px"
		}
		if (content.offsetLeft + content.offsetWidth < container.offsetLeft + container.offsetWidth) {
			content.style.left = container.offsetWidth - content.offsetWidth + "px"
		}
	}

	get showArrows() {
		if (!this.contentElement || !this.containerElement)
			return false
		return this.containerElement.nativeElement.offsetWidth < this.contentElement.nativeElement.offsetWidth
	}

	isDisabled(column: { getTitle: () => string, getHoverTitle: () => string, source: T, disabled?: () => boolean }) {
		if (this.isDisplay())
			return true
		return column.disabled ? column.disabled() : false
	}

	isDisplay() {
		return this.display ? "" : null
	}
}
