﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { UserService } from "idun-main/user.service"
import { LanguageService } from "idun-layout/language.service"

import { PatientSummary } from "idun-common/models/patient-summary"
import { Permission } from "idun-common/models/staff-group"

@Injectable()
export class PatientSummaryService extends ExtendedService<PatientSummary> {
	constructor(
		private userService: UserService,
		http: Http,
		l: LanguageService
	) {
		super(http, l,
			PatientSummary,
			"api/PatientSummary",
			"patient",
			() => l.patient
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.EditPatients)
		this.icon = "Patient"
		this.color = "var(--color-patient)"
		this.loadAll = false

		this.setupList({
			destination: (model: PatientSummary) => "patient-journal/" + model.DBID
				+ "(left-content:patient/" + model.DBID + ")",
			entryHeader: (model: PatientSummary) => model.Name,
			contentList: [(model: PatientSummary) => {
				return this.l.common.birthdate + ": " + model.Birthdate
			}],
			buttonList: [(model: PatientSummary) => {
				return {
					callback: (event) => {
						event.stopPropagation()
						this.userService.selectPatient(model.DBID)
					}, getText: () => this.l.common.select
				}
			}]
		})
		this.listInfo.searchTypes.push("Name")
		this.listInfo.searchTypes.push("UniqueIdentification")
	}
}
