import { Component, Input, Output, EventEmitter } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { CalendarEntryProvider } from "idun-calendar/calendar-entry-provider"

@Component({
	selector: "idun-column-box",
	templateUrl: "column-box.component.html",
	styleUrls: ["column-box.component.scss"]
})
export class ColumnBoxComponent {
	@Input() mode: "entries" | "colors" = "entries"
	@Input() allowChangingMode = true
	@Input() providers: CalendarEntryProvider[]
	@Input() selected: string
	@Input() colors: ColorRow[]
	@Input() colorMode: string
	@Input() colorModes: ColorMode[]
	@Input() colorTitle: string

	@Output() addColumn = new EventEmitter()
	@Output() selectColumn = new EventEmitter<string>()
	@Output() removeColumn = new EventEmitter<string>()
	@Output() setColorMode = new EventEmitter<string>()

	constructor(
		public l: LanguageService
	) { }

	get selections(): Selection[] {
		return this.providers.map((provider, i) => {
			return {
				index: i + "",
				text: () => provider.getTitle() + " - " + provider.getSubtitle(),
				hoverText: () => provider.getHovertitle(),
				selected: i === +this.selected
			}
		})
	}

	getColor(hue) {
		return `hsla(${hue}, 100%, 45%, 0.6)`
	}
}

interface Selection {
	index: string
	text: () => string,
	hoverText: () => string,
	selected: boolean
}

export interface ColorMode {
	name: string
	title: () => string
}

export interface ColorRow {
	title: () => string
	hue: number
}

