import { NgModule } from "@angular/core"
import { FormsModule } from "@angular/forms"
import { CommonModule } from "@angular/common"
import { IdunLayoutModule } from "idun-layout/idun-layout.module"
import { CalendarComponent } from "./calendar.component"
import { CalendarDayComponent } from "./calendar-day.component"
import { CalendarWeekComponent } from "./calendar-week.component"
import { CalendarEntryHorisontalComponent } from "./calendar-entry-horisontal.component"
import { CalendarEntryVerticalComponent } from "./calendar-entry-vertical.component"
import { CalendarColumnDisplayComponent } from "./calendar-column-display.component"
import { MultiCalendarComponent } from "./multi-calendar.component"
import { MultiCalendarRowsComponent } from "./multi-calendar-rows.component"
import { MultiCalendarDayRowsComponent } from "./multi-calendar-day-rows.component"

@NgModule({
	imports: [FormsModule, CommonModule, IdunLayoutModule],
	declarations: [
		CalendarComponent,
		CalendarDayComponent,
		CalendarWeekComponent,
		CalendarEntryHorisontalComponent,
		CalendarEntryVerticalComponent,
		CalendarColumnDisplayComponent,
		MultiCalendarComponent,
		MultiCalendarRowsComponent,
		MultiCalendarDayRowsComponent
	],
	exports: [
		CalendarComponent,
		CalendarWeekComponent,
		MultiCalendarComponent
	]
})
export class IdunCalendarModule { }
