﻿import { Resource } from "idun-common/models/resource"
import { ModelTestData } from "./model-test-data"
import { CompetenceSet } from "idun-common/models/competence-set"
import { TimeInterval } from "idun-common/models/time-interval"
import { getCalendarEventTestDataForResource } from "./calendar-event-test-data"
import { getProcedureEventTestDataForResource } from "./procedure-event-test-data"

const baseUrl = "api/resource/"

export const resourceTestData = [
	new Resource({
		DBID: 1,
		Title: "Røntgenrum",
		Abbreviation: "Rø",
		Description: ""
	}),
]

ModelTestData.loadFromLocalStorage(baseUrl, resourceTestData, Resource)

export const testResourceCompetences: { [index: number]: CompetenceSet } = {}

const otherResponses = {}

otherResponses[baseUrl + "isresourceavailable"] = (parameters: { id: number, start: Date, end: Date }) => {
	const interval = TimeInterval.fromInterval(parameters)
	const isAvailable = !hasEventsDuring(interval, parameters.id)
	return JSON.stringify(isAvailable)
}

function hasEventsDuring(interval: TimeInterval, resourceId: number) {
	return getCalendarEventTestDataForResource(resourceId, interval).length > 0
		|| getProcedureEventTestDataForResource(resourceId, interval).length > 0
}

export function ResourceResponses() {
	return {
		...ModelTestData.commonLocalStorageResponses(Resource, baseUrl, resourceTestData),
		...otherResponses
	}
}
