﻿import { Procedure, ProcedureVariation } from "idun-common/models/procedure"
import { ModelTestData } from "./model-test-data"
import { TimeInterval } from "idun-common/models/time-interval"
import { calendarEventTestData, getCalendarEventTestDataForStaff } from "./calendar-event-test-data"
import { procedureEventTestData, getProcedureEventTestDataForStaff } from "./procedure-event-test-data"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { flatten } from "utility/common"
import { staffTestData } from "./staff-test-data"
import { CalendarEvent } from "idun-common/models/calendar-event"
import { resourceTestData, testResourceCompetences } from "./resource-test-data"
import { workIntervalTestData } from "./work-interval-test-data"

const baseUrl = "api/procedure/"

export const procedureTestData = []

ModelTestData.loadFromLocalStorage(baseUrl, procedureTestData, Procedure)

function findAvailableStaff(interval: TimeInterval) {
	return staffAtWork(interval)
		.filter(id => !hasEventsDuring(interval, id))
		.map(id => staffTestData.find(e => e.DBID === id))
}

function staffAtWork(interval: TimeInterval) {
	return workIntervalTestData
		.filter(({ Times }) => Times.contains(interval))
		.map(e => e.StaffID)
}

function hasEventsDuring(interval: TimeInterval, staffId: number) {
	return getCalendarEventTestDataForStaff(staffId, interval).length > 0
		|| getProcedureEventTestDataForStaff(staffId, interval).length > 0
}

export function findOccupiedStaff(
	interval: TimeInterval,
	calendarEvents: CalendarEvent[] = calendarEventTestData,
	procedureEvents: ProcedureEvent[] = procedureEventTestData
) {
	return [
		...occupiedStaffOf(calendarEvents, interval),
		...occupiedStaffOf(procedureEvents, interval)]
		.distinct()
}

function occupiedStaffOf(
	events: { OccupiedStaff: number[], Times: TimeInterval }[],
	interval: TimeInterval
) {
	return flatten(events
		.filter(e => e.Times.intersects(interval))
		.map(e => e.OccupiedStaff))
}

function findAvailableResources(interval: TimeInterval) {
	const occupiedResources = findOccupiedResources(interval)
	return resourceTestData.filter(e => !occupiedResources.includes(e.DBID))
}

export function findOccupiedResources(
	interval: TimeInterval,
	calendarEvents: CalendarEvent[] = calendarEventTestData,
	procedureEvents: ProcedureEvent[] = procedureEventTestData
) {
	return [
		...occupiedResourcesOf(calendarEvents, interval),
		...occupiedResourcesOf(procedureEvents, interval)]
		.distinct()
}

function occupiedResourcesOf(
	events: { OccupiedResources: number[], Times: TimeInterval }[],
	interval: TimeInterval
) {
	return flatten(events
		.filter(e => e.Times.intersects(interval))
		.map(e => e.OccupiedResources))
}

const otherResponses = {}

otherResponses[baseUrl + "getavailablestafffor"] = (parameters: { id: number, start: Date, end: Date }) => {
	const procedure = procedureTestData.find(e => e.DBID === parameters.id)
	const staff = findAvailableStaff(TimeInterval.fromInterval(parameters))
		.filter(e => e.Competences.canPerform(procedure))
		.map(e => e.DBID)
	return JSON.stringify(staff)
}

otherResponses[baseUrl + "getavailableresourcesfor"] = (parameters: { id: number, start: Date, end: Date }) => {
	const procedure = procedureTestData.find(e => e.DBID === parameters.id)
	const interval = TimeInterval.fromInterval(parameters)
	const resources = findAvailableResources(interval)
		.filter(e => testResourceCompetences[e.DBID].canPerform(procedure))
		.map(e => e.DBID)
	return JSON.stringify(resources)
}

export function ProcedureResponses() {
	return {
		...ModelTestData.commonLocalStorageResponses(Procedure, baseUrl, procedureTestData),
		...otherResponses
	}
}
