﻿import { Procedure } from "./procedure"
import { ProcedureType } from "./procedure-type"
import { Resource } from "./resource"

export class CompetenceSet {
	AllowedProcedureTypes: number[]
	AllowedProcedures: number[]
	DisallowedProcedures: number[]

	constructor(source?: any) {
		this.AllowedProcedureTypes = []
		this.AllowedProcedures = []
		this.DisallowedProcedures = []
		if (source) {
			for (const prop in source) this[prop] = source[prop]
			if (source.AllowedProcedureTypes)
				this.AllowedProcedureTypes = source.AllowedProcedureTypes.map(e => e)
			if (source.AllowedProcedures)
				this.AllowedProcedures = source.AllowedProcedures.map(e => e)
			if (source.DisallowedProcedures)
				this.DisallowedProcedures = source.DisallowedProcedures.map(e => e)
		}
	}

	canPerform(procedure: Procedure) {
		if (this.DisallowedProcedures.includes(procedure.DBID))
			return false
		if (this.AllowedProcedures.includes(procedure.DBID))
			return true
		return this.AllowedProcedureTypes.includes(procedure.ProcedureTypeID)
	}

	performableProcedures(allProcedures: Procedure[]) {
		return allProcedures.filter(e => this.canPerform(e))
	}

	toggleProcedureCompetence(procedure: Procedure) {
		const hasCompetence = this.canPerform(procedure)
		this.removeProcedureExceptions(procedure)
		if (hasCompetence)
			this.DisallowedProcedures.push(procedure.DBID)
		else
			this.AllowedProcedures.push(procedure.DBID)
	}

	toggleProcedureTypeCompetence(procedureType: ProcedureType, procedures: Procedure[]) {
		const index = this.AllowedProcedureTypes.indexOf(procedureType.DBID)
		const relevantProcedures = procedures.filter(e => e.ProcedureTypeID === procedureType.DBID)
		for (const i in relevantProcedures)
			this.removeProcedureExceptions(relevantProcedures[i])
		if (index > -1)
			this.AllowedProcedureTypes.splice(index, 1)
		else
			this.AllowedProcedureTypes.push(procedureType.DBID)
	}

	private removeProcedureExceptions(procedure: Procedure) {
		let index = this.AllowedProcedures.indexOf(procedure.DBID)
		if (index > -1)
			this.AllowedProcedures.splice(index, 1)
		index = this.DisallowedProcedures.indexOf(procedure.DBID)
		if (index > -1)
			this.DisallowedProcedures.splice(index, 1)
	}

	static competencesForResource(resource: Resource, procedureTypes: ProcedureType[], procedures: Procedure[]) {
		const competences = new CompetenceSet()
		for (const i in procedureTypes) {
			const procedureType = procedureTypes[i]
			const relevantProcedures = procedures.filter(p => p.ProcedureTypeID === procedureType.DBID)
			const proceduresNeedingResource = relevantProcedures.filter(p => p.ResourceRequirements.includes(resource.DBID))
			if (proceduresNeedingResource.length === relevantProcedures.length)
				competences.AllowedProcedureTypes.push(procedureType.DBID)
		}
		competences.AllowedProcedures = procedures
			.filter(p => p.ResourceRequirements.includes(resource.DBID) && competences.AllowedProcedureTypes.includes(p.ProcedureTypeID))
			.map(e => e.DBID)
		return competences
	}

	competencesSharedBy(other: CompetenceSet, procedures: Procedure[]): "none" | "some" | "all" | "more" {
		const proceduresThisCanPerform = procedures.filter(p => this.canPerform(p))
		const proceduresOtherCanPerform = procedures.filter(p => other.canPerform(p))
		const proceduresBothCanPerform = proceduresThisCanPerform.filter(p => other.canPerform(p))
		if (proceduresThisCanPerform.length === 0)
			return "none"
		if (proceduresThisCanPerform.length === proceduresBothCanPerform.length)
			return proceduresOtherCanPerform.length > proceduresBothCanPerform.length ? "more" : "all"
		if (proceduresBothCanPerform.length)
			return "some"
		return "none"
	}
}
