import { Component, OnInit, ElementRef } from "@angular/core"
import { Router, ActivatedRoute } from "@angular/router"
import { FormComponent } from "idun-common/form-component"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LanguageService } from "idun-layout/language.service"
import { LayoutService } from "idun-layout/layout.service"
import { IdunId } from "utility/idun-id"

import { CalendarEvent } from "idun-common/models/calendar-event"
import { CalendarEventService } from "idun-main/model-services/calendar-event.service"
import { Patient, TelephoneNumber } from "idun-common/models/patient"
import { PatientService } from "idun-main/model-services/patient.service"
import { PatientSummaryService } from "idun-main/model-services/patient-summary.service"
import { ProcedureService } from "idun-main/model-services/procedure.service"

import { BookingRequest, RequestStatus } from "idun-common/models/booking-request"
import { BookingRequestService } from "idun-main/model-services/booking-request.service"

@Component({
	selector: "idun-booking-request",
	templateUrl: "booking-request.component.html"
})
export class BookingRequestComponent extends FormComponent<BookingRequest> implements OnInit {
	navigater: NavigationHandler
	request: BookingRequest
	event: CalendarEvent
	patient: Patient = null

	constructor(
		private myElement: ElementRef,
		private router: Router,
		private route: ActivatedRoute,
		private layoutService: LayoutService,
		private bookingRequestService: BookingRequestService,
		private calendarEventService: CalendarEventService,
		private patientService: PatientService,
		public l: LanguageService
	) {
		super(bookingRequestService)
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit(): void {
		this.handleRouteParameters(this.route)
	}

	onModelUpdate(request: BookingRequest) {
		this.request = request
		this.parsePatientData()

	}

	parsePatientData() {
		const patientData = JSON.parse(this.request.PatientData)
		this.patientService.getPatientFromCpr(patientData.cpr).then((patient: Patient) => {
			if (patient.DBID) {
				this.patient = patient
				// TODO: Compare to patient data from request
			}
			else {
				this.patient = new Patient(patientData)
				const idunid = new IdunId()
				this.patient.UniqueIdentification = idunid.identification
				this.patient.TelephoneNumbers[0] = new TelephoneNumber()
				this.patient.TelephoneNumbers[0].Primary = true
				this.patient.TelephoneNumbers[0].Type = "other"
				this.patient.TelephoneNumbers[0].Number = patientData.Telephone
			}
		})
	}

	acceptPatient() {
		// TODO: Make this work with patient event model (Issue #1884)

		// this.event = new CalendarEvent().setupProcedureEvent(
		// 	this.request.ProcedureID, {
		// 		time: <any>this.request.Times,
		// 		patientID: this.patient.DBID,
		// 		procedureService: this.procedureService,
		// 		patientSummaryService: this.patientSummaryService
		// 	})
	}

	updatedPatient(patient) {
		this.patient = patient
	}

	accept() {
		this.calendarEventService.add(this.event).then((event: CalendarEvent) => {
			this.request.CalendarEventID = event.DBID
			this.request.Status = RequestStatus.Accepted
			this.bookingRequestService.update(this.request)
			this.navigater.current("booking-request/list")
		})
	}

	deny() {
		this.request.Status = RequestStatus.Denied
		this.bookingRequestService.update(this.request)
		this.navigater.current("booking-request/list")
	}
}
