﻿import { IdunModel } from "./idun-model"
import { DateHelper } from "utility/date-helper"
import { UnsavedFile, DatabaseFileReference } from "./database-file"

export class JournalEntry extends IdunModel {
	DBID: number
	Date: string
	PatientID: number
	StaffID: number
	Description: TextEntry[]
	ExternalStaff: string
	Files: (UnsavedFile | DatabaseFileReference)[]

	constructor(source?: any) {
		super()
		this.Description = [new TextEntry({ Title: "new", Content: "" })]
		this.Files = []
		this.Date = DateHelper.dateTimeToString(new Date())
		if (source) {
			for (const prop in source) this[prop] = source[prop]
			this.Date = DateHelper.dateTimeToString(DateHelper.parseDate(source.Date))
			if (source.Description)
				this.Description = source.Description.map(e => new TextEntry(e))
			if (source.Files)
				this.Files = source.Files.map(e => new DatabaseFileReference(e))
		}
	}

	getTitle(): string {
		return this.Description[this.Description.length - 1].Content.split("\n")[0].substring(0, 50)
	}

	getTime() {
		return DateHelper.parseDate(this.Date).getTime()
	}
}

export class TextEntry {
	Title: string
	Content: string

	constructor(source?: any) {
		if (source)
			for (const prop in source) this[prop] = source[prop]
	}
}
