import { DateHelper } from "utility/date-helper"
import { TimeInterval } from "./time-interval"

export class TimeIntervalSeries {
	public Intervals: TimeInterval[] = []

	constructor(...intervals: TimeInterval[]) {
		this.Intervals = []
		for (const interval of intervals)
			this.Intervals.push(new TimeInterval(interval))
	}

	static empty(): TimeIntervalSeries {
		return new TimeIntervalSeries()
	}

	isEmpty() {
		if (this.Intervals == null)
			return true
		if (this.Intervals.length === 0)
			return true
		if (this.Intervals.length === 1 && this.Intervals[0].StartTime.getTime() === this.Intervals[0].EndTime.getTime())
			return true
		return false
	}

	isOrdered() {
		for (let i = 0; i < this.Intervals.length - 1; i++)
			if (this.Intervals[i + 1].StartTime.getTime() <= this.Intervals[i].EndTime.getTime())
				return false
		return true
	}

	order() {
		const sorted = this.Intervals.sort((a, b) => b.StartTime.getTime() - a.StartTime.getTime())
		for (let i = 0; i < sorted.length - 1; i++) {
			if (sorted[i + 1].StartTime.getTime() <= sorted[i].EndTime.getTime()) {
				sorted[i] = TimeInterval.from(sorted[i].StartTime, sorted[i + 1].EndTime)
				sorted.splice(i + 1, 1)
				i--
			}
		}
		this.Intervals = sorted
	}

	intersection(other: TimeIntervalSeries | TimeInterval): TimeIntervalSeries {
		if (other instanceof TimeIntervalSeries)
			return this.intersectionWithTimeIntervalSeries(other)
		return this.intersectionWithTimeInterval(other)
	}

	private intersectionWithTimeIntervalSeries(other: TimeIntervalSeries) {
		let result = TimeIntervalSeries.empty()
		const intervalSeries = []
		for (const interval of other.Intervals)
			intervalSeries.push(this.intersection(interval))
		for (const series of intervalSeries)
			result = result.add(series)
		return result
	}

	private intersectionWithTimeInterval(interval: TimeInterval): TimeIntervalSeries {
		const intervals = []
		for (const time of this.Intervals) {
			const temp = interval.intersection(time)
			if (temp)
				intervals.push(temp)
		}
		return new TimeIntervalSeries(...intervals)
	}

	add(other: TimeIntervalSeries | TimeInterval): TimeIntervalSeries {
		if (other instanceof TimeIntervalSeries)
			return this.addTimeIntervalSeries(other)
		return this.addTimeInterval(other)
	}

	private addTimeIntervalSeries(other: TimeIntervalSeries): TimeIntervalSeries {
		let result = new TimeIntervalSeries(...this.Intervals)
		for (const interval of other.Intervals)
			result = result.add(interval)
		return result
	}

	private addTimeInterval(interval: TimeInterval): TimeIntervalSeries {
		if (this.isEmpty())
			return new TimeIntervalSeries(interval)

		interval = new TimeInterval(interval)
		const list: TimeInterval[] = []
		if (interval.EndTime.getTime() < this.Intervals[0].StartTime.getTime()) {
			list.push(interval)
			list.push(...this.Intervals)
			return new TimeIntervalSeries(...list)
		}

		if (this.Intervals[this.Intervals.length - 1].EndTime.getTime() < interval.StartTime.getTime()) {
			list.push(...this.Intervals)
			list.push(interval)
			return new TimeIntervalSeries(...list)
		}

		let insertionPoint = -1
		let removeUntil = -1
		if (!this.isOrdered())
			this.order()
		for (let i = 0; i < this.Intervals.length; i++) {
			// this code assumes that intervals are ordered chronologically
			if (removeUntil === -1)
				insertionPoint = i
			if (this.Intervals[i].EndTime.getTime() < interval.StartTime.getTime())
				continue
			if (interval.EndTime.getTime() < this.Intervals[i].StartTime.getTime())
				break
			if (this.Intervals[i].contains(interval))
				return new TimeIntervalSeries(...this.Intervals)

			if (interval.StartTime.getTime() <= this.Intervals[i].StartTime.getTime() && interval.EndTime.getTime() < this.Intervals[i].EndTime.getTime()) {
				interval.EndTime = this.Intervals[i].EndTime
				removeUntil = i
				break
			}
			else if (this.Intervals[i].StartTime.getTime() < interval.StartTime.getTime())
				interval.StartTime = this.Intervals[i].StartTime
			removeUntil = i
		}

		for (let n = 0; n < insertionPoint; n++)
			list.push(this.Intervals[n])
		list.push(interval)
		if (removeUntil !== -1)
			for (let j = removeUntil + 1; j < this.Intervals.length; j++)
				list.push(this.Intervals[j])

		return new TimeIntervalSeries(...list)
	}

	subtract(other: TimeIntervalSeries | TimeInterval): TimeIntervalSeries {
		if (other instanceof TimeIntervalSeries)
			return this.subtractTimeIntervalSeries(other)
		return this.subtractTimeInterval(other)
	}

	subtractTimeIntervalSeries(other: TimeIntervalSeries): TimeIntervalSeries {
		let result = new TimeIntervalSeries(...this.Intervals)
		for (const interval of other.Intervals)
			result = result.subtract(interval)
		return result
	}

	subtractTimeInterval(interval: TimeInterval): TimeIntervalSeries {
		const list: TimeInterval[] = []
		for (const inter of this.Intervals) {
			const intervals = TimeIntervalSeries.subtractTimeIntervals(inter, interval)
			for (const result of intervals.Intervals)
				list.push(result)
		}
		return new TimeIntervalSeries(...list)
	}

	static subtractTimeIntervals(first: TimeInterval, other: TimeInterval) {
		if (first.equals(other) || other.contains(first))
			return TimeIntervalSeries.empty()
		if (first.contains(other)) {
			if (first.StartTime.getTime() === other.StartTime.getTime())
				return new TimeIntervalSeries(TimeInterval.from(other.EndTime, first.EndTime))
			if (first.EndTime.getTime() === other.EndTime.getTime())
				return new TimeIntervalSeries(TimeInterval.from(first.StartTime, other.StartTime))
			return new TimeIntervalSeries(
				TimeInterval.from(first.StartTime, other.StartTime),
				TimeInterval.from(other.EndTime, first.EndTime))
		}
		if (first.intersects(other)) {
			if (first.StartTime.getTime() < other.StartTime.getTime())
				return new TimeIntervalSeries(TimeInterval.from(first.StartTime, other.StartTime))
			return new TimeIntervalSeries(TimeInterval.from(other.EndTime, first.EndTime))
		}
		return new TimeIntervalSeries(TimeInterval.from(first.StartTime, first.EndTime))
	}

	static mergeIntervalIntoList(
		interval: TimeInterval,
		intervals: TimeInterval[]
	) {
		const intersectingIntervals = this.removeAndReturnIntervalsIntersecting(interval, intervals)
		this.updateStartAndEndTimes(interval, intersectingIntervals)
		intervals.push(interval)
	}

	private static removeAndReturnIntervalsIntersecting(
		baseInterval: TimeInterval,
		intervals: TimeInterval[]
	) {
		const intersectingIntervals = []
		for (let i = 0; i < intervals.length; i++) {
			const interval = intervals[i]
			if (!baseInterval.intersectsInclusive(interval))
				continue
			intersectingIntervals.push(interval)
			intervals.splice(i, 1)
			i--
		}
		return intersectingIntervals
	}

	private static updateStartAndEndTimes(
		baseInterval: TimeInterval,
		intersectingIntervals: TimeInterval[]
	) {
		baseInterval.StartTime = intersectingIntervals
			.reduce(this.earliestTime, baseInterval.StartTime)
		baseInterval.EndTime = intersectingIntervals
			.reduce(this.latestTime, baseInterval.EndTime)
	}

	private static earliestTime(date: Date, interval: TimeInterval) {
		if (date.getTime() < interval.StartTime.getTime())
			return date
		return interval.StartTime
	}

	private static latestTime(date: Date, interval: TimeInterval) {
		if (date.getTime() < interval.EndTime.getTime())
			return interval.EndTime
		return date
	}
}
