﻿import { Component, HostBinding } from "@angular/core"
import { CalendarEntryBaseComponent } from "./calendar-entry-base.component"

@Component({
	selector: "idun-calendar-entry-vertical",
	templateUrl: "calendar-entry-vertical.component.html",
	styleUrls: ["calendar-entry-base.component.scss", "calendar-entry-vertical.component.scss"]
})
export class CalendarEntryVerticalComponent extends CalendarEntryBaseComponent {
	@HostBinding("style.grid-template-rows") get grid() {
		return `${this.startLength}fr ${this.entryLength}fr ${this.endLength}fr`
	}

	onMouseMove = (event: MouseEvent) => {
		if (!this.calendarEventEntry)
			return
		this.dragHandler.onMouseMove(this.mouseMinutePosition(event))
	}

	private mouseMinutePosition(event: MouseEvent) {
		const rect = this.calendarEventEntry.nativeElement.getBoundingClientRect()
		const mouseDeltaPixels = event.clientY - rect.top
		const totalPixelHeight = this.calendarEventEntry.nativeElement.parentElement.clientHeight
		const minuteSize = totalPixelHeight / (this.endMinute - this.startMinute)
		return Math.floor(mouseDeltaPixels / minuteSize)
	}

	get showIcon() {
		return this.entry.statusIcon
	}

	get startLength() {
		return this.sizeFrom(this.findStartOffset())
	}

	get entryLength() {
		return this.sizeFrom(this.findShownLength())
	}

	get endLength() {
		return this.sizeFrom(this.endMinute - this.entryEndMinute)
	}

	get textMargin() {
		if (this.entry.statusIcon)
			return "1.5rem"
		return "0"
	}
}
