﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { LanguageService } from "idun-layout/language.service"

import { Partner } from "idun-common/models/partner"
import { ProcedureService } from "./procedure.service"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { FormNumberRow } from "idun-layout/form/form-number.component"
import { FormTextareaRow } from "idun-layout/form/form-textarea.component"
import { FormProcedureChangesRow } from "idun-common/form/form-procedure-changes.component"
import { ProcedureTypeService } from "./procedure-type.service"
import { TreatmentCodeService } from "./treatment-code.service"
import { UserService } from "idun-main/user.service"
import { Permission } from "idun-common/models/staff-group"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"

@Injectable()
export class PartnerService extends ExtendedService<Partner> {
	constructor(
		http: Http,
		procedureService: ProcedureService,
		procedureTypeService: ProcedureTypeService,
		treatmentCodeService: TreatmentCodeService,
		userService: UserService,
		l: LanguageService
	) {
		super(http, l,
			Partner,
			"api/Partner",
			"partner",
			() => l.partner
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.EditPartners)
		this.icon = "Fundamental"
		this.color = "var(--color-economics)"

		this.setupForm({
			getHeader: (model) => l.partner.nameSingular + (model.Name ? ": " + model.Name : ""),
			rows: [
				new FormTextRow("Name", {
					label: () => l.common.name,
					requirements: [ValidationRequirement.ValueRequired(l)]
				}),
				new FormNumberRow("PercentageDiscount", {
					label: () => l.partner.discount,
					hint: () => l.partner.discountHint,
					requirements: [ValidationRequirement.ValueRequired(l)],
					endLabel: () => "%"
				}),
				new FormTextareaRow("Notes", {
					label: () => l.patient.notes
				}),
				new FormProcedureChangesRow("ProcedureChanges", {
					label: () => l.partner.procedureChanges,
					hint: () => l.partner.procedureChangesHint
				})
			]
		})
		this.setupList({
			entryHeader: (model) => model.Name,
			contentList: [(model) => {
				return this.l.partner.discount + ": " + model.PercentageDiscount + "%" + (model.Notes ? " - " + model.Notes : "")
			}]
		})
	}
}
