import { Injectable } from "@angular/core"
import { Http, Headers } from "@angular/http"
import { RequestHelper } from "utility/request-helper"
import { environment } from "../environments/environment"
import { BehaviorSubject } from "rxjs"
import { Language } from "./language/language"

@Injectable()
export class LanguageService {
	dictionary: { [text: string]: string } = {}
	temporary: { [text: string]: string } = {}
	hasLoaded$ = new BehaviorSubject<boolean>(false)

	language = new Language()

	get booking() { return this.proxiedTranslationCategory(this.language.booking) }
	get bookingHome() { return this.proxiedTranslationCategory(this.language.bookingHome) }
	get bookingOverview() { return this.proxiedTranslationCategory(this.language.bookingOverview) }
	get bookingRequest() { return this.proxiedTranslationCategory(this.language.bookingRequest) }
	get bookmark() { return this.proxiedTranslationCategory(this.language.bookmark) }
	get calendar() { return this.proxiedTranslationCategory(this.language.calendar) }
	get calendarEvent() { return this.proxiedTranslationCategory(this.language.calendarEvent) }
	get cardReaderToken() { return this.proxiedTranslationCategory(this.language.cardReaderToken) }
	get clinic() { return this.proxiedTranslationCategory(this.language.clinic) }
	get common() { return this.proxiedTranslationCategory(this.language.common) }
	get commonForm() { return this.proxiedTranslationCategory(this.language.commonForm) }
	get country() { return this.proxiedTranslationCategory(this.language.country) }
	get date() { return this.proxiedTranslationCategory(this.language.date) }
	get economic() { return this.proxiedTranslationCategory(this.language.economic) }
	get edifact() { return this.proxiedTranslationCategory(this.language.edifact) }
	get intro() { return this.proxiedTranslationCategory(this.language.intro) }
	get invoice() { return this.proxiedTranslationCategory(this.language.invoice) }
	get journalEntry() { return this.proxiedTranslationCategory(this.language.journalEntry) }
	get journalTemplate() { return this.proxiedTranslationCategory(this.language.journalTemplate) }
	get partner() { return this.proxiedTranslationCategory(this.language.partner) }
	get patient() { return this.proxiedTranslationCategory(this.language.patient) }
	get patientEvent() { return this.proxiedTranslationCategory(this.language.patientEvent) }
	get patientNote() { return this.proxiedTranslationCategory(this.language.patientNote) }
	get periodPlan() { return this.proxiedTranslationCategory(this.language.periodPlan) }
	get procedure() { return this.proxiedTranslationCategory(this.language.procedure) }
	get procedureEvent() { return this.proxiedTranslationCategory(this.language.procedureEvent) }
	get procedureType() { return this.proxiedTranslationCategory(this.language.procedureType) }
	get resource() { return this.proxiedTranslationCategory(this.language.resource) }
	get sidebar() { return this.proxiedTranslationCategory(this.language.sidebar) }
	get settings() { return this.proxiedTranslationCategory(this.language.settings) }
	get staff() { return this.proxiedTranslationCategory(this.language.staff) }
	get staffNotification() { return this.proxiedTranslationCategory(this.language.staffNotification) }
	get staffGroup() { return this.proxiedTranslationCategory(this.language.staffGroup) }
	get statistics() { return this.proxiedTranslationCategory(this.language.statistics) }
	get statusRequest() { return this.proxiedTranslationCategory(this.language.statusRequest) }
	get statusType() { return this.proxiedTranslationCategory(this.language.statusType) }
	get figure() { return this.proxiedTranslationCategory(this.language.figure) }
	get treatmentCode() { return this.proxiedTranslationCategory(this.language.treatmentCode) }
	get treatmentCodeType() { return this.proxiedTranslationCategory(this.language.TreatmentCodeType) }

	proxiedTranslationCategory<T extends Object>(original: T) {
		return new Proxy<T>(original, {
			get: function (target, name) {
				return target[name] || "MISSING:" + name.toString()
			}
		})
	}

	constructor(
		private http: Http
	) {
		if (environment.production)
			this.loadProductionTranslations()
		else
			this.loadDevelopmentTranslations()
	}

	private loadDevelopmentTranslations() {
		this.loadLanguage("dansk")
			.then((language) => {
				this.language = new Language(language)
				this.language.language = "dansk"
				this.hasLoaded$.next(true)
			})
	}

	private loadProductionTranslations() {
		RequestHelper.getRequest(this.http, "api/User/GetLanguage").then((response: any) => {
			this.language = new Language(response)
			this.hasLoaded$.next(true)
		}).catch(() => {
			this.dictionary = {}
		})
	}

	loadLanguage(language: string): Promise<Language> {
		const headers: Headers = new Headers({
			"Content-Type": "application/json"
		})
		return this.http.get("assets/languages/" + language + ".json", { headers: headers })
			.toPromise()
			.then((data: any) => {
				return new Language(data.json())
			})
	}

	format(text: string, ...args: any[]) {
		if (!text)
			return ""
		for (let i = 0; i < args.length; i++) {
			const reg = new RegExp("\\{" + i + "\\}", "gm")
			text = text.replace(reg, args[i])
		}
		return text
	}

	updateLanguage(language: string) {
		RequestHelper.postRequest(this.http, "api/User/UpdateLanguage", { language: language }).then((response) => {
			this.language = response.language
			const headers: Headers = new Headers({
				"Content-Type": "application/json"
			})
			this.http.get("assets/languages/" + response.language + ".json", { headers: headers })
				.toPromise()
				.then((data: any) => {
					this.dictionary = data.json()
				})
		})
	}

	abbreviatedDayName(day: Date | number) {
		const dayOfWeek = day instanceof Date
			? (day.getDay() || 7) - 1
			: day
		switch (dayOfWeek) {
			case 0: return this.date.mondayAbr
			case 1: return this.date.tuesdayAbr
			case 2: return this.date.wednesdayAbr
			case 3: return this.date.thursdayAbr
			case 4: return this.date.fridayAbr
			case 5: return this.date.saturdayAbr
			case 6: return this.date.sundayAbr
		}
	}

	dayName(day: Date | number) {
		const dayOfWeek = day instanceof Date
			? (day.getDay() || 7) - 1
			: day
		switch (dayOfWeek) {
			case 0: return this.date.monday
			case 1: return this.date.tuesday
			case 2: return this.date.wednesday
			case 3: return this.date.thursday
			case 4: return this.date.friday
			case 5: return this.date.saturday
			case 6: return this.date.sunday
		}
	}
}
