export class XREF01Envelope {
	ID: number													// M, KuvertID
	senderLocation: number							// M, AfsLok
	recieverLocation: number						// M, ModtLok
	sentDate: Date											// M, KuvSendtDato + KuvSendtKl
	letters: XREF01Letter[]

	// Other mandatory data
	UNOC: string												// M, UNOC
	KUVKVIT: string											// M, KUVKVIT
	AntUNH: number											// M, AntUNH

	isEdifactEnvelopeValid(): boolean {
		return this.areMandatoryFieldsValid()
			&& this.areLettersValid()
	}

	areMandatoryFieldsValid(): boolean {
		return this.ID
			&& this.senderLocation
			&& this.recieverLocation
			&& this.sentDate
			&& this.UNOC
			&& this.KUVKVIT
			&& !!this.AntUNH
	}

	areLettersValid(): boolean {
		if (!this.letters.length)
			return false
		for (const l of this.letters)
			if (!l.isLetterValid()) return false
		return true
	}
}
