﻿import { SearchableIdunModel } from "./idun-model"
import { DateHelper } from "utility/date-helper"
import { IdunId } from "utility/idun-id"
import { TextEntry } from "./journal-entry"

export class Patient extends SearchableIdunModel {
	DBID: number
	UniqueIdentification: string
	Name: string
	Address: string
	Zipcode: string
	Email: string
	TelephoneNumbers: TelephoneNumber[]
	Birthdate: string
	Gender: Gender
	CreationTime: string
	Cave: TextEntry[]

	constructor(source?: any) {
		super()
		this.Address = ""
		this.Zipcode = ""
		this.TelephoneNumbers = [new TelephoneNumber({ Primary: true })]
		this.Cave = [new TextEntry({ Title: "new", Content: "" })]
		if (source) {
			for (const prop in source) this[prop] = source[prop];
			(<any>this).CPR = undefined
			this.Birthdate = DateHelper.dateToString(DateHelper.parseDate(source.Birthdate))
			if (source.CreationTime)
				this.CreationTime = DateHelper.dateToString(DateHelper.parseDate(source.CreationTime))
			if (!source.TelephoneNumbers)
				this.TelephoneNumbers = [new TelephoneNumber()]
			else
				for (let i = 0; i < source.TelephoneNumbers.length; i++)
					this.TelephoneNumbers[i] = new TelephoneNumber(source.TelephoneNumbers[i])
			if (source.Cave)
				this.Cave = this.Cave.length > 0 ? source.Cave.map(e => new TextEntry(e)) : [new TextEntry({ Title: "new", Content: "" })]

		}
	}

	getTitle(): string {
		return this.Name
	}

	getIdDescription() {
		if (this.hasCprNumber())
			return this.getCprNumber()
		else
			return this.UniqueIdentification
	}

	hasCprNumber(): boolean {
		if (this.UniqueIdentification)
			return this.UniqueIdentification.charAt(2) === "c"
		return false
	}

	getCprNumber(): string {
		const id = new IdunId(this.UniqueIdentification)
		return id.toCpr()
	}

	getPrimaryPhone() {
		for (const i in this.TelephoneNumbers)
			if (this.TelephoneNumbers[i].Primary)
				return this.TelephoneNumbers[i]
		return this.TelephoneNumbers[0]
	}

	updateFromIdentification(identification?: string): void {
		identification = identification === undefined ? this.UniqueIdentification : identification
		const idunid: IdunId = new IdunId(identification)
		if (idunid.isValid()) {
			this.Birthdate = idunid.year + "-" + idunid.month + "-" + idunid.day
			this.Gender = idunid.gender === "m" ? Gender.Male : Gender.Female
		}
	}

	isMale() {
		return this.Gender === Gender.Male
	}

	isFemale() {
		return this.Gender === Gender.Female
	}

	searchAttributes() {
		return [this.UniqueIdentification]
	}
}

export class TelephoneNumber {
	Number: string
	Type: string
	Primary: boolean

	constructor(source?: any) {
		this.Type = ""
		if (source)
			for (const prop in source) this[prop] = source[prop]
	}
}

export enum Gender { Male, Female }
