﻿import { Component, forwardRef, ViewChild } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { FormRow, FieldInput, FormRowOptions } from "./form-row"
import { LanguageService } from "idun-layout/language.service"
import { TextEntry } from "idun-common/models/journal-entry"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormTextHistoryComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormTextHistoryComponent),
	multi: true
}

@Component({
	selector: "idun-form-text-history",
	templateUrl: "form-text-history.component.html",
	styleUrls: ["form-text-history.component.scss"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormTextHistoryComponent extends FormBasicComponent<TextEntry[]> {
	rowClass = FormTextHistoryRow
	@ViewChild("textarea") textarea

	shownEntry: TextEntry
	showHistory = ""

	_height = "1px"

	constructor(public l: LanguageService) {
		super()
	}

	get shownValue(): string {
		return this.value && this.value.length ? this.value[this.value.length - 1].Content : ""
	}

	set shownValue(val: string) {
		if (!this.value)
			return
		this.value[this.value.length - 1].Content = val
		this.onUpdateFromWithin()
		this.onChangeCallback(this.value)
	}

	get height() {
		return this._height
	}

	titleFrom(entry: TextEntry) {
		return entry.Title.split(" - ")[1]
	}

	dateFrom(entry: TextEntry) {
		return entry.Title.split(" - ")[0]
	}

	onValueChange() {
		if (this.textarea && this._height !== (this.textarea.nativeElement.scrollHeight + "px"))
			setTimeout(() => {
				if (this.textarea)
					this._height = this.textarea.nativeElement.scrollHeight + "px"
			})
		if (!this.value || !this.value.push)
			return
		const lastElement = this.value[this.value.length - 1]
		if (lastElement && lastElement.Title !== "new")
			this.value.push({ Title: "new", Content: lastElement.Content })
	}

	historyList() {
		if (!this.value || !this.value.length)
			return []
		const reversedList = [...this.value].reverse()
		while (reversedList.length > 1 && reversedList[0].Content === this.value[this.value.length - 1].Content)
			reversedList.splice(0, 1)

		return reversedList
	}

	lastClickTime = new Date().getTime()
	showHistoryEntry(entry: TextEntry) {
		const newClickTime = new Date().getTime()
		if (newClickTime - this.lastClickTime < 100)
			return
		this.lastClickTime = newClickTime

		this.shownEntry = this.shownEntry === entry
			? undefined
			: entry
	}
}

export class FormTextHistoryRow extends FormRow {
	constructor(
		field: FieldInput,
		private l: LanguageService,
		options: FormRowOptions = {}
	) {
		super(field, options)
	}

	basicValidityErrors(value) {
		if (!value)
			return
		if (typeof value === "string")
			return () => "unknown error"
		if (!value.length)
			return () => "unknown error"
		if (!value[value.length - 1].Content)
			return () => this.l.commonForm.entryMustHaveContent
		if (!value[0].Content)
			return () => this.l.commonForm.entryMustHaveContent
		if (!value[0].Title)
			return () => this.l.commonForm.entryMustHaveTitle
	}
}
