﻿import { Component, OnInit, Input } from "@angular/core"
import { ActivatedRoute } from "@angular/router"
import { FormComponent } from "idun-common/form-component"
import { LanguageService } from "idun-layout/language.service"
import { Bookmark } from "idun-common/models/bookmark"
import { BookmarkService } from "idun-main/model-services/bookmark.service"

@Component({
	selector: "idun-bookmark",
	templateUrl: "bookmark.component.html",
	styleUrls: ["bookmark.component.scss"]
})
export class BookmarkComponent extends FormComponent<Bookmark> implements OnInit {
	@Input() showNavigation = false
	@Input("bookmark")
	set inputBookmark(bookmark: Bookmark) {
		this.original = bookmark
		this.bookmark = new Bookmark(bookmark)
		this.updateBookmarkIcon()
	}
	display = true
	bookmark: Bookmark
	shownBookmarkIndex: number

	bookmarkIcons = [
		"Settings",
		"Calendar",
		"CalendarBooking",
		"CalendarDoctor",
		"CalendarPatient",
		"CalendarRoom",
		"Clinic",
		"Economy",
		"Patient",
		"PatientAdd",
		"PatientList",
		"Doctor"
	]

	get image() {
		return this.bookmarkIcons[this.shownBookmarkIndex]
	}

	constructor(
		private route: ActivatedRoute,
		private bookmarkService: BookmarkService,
		public l: LanguageService
	) { super(bookmarkService) }

	ngOnInit(): void {
		if (!this.bookmark) {
			this.bookmark = new Bookmark()
			this.original = new Bookmark()
			this.handleRouteParameters(this.route)
		}
	}

	onModelUpdate(bookmark: Bookmark) {
		this.bookmark = bookmark
		this.original = JSON.parse(JSON.stringify(bookmark))
		this.updateBookmarkIcon()
	}

	private updateBookmarkIcon() {
		this.shownBookmarkIndex = this.findBookmarkIndex()
	}

	private findBookmarkIndex() {
		for (let i = 0; i < this.bookmarkIcons.length; i++)
			if (this.bookmark.IconPath === this.bookmarkIcons[i])
				return i
	}

	submit(): void {
		this.switchToDisplay()
		this.baseOnSubmit(this.bookmark)
	}

	cancel() {
		this.switchToDisplay()
		this.bookmark = new Bookmark(this.original)
	}

	delete() {
		this.switchToDisplay()
		this.bookmarkService.delete(this.bookmark.DBID)
	}

	nextBookmarkIcon() {
		this.shownBookmarkIndex++
		if (this.bookmarkIcons.length <= this.shownBookmarkIndex)
			this.shownBookmarkIndex = 0
		this.bookmark.IconPath = this.bookmarkIcons[this.shownBookmarkIndex]
	}

	previousBookmarkIcon() {
		this.shownBookmarkIndex--
		if (this.shownBookmarkIndex < 0)
			this.shownBookmarkIndex = this.bookmarkIcons.length - 1

		this.bookmark.IconPath = this.bookmarkIcons[this.shownBookmarkIndex]
	}
}
