import { ClinicTranslations } from "./clinic-translations"
import { CommonTranslations } from "./common-translations"
import { CountryTranslations } from "./country-translations"
import { DateTranslations } from "./date-translations"
import { EdifactTranslations } from "./edifact-translations"
import { PatientTranslations } from "./patient-translations"
import { ProcedureTranslations } from "./procedure-translations"
import { StaffTranslations } from "./staff-translations"
import { FigureTranslations } from "./figure-translations"
import { IntroTranslations } from "./intro-translations"
import { SidebarTranslations } from "./sidebar-translations"
import { StatisticsTranslations } from "./statistics-translations"
import { BookingRequestTranslations } from "./booking-request-translations"
import { BookmarkTranslations } from "./bookmark-translations"
import { CalendarEventTranslations } from "./calendar-event-translations"
import { CardReaderTokenTranslations } from "./card-reader-token-translations"
import { InvoiceTranslations } from "./invoice-translations"
import { JournalEntryTranslations } from "./journal-entry-translations"
import { JournalTemplateTranslations } from "./journal-template-translations"
import { PatientNoteTranslations } from "./patient-note-translations"
import { PartnerTranslations } from "./partner-translations"
import { PeriodPlanTranslations } from "./period-plan-translations"
import { ProcedureTypeTranslations } from "./procedure-type-translations"
import { ResourceTranslations } from "./resource-translations"
import { StaffGroupTranslations } from "./staff-group-translations"
import { StatusRequestTranslations } from "./status-request-translations"
import { StatusTypeTranslations } from "./status-type-translations"
import { TreatmentCodeTranslations } from "./treatment-code-translations"
import { TreatmentCodeTypeTranslations } from "./treatment-code-type-translations"
import { BookingHomeTranslations } from "./booking-home-translations"
import { BookingOverviewTranslations } from "./booking-overview-translations"
import { CalendarTranslations } from "./calendar-translations"
import { CommonFormTranslations } from "./common-form-translations"
import { EconomicTranslations } from "./economic-translations"
import { SettingsTranslations } from "./settings-translations"
import { StaffNotificationTranslations } from "./staff-notification-translations"
import { PatientEventTranslations } from "./patient-event-translations"
import { ProcedureEventTranslations } from "./procedure-event-translations"
import { BookingTranslations } from "./booking-translations"

export class Language {
	language: string

	common = new CommonTranslations()
	commonForm = new CommonFormTranslations()
	booking = new BookingTranslations()
	bookingHome = new BookingHomeTranslations()
	bookingOverview = new BookingOverviewTranslations()
	bookingRequest = new BookingRequestTranslations()
	bookmark = new BookmarkTranslations()
	calendar = new CalendarTranslations
	calendarEvent = new CalendarEventTranslations()
	cardReaderToken = new CardReaderTokenTranslations()
	clinic = new ClinicTranslations()
	country = new CountryTranslations()
	date = new DateTranslations()
	economic = new EconomicTranslations()
	edifact = new EdifactTranslations()
	intro = new IntroTranslations()
	invoice = new InvoiceTranslations()
	journalEntry = new JournalEntryTranslations()
	journalTemplate = new JournalTemplateTranslations()
	partner = new PartnerTranslations()
	patient = new PatientTranslations()
	patientEvent = new PatientEventTranslations()
	patientNote = new PatientNoteTranslations()
	periodPlan = new PeriodPlanTranslations()
	procedure = new ProcedureTranslations()
	procedureEvent = new ProcedureEventTranslations()
	procedureType = new ProcedureTypeTranslations()
	resource = new ResourceTranslations()
	sidebar = new SidebarTranslations()
	settings = new SettingsTranslations()
	staff = new StaffTranslations()
	staffNotification = new StaffNotificationTranslations()
	staffGroup = new StaffGroupTranslations()
	statistics = new StatisticsTranslations()
	statusRequest = new StatusRequestTranslations()
	statusType = new StatusTypeTranslations()
	figure = new FigureTranslations()
	treatmentCode = new TreatmentCodeTranslations()
	TreatmentCodeType = new TreatmentCodeTypeTranslations()

	constructor(source?: any) {
		if (!source)
			return

		this.language = source.language

		for (const category in this) {
			if (category === "language")
				continue
			this.copyFrom(source, category)
		}
	}

	private copyFrom(source: any, category: string) {
		for (const key in this[category])
			this[category][key] = (source[category] || {})[key]
	}
}
