import { Component, forwardRef, Input, OnInit, OnDestroy } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { BaseService } from "idun-common/base-service"
import { ExtendedService } from "idun-common/extended-service"
import { IdunModel } from "idun-common/models/idun-model"
import { Subscriber } from "utility/subscriber"
import { FormRow, FormRowOptions, FieldInput } from "idun-layout/form/form-row"
import { LanguageService } from "idun-layout/language.service"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormSelectModelComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormSelectModelComponent),
	multi: true
}

@Component({
	selector: "idun-form-select-model",
	templateUrl: "form-select-model.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormSelectModelComponent extends FormBasicComponent<number> implements ControlValueAccessor, OnDestroy {
	rowClass = FormSelectModelRow
	@Input() allowEmpty = true
	@Input() emptyText = ""
	@Input() service: BaseService<any>

	list: any[]
	selected: IdunModel
	subscriber = new Subscriber()

	constructor(public l: LanguageService) {
		super()
	}

	serviceHasComponent() {
		return this.service instanceof ExtendedService
	}

	onValueChange() {
		if (+this.value) {
			this.subscriber.subscribe(this.service.getModel$(this.value), (model) => {
				this.selected = model
			}, "model")
		}
		else if (this.subscriber.namedSubs["model"]) {
			this.subscriber.namedSubs["model"].unsubscribe()
			this.selected = undefined
		}
	}

	ngOnDestroy(): void {
		this.subscriber.unsubscribe()
	}
}

export class FormSelectModelRow extends FormRow {
	constructor(
		field: FieldInput,
		options: Options
	) {
		super(field, options)
	}
}

interface Options extends FormRowOptions {
	service: BaseService<any>,
	allowEmpty?: boolean
}
