import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { Observable } from "rxjs"
import { LanguageService } from "idun-layout/language.service"
import { ProcedureEvent } from "idun-common/models/procedure-event"

@Injectable()
export class ProcedureEventService extends ExtendedService<ProcedureEvent> {

	constructor(
		http: Http,
		l: LanguageService
	) {
		super(http, l,
			ProcedureEvent,
			"api/ProcedureEvent",
			"procedure-event",
			() => l.procedureEvent
		)
		this.loadAll = false
		this.setupList({
			entryHeader: (model) => "procedure event title",
			contentList: [(model) => {
				return model.getTitle()
			}]
		})
	}

	getEvents(start: Date, end: Date): Promise<Observable<ProcedureEvent[]>> {
		return this.baseGetSubList("GetEvents", { start: start, end: end })
	}

	getEventsForStaff(staffId: number, start: Date, end: Date): Promise<Observable<ProcedureEvent[]>> {
		return this.baseGetSubList("GetEventsForStaff", { id: staffId, start: start, end: end })
	}

	getEventsForResource(resourceId: number, start: Date, end: Date): Promise<Observable<ProcedureEvent[]>> {
		return this.baseGetSubList("GetEventsForResource", { id: resourceId, start: start, end: end })
	}
}
