import { Observable } from "rxjs"

export class Subscriber {
	subs: any[] = []
	namedSubs: any = {}

	unsubscribe(): void {
		for (let i = 0; i < this.subs.length; i++)
			if (this.subs[i])
				this.subs[i].unsubscribe()

		for (const subscriptionName in this.namedSubs)
			this.namedSubs[subscriptionName].unsubscribe()
	}

	subscribe<T>(observable: Observable<T> | Promise<Observable<T>>, onGet: ((T: T) => any), subscriptionName?: string) {
		if (observable instanceof Promise) {
			observable.then((obs: Observable<any>) => {
				this.subscribe(obs, onGet, subscriptionName)
			})
			return
		}
		if (!subscriptionName) {
			this.subs.push(observable.subscribe(onGet))
			return
		}
		if (this.namedSubs[subscriptionName])
			this.namedSubs[subscriptionName].unsubscribe()
		this.namedSubs[subscriptionName] = observable.subscribe(onGet)
	}
}
