import { ActivatedRoute } from "@angular/router"
import { Component, ViewChild, OnInit } from "@angular/core"
import { DateHelper } from "utility/date-helper"
import { RouteParameterComponent } from "idun-common/route-parameter-component"
import { PatientNoteService } from "../model-services/patient-note.service"
import { JournalEntryService } from "../model-services/journal-entry.service"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { AutoListComponent } from "idun-common/auto-list.component"
import { LanguageService } from "idun-layout/language.service"
import { JournalEntry } from "idun-common/models/journal-entry"
import { PatientNote } from "idun-common/models/patient-note"
import { StaffService } from "idun-main/model-services/staff.service"
import { StaffGroupService } from "idun-main/model-services/staff-group.service"
import { StaffGroup } from "idun-common/models/staff-group"
import { PatientService } from "idun-main/model-services/patient.service"
import { PdfCreatorService } from "idun-main/other-services/pdf-creator.service"
import { PatientEventService } from "idun-main/model-services/patient-event.service"
import { PatientEvent } from "idun-common/models/patient-event"

@Component({
	selector: "idun-patient-journal",
	templateUrl: "patient-journal.component.html",
	styleUrls: ["patient-journal.component.scss"]
})
export class PatientJournalComponent extends RouteParameterComponent implements OnInit {
	patientID: number
	cave: string
	currentEntry: any
	previousId: number
	patientEvents: PatientEvent[] = []
	journalEntries: JournalEntry[] = []
	notes: PatientNote[] = []
	staffGroups: StaffGroup[] = []
	timelineEntries: TimelineEntry[] = []
	pdfJournalEntries: PdfJournalEntry[] = []
	topics: string[] = []
	showPrintPopup = false
	@ViewChild(AutoListComponent) autoListComponent

	showPatientEvents = true
	showJournalEntries = true
	showReferrals = true
	showPatientNotes = true
	showOnlyDoubleSignatures = false
	showTopicArea = false
	showTopics: { [index: string]: boolean } = { empty: true }

	constructor(
		private route: ActivatedRoute,
		private patientService: PatientService,
		public journalEntryService: JournalEntryService,
		public patientNoteService: PatientNoteService,
		private patientEventService: PatientEventService,
		private staffService: StaffService,
		private staffGroupService: StaffGroupService,
		private procedureService: ProcedureService,
		private pdfCreator: PdfCreatorService,
		public l: LanguageService
	) {
		super()
	}

	ngOnInit(): void {
		this.parameterHandlers.id = (id: string) => {
			this.patientID = +id
			this.onUpdate()
		}
		this.handleRouteParameters(this.route)
	}

	onUpdate() {
		if (this.patientID) {
			this.subscribe(
				this.patientEventService.getEventsFor(this.patientID),
				(list) => {
					this.patientEvents = list
					this.updateTimelineEntries()
				},
				"events")
			this.subscribe(
				this.journalEntryService.getListForPatient(this.patientID),
				(list) => {
					this.journalEntries = list
					this.pdfJournalEntries = this.journalEntries.map(e => new PdfJournalEntry(this.l, e))
					this.pdfJournalEntries.sort((a, b) => b.time - a.time)
					this.updateTimelineEntries()
				},
				"journalEntries")
			this.subscribe(
				this.patientNoteService.getListForPatient(this.patientID),
				(list) => {
					this.notes = list
					this.topics = []
					this.showTopics = { empty: true }
					for (const note of list)
						for (const topic of note.Tags)
							this.showTopics[topic] = true
					this.topics = Object.keys(this.showTopics)
					this.updateTimelineEntries()
				},
				"notes")
			this.subscribeToList(this.staffGroupService, (roles) => {
				this.staffGroups = roles
			})
		}
	}

	journalDestination = (ting: any) => {
		return ting ? "journal-entry/add/" + this.patientID + "/" + ting.id + "/" + ting.side : "journal-entry/add/" + this.patientID
	}

	updateTimelineEntries() {
		this.timelineEntries = []

		if (this.showPatientEvents)
			this.timelineEntries.push(...this.patientEvents
				.map(e => TimelineEntry.fromPatientEvent(this.l, e, this.procedureService)))
		if (this.showJournalEntries) {
			if (this.showOnlyDoubleSignatures === true)
				this.showOnlyDoubleSignatures = false
			this.timelineEntries.push(...this.journalEntries
				.map(e => TimelineEntry.fromJournalEntry(this.l, e)))
		}
		if (this.showPatientNotes)
			this.timelineEntries.push(...this.notes
				.filter(this.noteHasShownTopic)
				.map(e => TimelineEntry.fromPatientNote(this.l, e,
					this.staffService, this.staffGroups)))

		this.timelineEntries.sort((a, b) => b.time - a.time)
	}

	isolateSignatures() {
		if (this.showOnlyDoubleSignatures === true) {
			this.showPatientNotes = false
			this.showJournalEntries = false
			this.showPatientEvents = false
			this.showReferrals = false
			this.timelineEntries = []
			this.timelineEntries.push(...this.journalEntries.filter(this.journalEntryHasSingleSignature)
				.map(e => TimelineEntry.fromJournalEntry(this.l, e)))
		}
		else {
			this.showJournalEntries = true
			this.showPatientNotes = true
			this.showPatientEvents = true
			this.showReferrals = true
			this.updateTimelineEntries()
		}
	}

	noteHasShownTopic = (note: PatientNote) => {
		if (note.Tags.length === 0)
			return this.showTopics.empty
		for (const topic of note.Tags)
			if (this.showTopics[topic])
				return true
		return false
	}

	journalEntryHasSingleSignature = (model: JournalEntry) => {
		return model.Description[model.Description.length - 1].Title.split(", ").length === 1
	}

	pdfGenerate() {
		const selectedJournalEntries = this.pdfJournalEntries
			.filter(x => x.selected)
			.map(x => x.journalEntry)
		const patient = this.patientService.getModel(this.patientID)
		this.pdfCreator.createPdfFrom(patient, selectedJournalEntries)
		this.showPrintPopup = false
	}

	toggleSelected(model: PdfJournalEntry) {
		model.selected = !model.selected
	}
}

class PdfJournalEntry {
	date: string
	time: number
	content: string
	journalEntry: JournalEntry
	selected = true

	constructor(l: LanguageService, source: JournalEntry) {
		this.time = source.getTime()
		this.date = source.Date
		this.content = source.getTitle()
		this.journalEntry = source
	}
}

class TimelineEntry {

	constructor(
		public title: () => string,
		public date: string,
		public time: number,
		public content: string,
		public destination: string,
		public icon: string,
		public highlight: boolean = false
	) { }

	static fromPatientEvent(
		l: LanguageService,
		source: PatientEvent,
		procedureService: ProcedureService
	) {
		const title = source.ProcedureEvents
			.map(e => procedureService.getModel(e.ProcedureId))
			.filter(e => e)
			.map(e => e.Title)
			.join(", ")
		return new TimelineEntry(
			() => l.patientEvent.nameSingular,
			DateHelper.dateTimeToString(source.ProcedureEvents[0].Times.StartTime),
			source.ProcedureEvents[0].Times.StartTime.getTime(),
			title,
			"/patient-event/" + source.DBID,
			"Calendar"
		)
	}

	static fromPatientNote(
		l: LanguageService,
		source: PatientNote,
		staffService: StaffService,
		staffGroups: StaffGroup[]
	) {
		const staffGroupDBID = staffService.getModel(source.StaffID).StaffRoleID
		const staffGroup = staffGroups.filter(s => s.DBID === staffGroupDBID)[0]
		const noteTitle = staffGroup !== undefined // Promise has resolved.
			? staffGroup.Title
			: ""

		return new TimelineEntry(
			() => l.format(l.patientNote.xNote, noteTitle),
			DateHelper.dateTimeToString(source.Date),
			source.getTime(),
			source.Tags.length > 0
				? source.Tags.join(", ") + " - " + source.getTitle()
				: source.getTitle(),
			"/patient-note/" + source.DBID,
			"Patient")
	}

	static fromJournalEntry(l: LanguageService, source: JournalEntry) {
		return new TimelineEntry(
			() => l.journalEntry.nameSingular,
			source.Date,
			source.getTime(),
			source.getTitle(),
			"/journal-entry/" + source.DBID,
			"Doctor")
	}
}
