import { Component, ElementRef, ViewChild, Input } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"

@Component({
	selector: "idun-checkbox-tree",
	templateUrl: "checkbox-tree.component.html"
})
export class CheckboxTreeComponent {
	@Input() display = false
	@Input() source = ""
	@Input() rowGroups: {
		getTitle: () => string,
		getHint: () => string,
		set: (value: boolean) => void,
		isSet: () => boolean,
		expanded: boolean,
		rows: {
			getTitle: () => string,
			getHint: () => string,
			set: (value: boolean) => void,
			isSet: () => boolean
		}[]
	}[] = []

	@ViewChild("container") containerElement: ElementRef
	@ViewChild("content") contentElement: ElementRef

	constructor(public l: LanguageService) { }
}
