﻿import { Component, OnInit } from "@angular/core"
import { ActivatedRoute } from "@angular/router"
import { ListComponent } from "idun-common/list-component"
import { LanguageService } from "idun-layout/language.service"

import { StaffNotification } from "idun-common/models/staff-notification"
import { StaffNotificationService } from "idun-main/model-services/staff-notification.service"
import { RouteParameterComponent } from "idun-common/route-parameter-component"


@Component({
	selector: "idun-notification-list",
	templateUrl: "staff-notification.component.html"
})
export class StaffNotificationComponent extends RouteParameterComponent implements OnInit {
	staffNotificationID: number
	notification: StaffNotification

	constructor(
		private notificationService: StaffNotificationService,
		public l: LanguageService,
		private route: ActivatedRoute,
	) { super() }

	ngOnInit(): void {
		this.parameterHandlers.id = (id: string) => {
			this.staffNotificationID = +id
			this.onUpdate()
		}
		this.handleRouteParameters(this.route)
	}

	onUpdate() {
		if (this.staffNotificationID) {
			this.subscribe(
				this.notificationService.getModel$(this.staffNotificationID), (notification) => {
					this.notification = notification
				}
			)
		}
	}
}
