import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { AutoFormInfo } from "idun-common/auto-form-info"
import { Observable } from "rxjs"
import { AutoFormComponent } from "idun-common/auto-form.component"
import { UserService } from "idun-main/user.service"
import { LanguageService } from "idun-layout/language.service"

import { PatientSummaryService } from "./patient-summary.service"
import { StaffService } from "./staff.service"
import { FormDateTimeRow } from "idun-layout/form/form-date-time.component"
import { FormTextareaRow } from "idun-layout/form/form-textarea.component"
import { PatientNote } from "idun-common/models/patient-note"
import { FormListStringRow } from "idun-layout/form/form-list-string.component"
import { FormListFileRow } from "idun-common/form/form-list-file.component"

import { environment } from "../../environments/environment"
import { DateHelper } from "utility/date-helper"
import { Permission } from "idun-common/models/staff-group"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"
import { LinkThing } from "idun-common/form/form-dynamic-extra.component"
import { Staff } from "idun-common/models/staff"
import { DatabaseFileReference, DatabaseFile } from "idun-common/models/database-file"

@Injectable()
export class PatientNoteService extends ExtendedService<PatientNote> {
	constructor(
		private userService: UserService,
		http: Http,
		patientSummaryService: PatientSummaryService,
		staffService: StaffService,
		l: LanguageService
	) {
		super(http, l,
			PatientNote,
			"api/PatientNote",
			"patient-note",
			() => l.patientNote
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.ReadPatients)
		this.hasReadPermission = () => userService.hasPermission(Permission.ReadPatients)
		this.icon = "Patient"
		this.color = "var(--color-patient)"
		this.showListLink = false
		this.loadAll = false

		this.formInfo = new AutoFormInfo(
			(model: PatientNote) => {
				if (!model.PatientID)
					return ""
				const patient = patientSummaryService.getModel(model.PatientID)
				return l.patient.note + " - " + (patient ? patient.Name : "")
			},
			[{
				rows: [
					new FormDateTimeRow("Date", {
						label: () => l.common.date,
						requirements: [ValidationRequirement.ValueRequired(l)],
						display: () => true,
						cssClasses: "margin-right",
						things: [
							new LinkThing<PatientNote>(
								model => (staffService.getModel(model.StaffID) || new Staff()).getTitle(),
								model => "staff/" + model.StaffID)
						]
					}),
					new FormTextareaRow("Content", {
						label: () => l.common.content
					}),
					new FormListStringRow("Tags", {
						label: () => l.patientNote.topics,
						hint: () => l.patientNote.tagHint
					}),
					new FormListFileRow("Files", {
						label: () => l.patientNote.files
					})
				]
			}], {
			parameterHandlers: {
				"id": (id: any, params: any) => { params.id = id },
				"patient": (patient: any, params: any) => { params.patientID = +patient }
			},
			finalHandler: (params: any, component: AutoFormComponent<PatientNote>) => {
				if (params.id === undefined) {
					const id = isNaN(params.patientID) ? this.userService.getCurrentPatientDBID() : params.patientID
					component.subscribe(this.userService.currentPatient$, (patient) => {
						if (!patient)
							return
						if (!component.model.DBID && component.model.Content === "") {
							component.model.PatientID = patient.DBID
							component.original.PatientID = patient.DBID
						}
					}, "currentPatient")

					component.subscribe(this.userService.user$, (user) => {
						if (!user || component.model.StaffID)
							return
						component.model.StaffID = user.StaffID
						component.original.StaffID = user.StaffID
					}, "currentUser")

					if (component.model.DBID)
						return
					component.model.PatientID = id
					component.original.PatientID = id
				}
				component.handleIDUpdate(params.id)
			},
			onSubmit: async (model: PatientNote, component: AutoFormComponent<PatientNote>) => {
				component.isSaving = true
				await this.uploadUnsavedFiles(model, component)
				component._onSubmit()
			}
		}
		)
		this.setupList({
			entryHeader: (model: PatientNote) => DateHelper.dateToString(model.Date),
			contentList: [(model: PatientNote) => model.Content],
			subscriberFunction: (component: AutoFormComponent<PatientNote>, callback: any, param: any) => {
				component.subscribe(this.getListForPatient(param),
					callback,
					"models")
			}
		})
	}

	async uploadUnsavedFiles(
		note: PatientNote,
		component: any
	) {
		for (let i = 0; i < note.Files.length; i++) {
			const file = note.Files[i]
			if (file instanceof DatabaseFileReference)
				continue

			component.saveText = this.l.format(this.l.patientNote.uploadingX, file.Name)
			note.Files[i] = await this.uploadFile(file.Name, file.Data)
		}
		component.saveText = undefined
	}

	getListForPatient(patientID: number): Promise<Observable<PatientNote[]>> {
		return this.baseGetSubList("GetListForPatient", { id: patientID })
	}

	uploadFile(fileName: string, fileData: File): Promise<DatabaseFileReference> {
		// test
		if (!environment.production)
			return new Promise((resolve) => {
				setTimeout(() => {
					resolve(new DatabaseFileReference({ DBID: Math.floor(Math.random() * 100000), Name: fileName }))
				}, 1000)
			})
		// endtest */

		return new Promise((resolve) => {
			const formData = new FormData()
			formData.append("files", fileData, fileName)

			const request = new XMLHttpRequest()
			request.onload = (e) => {
				resolve(new DatabaseFileReference(JSON.parse((<any>e.target).response)))
			}
			// TODO: handle errors
			request.open("POST", "/api/PatientNote/UploadFile")
			request.send(formData)
		})
	}
}
