﻿import { Component, forwardRef, Input, OnInit, OnDestroy, Output, EventEmitter, ViewChild } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { Subscriber } from "utility/subscriber"
import { LanguageService } from "idun-layout/language.service"
import { Procedure, ProcedureVariation } from "idun-common/models/procedure"
import { ProcedureSetup, PriceVariation } from "idun-common/models/partner"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { TreatmentCodeService } from "idun-main/model-services/treatment-code.service"
import { UserService } from "idun-main/user.service"
import { DateHelper } from "utility/date-helper"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => ProcedureSetupComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => ProcedureSetupComponent),
	multi: true
}

@Component({
	selector: "idun-procedure-setup",
	templateUrl: "procedure-setup.component.html",
	styleUrls: ["procedure-setup.component.scss"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class ProcedureSetupComponent
	extends FormBasicComponent<ProcedureSetup>
	implements ControlValueAccessor, OnDestroy, OnInit {

	@Input() discount = 0
	@Output() supportedChange = new EventEmitter()
	subscriber = new Subscriber()
	procedure = new Procedure()
	newValue: any = {}
	currency = "DKK"

	@ViewChild("priceInput") priceInput: any

	constructor(
		public l: LanguageService,
		public userService: UserService,
		public procedureService: ProcedureService,
		public treatmentCodeService: TreatmentCodeService
	) { super() }

	ngOnInit() {
		this.userService.clinicSettings$.subscribe((settings) => {
			this.currency = settings.Currency
		})
	}

	ngOnDestroy() {
		this.subscriber.unsubscribe()
	}

	protected onValueChange() {
		if (!this.value)
			return
		if (this.procedure.DBID === this.value.ProcedureID)
			return
		this.subscriber.subscribe(this.procedureService.getModel$(this.value.ProcedureID), model => {
			this.procedure = model
			this.syncVariations()
		}, "procedure")
	}

	private syncVariations() {
		while (this.value.Variations.length < this.procedure.Variations.length)
			this.value.Variations.push(new PriceVariation())
		while (this.value.Variations.length > this.procedure.Variations.length)
			this.value.Variations.pop()
	}

	discountedBasePrice(procedureVariation: ProcedureVariation) {
		return (+procedureVariation.Price * (1 - this.discount / 100)).toFixed(2)
	}

	usesBasePrice(variation: PriceVariation) {
		if (this.priceInput && this.priceInput.nativeElement === document.activeElement)
			return false
		return variation.Price === null || variation.Price === undefined
	}

	addCode(variation: PriceVariation, code: any) {
		variation.TreatmentCodeIDs.push(code)
	}

	codeFor(id: number) {
		const code = this.treatmentCodeService.getModel(id)
		if (!code)
			return ""
		return code.getShortTitle()
	}

	setSupported(newValue: boolean) {
		this.value.Supported = newValue
		this.supportedChange.emit()
	}

	onChangePrice() {
		this.value.ChangedTime = DateHelper.dateToString(new Date()) + " " + DateHelper.timeToString(new Date())
	}
}
