﻿import { Component, OnInit, ViewChild } from "@angular/core"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { UserService, ClinicSettings } from "idun-main/user.service"
import { LanguageService } from "idun-layout/language.service"
import { Http } from "@angular/http"
import { CardReaderTokenService } from "idun-main/model-services/card-reader-token.service"
import { CardReaderToken } from "idun-common/models/card-reader-token"

@Component({
	selector: "idun-clinic-settings",
	templateUrl: "clinic-settings.component.html"
})
export class ClinicSettingsComponent extends BaseSubscriber implements OnInit {
	display = true
	settings: ClinicSettings = new ClinicSettings()
	originalSettings: ClinicSettings = new ClinicSettings()
	cardReaderTokens: CardReaderToken[] = []

	@ViewChild("map") mapElement
	map
	marker
	window
	geocoder

	currencyOptions = ["AED", "AFN", "ALL", "AMD", "ANG", "AOA", "ARS", "AUD", "AWG",
		"AZN", "BAM", "BBD", "BDT", "BGN", "BHD", "BIF", "BMD", "BND", "BOB", "BOV", "BRL",
		"BSD", "BTN", "BWP", "BYN", "BZD", "CAD", "CDF", "CHE", "CHF", "CHW", "CLF", "CLP",
		"CNY", "COP", "COU", "CRC", "CUC", "CUP", "CVE", "CZK", "DJF", "DKK", "DOP", "DZD",
		"EGP", "ERN", "ETB", "EUR", "FJD", "FKP", "GBP", "GEL", "GHS", "GIP", "GMD", "GNF",
		"GTQ", "GYD", "HKD", "HNL", "HRK", "HTG", "HUF", "IDR", "ILS", "INR", "IQD", "IRR",
		"ISK", "JMD", "JOD", "JPY", "KES", "KGS", "KHR", "KMF", "KPW", "KRW", "KWD", "KYD",
		"KZT", "LAK", "LBP", "LKR", "LRD", "LSL", "LYD", "MAD", "MDL", "MGA", "MKD", "MMK",
		"MNT", "MOP", "MRU", "MUR", "MVR", "MWK", "MXN", "MXV", "MYR", "MZN", "NAD", "NGN",
		"NIO", "NOK", "NPR", "NZD", "OMR", "PAB", "PEN", "PGK", "PHP", "PKR", "PLN", "PYG",
		"QAR", "RON", "RSD", "RUB", "RWF", "SAR", "SBD", "SCR", "SDG", "SEK", "SGD", "SHP",
		"SLL", "SOS", "SRD", "SSP", "STN", "SVC", "SYP", "SZL", "THB", "TJS", "TMT", "TND",
		"TOP", "TRY", "TTD", "TWD", "TZS", "UAH", "UGX", "USD", "USN", "UYI", "UYU", "UYW",
		"UZS", "VES", "VND", "VUV", "WST", "XAF", "XAG", "XAU", "XBA", "XBB", "XBC", "XBD",
		"XCD", "XDR", "XOF", "XPD", "XPF", "XPT", "XSU", "XTS", "XUA", "XXX", "YER", "ZAR",
		"ZMW", "ZWL"]

	constructor(
		public cardReaderTokenService: CardReaderTokenService,
		private userService: UserService,
		private http: Http,
		public l: LanguageService
	) {
		super()
		this.userService.loadClinicSettings()
		this.window = (<any>window)
	}

	ngOnInit(): void {
		this.subscribe(this.cardReaderTokenService.getModels$(), (tokens) => {
			this.cardReaderTokens = tokens
		})
		this.subscribe(this.userService.clinicSettings$, settings => {
			this.settings = settings
			if (!this.marker && this.geocoder)
				this.geocode()
		})
		this.initMapWhenReady()
	}

	initMapWhenReady = () => {
		if (this.window.mapReady)
			this.initMap()
		else
			setTimeout(() => this.initMapWhenReady(), 500)
	}

	initMap() {
		this.map = new this.window.google.maps.Map(this.mapElement.nativeElement, {
			center: { lat: 56.161532, lng: 10.408827 },
			zoom: 7,
			fullscreenControl: false,
			streetViewControl: false
		})
		this.geocoder = new this.window.google.maps.Geocoder()
		if (!this.marker)
			this.geocode()
	}

	editButton = [
		{ icon: "Edit", show: () => this.display, action: () => this.edit() }
	]

	edit() {
		this.display = false
		this.originalSettings = this.settings
		this.settings = new ClinicSettings(this.originalSettings)
	}

	submit() {
		this.display = true
		this.userService.updateClinicSettings(this.settings)
	}

	cancel() {
		this.display = true
		this.settings = this.originalSettings
		this.geocode()
	}

	getLocation(event) {
		if (typeof event === "string")
			return
		this.geocode((results) => {
			this.getTimezone(results[0].geometry.location)
		})
	}

	geocode(onsuccess?) {
		if (!this.settings.Location)
			return
		this.geocoder.geocode({ address: this.settings.Location }, (results, status) => {
			if (status !== "OK")
				return console.error("Geocode was not successful for the following reason: " + status)
			this.map.setCenter(results[0].geometry.location)
			if (this.marker)
				this.marker.setMap(null)
			this.marker = new this.window.google.maps.Marker({
				map: this.map,
				position: results[0].geometry.location
			})
			if (onsuccess)
				onsuccess(results)
		})
	}

	getTimezone(location) {
		this.http.get("https://maps.googleapis.com/maps/api/timezone/json?location=" + location.lat() + "," + location.lng() +
			"&timestamp=" + Math.floor(new Date().getTime() / 1000) +
			"&key=AIzaSyBuaVJ9AcAycpQ61wOph56Qoh_XBb3Z030")
			.toPromise()
			.then((response) => {
				const r = response.json()
				console.log(r)
				if (r.error_message)
					console.log(r.error_message)
				else
					this.settings.Timezone = r.timeZoneId
			})
			.catch((response) => {
				console.log(response.json())
			})
	}

	addNewToken() {
		this.cardReaderTokenService.add(CardReaderToken.generateNew())
	}

	deleteToken(token: CardReaderToken) {
		this.cardReaderTokenService.delete(token.DBID)
	}
}
