﻿import { Component, OnInit, ElementRef } from "@angular/core"
import { combineLatest } from "rxjs"
import { DateHelper } from "utility/date-helper"
import { LanguageService } from "idun-layout/language.service"

import { Staff } from "idun-common/models/staff"
import { StaffService } from "idun-main/model-services/staff.service"
import { ProcedureTypeService } from "../../model-services/procedure-type.service"
import { StatusTypeService } from "../../model-services/status-type.service"
import { PeriodPlan, PlanningRole } from "idun-common/models/period-plan"
import { PeriodPlanService } from "../../model-services/period-plan.service"
import { ResourceService } from "../../model-services/resource.service"
import { SharedCompetences } from "idun-common/models/resource-utilisation"
import { ProcedureService } from "../../model-services/procedure.service"
import { Procedure } from "idun-common/models/procedure"
import { StaffGroupService } from "../../model-services/staff-group.service"
import { ProcedureType } from "idun-common/models/procedure-type"
import { RouteParameterComponent } from "idun-common/route-parameter-component"
import { ActivatedRoute, Router } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LayoutService } from "idun-layout/layout.service"


@Component({
	selector: "idun-plan-implementer",
	templateUrl: "plan-implementer.component.html",
	styleUrls: ["plan-implementer.component.scss"]
})
export class PlanImplementerComponent extends RouteParameterComponent implements OnInit {
	navigater: NavigationHandler

	staff: Staff[]
	procedures: Procedure[]
	procedureTypes: ProcedureType[]
	plans: PeriodPlan[]

	selectedPlan: PeriodPlan

	planStart = new Date()
	planEnd = DateHelper.newDate(new Date(), { relative: true, month: 1 })
	staffChoices: { Staff: Staff, Choice: string }[] = []
	sharedCompetences: SharedCompetences<Staff, PlanningRole>
	shownComparison: Staff
	comparison = ""

	rows: {
		getTitle: () => string,
		getHint?: () => string,
		getColor?: (target: Staff) => string,
		onClick?: (target: Staff) => void,
		source: PlanningRole
	}[] = []
	columns: {
		getTitle: () => string,
		getHoverTitle: () => string,
		tiebreaker: () => number,
		source: Staff
	}[] = []

	showPopup = false
	popupStaff: Staff
	popupPlanningRole: PlanningRole

	popupRowGroups: {
		getTitle: () => string,
		toggle: (target: Staff) => void,
		get: (target: Staff) => boolean,
		expanded: boolean,
		rows: { getTitle: () => string, toggle: (target: Staff) => void, get: (target: Staff) => boolean }[]
	}[] = []
	popupColumns: {
		getTitle: () => string,
		getHoverTitle: () => string,
		source: any
	}[] = []

	constructor(
		public router: Router,
		public myElement: ElementRef,
		public layoutService: LayoutService,
		public statusTypeService: StatusTypeService,
		public staffService: StaffService,
		public periodPlanService: PeriodPlanService,
		public procedureTypeService: ProcedureTypeService,
		public procedureService: ProcedureService,
		public resourceService: ResourceService,
		public staffGroupService: StaffGroupService,
		public l: LanguageService,
		private route: ActivatedRoute
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit() {
		this.subscribe(
			combineLatest(
				this.staffService.getModels$(),
				this.procedureService.getModels$(),
				this.procedureTypeService.getModels$(),
				this.periodPlanService.getModels$()
			), (values) => {
				this.staff = values[0]
				this.procedures = values[1]
				this.procedureTypes = values[2]
				this.plans = values[3]
				if (!this.selectedPlan)
					this.selectedPlan = this.plans[0]
				this.staffChoices = this.staff.map(e => { return { Staff: e, Choice: "_ignore" } })
				this.calculateSharedCompetences()
				this.updateColumns()
				this.updateRows()
			})
		this.subscribeToList(this.staffGroupService, (roles) => {
			this.updateColumns()
		})
		this.parameterHandlers.id = (planImplementerId: string) => {
			if (+planImplementerId)
				this.loadForId(+planImplementerId)
		}
		this.handleRouteParameters(this.route)
	}

	private loadForId(planImplementerId: number) {
		this.subscribeToItem(this.periodPlanService, planImplementerId, model => {
			this.selectedPlan = model
			this.calculateSharedCompetences()
			this.updateRows()
		})
	}

	calculateSharedCompetences() {
		this.sharedCompetences = new SharedCompetences(
			this.staff,
			this.selectedPlan.StaffRoles,
			e => e.Competences,
			e => e.Competences,
			this.procedures)
	}

	implementPlan() {
		this.periodPlanService.implementPlan(this.selectedPlan.DBID, this.planStart, this.planEnd,
			this.staffChoices.map(e => { return { StaffID: e.Staff.DBID, Choice: e.Choice } })).then(() => {
				this.navigater.current("calendar-overview")
			})
	}

	closePage() {
		this.navigater.current("period-plan/list")
	}

	rolesLeft() {
		return this.selectedPlan.StaffRoles
			.filter(e => !this.staffChoices.find(c => c.Choice === e.Title))
			.map(e => e.Title)
			.join(", ")
	}

	toggleComparisonFor(staffChoice: { Staff: Staff, Choice: string }) {
		if (this.shownComparison === staffChoice.Staff)
			return this.shownComparison = undefined
		this.shownComparison = staffChoice.Staff
		const role = this.selectedPlan.StaffRoles.find(e => e.Title === staffChoice.Choice)
		this.comparison = this.l.periodPlan.xSharedCompetences, this.sharedCompetences.for(staffChoice.Staff, role)
	}

	private updateRows() {
		this.rows = []
		for (const p of this.selectedPlan.StaffRoles) {
			this.rows.push({
				getTitle: () => p.Title,
				getHint: () => p.Title,
				getColor: (s) => this.getColor(s, p),
				onClick: (s) => {
					this.popupStaff = s
					this.popupPlanningRole = p
					this.updatePopupColumns()
					this.updatePopupRowGroups()
					this.showPopup = true
				},
				source: p
			})
		}
	}

	private updateColumns() {
		this.columns = this.staff.map(s => {
			return {
				getTitle: () => s.Abbreviation,
				getHoverTitle: () => s.Name,
				getColumnCategory: () => {
					return this.staffGroupService.getModel(s.StaffRoleID) ? this.staffGroupService.getModel(s.StaffRoleID).getTitle() : this.l.common.other
				},
				tiebreaker: () => {
					let t = 0
					for (const p of this.selectedPlan.StaffRoles)
						if (this.sharedCompetences.for(s, p) === "more")
							t += 1 * this.selectedPlan.StaffRoles.length
						else if (this.sharedCompetences.for(s, p) === "all")
							t++
						else if (this.sharedCompetences.for(s, p) === "some")
							t += 1 / this.selectedPlan.StaffRoles.length
					return t
				},
				source: s
			}
		})
	}

	getColor(staff: Staff, role: PlanningRole): string {
		switch (this.sharedCompetences.for(staff, role)) {
			case "more":
				return "dodgerblue"
			case "all":
				return "rgba(41, 204, 0, 0.8)" // green
			case "none":
				return "" // red (currently transparent)
			case "some":
				return "rgba(204, 184, 0, 0.8)" // yellow
		}
	}

	private updatePopupColumns() {
		this.popupColumns = [{
			getTitle: () => this.popupPlanningRole.Title,
			getHoverTitle: () => this.popupPlanningRole.Title,
			source: this.popupPlanningRole
		}, {
			getTitle: () => this.popupStaff.Abbreviation,
			getHoverTitle: () => this.popupStaff.Name,
			source: this.popupStaff
		}]
	}

	private updatePopupRowGroups() {
		this.popupRowGroups = []
		for (const i in this.procedureTypes)
			this.popupRowGroups.push(this.newRowGroup(this.procedureTypes[i]))
	}

	private newRowGroup(procedureType: ProcedureType) {
		return {
			getTitle: () => procedureType.getTitle(),
			toggle: (target: any) => {
				target.Competences.toggleProcedureTypeCompetence(procedureType, this.procedures)
			},
			get: (target: any) => target.Competences.AllowedProcedureTypes.includes(procedureType.DBID),
			expanded: false,
			rows: this.procedures
				.filter(e => e.ProcedureTypeID === procedureType.DBID)
				.map(procedure => this.newRow(procedure))
		}
	}

	private newRow(procedure: Procedure) {
		return {
			getTitle: () => procedure.getTitle(),
			toggle: (target: any) => {
				target.Competences.toggleProcedureCompetence(procedure)
			},
			get: (target: any) => target.Competences.canPerform(procedure),
		}
	}

	closePopup() {
		this.showPopup = false
		this.popupStaff = undefined
		this.popupPlanningRole = undefined
	}
}
