import { Injectable } from "@angular/core"
import { Headers, Http } from "@angular/http"
import { BaseService } from "idun-common/base-service"
import { Observable } from "rxjs"
import { Status } from "idun-common/models/status"

@Injectable()
export class StatusService extends BaseService<Status> {
	constructor(http: Http) {
		super(http, Status, "api/Status")
		this.loadAll = false
	}

	getNewModel(source?: any): Status {
		return new Status(source)
	}

	getStatusList(staffID: number, start: Date, end: Date): Promise<Observable<Status[]>> {
		return this.baseGetSubList("GetStatusList", { id: staffID, start: start, end: end })
	}
}
