﻿export class Cache {
	cache: { [index: string]: { time: Date, data: any } } = {}
	pending: { [index: string]: ((response: any) => any)[] } = {}

	public push(key: string, data: any) {
		this.cache[key] = { time: new Date(), data: data }
		for (const i in this.pending[key]) {
			this.pending[key][i](data)
		}
		delete this.pending[key]
	}

	public get(key: string) {
		this.trimCache()
		return this.cache[key] ? this.cache[key].data : undefined
	}

	private trimCache() {
		const now = new Date().getTime()
		for (const key in this.cache)
			if (now - this.cache[key].time.getTime() > 10000)
				delete this.cache[key]
	}
}
