﻿import { Component, forwardRef, Input } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { FormRow, FormRowOptions, FieldInput } from "./form-row"
import { LanguageService } from "idun-layout/language.service"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormDateIntervalComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormDateIntervalComponent),
	multi: true
}

@Component({
	selector: "idun-form-date-interval",
	templateUrl: "form-date-interval.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class FormDateIntervalComponent extends FormBasicComponent<{ StartTime: Date, EndTime: Date }> {
	rowClass = FormDateIntervalRow
	@Input() hideDate = false

	constructor(public l: LanguageService) {
		super()
	}

	onStartTimeChange() {
		if (this.value.EndTime.getTime() <= this.value.StartTime.getTime()) {
			this.value.EndTime = new Date(this.value.StartTime)
			this.value.EndTime.setMinutes(this.value.EndTime.getMinutes() + 1)
		}
	}

	onEndTimeChange() {
		if (this.value.EndTime.getTime() <= this.value.StartTime.getTime()) {
			this.value.StartTime = new Date(this.value.EndTime)
			this.value.StartTime.setMinutes(this.value.StartTime.getMinutes() - 1)
		}
	}
}

export class FormDateIntervalRow extends FormRow {
	constructor(
		field: FieldInput,
		private l: LanguageService,
		options: Options = {}
	) {
		super(field, options)
	}

	basicValidityErrors(value) {
		if (!value)
			return
		if (typeof value === "string")
			return () => "unknown error"
		if (value.EndTime.getTime() < value.StartTime.getTime())
			return () => this.l.commonForm.endTimeMustBeAfterStartTime
	}
}

interface Options extends FormRowOptions {
	hideDate?: boolean
}
