import { Observable ,  Subject } from "rxjs"
import { LayoutService } from "./layout.service"

export enum ScreenSpace { Mobile, OnePage, PageAndBars, TwoPages, Full }

export class SizeController {
	fontSize = 14
	disableTwoPages = false
	screenSpace: ScreenSpace
	_screenSpace$: Subject<ScreenSpace>
	get screenSpace$(): Observable<ScreenSpace> {
		return this._screenSpace$.asObservable()
	}
	get isSinglePageLayout() {
		return this.disableTwoPages ||
			this.screenSpace === ScreenSpace.Mobile ||
			this.screenSpace === ScreenSpace.OnePage ||
			this.screenSpace === ScreenSpace.PageAndBars
	}

	get screenWidth() {
		return window.innerWidth / this.fontSize
	}

	get screenHeight() {
		return window.innerHeight / this.fontSize
	}

	get hasSpaceForSidebars() {
		return this.screenSpace === ScreenSpace.Full ||
			this.screenSpace === ScreenSpace.PageAndBars ||
			(this.screenSpace === ScreenSpace.TwoPages && !this.layoutService.navigationController.twoPagesOpen)
	}

	constructor(private layoutService: LayoutService) {
		this._screenSpace$ = <Subject<ScreenSpace>>new Subject()
	}

	zoomIn() {
		this.setFontSize(this.fontSize + 2)
		return this.fontSize
	}

	zoomOut() {
		this.setFontSize(this.fontSize - 2)
		return this.fontSize
	}

	resetZoom() {
		this.setFontSize(14)
		return this.fontSize
	}

	setFontSize(fontSize: number) {
		if (fontSize < 8)
			fontSize = 8
		const root = <HTMLElement>document.querySelector(":root")
		this.fontSize = fontSize
		root.style.fontSize = this.fontSize + "px"
	}

	update() {
		let newScreenSpace: any
		if (this.screenWidth <= 51) {
			newScreenSpace = ScreenSpace.Mobile
		}
		else if (51 < this.screenWidth && this.screenWidth <= 73.5) {
			newScreenSpace = ScreenSpace.OnePage
		}
		else if (73.5 < this.screenWidth && this.screenWidth <= 100) {
			newScreenSpace = ScreenSpace.PageAndBars
		}
		else if (100 < this.screenWidth && this.screenWidth <= 123.5) {
			newScreenSpace = ScreenSpace.TwoPages
		}
		else if (123.5 < this.screenWidth) {
			newScreenSpace = ScreenSpace.Full
		}
		if (newScreenSpace !== this.screenSpace) {
			this.screenSpace = newScreenSpace
			this.layoutService.ngZone.run(() => this._screenSpace$.next(this.screenSpace))
		}
	}
}
