﻿import { TimeInterval } from "idun-common/models/time-interval"
import { WorkInterval } from "idun-common/models/work-interval"

const baseUrl = "api/workinterval/"

export const workIntervalTestData = [
	new WorkInterval({
		DBID: 1,
		StaffID: 1,
		Times: TimeInterval.from(new Date(2000, 1, 1, 8), new Date(2000, 1, 1, 18))
	})
]

const otherResponses = {}

otherResponses[baseUrl + "getworkintervals"] = (parameters: { id: number, start: Date, end: Date }) => {
	const interval = TimeInterval.fromInterval(parameters)
	const result = getWorkIntervalTestDataFor(parameters.id, interval)
	return JSON.stringify(result)
}

otherResponses[baseUrl + "getallworkintervals"] = (parameters: { start: Date, end: Date }) => {
	const interval = TimeInterval.fromInterval(parameters)
	const result = workIntervalTestData
		.filter(({ Times }) => interval.intersects(Times))
	return JSON.stringify(result)
}

export function WorkIntervalResponses() {
	return {
		// ...ModelTestData.commonResponses(Status, baseUrl, list),
		...otherResponses
	}
}

export function getWorkIntervalTestDataFor(staffId: number, interval: TimeInterval) {
	return workIntervalTestData
		.filter(({ StaffID }) => StaffID === staffId)
		.filter(({ Times }) => interval.intersects(Times))
}
