import { Component, Input, OnInit, ElementRef } from "@angular/core"
import { Router } from "@angular/router"
import { NavigationHandler } from "../navigation-handler"
import { LayoutService } from "../layout.service"

@Component({
	selector: "idun-link",
	templateUrl: "link.component.html",
	styleUrls: ["link.component.scss"]
})
export class LinkComponent implements OnInit {
	navigater: NavigationHandler
	@Input() destination = "home"
	@Input() colouring = "neutral"
	showArrows = false
	side = "middle"
	hover = { right: "out", left: "out", middle: "out" }

	constructor(
		private myElement: ElementRef,
		private router: Router,
		private layoutService: LayoutService
	) {
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit(): void {
		this.showArrows = !this.layoutService.sizeController.isSinglePageLayout
		this.layoutService.sizeController._screenSpace$.subscribe(() => {
			this.showArrows = !this.layoutService.sizeController.isSinglePageLayout
		})
	}

	mouseover(side, event) {
		if (event)
			event.preventDefault()
		this.side = side
		this.hover[side] = "over"
		this.layoutService.showShadowOverlays(side)
	}

	mouseout(side) {
		if (event)
			event.preventDefault()
		this.hover[side] = "out"
		this.layoutService.hideShadowOverlays(side)
	}
}
