import { Component, Input, HostBinding } from "@angular/core"

@Component({
	selector: "idun-expandable-box",
	template: "<ng-content></ng-content>",
	styleUrls: ["expandable-box.component.scss"]
})
export class ExpandableBoxComponent {
	private _showContent = false
	@Input() set showContent(value: boolean) {
		if (!!value === this._showContent)
			return
		this._showContent = !!value
		if (this._showContent)
			this.doShow()
		else
			this.doHide()
	}
	state = ShowState.NoHeight

	@HostBinding("class.unlimited-height") get unlimitedHeight() {
		return this.state === ShowState.UnlimitedHeight
	}

	@HostBinding("class.limited-height") get limitedHeight() {
		return this.state === ShowState.LimitedHeight
	}

	@HostBinding("class.no-height") get noHeight() {
		return this.state === ShowState.NoHeight
	}

	async doHide() {
		console.log("doHide")
		this.state = ShowState.LimitedHeight
		await this.sleep(1)
		this.state = ShowState.NoHeight
	}

	async doShow() {
		console.log("doShow")
		this.state = ShowState.LimitedHeight
		await this.sleep(300)
		this.state = ShowState.UnlimitedHeight
	}

	private sleep(ms: number) {
		return new Promise(resolve => setTimeout(resolve, ms))
	}
}

enum ShowState { UnlimitedHeight, LimitedHeight, NoHeight }
