﻿import { Type } from "@angular/core"
import { Http } from "@angular/http"
import { IdunModel } from "idun-common/models/idun-model"
import { AutoListInfo } from "./auto-list-info"
import { AutoFormInfo } from "./auto-form-info"
import { BaseService } from "./base-service"
import { LanguageService } from "idun-layout/language.service"
import { FormRow } from "idun-layout/form/form-row"
import { ModelTranslations } from "idun-layout/language/model-translations"

export abstract class ExtendedService<Model extends IdunModel> extends BaseService<Model> {
	listInfo: AutoListInfo<Model>
	formInfo: AutoFormInfo<Model>
	hasReadPermission = () => true
	hasEditPermission = () => true
	icon: string
	color: number | string
	showListLink = true

	constructor(
		protected http: Http,
		public l: LanguageService,
		public modelClass: Type<Model>,
		public baseUrl: string,
		public ngUrl: string,
		public translations: () => ModelTranslations
	) {
		super(http, modelClass, baseUrl)
	}

	setupList(
		settings: {
			destination?: (model: Model) => string,
			listHeader?: string | (() => string),
			entryHeader?: (model: Model) => string,
			contentList?: ((model: Model) => string)[],
			linkList?: ((model: Model) => { destination: string, getText: () => string })[],
			buttonList?: ((model: Model) => { callback: (event) => void, getText: () => string })[],
			colors?: (model: Model) => string,
			groups?: { getHeader: () => string, filter: (list: Model[]) => Model[] }[],
			subscriberFunction?: (component: any, callback: (list: Model[]) => void, param: any) => void,
			pageLimit?: number
			helpText?: () => string
		} = {}
	) {
		const defaultSettings = {
			listHeader: () => this.translations().namePlural,
			entryHeader: (model: Model) => { return model.getTitle() },
			linkList: [],
			destination: (model) => this.ngUrl + "/" + model.DBID,
			service: this,
			helpText: () => this.translations().listHelpText
		}
		for (const key in settings)
			defaultSettings[key] = settings[key]
		this.listInfo = new AutoListInfo(defaultSettings)
	}

	setupForm(
		settings: {
			getHeader?: (model: Model) => string,
			rows: (FormRow |
				((model: Model) => string) |
			{ header: (model: Model) => string, headerHelp?: () => string, hidden?: boolean, show?: (model: Model) => boolean })[]
			parameterHandlers?: any,
			finalHandler?: any,
			options?: any
		} = { rows: [] }
	) {
		if (!settings.getHeader)
			settings.getHeader = () => this.translations().modelDetails

		const groups = []
		let currentGroup
		for (const i in settings.rows) {
			currentGroup = this.handleRow(settings, i, groups, currentGroup)
		}
		const options = {
			parameterHandlers: settings.parameterHandlers,
			finalHandler: settings.finalHandler,
			helpText: () => this.translations().formHelpText
		}
		for (const key in settings.options)
			options[key] = settings.options[key]
		this.formInfo = new AutoFormInfo(settings.getHeader, groups, options)
	}

	private handleRow(settings: any, index: string, groups: any[], currentGroup: any) {
		const row = settings.rows[index]
		if (!(row instanceof FormRow)) {
			if (row instanceof Function) {
				currentGroup = { getHeader: row, rows: [] }
				groups.push(currentGroup)
			} else {
				currentGroup = { getHeader: row.header, headerHelp: row.headerHelp, rows: [], hidden: row.hidden, showHeader: row.show }
				groups.push(currentGroup)
			}
		}
		else if (index === "0") {
			currentGroup = { rows: [row] }
			groups.push(currentGroup)
		}
		else {
			currentGroup.rows.push(row)
		}
		return currentGroup
	}
}
