﻿import { Component, Input } from "@angular/core"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"


export abstract class ExtraThing<T> {
	constructor(
		public text: (model: T, display: boolean) => string,
		public show?: (model: T, display: boolean) => boolean
	) {
		if (!show)
			this.show = () => true
	}
}

@Component({
	selector: "idun-form-dynamic-extra",
	templateUrl: "form-dynamic-extra.component.html",
	styleUrls: ["form-dynamic-extra.component.scss"]
})
export class FormDynamicExtraComponent extends FormBasicComponent<any> {
	@Input() autoform
	@Input() model
	@Input() thing: ExtraThing<any>
	data = {}

	classes = {
		LabelThing: LabelThing,
		ButtonThing: ButtonThing,
		LinkThing: LinkThing,
		SelectThing: SelectThing,
		SwitchThing: SwitchThing
	}

	thingIs(thingClass) {
		return this.thing instanceof thingClass
	}
}

export class LabelThing<T> extends ExtraThing<T> {
	constructor(
		text: (model: T, display: boolean) => string,
		show?: (model: T, display: boolean) => boolean,
		public title?: (model: T, display: boolean) => string
	) {
		super(text, show)
		if (!title)
			this.title = () => ""
	}
}

export class ButtonThing<T> extends ExtraThing<T> {
	constructor(
		text: (model: T, display: boolean) => string,
		public click: (model: T, component: any) => void,
		show?: (model: T, display: boolean) => boolean,
		public leftText?: (model: T, display: boolean) => string,
		public rightText?: (model: T, display: boolean) => string,
		public dark?: boolean
	) {
		super(text, show)
	}
}

export class LinkThing<T> extends ExtraThing<T> {
	constructor(
		text: (model: T) => string,
		public destination: (model: T) => string,
		show?: (model: T, display: boolean) => boolean,
		public colouring?: string
	) {
		super(text, show)
	}
}

export class SelectThing<T> extends ExtraThing<T> {
	constructor(
		text: (model: T) => string,
		public select: (model: T, value) => void,
		public options: (model: T) => { text: () => string, value: string }[],
		show?: (model: T, display: boolean) => boolean
	) {
		super(text, show)
	}
}

export class SwitchThing<T> extends ExtraThing<T> {
	constructor(
		public options: { text: (model: T) => string, selected: (model: T) => boolean, click: (model: T) => void }[],
		show?: (model: T, display: boolean) => boolean
	) {
		super(null, show)
	}
}
