import { RouteParameterComponent } from "idun-common/route-parameter-component"
import { OnInit, Component, Input, EventEmitter, Output } from "@angular/core"
import { Staff } from "idun-common/models/staff"
import { BasicCalendarProvider } from "idun-calendar/basic-calendar-provider"
import { LanguageService } from "idun-layout/language.service"
import { BasicCalendarEntry } from "idun-calendar/basic-calendar-entry"
import { DateHelper, changeDate, newDateFrom } from "utility/date-helper"
import { TimeInterval } from "idun-common/models/time-interval"
import { PeriodPlanService } from "../model-services/period-plan.service"
import { StatusTypeService } from "../model-services/status-type.service"
import { TimeIntervalSeries } from "idun-common/models/time-interval-series"

@Component({
	selector: "idun-staff-work-hours",
	templateUrl: "staff-work-hours.component.html",
	styleUrls: ["staff-work-hours.component.scss"]
})
export class StaffWorkHoursComponent extends RouteParameterComponent implements OnInit {
	@Input() staff: Staff
	@Input() display = false

	@Output() close = new EventEmitter()

	date = new Date(1900, 0)
	entryProviders: BasicCalendarProvider[]
	selectedWeek = 1

	constructor(
		public l: LanguageService,
		public periodPlanService: PeriodPlanService,
		public statusTypeService: StatusTypeService
	) {
		super()
	}

	ngOnInit(): void {
		this.updateProviders()
	}

	updateProviders(): void {
		const entries = this.staff.WorkTimes
			.map(x => this.calendarEntryFrom(x))
		this.entryProviders = [new BasicCalendarProvider({
			title: () => this.staff.Name || this.l.common.newStaff,
			entries: (date: Date) =>
				entries.filter(x => DateHelper.isSameDate(x.source.Interval.StartTime, date))
		})]
	}

	private calendarEntryFrom(interval: TimeInterval) {
		const display = () => this.display
		return new BasicCalendarEntry(
			{ Interval: interval },
			{
				boxHue: 120,
				get allowResize() { return !display() },
				moveStartMinute: (minuteChange: number) => {
					changeDate(interval.StartTime, { minutes: minuteChange })
					TimeIntervalSeries.mergeIntervalIntoList(interval, this.staff.WorkTimes)
					this.updateProviders()
				},
				moveEndMinute: (minuteChange: number) => {
					changeDate(interval.EndTime, { minutes: minuteChange })
					TimeIntervalSeries.mergeIntervalIntoList(interval, this.staff.WorkTimes)
					this.updateProviders()
				},
				delete: () => {
					const index = this.staff.WorkTimes.indexOf(interval)
					this.staff.WorkTimes.splice(index, 1)
					this.updateProviders()
				}
			})
	}

	addEntry(data: { provider: BasicCalendarProvider, time: Date }) {
		const interval = TimeInterval.fromDuration(data.time, {hours: 1})
		TimeIntervalSeries.mergeIntervalIntoList(interval, this.staff.WorkTimes)
		this.updateProviders()
	}

	changeWeek(week: number) {
		this.selectedWeek = week
		this.date = newDateFrom({ days: week * 7 }, new Date(1900, 0))
	}
}
