import { Component, Input, Output, EventEmitter } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { CalendarEntryProvider } from "./calendar-entry-provider"
import { DateHelper } from "utility/date-helper"

@Component({
	selector: "idun-multi-calendar-rows",
	templateUrl: "multi-calendar-rows.component.html",
	styleUrls: ["multi-calendar-rows.component.scss"]
})
export class MultiCalendarRowsComponent {
	@Input() shownDays: { title: () => string, date: Date }[]
	@Input() periods: { length: number, title: () => string }[] = [{ length: 7, title: () => "sdfg" }]
	@Input() showDate = true
	@Input() selectedDate: Date
	@Output() selectedDateChange = new EventEmitter<Date>()

	_entryProviders: CalendarEntryProvider[]
	@Input() set entryProviders(value: CalendarEntryProvider[]) {
		this._entryProviders = value
		this.providerDays = new Map()
	}
	get entryProviders(): CalendarEntryProvider[] {
		return this._entryProviders.filter(e => e)
	}

	providerDays: Map<CalendarEntryProvider, DayInfo[]> = new Map()

	constructor(
		public l: LanguageService
	) { }

	clickProvider(provider: CalendarEntryProvider) {
		if (provider.onTitleClick)
			provider.onTitleClick(this.shownDays[0].date)
	}

	getProviderDays(provider: CalendarEntryProvider) {
		if (!this.entryProviders)
			return []
		if (!this.providerDays.has(provider))
			this.generateDays(provider)
		return this.providerDays.get(provider)
	}

	generateDays(provider: CalendarEntryProvider) {
		this.providerDays.set(provider, [])
		for (const day of this.shownDays) {
			const entries = provider.getEntries(day.date)
			let opacity = 1
			const color = this.getPrioritisedColor(entries)
			for (const entry of entries)
				opacity = opacity && (entry.ghost ? 0.3 : 1)
			const info = {
				...day,
				hasEntries: entries.length > 0,
				isWeekend: day.date.getDay() === 0 || day.date.getDay() === 6,
				color: `hsla(${color.hue}, ${color.saturation}%, ${opacity < 1 ? 30 : 45}%, 0.6)`,
				opacity: opacity
			}
			this.providerDays.get(provider).push(info)
		}
	}

	private getPrioritisedColor(entries) {
		const color = { hue: 360, saturation: 100 }
		for (const entry of entries) {
			if (color.hue > entry.boxHue
				|| (entry.boxSaturation === 0 && color.hue === 100)) {
				color.hue = entry.boxHue
				color.saturation = entry.boxSaturation >= 0 ? entry.boxSaturation : 100
			}
		}
		return color
	}

	isSelectedDate(date: Date) {
		if (!this.selectedDate)
			return false
		return DateHelper.isSameDate(this.selectedDate, date)
	}

	selectDate(date: Date) {
		this.selectedDate = this.isSelectedDate(date)
			? undefined
			: date
		this.selectedDateChange.emit(this.selectedDate)
	}
}

interface DayInfo {
	title: () => string
	date: Date
	hasEntries: boolean
	isWeekend: boolean
	color: string
	opacity: number
}
