﻿import { Component, Input, Output, EventEmitter } from "@angular/core"
import { LanguageService } from "../language.service"

@Component({
	selector: "idun-submit-cancel-delete",
	templateUrl: "./submit-cancel-delete.component.html",
	styleUrls: ["./submit-cancel-delete.component.scss"]
})
export class SubmitCancelDeleteComponent {
	@Input() valid = true
	@Input() showSubmit = true
	@Input() showCancel = true
	@Input() showDelete = true

	@Input() submitText: string
	@Input() cancelText: string
	@Input() deleteText: string

	@Input() disabled = false

	@Output() submit = new EventEmitter()
	@Output() cancel = new EventEmitter()
	@Output() delete = new EventEmitter()

	constructor(public l: LanguageService) { }
}
