﻿import { Component, OnInit, ElementRef } from "@angular/core"
import { Router } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LanguageService } from "idun-layout/language.service"

import { UserService } from "idun-main/user.service"
import { LayoutService } from "idun-layout/layout.service"
import { BaseSubscriber } from "idun-common/base-subscriber"

@Component({
	selector: "idun-home",
	templateUrl: "home.component.html",
	styleUrls: ["home.component.scss"]
})
export class HomeComponent extends BaseSubscriber implements OnInit {
	navigater: NavigationHandler
	clinic: string
	show = true
	
	constructor(
		private myElement: ElementRef,
		private router: Router,
		private userService: UserService,
		private layoutService: LayoutService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
		this.userService.loadClinicSettings()
	}

	ngOnInit(): void {
		this.subscribe(this.userService.clinicSettings$, settings => {
			this.clinic = settings.ClinicName
		})
	}
}
