import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { AutoFormInfo } from "idun-common/auto-form-info"
import { Observable } from "rxjs"
import { AutoFormComponent } from "idun-common/auto-form.component"
import { UserService } from "idun-main/user.service"
import { LanguageService } from "idun-layout/language.service"
import { DateHelper } from "utility/date-helper"

import { PatientSummaryService } from "./patient-summary.service"
import { JournalTemplateService } from "./journal-template.service"
import { JournalEntry } from "idun-common/models/journal-entry"
import { FormDateRow } from "idun-layout/form/form-date.component"
import { FormTextHistoryRow } from "idun-layout/form/form-text-history.component"
import { FormTemplateChooserRow } from "idun-common/form/form-template-chooser.component"
import { ButtonThing, LabelThing } from "idun-common/form/form-dynamic-extra.component"
import { Permission } from "idun-common/models/staff-group"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"
import { FormListFileRow } from "idun-common/form/form-list-file.component"
import { DatabaseFileReference, DatabaseFile } from "idun-common/models/database-file"
import { environment } from "../../environments/environment"
import { FormTextRow } from "idun-layout/form/form-text.component"

@Injectable()
export class JournalEntryService extends ExtendedService<JournalEntry> {
	constructor(
		private userService: UserService,
		http: Http,
		patientSummaryService: PatientSummaryService,
		private journalTemplateService: JournalTemplateService,
		l: LanguageService
	) {
		super(http, l,
			JournalEntry,
			"api/JournalEntry",
			"journal-entry",
			() => l.journalEntry
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.EditJournalEntries)
		this.hasReadPermission = () => userService.hasPermission(Permission.ReadPatients)
		this.icon = "Patient"
		this.color = "var(--color-patient)"
		this.showListLink = false
		this.loadAll = false
		this.formInfo = new AutoFormInfo(
			(model: JournalEntry) => {
				if (!model.PatientID)
					return ""

				const patient = patientSummaryService.getModel(model.PatientID)
				return l.journalEntry.nameSingular + " - " + (patient ? patient.Name : "")
			},
			[{
				rows: [
					new FormTextRow("Date", {
						label: () => l.common.date,
						requirements: [ValidationRequirement.ValueRequired(l)],
						display: () => true
					}),
					new FormTextHistoryRow("Description", l, {
						label: () => l.common.description,
						hint: () => l.journalEntry.descriptionHint,
						requirements: [ValidationRequirement.ValueRequired(l)],
						things: [
							this.signatureLabel(),
							this.templateButton()]
					}),
					new FormListFileRow("Files", {
						label: () => l.patientNote.files
					})
				]
			}], {
			parameterHandlers: {
				"id": (id: any, params: any) => { params.id = id },
				"patient": (patient: any, params: any) => { params.patientID = +patient },
				"template": (template: any, params: any) => { params.templateID = +template },
				"side": (side: any, params: any) => { params.side = side }
			},
			finalHandler: (params: any, component: AutoFormComponent<JournalEntry>) => {
				if (params.id === undefined) {
					const id = isNaN(params.patientID) ? this.userService.getCurrentPatientDBID() : params.patientID
					component.subscribe(this.userService.currentPatient$, (patient) => {
						if (!patient)
							return
						if (!component.model.DBID && component.model.Description[0].Content === "") {
							component.model.PatientID = patient.DBID
							component.original.PatientID = patient.DBID
						}
					}, "currentPatient")

					if (!component.model.DBID) {
						component.model.PatientID = id
						component.original.PatientID = id
					}

					if (params.templateID) {
						component.subscribe(this.journalTemplateService.getSingle$(params.templateID), (template) => {
							component.model.Description[component.model.Description.length - 1].Content = template.Content
						})
					}
					return
				}
				component.handleIDUpdate(params.id)
			},
			onSubmit: async (model: JournalEntry, component: AutoFormComponent<JournalEntry>) => {
				const latestEntry = model.Description[model.Description.length - 1]
				const previousEntry = model.Description.length > 1
					? model.Description[model.Description.length - 2]
					: null

				if (latestEntry.Title === "new")
					latestEntry.Title = this.titleForNewTextEntry(userService)

				if (previousEntry && previousEntry.Content === latestEntry.Content) {
					previousEntry.Title = `${latestEntry.Title}, ${previousEntry.Title}`
					model.Description.pop()
				}
				component.isSaving = true
				await this.uploadUnsavedFiles(model, component)
				component._onSubmit()
			},
			popup: {
				getHeader: () => { return this.l.journalEntry.chooseATemplate },
				onClose: (component) => { component.showPopup = false },
				rows: [
					new FormTemplateChooserRow("", {
						change: (model, value, component: AutoFormComponent<JournalEntry>) => {
							if (!value) {
								return
							}
							model.templatetitle = value.title
							if (typeof value.description === "string") {
								const currentDescription = model.Description[model.Description.length - 1].Content
								model.Description[model.Description.length - 1].Content = (currentDescription ? currentDescription + "\n" : "") + value.description
							}
							component.showPopup = false
							component.validate()
						}
					})
				]
			},
			helpText: () => this.translations().formHelpText
		}
		)
		this.setupList({
			entryHeader: (model: JournalEntry) => model.Date,
			contentList: [(model: JournalEntry) => model.Description[0].Content],
			subscriberFunction: (component: AutoFormComponent<JournalEntry>, callback: any, param: any) => {
				component.subscribe(this.getListForPatient(param),
					callback,
					"models")
			}
		})
	}

	async uploadUnsavedFiles(
		entry: JournalEntry,
		component: any
	) {
		for (let i = 0; i < entry.Files.length; i++) {
			const file = entry.Files[i]
			if (file instanceof DatabaseFileReference)
				continue

			component.saveText = this.l.format(this.l.patientNote.uploadingX, file.Name)
			entry.Files[i] = await this.uploadFile(file.Name, file.Data)
		}
		component.saveText = undefined
	}

	uploadFile(fileName: string, fileData: File): Promise<DatabaseFileReference> {
		// test
		if (!environment.production)
			return new Promise((resolve) => {
				setTimeout(() => {
					resolve(new DatabaseFileReference({ DBID: Math.floor(Math.random() * 100000), Name: fileName }))
				}, 1000)
			})
		// endtest */

		return new Promise((resolve) => {
			const formData = new FormData()
			formData.append("files", fileData, fileName)

			const request = new XMLHttpRequest()
			request.onload = (e) => {
				resolve(new DatabaseFileReference(JSON.parse((<any>e.target).response)))
			}
			// TODO: handle errors
			request.open("POST", "/api/PatientNote/UploadFile")
			request.send(formData)
		})
	}

	signatureLabel() {
		return new LabelThing<JournalEntry>(
			(model) => {
				if (model.Description.length < 2)
					return this.l.journalEntry.notSigned
				const signatures = []
				const datedSignatures = model.Description[model.Description.length - 2].Title.split(", ")
				datedSignatures.forEach((signature) => {
					signatures.push(signature.split(" - ")[1])
				})
				const unique = signatures
					.filter((v, i) => signatures.indexOf(v) === i)
					.filter(e => e !== "new")
				const stringify = unique.join(", ")
				return this.l.journalEntry.signatures + ": " + stringify
			},
			() => true,
			(model) => {
				if (model.Description.length < 2)
					return ""
				return model.Description[model.Description.length - 2].Title
			})
	}

	templateButton() {
		return new ButtonThing<JournalEntry>(
			() => this.l.journalEntry.chooseTemplate,
			(_, component) => {
				component.showPopup = true
			},
			(_, display) => {
				return !display
			}
		)
	}

	titleForNewTextEntry(userService: UserService) {
		return DateHelper.dateToString(new Date())
			+ " " + DateHelper.timeToString(new Date())
			+ " - " + userService.user.Name
	}

	getListForPatient(patientID: number): Promise<Observable<JournalEntry[]>> {
		return this.baseGetSubList("GetListForPatient", { id: patientID })
	}

}
