import { Component, Input, HostBinding } from "@angular/core"
import { LanguageService } from "../language.service"

@Component({
	selector: "idun-warning-box",
	templateUrl: "warning-box.component.html",
	styleUrls: ["warning-box.component.scss"]
})
export class WarningBoxComponent {
	@Input() showHeader = true
	@Input() headerText: string
	@Input() warnings: { [index: string]: Warning }

	@HostBinding("style.display") get showBox() {
		if (this.anyShown() || !this.warnings)
			return ""
		return "none"
	}

	constructor(
		public l: LanguageService
	) { }

	anyShown() {
		if (!this.warnings)
			return false
		return Object.keys(this.warnings).some(x => this.warnings[x].show)
	}

	shownWarnings() {
		if (!this.warnings)
			return []
		const shownWarnings = []
		for (const key in this.warnings)
			if (this.warnings[key].show)
				shownWarnings.push(this.warnings[key])
		return shownWarnings
	}
}

export class Warning {
	constructor(
		public text: () => string,
		public show = false
	) { }
}
