﻿import { TreatmentCodeType } from "idun-common/models/treatment-code-type"
import { ModelTestData } from "./model-test-data"

const baseUrl = "api/treatmentcodetype/"

const list = [
	new TreatmentCodeType({
		DBID: 1,
		Description: "Semi-auto Russian DMR rifle.",
		Title: "SKS"
	}),
	new TreatmentCodeType({
		DBID: 2,
		Description: "Overenskomst forhandlede takster mellem FAPS og Danske Regioner",
		Title: "Sygesikringstakster"
	})
]

ModelTestData.loadFromLocalStorage(baseUrl, list, TreatmentCodeType)

export function TreatmentCodeTypeResponses() {
	return {
		...ModelTestData.commonLocalStorageResponses(TreatmentCodeType, baseUrl, list)
	}
}
