﻿import { Type } from "@angular/core"
import { IdunModel } from "idun-common/models/idun-model"

const deleted = {}

export abstract class ModelTestData {

	static commonResponses<Model extends IdunModel>(
		modelClass: Type<Model>,
		baseUrl: string,
		list: Model[],
		alternates: any = {}
	) {
		const responses = {}
		responses[baseUrl + "get"] = ModelTestData.getResponse(list)
		responses[baseUrl + "getlist"] = ModelTestData.getListResponse(list)
		responses[baseUrl + "getpage"] = ModelTestData.getPageResponse(list)
		responses[baseUrl + "add"] = ModelTestData.addResponse(modelClass, list)
		responses[baseUrl + "update"] = ModelTestData.updateResponse(modelClass, list)
		responses[baseUrl + "markfordeletion"] = ModelTestData.deleteResponse(modelClass, list)
		responses[baseUrl + "revertdelete"] = ModelTestData.cancelDeleteResponse(modelClass, list)
		responses[baseUrl + "getcount"] = ModelTestData.getCountResponse(list)
		for (const i in alternates)
			responses[baseUrl + i] = alternates[i]
		return responses
	}

	static commonLocalStorageResponses<Model extends IdunModel>(
		modelClass: Type<Model>,
		baseUrl: string,
		list: Model[],
		alternates: any = {}
	) {
		const responses = {}
		responses[baseUrl + "get"] = ModelTestData.getResponse(list)
		responses[baseUrl + "getlist"] = ModelTestData.getListResponse(list)
		responses[baseUrl + "getpage"] = ModelTestData.getPageResponse(list)
		responses[baseUrl + "add"] = this.localStorageResponse(baseUrl, list, ModelTestData.addResponse(modelClass, list))
		responses[baseUrl + "update"] = this.localStorageResponse(baseUrl, list, ModelTestData.updateResponse(modelClass, list))
		responses[baseUrl + "markfordeletion"] = this.localStorageResponse(baseUrl, list, ModelTestData.deleteResponse(modelClass, list))
		responses[baseUrl + "revertdelete"] = this.localStorageResponse(baseUrl, list, ModelTestData.cancelDeleteResponse(modelClass, list))
		responses[baseUrl + "getcount"] = ModelTestData.getCountResponse(list)
		for (const i in alternates)
			responses[baseUrl + i] = alternates[i]
		return responses
	}

	static getResponse<Model extends IdunModel>(list: Model[]) {
		return (parameters) => {
			for (const i in list)
				if (+list[i].DBID === +parameters.id)
					return JSON.stringify(list[i])
			return ""
		}
	}

	static getListResponse<Model extends IdunModel>(list: Model[]) {
		return (parameters) => {
			return JSON.stringify(list)
		}
	}

	static getPageResponse<Model extends IdunModel>(list: Model[]) {
		return (parameters) => {
			const result = []
			const offset = parameters.page * parameters.pageSize
			for (let i = 0; i < parameters.pageSize && i + offset < list.length; i++)
				result[i] = list[i + offset]
			return JSON.stringify(result)
		}
	}

	static addResponse<Model extends IdunModel>(modelClass: Type<Model>, list: Model[]) {
		return (parameters) => {
			const newModel = new modelClass(parameters)
			if (list.length)
				newModel.DBID = (+list[list.length - 1].DBID) + 1
			else
				newModel.DBID = 1
			list.push(newModel)
			return JSON.stringify(newModel)
		}
	}

	static localStorageResponse<Model extends IdunModel>(
		baseUrl: string,
		list: Model[],
		baseFunction: (parameters: any) => string
	) {
		return (parameters) => {
			const response = baseFunction(parameters)
			localStorage[baseUrl] = JSON.stringify(list)
			return response
		}
	}

	static updateResponse<Model extends IdunModel>(modelClass: Type<Model>, list: Model[]) {
		return (parameters) => { // TODO: this should make some failure noise if the isn't something with the given DBID
			let index
			for (index in list)
				if (+list[index].DBID === +parameters.DBID)
					break
			const newModel = new modelClass(parameters)
			list[index] = newModel
			return JSON.stringify(newModel)
		}
	}

	static deleteResponse<Model extends IdunModel>(modelClass: Type<Model>, list: Model[]) {
		return (parameters) => {
			const index = list.findIndex(x => +x.DBID === +parameters.id)
			if (index >= 0) {
				if (!deleted[modelClass.name])
					deleted[modelClass.name] = []
				const model = list[index]
				deleted[modelClass.name][model.DBID] = model
				list.splice(index, 1)
			}
			return JSON.stringify("")
		}
	}

	static cancelDeleteResponse<Model extends IdunModel>(modelClass: Type<Model>, list: Model[]) {
		return (parameters) => {
			list.push(deleted[modelClass.name][parameters.id])
			return JSON.stringify(true)
		}
	}

	static getCountResponse<Model extends IdunModel>(list: Model[]) {
		return (parameters) => {
			return JSON.stringify({ count: list.length })
		}
	}

	static loadFromLocalStorage(baseUrl: string, destinationList: any[], modelClass: Type<any>) {
		if (!localStorage[baseUrl])
			return
		const stored = modelClass
			? JSON.parse(localStorage[baseUrl]).map(x => new modelClass(x))
			: JSON.parse(localStorage[baseUrl])
		destinationList.splice(0, destinationList.length)
		destinationList.push(...stored)
	}
}
