﻿import { CalendarEntryProvider } from "idun-calendar/calendar-entry-provider"
import { PlanningRole } from "idun-common/models/period-plan"
import { BasicCalendarEntry } from "idun-calendar/basic-calendar-entry"
import { CalendarEntry } from "idun-calendar/calendar-entry"
import { TimeInterval } from "idun-common/models/time-interval"
import { DateHelper, changeDate } from "utility/date-helper"
import { TimeIntervalSeries } from "idun-common/models/time-interval-series"

export class PeriodPlanStaffRoleProvider implements CalendarEntryProvider {
	entries: BasicCalendarEntry<{ source: TimeInterval, Interval: TimeInterval }>[]

	constructor(
		public role: PlanningRole,
		private display: () => boolean,
		private updateProviders: () => void
	) {
		this.updateEntries()
	}

	getEntries(date?: Date): CalendarEntry[] {
		const interval = TimeInterval.fromInterval(DateHelper.dayInterval(date))
		return this.entries.filter(e => e.interval.intersects(interval))
	}

	getTitle() {
		return this.role.Title
	}

	getSubtitle() {
		return ""
	}

	getHovertitle() {
		return this.role.Title
	}

	updateEntries() {
		this.entries = this.role.Intervals
			.map(e => this.calendarEntryFrom(e))
	}

	private calendarEntryFrom(interval: TimeInterval) {
		const display = this.display
		return new BasicCalendarEntry(
			{ source: interval, Interval: interval },
			{
				get hoverText() { return interval.toShortString() },
				boxHue: 120,
				get allowResize() { return !display() },
				moveStartMinute: (minuteChange: number) =>
					this.moveStartMinute(interval, minuteChange),
				moveEndMinute: (minuteChange: number) =>
					this.moveEndMinute(interval, minuteChange),
				delete: () =>
					this.delete(interval),
			})
	}

	private moveStartMinute(interval: TimeInterval, minuteChange: number) {
		changeDate(interval.StartTime, { minutes: minuteChange })
		TimeIntervalSeries.mergeIntervalIntoList(interval, this.role.Intervals)
		this.updateProviders()
	}

	private moveEndMinute(interval: TimeInterval, minuteChange: number) {
		changeDate(interval.EndTime, { minutes: minuteChange })
		TimeIntervalSeries.mergeIntervalIntoList(interval, this.role.Intervals)
		this.updateProviders()
	}

	private delete(interval: TimeInterval) {
		const index = this.role.Intervals.indexOf(interval)
		this.role.Intervals.splice(index, 1)
		this.updateProviders()
	}
}
