﻿import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from "@angular/core"
import { Figure, FigureView, SubFigureView } from "./figure.interface"

@Component({
	selector: "idun-figure",
	templateUrl: "./figure.component.html",
	styleUrls: ["figure.component.css"]
})
export class FigureComponent implements OnInit {
	@ViewChild("selectionCanvas") selectionCanvas: any
	selectionContext: CanvasRenderingContext2D

	@Input() figure: Figure
	_view = ""
	@Input() set view(value: string) {
		this._view = value
		this.updateBackground()
	}
	get view() { return this._view }
	_subview = ""
	@Input() set subview(value: string) {
		if (this._subview !== value)
			this.subviewChange.emit(value)
		this._subview = value
		this.updateBackground()
	}
	get subview() { return this._subview }
	@Output() subviewChange = new EventEmitter()
	@Input() selectedParts: string[] = []
	@Output() selectedPartsChange = new EventEmitter()
	selectedGroup: string[]

	currentPartSelector: PartSelector // the part that's hovered at
	@Output() currentPathChange = new EventEmitter<string>()

	backgroundImage = ""
	get currentOverlayImages(): string[] {
		if (!this.currentPartSelector)
			return []
		const group = this.findGroupFor(this.currentPartSelector)
		if (group && this.selectedGroup !== group)
			return group.map(x => this.getImage({ view: this.view, subview: this.subview, part: x }))
		return [this.getImage(this.currentPartSelector)]
	}

	width: number
	height: number

	constructor() { }

	findGroupFor(selector: PartSelector) {
		for (const group of this.viewFor(selector.view, selector.subview).groups)
			if (group.includes(selector.part))
				return group
	}

	overlayImageFor(path: string) {
		const selector = this.selectorFromPath(path)
		if (!selector || !selector.subview && this.subview)
			return ""
		return this.getImage(selector)
	}

	selectorFromPath(path: string): PartSelector {
		if (!path)
			return null
		const parts = path.split("/")
		if (parts.length === 3)
			return {
				view: parts[0],
				subview: parts[1],
				part: parts[2]
			}
		else if (parts.length === 2)
			return {
				view: parts[0],
				part: parts[1]
			}
		return null
	}

	getImage(selector: PartSelector) {
		if (!selector)
			return ""
		return this.imageFor(this.partFor(selector))
	}

	ngOnInit() {
		this.selectionContext = this.selectionCanvas.nativeElement.getContext("2d")

		this.width = this.selectionCanvas.nativeElement.width
		this.height = this.selectionCanvas.nativeElement.height

		this.updateBackground()

		this.selectionCanvas.nativeElement.onmousemove = this.onMouseMove
		this.selectionCanvas.nativeElement.onclick = this.onMouseClick
		this.selectionCanvas.nativeElement.onmouseleave = this.onMouseLeave
	}

	updateBackground() {
		this.backgroundImage = ""
		if (!this.view)
			return
		this.backgroundImage = this.imageFor(this.currentView())

		if (!this.selectionContext)
			return
		this.selectionContext.clearRect(0, 0, this.width, this.height)
		this.insertImage(this.selectionImageFor(this.currentView()), this.selectionContext)
	}

	onMouseMove = (event: MouseEvent) => {
		const newPart = this.getPartSelector(event)
		if (JSON.stringify(newPart) !== JSON.stringify(this.currentPartSelector))
			this.currentPathChange.emit(this.pathFrom(newPart))
		this.currentPartSelector = newPart
	}

	onMouseClick = (event: MouseEvent) => {
		if (this.mouseIsOutsideBody())
			this.selectView()
		else if (event.shiftKey)
			this.addCurrentToSelected()
		else if (this.view === "sections")
			this.selectSections()
		else if (this.currentPartIsSubview())
			this.selectSubview()
		else
			this.selectPartOrGroup()
		this.selectedPartsChange.emit(this.selectedParts)
	}

	private mouseIsOutsideBody() {
		return !this.currentPartSelector
	}

	private addCurrentToSelected() {
		const path = this.pathFrom(this.currentPartSelector)
		if (!this.selectedParts.includes(path))
			this.selectedParts.push(path)
	}

	private selectSections() {
		if (!this.SectionSelected())
			return this.selectPart(this.currentPartSelector)

		const parts = Object.keys(this.figure.sections.parts)
		const selectedIndex = this.indexOf(this.selectedParts[0], parts)
		const currentIndex = this.indexOf(this.pathFrom(this.currentPartSelector), parts)
		this.selectedParts = parts
			.filter((_, index) => this.numberIsWithin(index, selectedIndex, currentIndex))
			.map(part => this.pathFrom({ view: this.view, subview: this.subview, part: part }))
		this.selectedGroup = null
	}

	private SectionSelected() {
		if (this.selectedParts.length !== 1)
			return false
		return this.selectedParts[0].startsWith("sections/")
	}

	private indexOf(part: string, parts: string[]) {
		return parts.indexOf(part.split("/")[1])
	}

	private numberIsWithin(number: number, boundA: number, boundB: number) {
		return (boundA <= number && number <= boundB) || (boundB <= number && number <= boundA)
	}

	private selectPartOrGroup() {
		const group = this.findGroupFor(this.currentPartSelector)
		if (!group)
			return this.selectPart(this.currentPartSelector)
		const parts = (this.selectedParts || [])
			.map((path) => this.selectorFromPath(path))
			.filter(selector => selector)
			.map(selector => selector.part)
		var selectedGroupParts = 0
		for (const part of group)
			if (parts.includes(part))
				selectedGroupParts++
		if (selectedGroupParts === group.length)
			return this.selectPart(this.currentPartSelector)
		this.selectGroup(group)
	}

	private currentPartIsSubview() {
		if (this.subview)
			return false
		return !!this.figure[this.view].subviews[this.currentPartSelector.part]
	}

	private selectView() {
		this.selectedParts = [this.view]
		this.selectedGroup = null
	}

	private selectSubview() {
		this.subview = this.currentPartSelector.part
		this.currentPartSelector = null
		this.selectedParts = [this.view + "/" + this.subview]
		this.selectedGroup = null
	}

	private selectPart(selector: PartSelector) {
		this.selectedParts = [this.pathFrom(selector)]
		this.selectedGroup = null
	}

	private selectGroup(group: string[]) {
		this.selectedParts = group.map(part => this.pathFrom({
			view: this.view,
			subview: this.subview,
			part: part
		}))
		this.selectedGroup = group
	}

	onMouseLeave = (_: MouseEvent) => {
		this.currentPartSelector = null
		this.currentPathChange.emit("")
	}

	getMousePosition(event: MouseEvent) {
		const rect = this.selectionCanvas.nativeElement.getBoundingClientRect()
		return {
			x: (event.clientX - rect.left) * (this.width / rect.width),
			y: (event.clientY - rect.top) * (this.height / rect.height)
		}
	}

	getPartSelector(event: MouseEvent): PartSelector {
		const parts = this.subview
			? this.figure[this.view].subviews[this.subview].parts
			: this.figure[this.view].parts
		const { x, y } = this.getMousePosition(event)
		const pixelColor = this.selectionContext.getImageData(x, y, 1, 1).data

		for (const key in parts)
			if (this.partHasColor(parts[key], pixelColor))
				return { view: this.view, subview: this.subview, part: key }
		return null
	}

	private pathFrom(part: PartSelector) {
		if (!part)
			return ""
		return part.view + "/" + (part.subview ? part.subview + "/" : "") + part.part
	}

	partHasColor(part: { color: number[] }, color: any) {
		return part.color[0] === color[0] &&
			part.color[1] === color[1] &&
			part.color[2] === color[2]
	}

	insertImage(i: string, context: CanvasRenderingContext2D) {
		const image = new Image()
		image.src = i
		image.onload = () => {
			context.drawImage(image, 0, 0)
		}
	}

	currentView() {
		return this.viewFor(this.view, this.subview)
	}

	viewFor(view: string, subview: string): FigureView | SubFigureView {
		const figure = this.figure[view]
		if (!figure)
			return
		const subfigure = figure.subviews[subview]
		return subfigure || figure
	}

	partFor(selector: Selector) {
		const view = this.viewFor(selector.view, selector.subview)
		const part = selector.part ? view.parts[selector.part] : view
		return part || <FigureView>{}
	}

	selectionImageFor(view: { selection: string }) {
		return "assets/images" + view.selection
	}

	imageFor(view: { image: string }) {
		return "assets/images" + view.image
	}
}

interface PartSelector {
	view: string
	subview?: string
	part: string
}

interface Selector {
	view: string
	subview?: string
	part?: string
}
