﻿import { Input, Output, EventEmitter, HostListener, ViewChild, ElementRef } from "@angular/core"
import { CalendarEntry } from "idun-calendar/calendar-entry"
import { hasValue } from "utility/common"
import { DragHandler } from "./drag-handler"

export abstract class CalendarEntryBaseComponent {
	@ViewChild("calendarEventEntry") calendarEventEntry: ElementRef
	@Input() entry: CalendarEntry
	@Input() baseDate: Date
	@Input() startHour: number
	@Input() endHour: number
	@Output() selectEntry = new EventEmitter<CalendarEntry>()

	dragHandler = new DragHandler(
		() => this.baseDate,
		() => this.startHour,
		() => this.endHour,
		() => this.entry
	)

	onMouseDownStartEdge() {
		this.dragHandler.onMouseDownStartEdge()
		this.registerHandlers()
	}

	onMouseDownEndEdge() {
		this.dragHandler.onMouseDownEndEdge()
		this.registerHandlers()
	}

	private registerHandlers() {
		const mouseUpHandler = () => {
			this.dragHandler.onMouseUp()
			window.document.removeEventListener("mouseup", mouseUpHandler)
			window.document.removeEventListener("mousemove", this.onMouseMove)
		}
		window.document.addEventListener("mouseup", mouseUpHandler)
		window.document.addEventListener("mousemove", this.onMouseMove)
	}

	abstract onMouseMove(event: MouseEvent): void

	get opacity() {
		if (this.entry.ghost)
			return "0.3"
		return "1"
	}

	get pointerEvents() {
		if (this.entry.clickable)
			return "auto"
		return "none"
	}

	get boxColor() {
		const hue = hasValue(this.entry.boxHue)
			? this.entry.boxHue
			: 250
		return `hsla(${hue}, ${hasValue(this.entry.boxSaturation) ? this.entry.boxSaturation : 100}%, 45%, 0.6)`
	}

	get borderColor() {
		const hue = hasValue(this.entry.borderHue)
			? this.entry.borderHue
			: this.entry.boxHue
		return `hsl(${hue}, 100%, ${this.entry.boxSaturation === 0 ? 100 : 50}%)`
	}

	protected get startMinute() {
		return this.startHour * 60
	}

	protected get endMinute() {
		return this.endHour * 60
	}

	protected get entryStartMinute() {
		return this.dragHandler.shownStartMinute
			|| this.entry.getStartMinute(this.baseDate)
	}

	protected get entryEndMinute() {
		return this.dragHandler.shownEndMinute
			|| this.entry.getStartMinute(this.baseDate) + this.entry.length
	}

	get hidden() {
		return this.endMinute <= this.entryStartMinute
			|| this.entryEndMinute <= this.startMinute
	}

	protected findShownLength() {
		let startPoint = this.entryStartMinute
		let endPoint = this.entryEndMinute
		if (startPoint < this.startMinute)
			startPoint = this.startMinute
		if (endPoint > this.endMinute)
			endPoint = this.endMinute
		return endPoint - startPoint
	}

	protected findStartOffset() {
		return this.entryStartMinute - this.startMinute
	}

	protected sizeFrom(minutes: number) {
		if (minutes < 0)
			minutes = 0
		return "" + minutes
	}
}
