import { Component, Input, HostBinding, HostListener } from "@angular/core"

@Component({
	selector: "idun-form-input-container",
	templateUrl: "form-input-container.component.html",
	styleUrls: ["form-input-container.component.scss"]
})
export class FormInputContainerComponent {
	@Input() display: boolean
	@Input() showLabel = true
	@Input() showAsterisk = false
	@Input() subtleLabel = true
	private _hint = ""
	@Input() set hint(text: string) {
		if (!text)
			this._hint = ""
		else
			this._hint = text.substr(0, 5) === "hint-" ? "" : text
	}
	get hint() {
		return this._hint
	}
	@Input() error: string
	@Input() label = "label"
	@HostBinding("class.display-label") bla = true

	showHint = false
}
