﻿import { Component, HostBinding } from "@angular/core"
import { CalendarEntryBaseComponent } from "./calendar-entry-base.component"

@Component({
	selector: "idun-calendar-entry-horisontal",
	templateUrl: "calendar-entry-horisontal.component.html",
	styleUrls: ["calendar-entry-base.component.scss", "calendar-entry-horisontal.component.scss"]
})
export class CalendarEntryHorisontalComponent extends CalendarEntryBaseComponent {
	@HostBinding("style.grid-template-columns") get grid() {
		return `${this.startLength}fr ${this.entryLength}fr ${this.endLength}fr`
	}

	onMouseMove = (event: MouseEvent) => {
		if (!this.calendarEventEntry)
			return
		this.dragHandler.onMouseMove(this.mouseMinutePosition(event))
	}

	private mouseMinutePosition(event: MouseEvent) {
		const rect = this.calendarEventEntry.nativeElement.getBoundingClientRect()
		const mouseDeltaPixels = event.clientX - rect.left
		const totalPixelWidth = this.calendarEventEntry.nativeElement.parentElement.clientWidth
		const minuteSize = totalPixelWidth / (this.endMinute - this.startMinute)
		return Math.floor(mouseDeltaPixels / minuteSize)
	}

	get startLength() {
		return this.sizeFrom(this.findStartOffset())
	}

	get entryLength() {
		return this.sizeFrom(this.findShownLength())
	}

	get endLength() {
		return this.sizeFrom(this.endMinute - this.entryEndMinute)
	}
}
