import { Component, Input, HostBinding, HostListener } from "@angular/core"

@Component({
	selector: "idun-label",
	template: `
		<ng-content></ng-content>
		<div *ngIf="showHint" class="hint-box"></div>
		<div *ngIf="showHint" class="hint">{{hint}}</div>
		<div *ngIf="hint" class="hint-toggle hover-show" (click)="showHint = showHint">?</div>`,
	styleUrls: ["label.component.scss"]
})
export class LabelComponent {
	@Input() display: boolean
	private _hint = ""
	@Input() set hint(text: string) {
		if (!text)
			this._hint = ""
		else
			this._hint = text.substr(0, 5) === "hint-" ? "" : text
	}
	get hint() {
		return this._hint
	}
	@HostBinding("style.cursor") get title() {
		if (this.hint)
			return "help"
	}
	@HostBinding("class.edit-label") get blab() {
		return !this.display
	}
	@HostListener("click") toggleHint() {
		if (this.hint)
			this.showHint = !this.showHint
	}
	@HostBinding("class.display-label") bla = true

	showHint = false
}
