﻿import { Component, Input } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { CalendarEntry } from "./calendar-entry"

@Component({
	selector: "idun-calendar-column-display",
	templateUrl: "calendar-column-display.component.html",
	styleUrls: ["calendar-column-display.component.scss"]
})
export class CalendarColumnDisplayComponent {
	@Input() columns: CalendarColumn[]
	@Input() date: Date

	timeBlocks = []
	baseStartHour = 6
	baseEndHour = 18
	startHour = 6
	endHour = 18
	@Input() hourHeight = 6
	blockHeight = 1

	get showEarlyTimes() {
		return this.startHour === 0
	}

	get showLateTimes() {
		return this.endHour === 24
	}

	showAllColumns = false

	constructor(public l: LanguageService) {
		this.createTimeBlocks()
	}

	toggleEarlyTimes() {
		this.startHour = this.showEarlyTimes
			? this.baseStartHour
			: 0
		this.createTimeBlocks()
	}

	toggleLateTimes() {
		this.endHour = this.showLateTimes
			? this.baseEndHour
			: 24
		this.createTimeBlocks()
	}

	private createTimeBlocks() {
		this.timeBlocks = []
		for (let i: number = this.startHour; i < this.endHour; i += this.blockHeight) {
			const minutes = Math.floor((i % 1) * 60)
			const hours = Math.floor(i)
			const block = {
				hours: hours,
				minutes: minutes,
				hourText: "" + hours,
				minuteText: minutes < 10 ? "0" + minutes : "" + minutes
			}
			this.timeBlocks.push(block)
		}
	}

	currentColumns() {
		if (this.showAllColumns)
			return this.columns
		return this.columns.filter(e => !e.hideByDefault())
	}

	zoomIn() {
		this.hourHeight *= 1.414
	}

	zoomOut() {
		this.hourHeight /= 1.414
	}

	addEntry(
		column: CalendarColumn,
		time: {
			hours: number;
			minutes: number;
			hourText: string;
			minuteText: string;
		}
	) {
		column.addEntry(this.dateFromBlockAndColumn(time, column))
	}

	private dateFromBlockAndColumn(block, column: CalendarColumn) {
		const date = new Date(column.date())
		date.setHours(block.hours, block.minutes, 0)
		return date
	}
}

export interface CalendarColumn {
	getEntries: () => CalendarEntry[],
	title: () => string,
	subtitle: () => string,
	hovertitle: () => string,
	date: () => Date,
	addEntry: (time: Date) => void,
	selectEntry: (event: CalendarEntry) => void,
	hideByDefault: () => boolean
	isWeekend?: boolean
}
