﻿import { CalendarEntry } from "./calendar-entry"

export class DragHandler {
	shownStartMinute: number
	shownEndMinute: number
	snapInterval = 5
	minimumTimeLength = 20

	currentEdge: DraggableEdge
	dragging = false
	private destinationMinute: number

	constructor(
		private baseDate: () => Date,
		private startHour: () => number,
		private endHour: () => number,
		private entry: () => CalendarEntry
	) {
	}

	onMouseDownStartEdge(): void {
		this.dragging = true
		this.currentEdge = DraggableEdge.Start
	}

	onMouseDownEndEdge(): void {
		this.dragging = true
		this.currentEdge = DraggableEdge.End
	}

	onMouseUp(): void {
		this.dragging = false
		this.currentEdge = undefined
		this.updateEntry()
		this.shownStartMinute = undefined
		this.shownEndMinute = undefined
	}

	private updateEntry() {
		if (this.shouldUpdateStart())
			this.entry().moveStartMinute(this.shownStartMinute - this.currentEntryStart())
		if (this.shouldUpdateEnd())
			this.entry().moveEndMinute(this.shownEndMinute - this.currentEntryEnd())
	}

	private shouldUpdateStart() {
		return this.shownStartMinute && this.shownStartMinute !== this.currentEntryStart()
	}

	private shouldUpdateEnd() {
		return this.shownEndMinute && this.shownEndMinute !== this.currentEntryEnd()
	}

	private currentEntryStart() {
		return this.entry().getStartMinute(this.baseDate())
	}

	private currentEntryEnd() {
		return this.entry().getStartMinute(this.baseDate()) + this.entry().length
	}

	onMouseMove(minuteDeltaMinutes: number) {
		if (!this.dragging)
			return
		const minutePosition = this.findMinutePosition(minuteDeltaMinutes)
		this.destinationMinute = this.findNearestSnapValue(minutePosition)
		this.moveEdge()
	}

	private findMinutePosition(minuteDeltaMinutes: number): number {
		return minuteDeltaMinutes + (this.shownStartMinute || this.currentEntryStart())
	}

	private findNearestSnapValue(minutePosition: number): number {
		return Math.round(minutePosition / this.snapInterval) * this.snapInterval
	}

	private moveEdge() {
		switch (this.currentEdge) {
			case DraggableEdge.Start:
				return this.moveStartHandle()
			case DraggableEdge.End:
				return this.moveEndHandle()
		}
	}

	private moveStartHandle() {
		if (this.destinationIsTooLate())
			this.shownStartMinute = this.currentEntryEnd() - this.minimumTimeLength
		else if (this.destinationIsBeforeStartTime())
			this.shownStartMinute = this.startMinute
		else
			this.shownStartMinute = this.destinationMinute
	}

	private destinationIsTooLate() {
		return this.currentEntryEnd() - this.minimumTimeLength < this.destinationMinute
	}

	private destinationIsBeforeStartTime() {
		return this.destinationMinute < this.startMinute
	}

	private moveEndHandle() {
		if (this.destinationIsTooEarly())
			this.shownEndMinute = this.currentEntryStart() + this.minimumTimeLength
		else if (this.destinationIsAfterEndTime())
			this.shownStartMinute = this.startMinute
		else
			this.shownEndMinute = this.destinationMinute
	}

	private destinationIsTooEarly() {
		return this.destinationMinute < this.currentEntryStart() + this.minimumTimeLength
	}

	private destinationIsAfterEndTime() {
		return this.endMinute < this.destinationMinute
	}

	private get startMinute() {
		return this.startHour() * 60
	}

	private get endMinute() {
		return this.endHour() * 60
	}
}

enum DraggableEdge { Start, End }
