import { Component, forwardRef, Input, OnInit, OnDestroy } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { BaseService } from "idun-common/base-service"
import { ExtendedService } from "idun-common/extended-service"
import { IdunModel } from "idun-common/models/idun-model"
import { Subscriber } from "utility/subscriber"
import { FormRow, FormRowOptions, FieldInput } from "idun-layout/form/form-row"
import { ModelSet } from "../models/procedure"
import { LanguageService } from "idun-layout/language.service"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormModelSetsComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormModelSetsComponent),
	multi: true
}

@Component({
	selector: "idun-form-model-sets",
	templateUrl: "form-model-sets.component.html",
	styleUrls: ["form-model-sets.component.scss"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormModelSetsComponent extends FormBasicComponent<ModelSet[]> implements ControlValueAccessor, OnDestroy {
	rowClass = FormModelSetsRow
	@Input() service: BaseService<IdunModel>
	@Input() modelsLabel: () => string = () => "models"
	@Input() dataField = "data"
	@Input() dataType: "number" | "text" = "text"
	@Input() dataLabel: () => string = () => "data"
	@Input() dataEndLabel?: () => string

	subscriber = new Subscriber()

	constructor(public l: LanguageService) {
		super()
	}

	serviceHasComponent() {
		return this.service instanceof ExtendedService
	}

	getKeys(index?: string) {
		if (index) {
			return Object.keys(this.value[index].ModelIDs)
		}
		if (this.value)
			return Object.keys(this.value)
		else return []
	}

	ngOnDestroy(): void {
		this.subscriber.unsubscribe()
	}

	titleFromID(id: number) {
		const model = this.service.getModel(id)
		if (model)
			return model.getTitle()
		return ""
	}

	shortTitleFromID(id: number) {
		const model = this.service.getModel(id)
		if (model)
			return model.getShortTitle()
		return ""
	}

	addSet(): void {
		const newEntry: any = { Title: this.l.common.variation + " " + (this.value.length + 1) }
		if (this.dataField)
			newEntry[this.dataField] = this.value[0][this.dataField]
		this.value.push(new ModelSet(newEntry))
		this.onChangeCallback(this.value)
	}

	addModel(key, id): void {
		this.value[key].ModelIDs.push(id)
		this.onChangeCallback(this.value)
	}

	remove(index: string, idIndex?: string): void {
		if (+idIndex > -1) {
			this.value[index].ModelIDs.splice(+idIndex, 1)
			this.onChangeCallback(this.value)
			return
		}
		this.value.splice(+index, 1)
		this.onChangeCallback(this.value)
	}
}

export class FormModelSetsRow extends FormRow {
	constructor(
		field: FieldInput,
		options: Options
	) {
		super(field, options)
	}
}

interface Options extends FormRowOptions {
	modelsLabel: () => string
	dataField?: string
	dataType?: "number" | "text"
	dataEndLabel?: () => string
	service: BaseService<any>
}
