﻿import { SearchableIdunModel } from "./idun-model"

export class UnsavedFile {
	Name: string
	Data: File

	constructor(source?: any) {
		for (const prop in source) this[prop] = source[prop]
	}
}

export class DatabaseFileReference {
	DatabaseFileID: number
	Name: string

	constructor(source?: any) {
		for (const prop in source) this[prop] = source[prop]
	}
}

export class DatabaseFile extends SearchableIdunModel {
	DBID: number
	Name: string
	Data: File

	constructor(source?: any) {
		super()
		for (const prop in source) this[prop] = source[prop]
	}

	getTitle(): string {
		return this.Name
	}

	searchAttributes() {
		return [this.Name]
	}
}
