﻿import { Component, forwardRef, Input } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { TelephoneNumber } from "idun-common/models/patient"
import { LanguageService } from "idun-layout/language.service"
import { FormRow, FormRowOptions, FieldInput } from "idun-layout/form/form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormTelephoneComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormTelephoneComponent),
	multi: true
}

@Component({
	selector: "idun-form-telephone",
	templateUrl: "form-telephone.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormTelephoneComponent extends FormBasicComponent<TelephoneNumber[]> {
	rowClass = FormTelephoneRow
	@Input() hideTypes = false

	checkHasValue() {
		if (!this.value)
			return false
		if (!this.value.length)
			return false
		for (const i in this.value) {
			if (!this.value[i].Type)
				return false
			if (!this.value[i].Number)
				return false
		}
		return true
	}

	constructor(public l: LanguageService) {
		super()
	}

	getSelectValue(i): string {
		return this.value[i].Type
	}

	setSelectValue(value, i) {
		this.value[i].Type = value
	}

	setAsPrimaryPhone(i: number): void {
		for (let n = 0; n < this.value.length; n++)
			this.value[n].Primary = n === i
	}

	addAdditionalPhone(): void {
		if (this.value[this.value.length - 1].Number)
			this.value.push(new TelephoneNumber())
	}

	removePhone(i: number): void {
		this.value.splice(i, 1)
		this.value[0].Primary = true
	}

	getTypes() {
		const types = []
		for (const i in this.value) {
			const type = this.value[i].Type
			if (type && type !== "home" && type !== "mobile" && !types.includes(type))
				types.push(type)
		}
		return types
	}

	showAddButton() {
		return !this.display && this.value && this.allNumbersAreFilled()
	}

	private allNumbersAreFilled() {
		return this.value.every(phone => !!phone.Number && !!phone.Type)
	}
}

export class FormTelephoneRow extends FormRow {
	constructor(
		field: FieldInput,
		options: Options
	) {
		super(field, options)
	}
}

interface Options extends FormRowOptions {
	hideTypes?: boolean
}
