﻿import { Component, OnInit, Input } from "@angular/core"
import { ActivatedRoute, Data } from "@angular/router"
import { RouteParameterComponent } from "./route-parameter-component"
import { AutoListInfo } from "./auto-list-info"
import { IdunModel } from "./models/idun-model"
import { ExtendedService } from "idun-common/extended-service"
import { ModelService } from "idun-common/model.service"
import { LanguageService } from "idun-layout/language.service"

@Component({
	selector: "idun-auto-list-box",
	templateUrl: "auto-list-box.html"
})
export class AutoListBoxComponent<Model extends IdunModel> extends RouteParameterComponent implements OnInit {
	@Input() showAllWhenQueryIsEmpty = true
	@Input() showWhenEmpty = true
	@Input() showNavigation = true
	@Input() showSearch = true
	@Input() icon: string
	@Input() color: number | string
	@Input() listContentTextAlign = "left"
	@Input() addText: string

	@Input() pageSize = 20
	@Input() page = 0

	@Input() header: string
	@Input() helpText = () => ""
	@Input() modelType: string
	@Input() models: Model[]
	@Input() query: string
	@Input() param

	service: ExtendedService<Model>
	selectedModel: Model
	info: AutoListInfo<Model>

	constructor(
		protected route: ActivatedRoute,
		public modelService: ModelService,
		public l: LanguageService
	) {
		super()
	}

	ngOnInit(): void {
		if (this.modelType)
			this.updateService(this.modelType)
		else
			this.route.data.subscribe((value: Data) => {
				this.updateService(value["model"])
			})
	}

	updateService(model: string) {
		const service = this.modelService[model]
		if (!(service instanceof ExtendedService)) {
			console.error("trying to use autolist with baseservice")
			return
		}
		this.service = service
		this.info = this.service.listInfo
		if (this.pageSize === undefined)
			this.pageSize = this.info.pageSize
		if (service.icon !== undefined)
			this.icon = service.icon
		if (service.color !== undefined)
			this.color = service.color
		if (this.info.helpText !== undefined)
			this.helpText = this.info.helpText
	}

	getHeader() {
		return this.header || (this.info ? this.info.mainheader : "")
	}

	closePopup = () => {
		this.selectedModel = null
	}
}
