import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { Observable } from "rxjs"
import { DateHelper } from "utility/date-helper"
import { LanguageService } from "idun-layout/language.service"
import { CalendarEvent } from "idun-common/models/calendar-event"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"
import { FormTextareaRow } from "idun-layout/form/form-textarea.component"
import { FormListSelectModelRow } from "idun-common/form/form-list-select-model.component"
import { StaffService } from "./staff.service"
import { ResourceService } from "./resource.service"
import { FormDateIntervalRow } from "idun-layout/form/form-date-interval.component"

@Injectable()
export class CalendarEventService extends ExtendedService<CalendarEvent> {

	constructor(
		http: Http,
		staffService: StaffService,
		resourceService: ResourceService,
		l: LanguageService
	) {
		super(http, l,
			CalendarEvent,
			"api/CalendarEvent",
			"calendar-event",
			() => l.calendarEvent
		)
		this.loadAll = false
		this.showListLink = false
		this.setupForm({
			getHeader: (model: CalendarEvent) => this.l.calendarEvent.nameSingular + (model.Title ? ": " + model.Title : ""),
			rows: [
				new FormDateIntervalRow("Times", this.l, {}),
				new FormTextRow("Title", {
					label: () => l.common.title,
					requirements: [ValidationRequirement.ValueRequired(l)],
				}),
				new FormTextareaRow("Description", {
					label: () => l.common.description
				}),
				new FormListSelectModelRow("OccupiedStaff", {
					label: () => l.calendarEvent.occupiedStaff,
					hint: () => l.calendarEvent.occupiedStaffHint,
					service: staffService
				}),
				new FormListSelectModelRow("OccupiedResources", {
					label: () => l.calendarEvent.occupiedResources,
					hint: () => l.calendarEvent.occupiedResourcesHint,
					service: resourceService
				})
			]
		})
		this.setupList({
			entryHeader: (model: CalendarEvent) => {
				return DateHelper.dateToString(model.Times.StartTime) + ": " + DateHelper.timeIntervalToString(model.Times.StartTime, model.Times.EndTime)
			},
			contentList: [(model: CalendarEvent) => {
				return model.getTitle()
			}]
		})
	}

	getEvents(start: Date, end: Date): Promise<Observable<CalendarEvent[]>> {
		return this.baseGetSubList("GetEvents", { start: start, end: end })
	}

	getPersonalEvents(start: Date, end: Date): Promise<Observable<CalendarEvent[]>> {
		return this.baseGetSubList("GetPersonalEvents", { start: start, end: end })
	}

	getEventsForStaff(id: number, start: Date, end: Date): Promise<Observable<CalendarEvent[]>> {
		return this.baseGetSubList("GetEventsForStaff", { start: start, end: end, id: id })
	}

	getEventsForResource(id: number, start: Date, end: Date): Promise<Observable<CalendarEvent[]>> {
		return this.baseGetSubList("GetEventsForResource", { start: start, end: end, id: id })
	}
}
