﻿import { SearchableIdunModel } from "./idun-model"

export class StatusType extends SearchableIdunModel {
	DBID: number
	Title: string
	Working: boolean
	Hue: number

	constructor(source?: any) {
		super()
		this.Hue = 0.5
		for (const prop in source) this[prop] = source[prop]
	}

	getTitle(): string {
		return this.Title
	}

	searchAttributes() {
		return [this.Title]
	}
}
