﻿import { Component, Input, Output, EventEmitter } from "@angular/core"
import { DateHelper, changeDate } from "utility/date-helper"
import { LanguageService } from "idun-layout/language.service"

@Component({
	selector: "idun-date-navigation",
	templateUrl: "date-navigation.component.html",
	styleUrls: ["date-navigation.component.scss"]
})
export class DateNavigationComponent {
	private _mode: "day" | "week" | "year" = "week"
	@Input() set mode(value: "day" | "week" | "year") {
		this._mode = value
		this.updateEntries()
	}
	get mode() {
		return this._mode
	}

	private _date = new Date()
	@Input() set date(value: Date) {
		this._date = value
		this.updateCurrentMonth()
		this.updateEntries()
	}
	get date() {
		return this._date
	}

	@Output() selectDate = new EventEmitter<Date>()

	currentMonth: { month: number, year: number } = { month: 1, year: 1 }
	days: { date: Date, text: string, currentMonth: boolean, selected: boolean }[] = []
	years: { date: Date, text: string, selected: boolean }[] = []

	constructor(public l: LanguageService) { }

	private updateEntries() {
		if (this.mode === "year")
			this.updateYears()
		else
			this.updateDays()
	}

	private updateCurrentMonth() {
		this.currentMonth = { month: this.date.getMonth(), year: this.date.getFullYear() }
	}

	get title() {
		return this.mode === "year"
			? "" + this.currentMonth.year
			: this.getMonth() + " " + this.currentMonth.year
	}

	private getMonth(): string {
		return this.l.date[DateHelper.monthNames[this.currentMonth.month]]
	}

	private updateDays() {
		this.days = []
		const day = this.findFirstWeek().start
		for (let i = 0; i < 7 * 6; i++) { // always show 6 weeks
			this.days.push({
				date: new Date(day),
				text: "" + day.getDate(),
				currentMonth: day.getMonth() === this.currentMonth.month,
				selected: this.isDateSelected(day)
			})
			changeDate(day, { days: 1 })
		}
	}

	private findFirstWeek() {
		const day = new Date(this.currentMonth.year, this.currentMonth.month, 1)
		if (this.isMondayFirstOfFebruary(day))
			changeDate(day, { days: -1 })
		return DateHelper.weekInterval(day)
	}

	private isMondayFirstOfFebruary(date: Date) {
		return date.getMonth() === 1 && date.getDay() === 1
	}

	private updateYears() {
		this.years = []
		const firstYear = this.findFirstYear()
		for (let i = 0; i < 2 * 5; i++) {
			this.years.push({
				date: new Date(firstYear),
				text: "" + firstYear.getFullYear(),
				selected: this.isDateSelected(firstYear)
			})
			changeDate(firstYear, { years: 1 })
		}
	}

	private findFirstYear() {
		const firstYear = new Date(this.currentMonth.year, this.currentMonth.month, 1)
		firstYear.setFullYear(Math.floor(this.currentMonth.year / 10) * 10)
		return firstYear
	}

	private isDateSelected(date: Date): boolean {
		if (this.mode === "day")
			return DateHelper.isSameDate(date, this.date)
		if (this.mode === "week")
			return DateHelper.isSameWeek(date, this.date)
		return this.date.getFullYear() === date.getFullYear()
	}

	next() {
		if (this.mode === "year")
			this.currentMonth.year += 10
		else
			this.currentMonth.month += 1

		if (this.currentMonth.month > 11) {
			this.currentMonth.month = 0
			this.currentMonth.year++
		}
		this.updateEntries()
	}

	previous() {
		if (this.mode === "year")
			this.currentMonth.year -= 10
		else
			this.currentMonth.month -= 1

		if (this.currentMonth.month < 0) {
			this.currentMonth.month = 11
			this.currentMonth.year--
		}
		this.updateEntries()
	}
}
