﻿import { SearchableIdunModel } from "./idun-model"

export class Procedure extends SearchableIdunModel {
	DBID: number
	Title: string
	Abbreviation: string
	ProcedureTypeID: number
	Description: string

	ResourceRequirements: number[]
	Duration: number

	Variations: ProcedureVariation[]

	constructor(source?: any) {
		super()
		this.Variations = []
		this.ResourceRequirements = []
		if (!source)
			return

		for (const prop in source) this[prop] = source[prop]
		if (source.Variations)
			this.Variations = source.Variations.map(e => new ProcedureVariation(e))
		if (source.ResourceRequirements)
			this.ResourceRequirements = source.ResourceRequirements.map(e => e)
	}

	getTitle(): string {
		return this.Title
	}

	getShortTitle() {
		return this.Abbreviation
	}

	searchAttributes() {
		return [this.Title, this.Abbreviation]
	}
}

export class ModelSet {
	Title: string
	ModelIDs: number[]

	constructor(source?: any) {
		this.ModelIDs = []
		if (!source)
			return

		for (const prop in source) this[prop] = source[prop]
		if (source.ModelIDs)
			this.ModelIDs = source.ModelIDs.map(e => e)
	}
}

export class ProcedureVariation extends ModelSet {
	Placements: string[]
	Price: number

	constructor(source?: any) {
		super(source)
		this.Placements = []
		if (source && source.Placements)
			this.Placements = source.Placements.map(e => e)
	}
}
