import { Component, forwardRef, Input, OnDestroy } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { BaseService } from "idun-common/base-service"
import { ExtendedService } from "idun-common/extended-service"
import { IdunModel } from "idun-common/models/idun-model"
import { Subscriber } from "utility/subscriber"
import { FormRow, FormRowOptions, FieldInput } from "idun-layout/form/form-row"
import { LanguageService } from "idun-layout/language.service"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormListModelDataComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormListModelDataComponent),
	multi: true
}

@Component({
	selector: "idun-form-list-model-data",
	templateUrl: "form-list-model-data.component.html",
	styleUrls: ["form-list-model-data.component.scss"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormListModelDataComponent extends FormBasicComponent<any[]> implements ControlValueAccessor, OnDestroy {
	rowClass = FormListModelDataRow
	@Input() service: BaseService<IdunModel>
	@Input() idField = "id"
	@Input() dataField = "data"
	@Input() dataType: "number" | "text" = "text"
	@Input() dataLabel: () => string = () => "data"
	@Input() dataEndLabel?: () => string
	newValue

	subscriber = new Subscriber()

	constructor(public l: LanguageService) {
		super()
	}

	serviceHasComponent() {
		return this.service instanceof ExtendedService
	}

	getKeys() {
		if (this.value)
			return Object.keys(this.value)
		else return []
	}

	ngOnDestroy(): void {
		this.subscriber.unsubscribe()
	}

	getTitleFromID(id: number) {
		const model = this.service.getModel(id)
		if (model)
			return model.getTitle()
		return ""
	}

	add(id: number): void {
		if (!id)
			return
		const newEntry: any = {}
		newEntry[this.idField] = id
		newEntry[this.dataField] = null
		this.value.push(newEntry)
		this.newValue = ""
		this.onChangeCallback(this.value)
	}

	remove(index: string): void {
		this.value.splice(+index, 1)
		this.onChangeCallback(this.value)
	}
}

export class FormListModelDataRow extends FormRow {
	constructor(
		field: FieldInput,
		options: Options
	) {
		super(field, options)
	}
}

interface Options extends FormRowOptions {
	idField: string
	dataField: string
	dataType: "number" | "text"
	dataLabel: () => string
	dataEndLabel?: () => string
	service: BaseService<any>
}
