﻿import { Component, OnInit, OnDestroy } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { InvoiceService } from "idun-main/model-services/invoice.service"
import { Subscription } from "rxjs"

@Component({
	selector: "idun-economic-overview",
	templateUrl: "economic-overview.component.html"
})
export class EconomicOverviewComponent implements OnInit, OnDestroy {
	invoiceCount = 0
	subscription: Subscription

	constructor(
		private invoiceService: InvoiceService,
		public l: LanguageService
	) { }

	ngOnInit(): void {
		this.subscription = this.invoiceService.count$().subscribe(count => {
			this.invoiceCount = count
		})
	}

	ngOnDestroy() {
		this.subscription.unsubscribe()
	}
}
