﻿import { Component, forwardRef, Input, ViewChild } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { FormRow, FormRowOptions } from "idun-layout/form/form-row"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { FormTextareaRow } from "idun-layout/form/form-textarea.component"
import { FormTextHistoryRow } from "idun-layout/form/form-text-history.component"
import { FormNumberRow } from "idun-layout/form/form-number.component"
import { FormDateRow } from "idun-layout/form/form-date.component"
import { FormPasswordRow } from "idun-layout/form/form-password.component"
import { FormSelectRow } from "idun-layout/form/form-select.component"
import { FormSelectModelRow } from "./form-select-model.component"
import { FormListSelectModelRow } from "./form-list-select-model.component"
import { FormListBoxModelRow } from "./form-list-box-model.component"
import { FormListStringRow } from "idun-layout/form/form-list-string.component"
import { FormListModelDataRow } from "./form-list-model-data.component"
import { FormIdunidRow } from "./form-idunid.component"
import { FormEndDateRow } from "idun-layout/form/form-end-date.component"
import { FormTemplateChooserRow } from "./form-template-chooser.component"
import { FormFigurePlacementRow } from "./form-figure-placement.component"
import { FormDateIntervalRow } from "idun-layout/form/form-date-interval.component"
import { FormTelephoneRow } from "./form-telephone.component"
import { FormModelSetsRow } from "./form-model-sets.component"
import { FormProcedureVariationsRow } from "./form-procedure-variations.component"
import { FormProcedureChangesRow } from "./form-procedure-changes.component"
import { FormHueRow } from "idun-layout/form/form-hue.component"
import { FormListFileRow } from "./form-list-file.component"
import { FormDateTimeRow } from "idun-layout/form/form-date-time.component"
import { FormCompetencesRow } from "./form-competences.component"
import { FormExtraRow } from "./form-extra-row.component"
import { FormListSelectRow } from "idun-layout/form/form-list-select.component"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormDynamicComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormDynamicComponent),
	multi: true
}

@Component({
	selector: "idun-form-dynamic",
	templateUrl: "form-dynamic.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormDynamicComponent extends FormBasicComponent<any> {
	@ViewChild("test") test

	onValueChange() {
		if (this.test)
			this.test.value = this.value
	}

	validate() {
		if (this.test && this.test.validate)
			return this.test.validate()
		return null
	}

	setValue = (value) => {
		this.row.setValue(this.model, value)
		this.onChangeCallback(value)
	}

	@Input() autoform
	@Input() allowEdit
	@Input() model
	@Input() row: any

	classes = {
		FormTextRow: FormTextRow,
		FormTextareaRow: FormTextareaRow,
		FormTextHistoryRow: FormTextHistoryRow,
		FormHueRow: FormHueRow,
		FormNumberRow: FormNumberRow,
		FormDateRow: FormDateRow,
		FormDateTimeRow: FormDateTimeRow,
		FormPasswordRow: FormPasswordRow,
		FormSelectRow: FormSelectRow,
		FormSelectModelRow: FormSelectModelRow,
		FormListSelectRow: FormListSelectRow,
		FormListSelectModelRow: FormListSelectModelRow,
		FormListBoxModelRow: FormListBoxModelRow,
		FormListStringRow: FormListStringRow,
		FormListFileRow: FormListFileRow,
		FormListModelDataRow: FormListModelDataRow,
		FormProcedureVariationsRow: FormProcedureVariationsRow,
		FormProcedureChangesRow: FormProcedureChangesRow,
		FormModelSetsRow: FormModelSetsRow,
		FormIdunidRow: FormIdunidRow,
		FormEndDateRow: FormEndDateRow,
		FormTemplateChooserRow: FormTemplateChooserRow,
		FormFigurePlacementRow: FormFigurePlacementRow,
		FormExtraRow: FormExtraRow,
		FormDateIntervalRow: FormDateIntervalRow,
		FormTelephoneRow: FormTelephoneRow,
		FormCompetencesRow: FormCompetencesRow
	}

	rowIs(row, rowClass) {
		return row instanceof rowClass
	}
}
