﻿import { Component, ElementRef, forwardRef, Input, OnInit, OnDestroy, ViewChild, EventEmitter, Output } from "@angular/core"
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { BaseService } from "idun-common/base-service"
import { IdunModel } from "idun-common/models/idun-model"
import { Subscriber } from "utility/subscriber"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => SmartSelectComponent),
	multi: true
}

@Component({
	selector: "idun-smart-select",
	templateUrl: "smart-select.component.html",
	styleUrls: ["smart-select.component.scss"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class SmartSelectComponent extends FormBasicComponent<string> implements ControlValueAccessor, OnInit, OnDestroy {
	@Input() disabled = false
	@Input() allowEmpty = true
	@Input() placeholder = ""
	@Input() emptyText = ""
	@Input() service: BaseService<IdunModel>
	@ViewChild("dropdown") dropdown
	@ViewChild("textElement") textElement
	@Output() selectValue = new EventEmitter()

	subscriber = new Subscriber()
	lists: any[][] = []
	list: any[] = []
	currentpage = 0
	showEntries = false

	private _shownValue: any

	get shownValue(): string {
		return this._shownValue || this.emptyText
	}

	set shownValue(val: string) {
		this._shownValue = val
		this.innerValue = ""
		this.currentpage = 0
		const page = this.currentpage
		this.subscriber.subscribe(this.service.loadPage(this.currentpage, 20, this._shownValue),
			(list: any) => { this.updateLists(list, page) }, "list" + page)
	}

	onValueChange() {
		if (+this.value) {
			const model = this.service.getModel(+this.value)
			if (model)
				this._shownValue = model.getTitle()
		}
		else {
			this._shownValue = ""
		}
	}

	updateLists = (list, page) => {
		this.lists[page] = list
		this.list = [].concat(...(this.lists))
		const lowerCase = this.shownValue ? this.shownValue.toLowerCase() : ""
		let model = <any>this.list.find(e => e.getTitle().toLowerCase() === lowerCase)
		if (model) {
			this.value = model.DBID
			model = this.service.getModel(+this.value)
			if (model)
				this._shownValue = model.getTitle()
		}
	}

	constructor(
		public elementRef: ElementRef
	) {
		super()
	}

	handleFocus = (toggle) => {
		this.showEntries = !this.showEntries
		if (!toggle)
			this.showEntries = true
		if (this.showEntries)
			this.textElement.nativeElement.focus()
	}

	handleBlur = () => {
		setTimeout(() => {
			if (this.hasFocus())
				return
			this.showEntries = false
			if (!this.value && !this.allowEmpty)
				this.value = this.list[0].DBID
		}, 100)
	}

	hasFocus() {
		let element = document.activeElement
		while (element) {
			if (element === this.elementRef.nativeElement)
				return true
			element = element.parentElement
		}
		return false
	}

	ngOnInit(): void {
		const page = this.currentpage
		this.subscriber.subscribe(this.service.loadPage(this.currentpage, 20, this._shownValue),
			(list: any) => {
				this.updateLists(list, page)
				if (!this.hasFocus() && !this.value && !this.allowEmpty)
					this.value = list[0].DBID
			}, "list" + page)
		this.elementRef.nativeElement.addEventListener("blur", this.handleBlur, { passive: true })
		this.dropdown.nativeElement.addEventListener("blur", this.handleBlur, { passive: true })
		this.textElement.nativeElement.addEventListener("blur", this.handleBlur, { passive: true })
	}

	ngOnDestroy(): void {
		this.subscriber.unsubscribe()
	}

	loadMore() {
		this.currentpage++
		const page = this.currentpage
		this.subscriber.subscribe(this.service.loadPage(this.currentpage, 20, this._shownValue),
			(list: any) => { this.updateLists(list, page) }, "list" + page)
	}

	clickElement(element) {
		this.showEntries = false
		if (!element) {
			this.value = undefined
			this._shownValue = ""
			return
		}
		this.value = element.DBID
		if (this.value)
			this.selectValue.emit(this.value)
	}

	atEndOfList() {
		if (!this.lists.length)
			return true
		return this.lists[this.lists.length - 1].length < 20 // TODO: this fails if the number of elements is dividable by 20
	}
}
