export class Wordifier {
	onsetChars: string
	nucleusChars: string
	codaChars: string
	base: number

	constructor(
		onsetChars = "-pbtdkgmnwvfszjrlhqxc",
		nucleusChars = "aeuioy",
		codaChars = "|pbtdkgmnwvfszjrlhqxc"
	) {
		this.onsetChars = onsetChars
		this.nucleusChars = nucleusChars
		this.codaChars = codaChars
		this.base = onsetChars.length * nucleusChars.length * codaChars.length
	}

	integerDivide(number, divisor) {
		if (number.divideBy)
			return number.divideBy(divisor)
		return { division: Math.floor(number / divisor), remainder: number % divisor }
	}

	isTenishBase(someBase) {
		return /^10+$/.test(someBase.toString())
	}

	isGreaterThanBase(number) {
		if (number.parts)
			return number.parts.length > 1 || number.parts[0] >= this.base
		return number >= this.base
	}

	toBaseX(targetBase, number) {
		number = new Tal(number)
		const subNumbers = []
		while (this.isGreaterThanBase(number)) {
			const dividedNumber = this.integerDivide(number, targetBase)
			subNumbers.push(dividedNumber.remainder)
			number = dividedNumber.division
		}
		subNumbers.push(+number.toString())
		return subNumbers.reverse()
	}

	toBaseTenish(targetBase, number) {
		let stringNumber = number.toString()
		const n = targetBase.toString().length - 1

		const subNumbers = []
		while (stringNumber.length > n) {
			subNumbers.push(+stringNumber.slice(stringNumber.length - n))
			stringNumber = stringNumber.slice(0, stringNumber.length - n)
		}
		subNumbers.push(stringNumber)
		return subNumbers.reverse()
	}

	syllableFrom(number) {
		let divider = this.codaChars.length * this.nucleusChars.length
		let dividedNumber = this.integerDivide(number, divider)
		const onset = this.onsetChars[dividedNumber.division]
		number = dividedNumber.remainder
		divider = this.nucleusChars.length
		dividedNumber = this.integerDivide(number, divider)
		const coda = this.codaChars[dividedNumber.division]
		const nucleus = this.nucleusChars[dividedNumber.remainder]
		return onset + nucleus + coda
	}

	wordify(number) {
		const subNumbers = this.isTenishBase(this.base)
			? this.toBaseTenish(this.base, number)
			: this.toBaseX(this.base, number)
		const syllables = subNumbers.map(x => this.syllableFrom(x))
		let word = syllables.join("")
			.replace(/\|-/g, "")
			.replace(/\|/g, "")
		if (word[0] === "-")
			word = word.substr(1)

		return word
	}
}

class Tal {
	digitsPerPart = 6
	factorPerPart = 1000000
	parts = [0]

	constructor(source?: number | string) {
		if (!source)
			return
		this.parts = []
		let sourceString = source.toString()
		for (
			let part = sourceString.substr(-this.digitsPerPart);
			part.length > 0;
			part = sourceString.substr(-this.digitsPerPart)
		) {
			this.parts.unshift(+part)
			sourceString = sourceString.substr(0, sourceString.length - this.digitsPerPart)
		}
		this.removeLeadingZeros()
	}

	divideBy(number) {
		const result = new Tal()
		let remainder = 0
		for (const i in this.parts) {
			const currentPart = remainder * this.factorPerPart + this.parts[i]
			if (number > currentPart) {
				remainder = currentPart
				if (result.parts.length !== 0)
					result.parts.push(0)
				continue
			}
			remainder = currentPart % number
			result.parts.push(Math.floor(currentPart / number))
		}
		result.removeLeadingZeros()
		return { division: result, remainder: remainder }
	}

	removeLeadingZeros() {
		while (this.parts[0] === 0) {
			this.parts.shift()
		}
		if (this.parts.length === 0)
			this.parts.push(0)
	}

	isZero() {
		return this.parts.length === 1 && this.parts[0] === 0
	}

	toString() {
		let text = ""
		for (const i in this.parts)
			text += this.parts[i]
		return text
	}
}

