﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { LanguageService } from "idun-layout/language.service"

import { JournalTemplate } from "idun-common/models/journal-template"
import { FormTextareaRow } from "idun-layout/form/form-textarea.component"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { FormFigurePlacementRow } from "idun-common/form/form-figure-placement.component"
import { ButtonThing } from "idun-common/form/form-dynamic-extra.component"
import { Permission } from "idun-common/models/staff-group"
import { UserService } from "idun-main/user.service"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"
import { FormListStringRow } from "idun-layout/form/form-list-string.component"

@Injectable()
export class JournalTemplateService extends ExtendedService<JournalTemplate> {
	constructor(
		http: Http,
		l: LanguageService,
		userService: UserService
	) {
		super(http, l,
			JournalTemplate,
			"api/JournalTemplate",
			"journal-template",
			() => l.journalTemplate
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.EditJournalTemplates)
		this.icon = "Fundamental"
		this.color = "var(--color-fundamental)"

		this.setupForm({
			getHeader: (model: JournalTemplate) => { return l.journalTemplate.nameSingular + (model.Title ? ": " + model.Title : "") },
			rows: [
				new FormTextRow("Title", {
					label: () => l.common.title,
					hint: () => l.journalTemplate.titleHint,
					requirements: [ValidationRequirement.ValueRequired(l)],
				}),
				new FormTextareaRow("Content", {
					label: () => l.common.content,
					hint: () => l.journalTemplate.contentHint,
					requirements: [ValidationRequirement.ValueRequired(l)],
				}),
				new FormListStringRow("Placements", {
					label: () => l.figure.placement,
					hint: () => l.journalTemplate.placementHint,
					requirements: [ValidationRequirement.ValueRequired(l)],
					display: () => true,
					things: [new ButtonThing<JournalTemplate>(
						(model) => l.figure.choosePlacement,
						(model, component) => component.showPopup = true,
						(model, display) => !display
					)]
				})
			],
			options: {
				popup: {
					getHeader: () => l.figure.choosePlacement,
					onClose: (component) => { component.showPopup = false },
					rows: [
						new FormFigurePlacementRow("", {
							selectPlacement: (model, value, component) => {
								model.Placements = value
								component.showPopup = false
							}
						})
					]
				},
				helpText: () => this.translations().formHelpText
			}
		})
		this.setupList({
			entryHeader: (model: JournalTemplate) => model.Title,
			contentList: [(model: JournalTemplate) => model.Content]
		})
		this.listInfo.searchTypes.push("Title")
		this.listInfo.searchTypes.push("Content")
	}
}
