﻿import { Headers, Http } from "@angular/http"
import { environment } from "../environments/environment"
import { TestHelper } from "utility/test-helper"

export class RequestHelper {
	public static getRequest(http: Http, path: string, onResponse?: any): Promise<any> {
		if (!environment.production)
			return TestHelper.request(http, path.toLowerCase(), onResponse)
		const headers: Headers = new Headers({
			"Content-Type": "application/json"
		})
		return http.get(path, { headers: headers })
			.toPromise()
			.then(onResponse ? onResponse : function (response: any): any {
				return response.json()
			})
			.catch(this.handleError)
	}

	public static postRequest(http: Http, path: string, parameters: any, onResponse?: any): Promise<any> {
		if (!environment.production)
			return TestHelper.request(http, path.toLowerCase(), parameters, onResponse)
		const headers: Headers = new Headers({
			"Content-Type": "application/json"
		})
		return http.post(path, JSON.stringify(parameters), { headers: headers })
			.toPromise()
			.then(onResponse ? onResponse : function (response: any): any {
				return response.json()
			})
			.catch(this.handleError)
	}

	protected static handleError(error: any): Promise<void> {
		console.error("An error occurred", error)
		return Promise.reject(error.message || error)
	}
}
