import { Injectable } from "@angular/core"
import { JournalEntry, TextEntry } from "idun-common/models/journal-entry"
import { Patient } from "idun-common/models/patient"
import { LanguageService } from "idun-layout/language.service"
import * as jsPDF from "jspdf"

@Injectable()
export class PdfCreatorService {
	doc: jsPDF
	writer: PdfCreationValues

	constructor(
		public l: LanguageService
	) { }

	createPdfFrom(patient: Patient, journalEntries: JournalEntry[]) {
		this.initialisePdf()
		var journalEntryCounter = 0

		// Very first journal needs to include cave if present
		if (patient.Cave) {
			const cave = new JournalEntry
			cave.Description[0] = new TextEntry
			cave.Description[0].Content = patient.Cave[patient.Cave.length - 1].Content
			cave.Description[0].Title = "Cave"
			journalEntries.unshift(cave)
		}


		for (const journalEntry of journalEntries) {
			if (journalEntryCounter !== 0)
				this.doc.addPage()
			const journalContent = journalEntry.Description[journalEntry.Description.length - 1]
			this.addJournalEntryToPDF(patient, journalContent, this.writer.nextJournalPage)
			journalEntryCounter += 1
		}
		this.doc.save(`${patient.Name + " " + patient.getCprNumber()}.pdf`)
	}

	initialisePdf() {
		this.doc = new jsPDF({ orientation: "p", lineHeight: 0.6, unit: "mm", size: "a4" })
		this.writer = new PdfCreationValues()
	}

	lineheight = 0
	addJournalEntryToPDF(patient: Patient, journal: TextEntry, journalPage: number) {
		const lines = this.doc.splitTextToSize(journal.Content,
			(this.doc.internal.pageSize.width - this.writer.margins.left - this.writer.margins.right))
		const pages = Math.ceil(lines.length / this.writer.linesPerPage)
		this.writer.nextJournalPage = 0
		this.writer.linePointer = 0
		this.writer.linesLeft = lines.length
		this.writer.startLine = 5
		// For some reason lineHeight is not a static variable.
		if (journalPage === 0)
			this.lineheight = this.doc.internal.getLineHeight()
		this.writer.offset = this.lineheight * 2

		for (let i = 0; i < pages; i++) {
			if (i > 0)
				this.doc.addPage()
			this.setPatientForPdfPage(patient)
			this.setHeaderForPdfPage(i, journal)
			this.writeJournalToPdfPage(lines, i, journalPage)
		}
		return this.writer.nextJournalPage
	}

	writeJournalToPdfPage(lines, i: number, journalPage) {
		for (let j = 0; j < this.writer.linesPerPage; j++) {
			if (j < this.writer.linesPerPage && j <= this.writer.linesLeft - 1) {
				this.doc.text(lines[this.writer.linePointer], this.writer.margins.left,
					this.writer.offset + this.lineheight * j + this.lineheight * this.writer.startLine)
			}
			else {
				this.writer.linesLeft = this.writer.linesLeft - this.writer.linePointer
				this.writer.linePointer = 0
			}

			this.writer.linePointer += 1
		}
		this.doc.text(`${journalPage + i + 1}`,
			this.doc.internal.pageSize.getWidth() - (this.writer.margins.left / 2),
			this.writer.offset + this.lineheight * 28) // pagenumber
		this.writer.nextJournalPage = journalPage + i + 1
		this.writer.linesLeft = this.writer.linesLeft - this.writer.linesPerPage
	}

	setPatientForPdfPage(patient: Patient) {
		this.doc.setFontSize(16)
		this.doc.text(`${patient.Name + " " + patient.getCprNumber()} `, this.writer.margins.left, this.writer.offset + this.lineheight * 1)
		this.doc.text(`${patient.Address} `, this.writer.margins.left, this.writer.offset + this.lineheight * 2)
		this.doc.text(`${this.getPatientTelephoneNumbers(patient)} `, this.writer.margins.left, this.writer.offset + this.lineheight * 3)
	}

	setHeaderForPdfPage(i: number, journal: TextEntry) {
		if (i === 0 && journal.Title !== "Cave") { // New journal
			this.doc.text(`${this.l.patient.signatureText + ": "} `, this.writer.margins.left, this.writer.offset + this.lineheight * 5)
			this.doc.text(`${journal.Title}`, this.writer.margins.left, this.writer.offset + this.lineheight * 6)
			this.writer.startLine = 8
			this.writer.linesPerPage = 19
			this.doc.setFontSize(14)
		}
		else if (i === 0 && journal.Title === "Cave") {
			this.doc.text(`${this.l.patient.caveText + ": "} `, this.writer.margins.left, this.writer.offset + this.lineheight * 5)
			this.writer.startLine = 6
			this.writer.linesPerPage = 21
			this.doc.setFontSize(14)
		}
		else {
			this.writer.startLine = 5
			this.writer.linesPerPage = 23
			this.doc.setFontSize(14)
		}
	}

	getPatientTelephoneNumbers(patient: Patient) {
		var telephoneNumbers = ""
		for (const number of patient.TelephoneNumbers) {
			if (number.Primary)
				telephoneNumbers += "(Primær)" + number.Number + " "
			else
				telephoneNumbers += number.Number + " "
		}
		return telephoneNumbers
	}
}

class PdfCreationValues {
	margins = {
		top: 20,
		bottom: 40,
		left: 20,
		right: 20,
		width: 160
	}
	startLine = 0
	linesPerPage = 21 // (+4 incl patient)
	offset = 0
	linePointer = 0
	linesLeft = 0
	nextJournalPage = 0
}
