import { Injectable } from "@angular/core"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { PatientEvent } from "idun-common/models/patient-event"
import { PatientEventService } from "idun-main/model-services/patient-event.service"
import { TimeInterval } from "idun-common/models/time-interval"
import { newDateFrom } from "utility/date-helper"
import { PartnerService } from "idun-main/model-services/partner.service"
import { LanguageService } from "idun-layout/language.service"
import { Warning } from "idun-layout/components/warning-box.component"

@Injectable()
export class PatientEventCreationService {
	patientEventCreation: {
		patientEvent: PatientEvent
		warnings: {
			noAvailableStaff: Warning
			noAvailableResources: Warning
		}
		onStaffComplete: () => void
		onResourcesComplete: () => void
	}

	constructor(
		private patientEventService: PatientEventService,
		private partnerService: PartnerService,
		private procedureService: ProcedureService,
		private l: LanguageService,
	) { }

	createAt(time: Date) {
		this.setupNew()
		for (const procedureEvent of this.patientEventCreation.patientEvent.ProcedureEvents)
			time = this.setupProcedureEvent(procedureEvent, time)
		return this.patientEventCreation
	}

	private setupNew() {
		this.patientEventCreation = {
			patientEvent: new PatientEvent(this.patientEventService.patientEvent$.value),
			warnings: {
				noAvailableStaff: new Warning(() => this.l.booking.noAvailableStaffWarning),
				noAvailableResources: new Warning(() => this.l.booking.noAvailableResourcesWarning),
			},
			onStaffComplete: () => { },
			onResourcesComplete: () => { },
		}
	}

	private setupProcedureEvent(procedureEvent: ProcedureEvent, startTime: Date) {
		const nextTime = this.updateTime(procedureEvent, startTime)
		this.updateStaff(procedureEvent, this.patientEventCreation)
		this.updateResources(procedureEvent, this.patientEventCreation)
		return nextTime
	}

	private updateTime(procedureEvent: ProcedureEvent, startTime: Date) {
		const procedure = this.procedureService.getModel(procedureEvent.ProcedureId)
		const endTime = newDateFrom({ minutes: procedure.Duration }, startTime)
		procedureEvent.Times = TimeInterval.from(startTime, endTime)
		return endTime
	}

	private async updateStaff(procedureEvent: ProcedureEvent, creation) {
		const availableStaff = await this.procedureService.availableStaffFor(procedureEvent)
		if (availableStaff[0])
			procedureEvent.OccupiedStaff = [availableStaff[0]]
		else
			this.patientEventCreation.warnings.noAvailableStaff.show = true
		creation.onStaffComplete()
	}

	private async updateResources(procedureEvent: ProcedureEvent, creation) {
		const availableResources = await this.procedureService.availableResourcesFor(procedureEvent)
		if (availableResources[0])
			procedureEvent.OccupiedResources = [availableResources[0]]
		else if (this.procedureService.getModel(procedureEvent.ProcedureId).ResourceRequirements.length)
			this.patientEventCreation.warnings.noAvailableResources.show = true
		creation.onResourcesComplete()
	}

	updatePriceAndCodesOf(procedureEvent: ProcedureEvent) {
		if (!procedureEvent.PartnerId)
			return this.resetPriceAndCodes(procedureEvent)
		const partner = this.partnerService.getModel(procedureEvent.PartnerId)
		const procedure = this.procedureService.getModel(procedureEvent.ProcedureId)
		const changes = partner.ProcedureChanges.find(e => e.ProcedureID === procedure.DBID)
		if (!changes)
			return this.resetPriceAndCodes(procedureEvent)
		procedureEvent.Price = changes.priceFor(procedure, procedureEvent.Variation)
		const variation = changes.Variations.find(e => e.Title === procedureEvent.Variation)
		procedureEvent.PaymentCodeIds = variation.TreatmentCodeIDs.map(e => e)
	}

	private resetPriceAndCodes(procedureEvent: ProcedureEvent) {
		const procedure = this.procedureService.getModel(procedureEvent.ProcedureId)
		const variation = procedure.Variations.find(e => e.Title === procedureEvent.Variation)
		procedureEvent.Price = variation.Price
		procedureEvent.PaymentCodeIds = []
	}
}
