﻿const baseUrl = "api/clinic/"

const otherResponses = {}
let settings = {
	ClinicName: "Dev Clinic",
	Location: "Brødeløkken 28, 5540 Ullerslev",
	Website: "idunhealth.com/Clinic/DevClinic",
	Timezone: "Europe/Copenhagen",
	Currency: "DKK"
}

otherResponses[baseUrl + "getcurrentclinic"] = (parameters) => {
	return JSON.stringify("TestClinic")
}

otherResponses[baseUrl + "getsettings"] = (parameters) => {
	return JSON.stringify(settings)
}

otherResponses[baseUrl + "updatesettings"] = (parameters) => {
	return JSON.stringify(settings = parameters)
}

export function ClinicResponses() {
	return {
		...otherResponses
	}
}
