const otherResponses = {}

otherResponses[baseUrl + "getcompetences"] = (parameters: any) => {
	return JSON.stringify(["aba", "todo"])
}

otherResponses[baseUrl + "isstaffavailable"] = (parameters: { id: number, start: Date, end: Date }) => {
	const interval = TimeInterval.fromInterval(parameters)
	const isAvailable = isStaffAtWork(parameters.id, interval)
		&& !hasEventsDuring(interval, parameters.id)
	return JSON.stringify(isAvailable)
}

function isStaffAtWork(staffId: number, interval: TimeInterval) {
	return workIntervalTestData
		.some(x => x.Times.contains(interval) && x.StaffID === staffId)
}

function hasEventsDuring(interval: TimeInterval, staffId: number) {
	return getCalendarEventTestDataForStaff(staffId, interval).length > 0
		|| getProcedureEventTestDataForStaff(staffId, interval).length > 0
}

export function StaffResponses() {
	return {
		...ModelTestData.commonLocalStorageResponses(Staff, baseUrl, staffTestData),
		...otherResponses
	}
}
