﻿import { ExtendedService } from "idun-common/extended-service"
import { IdunModel } from "idun-common/models/idun-model"

export class AutoListInfo<Model extends IdunModel> {
	public destination: (model: Model) => string
	public listHeader: string | (() => string) = "List"
	public entryHeader: ((model: Model) => string) = () => "Header"
	public contentList: ((model: Model) => string)[] = []
	public linkList: ((model: Model) => { destination: string, getText: () => string })[] = []
	public buttonList: ((model: Model) => { callback: () => void, getText: () => string })[] = []
	public colors?: (model: Model) => string
	public groups?: { getHeader: () => string, filter: (list: Model[]) => Model[] }[]
	public subscriberFunction?: (component: any, callback: (list: Model[]) => void, param: any) => void
	public pageSize = 20
	public searchTypes: string[] = []
	public service: ExtendedService<Model>
	public helpText: () => string

	constructor(
		settings: {
			destination?: (model: Model) => string,
			listHeader?: string | (() => string),
			entryHeader?: ((model: Model) => string),
			contentList?: ((model: Model) => string)[],
			linkList?: ((model: Model) => { destination: string, getText: () => string })[],
			buttonList?: ((model: Model) => { callback: (event) => void, getText: () => string })[],
			colors?: (model: Model) => string,
			groups?: { getHeader: () => string, filter: (list: Model[]) => Model[] }[],
			subscriberFunction?: (component: any, callback: (list: Model[]) => void, param: any) => void,
			pageSize?: number,
			service: ExtendedService<Model>,
			helpText?: () => string
		}
	) {
		for (const key in settings) this[key] = settings[key]
	}

	get mainheader(): string {
		if (typeof this.listHeader === "string")
			return this.listHeader
		else
			return this.listHeader()
	}
}
