﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { StatusType } from "idun-common/models/status-type"
import { LanguageService } from "idun-layout/language.service"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { FormSelectRow } from "idun-layout/form/form-select.component"
import { OptionInfo } from "idun-layout/form/form-row"
import { FormHueRow } from "idun-layout/form/form-hue.component"
import { Colour } from "utility/colour"
import { Permission } from "idun-common/models/staff-group"
import { UserService } from "idun-main/user.service"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"

@Injectable()
export class StatusTypeService extends ExtendedService<StatusType> {
	constructor(
		http: Http,
		l: LanguageService,
		userService: UserService
	) {
		super(http, l,
			StatusType,
			"api/StatusType",
			"status-type",
			() => l.statusType
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.EditStatusType)
		this.icon = "Doctor"
		this.color = "var(--color-calendar)"

		this.setupForm({
			rows: [
				new FormTextRow("Title", {
					label: () => l.common.title,
					requirements: [ValidationRequirement.ValueRequired(l)],
				}),
				new FormSelectRow("Working", {
					label: () => l.statusType.availableForBooking,
					hint: () => l.statusType.availableForBookingHint,
					requirements: [ValidationRequirement.ValueRequired(l)],
					options: [
						new OptionInfo("true", () => l.statusType.available),
						new OptionInfo("false", () => l.common.notAvailable)]
				}),
				new FormHueRow("Hue", {
					label: () => l.common.color,
					requirements: [ValidationRequirement.ValueRequired(l)],
				})
			]
		})
		this.setupList({
			entryHeader: (model) => model.Title,
			contentList: [(model) => {
				return model.Working ? l.statusType.available : l.statusType.notAvailable
			}],
			colors: (model) => Colour.ToRgbString(Colour.HSVtoRGB(+model.Hue, 1, 1))
		})
	}
}
