﻿import { Component, forwardRef, Input } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { FormRow, OptionInfo, FieldInput, FormRowOptions } from "./form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormListStringComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormListStringComponent),
	multi: true
}

@Component({
	selector: "idun-form-list-string",
	templateUrl: "form-list-string.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormListStringComponent extends FormBasicComponent<string[]> {
	@Input() checkboxMode?: boolean
	@Input() options?: OptionInfo[]
	rowClass = FormListStringRow
	newValue = ""

	add(entry: string): void {
		this.value.push(entry)
		this.newValue = ""
		this.onChangeCallback(this.value)
	}

	remove(index: string): void {
		this.value.splice(+index, 1)
		this.onChangeCallback(this.value)
	}

	hasOption(option) {
		if (!this.value)
			return false
		return this.value.includes(option)
	}

	setOption(option, value) {
		if (value)
			this.value.push(option)
		else
			this.value.splice(this.value.indexOf(option), 1)
		this.onChangeCallback(this.value)
	}
}

export class FormListStringRow extends FormRow {
	constructor(
		field: FieldInput,
		options: Options = {}
	) {
		super(field, options)
	}
}

interface Options extends FormRowOptions {
	checkboxMode?: boolean,
	options?: OptionInfo[]
}
