import { Component, ViewChild, ViewContainerRef, ComponentFactoryResolver, OnInit } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { PatientBarComponent } from "./patient-bar.component"
import { HelpBarComponent } from "./help-bar.component"
import { EventListBarComponent } from "./event-list-bar.component"
import { StaffNotificationBarComponent } from "./staff-notification-bar.component"
import { StaffNotificationService } from "idun-main/model-services/staff-notification.service"

@Component({
	selector: "idun-info-bar",
	templateUrl: "info-bar.component.html",
	styleUrls: ["info-bar.component.scss"]
})
export class InfoBarComponent implements OnInit {
	@ViewChild("container", { read: ViewContainerRef }) container: ViewContainerRef

	tabs: Tab[] = [
		{ title: () => this.l.sidebar.currentPatient, icon: "Patient", component: PatientBarComponent },
		{ title: () => this.l.sidebar.notifications, icon: "Bell", component: StaffNotificationBarComponent },
		{ title: () => this.l.sidebar.myEvents, icon: "Calendar", component: EventListBarComponent },
		{ title: () => this.l.sidebar.help, icon: "Help", component: HelpBarComponent },
	]
	currentIndex = 0
	get currentTab() {
		return this.tabs[this.currentIndex]
	}
	unreadNotifications = 0

	constructor(
		private resolver: ComponentFactoryResolver,
		staffNotificationService: StaffNotificationService,
		public l: LanguageService
	) {
		staffNotificationService.unreadNotificationCount$.subscribe(count => this.unreadNotifications = count)
		staffNotificationService.loadModels()
	}

	ngOnInit() {
		this.selectTab(0)
	}

	selectTab(index) {
		this.currentIndex = index
		this.container.clear()
		const factory = this.resolver.resolveComponentFactory(this.currentTab.component)
		this.container.createComponent(factory)
	}
}

interface Tab {
	title: () => string
	icon: string
	component: any
}
