﻿import { ActivatedRoute } from "@angular/router"
import { BaseModelSubscriber } from "./base-model-subscriber"

export abstract class RouteParameterComponent extends BaseModelSubscriber {
	parameterHandlers: any = {}
	finalHandler: (params: any, component: any) => void

	protected handleRouteParameters(route: ActivatedRoute, finalHandler?: any) {
		this.subscribe(route.params, params => {
			const parameters = {}
			for (const param in this.parameterHandlers) {
				this.parameterHandlers[param](params[param], parameters)
			}
			finalHandler = finalHandler || this.finalHandler
			if (finalHandler)
				finalHandler(parameters, this)
		})
	}
}
