﻿import { Bookmark } from "idun-common/models/bookmark"
import { ModelTestData } from "./model-test-data"

const baseUrl = "api/bookmark/"

const list = [
	new Bookmark({
		DBID: 1,
		IconPath: "Settings",
		IdunRoutePath: "/patient-overview",
		MouseoverText: "Testbogmærke"
	})
]

ModelTestData.loadFromLocalStorage(baseUrl, list, Bookmark)

export function BookmarkResponses() {
	return {...ModelTestData.commonLocalStorageResponses(Bookmark, baseUrl, list)}
}
