import { Component, OnInit } from "@angular/core"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { DateHelper } from "utility/date-helper"
import { LanguageService } from "idun-layout/language.service"

import { BookingRequestService } from "idun-main/model-services/booking-request.service"
import { PatientEventService } from "idun-main/model-services/patient-event.service"
import { PatientEvent } from "idun-common/models/patient-event"

@Component({
	selector: "idun-patient-overview",
	templateUrl: "patient-overview.component.html"
})
export class PatientOverviewComponent extends BaseSubscriber implements OnInit {
	events: PatientEvent[]
	requests: any[]

	constructor(
		private bookingRequestService: BookingRequestService,
		private patientEventService: PatientEventService,
		public l: LanguageService
	) {
		super()
	}

	ngOnInit(): void {
		const interval = DateHelper.threeDayInterval(new Date)
		this.subscribe(
			this.patientEventService.getEvents(interval.start, interval.end),
			(events: any[]) => { this.events = events })
		this.subscribe(
			this.bookingRequestService.getUnhandledRequests(),
			(requests: any[]) => { this.requests = requests })
	}

	patientOverviewHelpText() {
		return this.l.patient.listHelpText
	}

	bookingRequestHelpText() {
		return this.l.patient.bookingRequestHelpText
	}

	patientEventsHelpText() {
		return this.l.patient.patientEventsHelpText
	}
}
