﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { LanguageService } from "idun-layout/language.service"
import { Resource } from "idun-common/models/resource"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { FormTextareaRow } from "idun-layout/form/form-textarea.component"
import { abbreviationFor } from "utility/abbreviations"
import { UserService } from "idun-main/user.service"
import { Permission } from "idun-common/models/staff-group"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"

@Injectable()
export class ResourceService extends ExtendedService<Resource> {
	constructor(
		http: Http,
		l: LanguageService,
		userService: UserService
	) {
		super(http, l,
			Resource,
			"api/Resource",
			"resource",
			() => l.resource
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.EditResources)
		this.icon = "Fundamental"
		this.color = "var(--color-fundamental)"

		this.setupForm({
			getHeader: (model: Resource) => this.l.resource.nameSingular + (model.Title ? ": " + model.Title : ""),
			rows: [
				new FormTextRow("Title", {
					label: () => l.common.title,
					requirements: [ValidationRequirement.ValueRequired(l)],
					width: 60,
					cssClasses: "padding-right",
					change: (model, value) => {
						if (value)
							model.Abbreviation = abbreviationFor(value,
								this.models.filter(e => e.DBID !== model.DBID).map(e => e.Abbreviation))
						else if (!model.Title)
							model.Abbreviation = ""
					}
				}),
				new FormTextRow("Abbreviation", {
					label: () => l.common.abbreviation,
					hint: () => l.resource.abbreviationHint,
					requirements: [
						ValidationRequirement.ValueRequired(l),
						new ValidationRequirement((value, model) => {
							if (this.models.find(x => x.Abbreviation === value && x.DBID !== model.DBID))
								return () => l.commonForm.abbreviationMustBeUnique
						})
					],
					width: 40
				}),
				new FormTextareaRow("Description", {
					label: () => l.common.description
				})
			]
		})
		this.setupList({
			entryHeader: (model: Resource) => model.Abbreviation,
			contentList: [(model: Resource) => model.Title]
		})
		this.listInfo.searchTypes.push("Title")
		this.listInfo.searchTypes.push("Description")
	}

	isResourceAvailable(
		resourceId: number,
		starttime: Date,
		endtime: Date
	): Promise<boolean> {
		return this.baseRequest(
			"isResourceAvailable",
			{ start: starttime, end: endtime, id: resourceId })
	}
}
