﻿import { CalendarEvent } from "idun-common/models/calendar-event"
import { ModelTestData } from "./model-test-data"
import { newWord } from "../random"
import { DateHelper } from "utility/date-helper"
import { TimeInterval } from "idun-common/models/time-interval"

const baseUrl = "api/calendarevent/"

const yesterday = new Date()
yesterday.setDate(new Date().getDate() - 1)
const tomorrow = new Date()
tomorrow.setDate(new Date().getDate() + 1)

const yesterdayInterval = DateHelper.dayInterval(yesterday)
const todayInterval = DateHelper.dayInterval(new Date())
const tomorrowInterval = DateHelper.dayInterval(tomorrow)

export const calendarEventTestData = [
	new CalendarEvent({
		DBID: 1,
		Title: "Meeting",
		Times: { StartTime: new Date(2000, 2, 2, 12, 30), EndTime: new Date(2000, 2, 2, 12, 45) },
		Description: newWord()
	}),
	new CalendarEvent({
		DBID: 2,
		Title: "Water flowers",
		Times: yesterdayInterval,
		Description: "Yesterday's patient"
	}),
	new CalendarEvent({
		DBID: 3,
		Title: "Clean xray room",
		Times: todayInterval,
		Description: "Today's patient"
	}),
	new CalendarEvent({
		DBID: 4,
		Title: "Journal writing",
		Times: tomorrowInterval,
		Description: "Tomorrow's patient"
	})
]

function eventsInInterval(interval: TimeInterval) {
	return calendarEventTestData.filter(({Times}) => Times.intersects(interval))
}

const otherResponses = {}

otherResponses[baseUrl + "getevents"] = (parameters: { start: Date, end: Date }) => {
	return JSON.stringify(eventsInInterval(TimeInterval.fromInterval(parameters)))
}

otherResponses[baseUrl + "getpersonalevents"] = (parameters: { start: Date, end: Date }) => {
	const result = getCalendarEventTestDataForStaff(1, TimeInterval.fromInterval(parameters))
	return JSON.stringify(result)
}

otherResponses[baseUrl + "geteventsforstaff"] = (parameters: { start: Date, end: Date, thing: string, id: number }) => {
	const result = getCalendarEventTestDataForStaff(parameters.id, TimeInterval.fromInterval(parameters))
	return JSON.stringify(result)
}

otherResponses[baseUrl + "geteventsforresource"] = (parameters: { start: Date, end: Date, thing: string, id: number }) => {
	const result = eventsInInterval(TimeInterval.fromInterval(parameters)).filter(e => e.OccupiedResources.includes(parameters.id))
	return JSON.stringify(result)
}

export function CalendarEventResponses() {
	return {
		...ModelTestData.commonResponses(CalendarEvent, baseUrl, calendarEventTestData, { getpage: () => "", getlist: () => "" }),
		...otherResponses
	}
}

export function getCalendarEventTestDataForStaff(id: number, interval: TimeInterval) {
	return eventsInInterval(interval)
	.filter(e => e.OccupiedStaff.includes(id))
}

export function getCalendarEventTestDataForResource(id: number, interval: TimeInterval) {
	return eventsInInterval(interval)
	.filter(e => e.OccupiedResources.includes(id))
}
