import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { BaseService } from "idun-common/base-service"
import { Observable } from "rxjs"
import { WorkInterval } from "idun-common/models/work-interval"

@Injectable()
export class WorkIntervalService extends BaseService<WorkInterval> {
	constructor(http: Http) {
		super(http, WorkInterval, "api/WorkInterval")
		this.loadAll = false
	}

	GetWorkIntervals(staffID: number, start: Date, end: Date): Promise<Observable<WorkInterval[]>> {
		return this.baseGetSubList("GetWorkIntervals", { id: staffID, start: start, end: end })
	}

	getAllWorkIntervals(start: Date, end: Date): Promise<Observable<WorkInterval[]>> {
		return this.baseGetSubList("GetAllWorkIntervals", { start: start, end: end })
	}
}
