﻿import { ExtraThing } from "idun-common/form/form-dynamic-extra.component"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"
import { AutoFormComponent } from "idun-common/auto-form.component"

export type FieldInput = string | string[] | { get: (model) => any, set: (model, value) => void, name: string }

export interface FormRowOptions {
	label?: () => string
	hint?: () => string
	requirements?: ValidationRequirement<any, any>[]
	change?: (model: any, value: any, component: any) => void
	show?: (model, display) => boolean
	display?: (model) => boolean
	cssClasses?: string
	width?: number
	things?: ExtraThing<any>[]
}

export abstract class FormRow {
	[index: string]: any
	public fields: string[] | { get: (model) => any, set: (model, value) => void, name: string }
	public requirements: ValidationRequirement<any, any>[] = []
	error?: () => string
	_change?: (model: any, value: any, component: any) => void

	constructor(
		field: FieldInput,
		options: FormRowOptions = {}
	) {
		if (field === "")
			this.fields = undefined
		else if (typeof field === "string")
			this.fields = [field]
		else
			this.fields = field

		for (const key in options) {
			if (key === "change")
				this._change = options[key]
			else if (["required", "show", "display"].includes(key))
				this[key] = options[key]
			else if (["label", "hint", "placeholder"].includes(key))
				Object.defineProperty(this, key, { get: options[key] })
			else
				this[key] = options[key]
		}
	}

	getWidth() {
		if (this.width)
			return this.width + "%"
		return "100%"
	}

	_name = Math.random() * 100000 + ""
	get name() {
		if (!this.fields)
			return this._name
		if (this.fields instanceof Array)
			return this.fields.join("-")
		if (this.fields.name)
			return this.fields.name
		return this._name
	}

	_val = null
	getValue(model) {
		if (!this.fields)
			return this._val
		if (!(this.fields instanceof Array))
			return this.fields.get(model)
		let value = model[this.fields[0]]
		for (let i = 1; i < this.fields.length; i++)
			value = value[this.fields[i]]
		return value
	}

	setValue(model, val) {
		if (!(this.fields instanceof Array))
			return this.fields.set(model, val)
		for (let i = 0; i < this.fields.length - 1; i++)
			model = model[this.fields[i]]
		model[this.fields[this.fields.length - 1]] = val
		this.change(model, val, null)
	}

	change(model, value, component: AutoFormComponent<any>) {
		if (value instanceof Event)
			return // sent by angular from an input element for who knows what reason
		if (this._change)
			this._change(model, value, component)
		this.validate(model, value)
		if (component)
			component.updateValidity()
	}

	validate(model, value?: any) {
		if (value === undefined)
			value = this.getValue(model)
		this.error = this.basicValidityErrors(value)
			|| this.firstErrorInRequirements(model, value)
	}

	// This can be overridden by inheriting classes to add basic validity checks
	// For example a date interval could check that start is before end
	basicValidityErrors(value): () => string {
		return
	}

	private firstErrorInRequirements(model, value) {
		for (const requirement of this.requirements) {
			const errorMessage = requirement.findError(value, model)
			if (errorMessage)
				return errorMessage
		}
	}

	show() {
		return true
	}

	display() {
		return false
	}
}

export class OptionInfo {
	constructor(
		public optionValue: string | (() => string),
		public optionTitle: string | (() => string),
		public optionHint?: string | (() => string)
	) { }

	get value() {
		if (typeof this.optionValue === "function")
			return this.optionValue()
		else
			return this.optionValue
	}

	get title() {
		if (typeof this.optionTitle === "function")
			return this.optionTitle()
		else
			return this.optionTitle
	}

	get hint() {
		if (!this.optionHint)
			return ""
		if (typeof this.optionHint === "function")
			return this.optionHint()
		else
			return this.optionHint
	}
}
