﻿import { CalendarEntryProvider } from "idun-calendar/calendar-entry-provider"
import { PlanningRole } from "idun-common/models/period-plan"
import { BasicCalendarEntry } from "idun-calendar/basic-calendar-entry"
import { CalendarEntry } from "idun-calendar/calendar-entry"
import { TimeInterval } from "idun-common/models/time-interval"
import { DateHelper, changeDate } from "utility/date-helper"
import { CalendarEvent } from "idun-common/models/calendar-event"

export enum PeriodPlanEventsProviderTarget { Role, Resource }

export class PeriodPlanEventsProvider implements CalendarEntryProvider {
	entries: BasicCalendarEntry<{ source: CalendarEvent, Interval: TimeInterval }>[]
	hideInMain = true
	isSubProvider = true

	constructor(
		public target: PeriodPlanEventsProviderTarget,
		public id: number,
		private events: CalendarEvent[],
		private display: () => boolean,
		private updateProviders: () => void
	) {
		this.updateEntries()
	}

	static forRole(
		index: number,
		events: CalendarEvent[],
		display: () => boolean,
		updateProviders: () => void
	) {
		return new PeriodPlanEventsProvider(
			PeriodPlanEventsProviderTarget.Role,
			index,
			events,
			display,
			updateProviders
		)
	}

	static forResource(
		id: number,
		events: CalendarEvent[],
		display: () => boolean,
		updateProviders: () => void
	) {
		return new PeriodPlanEventsProvider(
			PeriodPlanEventsProviderTarget.Resource,
			id,
			events,
			display,
			updateProviders
		)
	}

	getEntries(date?: Date): CalendarEntry[] {
		const interval = TimeInterval.fromInterval(DateHelper.dayInterval(date))
		return this.entries.filter(e => e.interval.intersects(interval))
	}

	getTitle() {
		return "Begivenheder"
	}

	getSubtitle() {
		return ""
	}

	getHovertitle() {
		return ""
	}

	updateEntries() {
		this.entries = this.events
			.filter(this.fitsTarget)
			.map(e => this.calendarEntryFrom(e))
	}

	private fitsTarget = (event: CalendarEvent) => {
		return this.target === PeriodPlanEventsProviderTarget.Role
			? event.OccupiedStaff.includes(this.id)
			: event.OccupiedResources.includes(this.id)
	}

	private calendarEntryFrom(event: CalendarEvent) {
		const display = this.display
		return new BasicCalendarEntry(
			{ source: event, Interval: event.Times },
			{
				get hoverText() { return event.Title + " - " + event.Times.toShortString() },
				boxHue: 180,
				get allowResize() { return !display() },
				moveStartMinute: (minuteChange: number) =>
					this.moveStartMinute(event, minuteChange),
				moveEndMinute: (minuteChange: number) =>
					this.moveEndMinute(event, minuteChange),
				delete: () =>
					this.delete(event),
			})
	}

	private moveStartMinute(event: CalendarEvent, minuteChange: number) {
		changeDate(event.Times.StartTime, { minutes: minuteChange })
		this.updateProviders()
	}

	private moveEndMinute(event: CalendarEvent, minuteChange: number) {
		changeDate(event.Times.EndTime, { minutes: minuteChange })
		this.updateProviders()
	}

	private delete(event: CalendarEvent) {
		const index = this.events.indexOf(event)
		this.events.splice(index, 1)
		this.updateProviders()
	}
}
