const otherResponses = {}

otherResponses[baseUrl + "getlistforpatient"] = (parameters) => {
	const result = list.filter(e => { return +parameters.id === +e.PatientID })
	return JSON.stringify(result)
}

export function JournalEntryResponses() {
	return {
		...ModelTestData.commonResponses(JournalEntry, baseUrl, list),
		...otherResponses
	}
}
