﻿// import { Patient, Gender } from "idun-common/models/patient"

export class IdunId {
	country: string // e.g. 'dk', 'no', 'gb' etc.
	idType: string // 'c' for cpr, 'i' for our custom idunid, 'a' if it's the default for the country
	gender: string // 'm' or 'f'
	day: string
	month: string
	year: string
	identification: string // the last n characters (4 when idType is 'cpr')

	invalid: boolean

	constructor(idunid?: string) {
		if (!idunid) {
			this.invalid = true
			return
		}
		else if (idunid.length > 12) {
			this.country = idunid.substr(0, 2)
			this.idType = idunid.substr(2, 1)
			this.gender = idunid.substr(3, 1)
			this.day = idunid.substr(4, 2)
			this.month = idunid.substr(6, 2)
			this.year = idunid.substr(8, 4)
			this.identification = idunid.substr(12)
		}
		else {
			this.invalid = true
			console.error("Could not parse idunid: " + idunid)
		}
	}

	toCpr() {
		if (this.invalid || this.idType !== "c" || this.identification.length !== 4) {
			console.error("IdunId is not valid cpr")
			return ""
		}
		else {
			return this.day + this.month + this.year.substr(2, 2) + "-" + this.identification
		}
	}

	loadFromCpr(cpr: string) {
		if (cpr.length === 11 && cpr.charAt(6) === "-")
			cpr = cpr.replace("-", "")
		if (cpr.length === 10) {
			this.country = "dk"
			this.idType = "c"
			this.gender = +cpr.charAt(9) % 2 === 0 ? "f" : "m"
			this.day = cpr.substr(0, 2)
			this.month = cpr.substr(2, 2)

			const c = +cpr.substr(6, 1)
			let year = +cpr.substr(4, 2)
			if ([0, 1, 2, 3].includes(c))
				year += 1900
			else if ([5, 6, 7, 8].includes(c))
				year += year <= 36 ? 2000 : (year >= 58 ? 1800 : 0)
			else if ([4, 9].includes(c))
				year += year > 36 ? 1900 : 2000
			this.year = year.toString()

			this.identification = cpr.substr(6, 4)
			this.invalid = false
			return true
		}
		console.error("Not valid cpr: " + cpr)
		this.invalid = true
		return false
	}

	loadFromPatient(patient) {
		this.invalid = true
		if (!patient.Address.Country)
			return false
		if (!patient.Birthdate)
			return false
		if (patient.Gender === undefined || <any>patient.Gender === "")
			return false
		this.country = patient.Address.Country
		this.idType = "i"
		this.gender = +patient.Gender === 1 ? "f" : "m"
		this.year = patient.Birthdate.substr(0, 4)
		this.month = patient.Birthdate.substr(5, 2)
		this.day = patient.Birthdate.substr(8, 2)
		this.identification = "" + Math.floor(Math.random() * 1000000)
		this.invalid = false
		return true
	}

	toString() {
		return this.isValid() ? "" + this.country + this.idType + this.gender + this.day + this.month + this.year + this.identification : ""
	}

	isValid() {
		if (this.invalid)
			return false
		if (this.country.length !== 2)
			return false
		if (this.idType.length !== 1)
			return false
		if (this.gender.length !== 1)
			return false
		if (this.day.length !== 2)
			return false
		if (this.month.length !== 2)
			return false
		if (this.year.length !== 4)
			return false
		if (this.identification.length === 0)
			return false
		return true
	}
}
