﻿import { SearchableIdunModel } from "./idun-model"

export class Resource extends SearchableIdunModel {
	DBID: number
	Title: string
	Abbreviation: string
	Description: string

	constructor(source?: any) {
		super()
		for (const prop in source) this[prop] = source[prop]
	}

	getTitle(): string {
		return this.Title
	}

	getShortTitle() {
		return this.Abbreviation
	}

	searchAttributes() {
		return [this.Title, this.Abbreviation]
	}
}
