import { CompetenceSet } from "idun-common/models/competence-set"
import { resourceTestData, testResourceCompetences } from "./test-data/resource-test-data"
import { procedureTypeTestData } from "./test-data/procedure-type-test-data"
import { procedureTestData, findOccupiedStaff } from "./test-data/procedure-test-data"
import { forDayInInterval, newResetDateFrom, isWeekend, newDateFrom, changeDate } from "./date-helper"
import { Resource } from "idun-common/models/resource"
import { PatientEvent } from "idun-common/models/patient-event"
import { random, randomFromList } from "./random"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { TimeInterval } from "idun-common/models/time-interval"
import { Procedure } from "idun-common/models/procedure"
import { staffTestData } from "./test-data/staff-test-data"
import { patientEventTestData } from "./test-data/patient-event-test-data"
import { procedureEventTestData } from "./test-data/procedure-event-test-data"
import { CalendarEvent } from "idun-common/models/calendar-event"
import { calendarEventTestData } from "./test-data/calendar-event-test-data"
import { workIntervalTestData } from "./test-data/work-interval-test-data"
import { WorkInterval } from "idun-common/models/work-interval"
import { patientTestData } from "./test-data/patient-test-data"

export function createTestData() {
	generateWorkIntervals()
	generateEvents()
}

function findResourceCompetences() {
	for (const resource of resourceTestData)
	testResourceCompetences[resource.DBID] = CompetenceSet.competencesForResource(
			resource,
			procedureTypeTestData,
			procedureTestData)
}

findResourceCompetences()

function generateWorkIntervals() {
	const day = new Date()
	day.setDate(day.getDate() - 500)
	for (let i = 0; i < 1000; i++) {
		day.setDate(day.getDate() + 1)
		if (day.getDay() === 0 || day.getDay() === 6)
			continue
		for (const staff of staffTestData)
			newWorkIntervalForDate(day, staff.DBID)
	}
}

function newWorkIntervalForDate(date: Date, staffID: number) {
	date.setHours(8, 0, 0, 0)
	workIntervalTestData.push(new WorkInterval({
		DBID: workIntervalTestData.length + 1,
		StaffID: staffID,
		Times: TimeInterval.from(new Date(date), new Date(date))
	}))
	date.setHours(16, 0, 0, 0)
	workIntervalTestData[workIntervalTestData.length - 1].Times.EndTime = new Date(date)
}

function generateEvents() {
	forDayInInterval(
		newResetDateFrom({ days: -100 }),
		newResetDateFrom({ days: 50 }),
		date => {
			if (!isWeekend(date))
				generateEventsForDay(date)
		})
}

function generateEventsForDay(date: Date) {
	const calendarEvents = createCalendarEventsFor(date)
	saveCalendarEvents(calendarEvents)
	const procedureEvents = []
	for (const resource of resourceTestData)
		generateForResource(resource, date, calendarEvents, procedureEvents)
}

function createCalendarEventsFor(date: Date) {
	return [
		createLunchEvent(date),
		createMeetingEvent(date),
		createCleaningEvent(date),
	].filter(e => e)
}

function createLunchEvent(date: Date) {
	return new CalendarEvent({
		Title: "Frokost",
		Times: {
			StartTime: newResetDateFrom({ hours: 12 }, date),
			EndTime: newResetDateFrom({ hours: 12, minutes: 45 }, date)
		},
		OccupiedStaff: staffTestData.map(e => e.DBID)
	})
}

function createMeetingEvent(date: Date) {
	if (random() < 0.5)
		return null
	const startMinute = randomStartMinute()
	const endMinute = startMinute + randomFromList([15, 30, 45, 60])
	return new CalendarEvent({
		Title: "Møde",
		Times: {
			StartTime: newResetDateFrom({ hours: Math.floor(startMinute / 60), minutes: startMinute % 60 }, date),
			EndTime: newResetDateFrom({ hours: Math.floor(endMinute / 60), minutes: endMinute % 60 }, date)
		},
		OccupiedStaff: randomStaff()
	})
}

function randomStartMinute() {
	return Math.floor((random() + 1) * 7 * 12) * 5
}

function createCleaningEvent(date: Date) {
	if (random() < 0.5)
		return null
	const startMinute = randomStartMinute()
	const endMinute = startMinute + randomFromList([10, 15, 15, 30])
	return new CalendarEvent({
		Title: "Rengøring",
		Times: {
			StartTime: newResetDateFrom({ hours: Math.floor(startMinute / 60), minutes: startMinute % 60 }, date),
			EndTime: newResetDateFrom({ hours: Math.floor(endMinute / 60), minutes: endMinute % 60 }, date)
		},
		OccupiedStaff: [randomFromList(staffTestData).DBID],
		OccupiedResources: [randomFromList(resourceTestData).DBID]
	})
}

function randomStaff() {
	const staffIds = [1, 2, 3, 4, 5, 6, 7]
		.filter(_ => random() < 0.3)
	if (staffIds.length === 0)
		return [1]
	return staffIds
}

function saveCalendarEvents(events: CalendarEvent[]) {
	for (const event of events) {
		event.DBID = calendarEventTestData.length
		calendarEventTestData.push(event)
	}
}

function generateForResource(
	resource: Resource,
	date: Date,
	calendarEvents: CalendarEvent[],
	procedureEvents: ProcedureEvent[]
) {
	const currentTime = newDateFrom({ hours: 8 }, date)
	const endTime = newDateFrom({ hours: 16 }, date).getTime()
	while (currentTime.getTime() < endTime) {
		const event = createEventFor(resource, currentTime, calendarEvents, procedureEvents)
		saveEvent(event)
		incrementTime(currentTime, event)
	}
}

function createEventFor(
	resource: Resource,
	startTime: Date,
	calendarEvents: CalendarEvent[],
	procedureEvents: ProcedureEvent[]
): PatientEvent {
	if (leaveEmpty(startTime))
		return null
	const procedure = randomProcedureFrom(testResourceCompetences[resource.DBID])
	if (!procedure)
		return null
	const procedureInterval = timeIntervalFor(procedure, startTime)
	if (resourceIsOccuppied(resource, procedureInterval, calendarEvents))
		return null
	const occupiedStaff = findOccupiedStaff(
		procedureInterval,
		calendarEvents,
		procedureEvents
	)
	const staff = findStaffFor(procedure, occupiedStaff)
	if (!staff)
		return null
	const patientEvent = new PatientEvent()
	patientEvent.PatientId = Math.ceil(random() * patientTestData.length)
	const procedureEvent = new ProcedureEvent()
	procedureEvent.ProcedureId = procedure.DBID
	procedureEvent.Times = procedureInterval
	procedureEvent.OccupiedResources = [resource.DBID]
	procedureEvent.OccupiedStaff = [staff.DBID]
	const variation = randomFromList(procedure.Variations)
	procedureEvent.Variation = variation.Title
	procedureEvent.Price = variation.Price
	procedureEvent.TreatmentCodeIds = variation.ModelIDs.map(e => e)
	procedureEvent.PartnerId = Math.floor(random() * 3)
	patientEvent.ProcedureEvents.push(procedureEvent)
	procedureEvents.push(procedureEvent)
	return patientEvent
}

function leaveEmpty(startTime: Date) {
	let filledFraction = 0.5
	if (startTime.getTime() < new Date().getTime())
		filledFraction = 0.9
	else if (startTime.getTime() < newResetDateFrom({ days: 14 }).getTime())
		filledFraction = 0.8
	return filledFraction < random()
}

function timeIntervalFor(procedure: Procedure, startTime: Date) {
	return TimeInterval.fromDuration(startTime, { minutes: procedure.Duration })
}

function resourceIsOccuppied(
	resource: Resource,
	interval: TimeInterval,
	events: CalendarEvent[]
) {
	const relevantEvents = events.filter(e => e.OccupiedResources.includes(resource.DBID))
	return relevantEvents.some(e => e.Times.intersects(interval))
}

function findStaffFor(
	procedure: Procedure,
	occupiedStaff: number[]
) {
	const availableStaff = staffThatCanPerform(procedure)
		.filter(e => !occupiedStaff.includes(e.DBID))
	return randomFromList(availableStaff)
}

function staffThatCanPerform(procedure: Procedure) {
	return staffTestData.filter(e => e.Competences.canPerform(procedure))
}

function randomProcedureFrom(competenceSet: CompetenceSet) {
	return randomFromList(competenceSet.performableProcedures(procedureTestData))
}

function saveEvent(event: PatientEvent) {
	if (!event)
		return
	event.DBID = patientEventTestData.length
	patientEventTestData.push(event)
	for (const procedureEvent of event.ProcedureEvents) {
		procedureEvent.DBID = procedureEventTestData.length
		procedureEvent.PatientEventId = event.DBID
		procedureEventTestData.push(procedureEvent)
	}
}

function incrementTime(time: Date, event: PatientEvent) {
	if (!event)
		return changeDate(time, { minutes: 30 })
	changeDate(time, { minutes: event.duration() })
}
