﻿import { Component, Input, Output, EventEmitter, ViewChild, OnInit, HostBinding } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { FigureComponent } from "idun-figure/figure.component"
import { figureViews } from "./figure"
import { Figure } from "./figure.interface"

@Component({
	selector: "idun-figure-selector",
	templateUrl: "./figure-selector.component.html",
	styleUrls: ["./figure-selector.component.scss"]
})
export class FigureSelectorComponent implements OnInit {
	_figure: Figure = figureViews
	@Input() figureWidth = ""
	@HostBinding("style.display") get useFlexDisplay() {
		return this.figureWidth ? "flex" : ""
	}
	@Input() set figure(value: Figure) {
		this._figure = value
		this.view = Object.keys(value)[0]
		this.subview = ""
	}
	get figure(): Figure {
		return this._figure
	}
	@Input() selectedParts: string[] = []
	@Output() selectedPartsChange = new EventEmitter()
	@Input() view = "areas"
	@Output() viewChange = new EventEmitter()
	@Input() subview = ""
	@Output() subviewChange = new EventEmitter()
	@Input() showSelectedText = true
	@Input() showCurrentText = true

	currentPart = ""

	@ViewChild(FigureComponent) figureComponent: FigureComponent

	layerOptions = []

	constructor(
		public l: LanguageService
	) { }

	ngOnInit() {
		this.layerOptions = []
		for (const key in figureViews)
			this.layerOptions.push({
				text: () => this.l.figure[key],
				selected: () => this.view === key,
				click: () => {
					this.subview = ""
					this.subviewChange.emit("")
					this.view = key
					this.viewChange.emit(this.view)
					this.updateSelectedParts()
				}
			})
	}

	updateSubview(subview: string) {
		if (!subview)
			this.updateSelectedParts()
		this.subview = subview
		this.subviewChange.emit(this.subview)
	}

	updateSelectedParts() {
		const part = this.view + (this.subview ? "/" + this.subview : "")
		this.selectedParts = [part]
		this.selectedPartsChange.emit(this.selectedParts)
	}
}
