﻿import { Component, ElementRef, forwardRef, Input, OnInit, OnDestroy, ViewChild, EventEmitter, Output, NgZone } from "@angular/core"
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { LanguageService } from "../language.service"
import { DateHelper } from "utility/date-helper"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => DateTimePickerComponent),
	multi: true
}

@Component({
	selector: "idun-date-time-picker",
	templateUrl: "date-time-picker.component.html",
	styleUrls: ["date-time-picker.component.scss"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class DateTimePickerComponent extends FormBasicComponent<Date> implements ControlValueAccessor, OnInit {
	shownValue: any
	showPicker = false
	hourInput = 10
	minuteInput = 30
	@Input() hideDate
	@ViewChild("dropdown") dropdown
	@ViewChild("inputBox") inputBox

	constructor(
		public elementRef: ElementRef,
		public ngZone: NgZone,
		public l: LanguageService
	) {
		super()
	}

	ngOnInit(): void {
		this.dropdown.nativeElement.addEventListener("blur", this.handleBlur, { passive: true })
		this.inputBox.nativeElement.addEventListener("blur", this.handleBlur, { passive: true })
		this.inputBox.nativeElement.addEventListener("keydown", this.handleKey)
	}

	increaseHours() {
		this.hourInput++
		if (this.hourInput > 23)
			this.hourInput = 0
		this.value.setHours(this.hourInput)
		this.onChangeCallback(this.value)
		this.updateValues()
	}

	decreaseHours() {
		this.hourInput--
		if (this.hourInput < 0)
			this.hourInput = 23
		this.value.setHours(this.hourInput)
		this.onChangeCallback(this.value)
		this.updateValues()
	}

	increaseMinutes() {
		this.minuteInput++
		if (this.minuteInput > 59) {
			this.minuteInput = 0
			this.value.setMinutes(this.minuteInput)
			this.increaseHours()
			return
		}
		this.value.setMinutes(this.minuteInput)
		this.onChangeCallback(this.value)
		this.updateValues()
	}

	decreaseMinutes() {
		this.minuteInput--
		if (this.minuteInput < 0) {
			this.minuteInput = 59
			this.value.setMinutes(this.minuteInput)
			this.decreaseHours()
			return
		}
		this.value.setMinutes(this.minuteInput)
		this.onChangeCallback(this.value)
		this.updateValues()
	}

	onValueChange() {
		if (!this.value) {
			this.shownValue = ""
			return
		}
		this.updateValues()
	}

	updateValues() {
		this.shownValue = (this.hideDate ? "" : DateHelper.dateToString(this.value) + " ") + DateHelper.timeToString(this.value)
		this.hourInput = this.value.getHours()
		this.minuteInput = this.value.getMinutes()
	}

	selectDate(date) {
		this.value.setFullYear(date.getFullYear(), date.getMonth(), date.getDate())
		this.onChangeCallback(this.value)
		this.updateValues()
	}

	setShowPicker() {
		this.showPicker = true
	}

	handleKey = (event) => {
		if (event.key === "Enter")
			return this.changeFromInput()
		if (event.key.length === 1 && !(+event.key >= 0 || event.key === ":" || event.key === "-"))
			event.preventDefault()
	}

	changeFromInput() {
		const parts = this.shownValue.trim().split(" ").filter(e => e)
		const date = DateHelper.parseDateString(parts[0] || "")
		const time = DateHelper.parseTimeString((this.hideDate ? parts[0] : parts[1]) || "", false)

		if (this.hideDate && !time.error) {
			this.value.setHours(time.hours, time.minutes)
			this.onChangeCallback(this.value)
		}
		else if (!(date.error || time.error)) {
			this.value.setFullYear(date.year, date.month - 1, date.date)
			this.value.setHours(time.hours, time.minutes)
			this.onChangeCallback(this.value)
		}
		this.updateValues()
	}

	hasFocus() {
		let element = document.activeElement
		while (element) {
			if (element === this.elementRef.nativeElement)
				return true
			element = element.parentElement
		}
		return false
	}

	handleBlur = () => {
		setTimeout(() => {
			this.ngZone.run(() => {
				if (!this.hasFocus())
					this.showPicker = false
			})
		}, 50)
		this.onChangeCallback(this.value)
	}
}
