﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { PatientSummaryService } from "./patient-summary.service"
import { PartnerService } from "./partner.service"
import { TreatmentCodeService } from "./treatment-code.service"
import { LanguageService } from "idun-layout/language.service"

import { Invoice, StatusCode } from "idun-common/models/invoice"
import { FormSelectModelRow } from "idun-common/form/form-select-model.component"
import { FormDateRow } from "idun-layout/form/form-date.component"
import { FormSelectRow } from "idun-layout/form/form-select.component"
import { OptionInfo } from "idun-layout/form/form-row"
import { FormListModelDataRow } from "idun-common/form/form-list-model-data.component"
import { UserService } from "../user.service"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"

@Injectable()
export class InvoiceService extends ExtendedService<Invoice> {
	constructor(
		patientSummaryService: PatientSummaryService,
		partnerService: PartnerService,
		treatmentCodeService: TreatmentCodeService,
		http: Http,
		userService: UserService,
		l: LanguageService
	) {
		super(http, l,
			Invoice,
			"api/Invoice",
			"invoice",
			() => l.invoice
		)
		this.loadAll = false
		userService.loadClinicSettings()
		this.setupForm({
			rows: [
				new FormSelectModelRow("PatientID", {
					label: () => l.patient.nameSingular,
					hint: () => l.invoice.patientHint,
					requirements: [ValidationRequirement.ValueRequired(l)],
					service: patientSummaryService
				}),
				new FormSelectModelRow("CompanyID", {
					label: () => l.invoice.company,
					hint: () => l.invoice.companyHint,
					requirements: [ValidationRequirement.ValueRequired(l)],
					service: partnerService
				}),
				new FormListModelDataRow("DescriptionCodes", {
					hint: () => l.invoice.codesAndPricesHint,
					label: () => l.invoice.codesAndPrices,
					idField: "Key",
					dataField: "Value",
					dataType: "number",
					dataLabel: () => l.common.price,
					dataEndLabel: () => userService.clinicSettings ? userService.clinicSettings.Currency : "",
					service: treatmentCodeService
				}),
				new FormDateRow("Date", {
					label: () => l.common.date,
					requirements: [ValidationRequirement.ValueRequired(l)],
				}),
				new FormSelectRow("Status", {
					label: () => l.common.status,
					hint: () => l.invoice.statusHint,
					requirements: [ValidationRequirement.ValueRequired(l)],
					options: [
						new OptionInfo("0", () => this.l.invoice.sent),
						new OptionInfo("1", () => this.l.invoice.delayedPayment),
						new OptionInfo("2", () => this.l.invoice.paid),
						new OptionInfo("3", () => this.l.invoice.debtCollections)]
				})
			]
		})
		this.setupList({
			entryHeader: (model: Invoice) => {
				const patient = patientSummaryService.getModel(model.PatientID)
				if (patient)
					return patient.Name
				return ""
			},
			contentList: [(model: Invoice) => {
				return model.Date
			}],
			groups:
				[{ getHeader: () => this.l.invoice.debtCollections, filter: (list) => this.getInvoicesWithStatus(StatusCode.DebtCollections, list) },
				{ getHeader: () => this.l.invoice.delayedPayment, filter: (list) => this.getInvoicesWithStatus(StatusCode.DelayedPayment, list) },
				{ getHeader: () => this.l.invoice.sent, filter: (list) => this.getInvoicesWithStatus(StatusCode.Sent, list) },
				{ getHeader: () => this.l.invoice.paid, filter: (list) => this.getInvoicesWithStatus(StatusCode.Paid, list) }]
		})
	}

	private getInvoicesWithStatus(status: StatusCode, list: Invoice[]) {
		const result = []
		if (list)
			for (let i = 0; i < list.length; i++)
				if (list[i].Status === status)
					result.push(list[i])
		return result
	}
}
