﻿import { IdunModel } from "./idun-model"
import { TimeInterval } from "./time-interval"

export class Status extends IdunModel {
	DBID: number
	StaffID: number
	StatusTypeID: number
	Interval: TimeInterval
	AssignedResourceID: number
	Periods: Period[]

	constructor(source?: any) {
		super()
		this.AssignedResourceID = 0
		this.Interval = new TimeInterval()
		this.Periods = []
		if (source) {
			for (const prop in source) this[prop] = source[prop]
			if (source.Periods)
				this.Periods = source.Periods.map(e => new Period(e))
			if (source)
				this.Interval = new TimeInterval(source.Interval)
		}
	}

	getTitle(): string {
		return ""
	}
}

export class Period {
	Interval: TimeInterval
	Assignment: string

	constructor(source?: any) {
		this.Interval = new TimeInterval()
		for (const prop in source) this[prop] = source[prop]
		if (source)
			this.Interval = new TimeInterval(source.Interval)
	}
}
