﻿import { BookingCalendarComponent } from "./booking-calendar.component"
import { BookingPageComponent } from "./booking-page.component"

export const bookingRoutes = [
	{ path: "booking", component: BookingPageComponent, outlet: "left-content" },
	{ path: "booking", component: BookingPageComponent },
	{ path: "booking-calendar/:date", component: BookingCalendarComponent, outlet: "left-content" },
	{ path: "booking-calendar/:date", component: BookingCalendarComponent },
	{ path: "booking-calendar", component: BookingCalendarComponent, outlet: "left-content" },
	{ path: "booking-calendar", component: BookingCalendarComponent }
]
