import { IdunModel } from "./idun-model"
import { TimeInterval } from "./time-interval"

export class ProcedureEvent extends IdunModel {
	DBID: number
	PatientEventId: number

	ProcedureId: number
	Variation: string
	Placements: string[]
	TreatmentCodeIds: number[]

	PartnerId: number
	PriceVariation: string
	PaymentCodeIds: number[]
	Price: number

	Times: TimeInterval
	OccupiedStaff: number[]
	OccupiedResources: number[]
	CompletionStatus: CompletionStatus
	PaymentStatus: PaymentStatus

	constructor(source?: any) {
		super()
		this.CompletionStatus = CompletionStatus.NotStarted
		this.PaymentStatus = PaymentStatus.NotStarted
		this.Times = new TimeInterval()
		this.Placements = []
		this.TreatmentCodeIds = []
		this.PaymentCodeIds = []
		this.OccupiedStaff = []
		this.OccupiedResources = []

		if (!source)
			return
		for (const prop in source) this[prop] = source[prop]
		if (source.Times)
			this.Times = new TimeInterval(source.Times)
		if (source.Placements)
			this.Placements = source.Placements.map(e => e)
		if (source.TreatmentCodeIds)
			this.TreatmentCodeIds = source.TreatmentCodeIds.map(e => e)
		if (source.PaymentCodeIds)
			this.PaymentCodeIds = source.PaymentCodeIds.map(e => e)
		if (source.OccupiedStaff)
			this.OccupiedStaff = source.OccupiedStaff.map(e => e)
		if (source.OccupiedResources)
			this.OccupiedResources = source.OccupiedResources.map(e => e)
	}

	getTitle(): string {
		throw new Error("Method should not be used")
	}
}

export enum CompletionStatus { NotStarted, InProgress, Finished }
export enum PaymentStatus { NotStarted, Sent, Paid }
