﻿import { Component, forwardRef } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { FormRow } from "./form-row"
import { Colour } from "utility/colour"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormHueComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormHueComponent),
	multi: true
}

@Component({
	selector: "idun-form-hue",
	template: `
<idun-form-input-container [hint]="hint" [label]="label" [display]="display" [error]="errorToShow()" [showAsterisk]="showAsterisk()">
	<div class="flex-row">
		<div id="color-div" [style.backgroundColor]="backgroundColor"></div>
		<input type="range" max="1" min="0" step="0.01" [(ngModel)]="value" (blur)="onBlur()"
				[disabled]="display" autocomplete="off">
		<ng-content></ng-content>
	</div>
</idun-form-input-container>`,
	styleUrls: ["form-hue.component.scss"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormHueComponent extends FormBasicComponent<number> {
	rowClass = FormHueRow

	get backgroundColor() {
		return Colour.ToRgbString(Colour.HSVtoRGB(+this.value, 1, 1))
	}
}

export class FormHueRow extends FormRow { }
