﻿import { Component, Input } from "@angular/core"

@Component({
	selector: "idun-button-check",
	template: `
		<button type="button" class="{{checked ? 'positive-button' : ''}}">
			<span [hidden]="!checked">✔</span>
		</button>`,
		styleUrls: ["button-check.component.scss"]
})
export class ButtonCheckComponent {
	@Input() checked = false
}
