﻿import { Component, OnInit, ElementRef, OnDestroy } from "@angular/core"
import { Router, ActivatedRoute } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LanguageService } from "idun-layout/language.service"
import { LayoutService } from "idun-layout/layout.service"
import { RouteParameterComponent } from "idun-common/route-parameter-component"

@Component({
	selector: "idun-standard-intro-pages",
	templateUrl: "standard-intro-pages.component.html",
	styleUrls: ["intro.scss"]
})
export class StandardIntroPagesComponent extends RouteParameterComponent implements OnInit, OnDestroy {
	navigater: NavigationHandler

	introPages: { [index: string]: IntroPage } = {
		...this.setupPages(),
		...this.templatePages(),
		...this.patientPages(),
	}

	page: IntroPage

	constructor(
		myElement: ElementRef,
		router: Router,
		layoutService: LayoutService,
		private route: ActivatedRoute,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit(): void {
		this.parameterHandlers.page = (page: string) => {
			this.page = this.introPages[page]
			this.navigater.disableNavigation()
			if (this.page.onLoad)
				this.page.onLoad()
		}
		this.handleRouteParameters(this.route)
	}

	navigate(destination: string, secondDestination?: string) {
		this.navigater.enableNavigation()
		if (secondDestination)
			this.navigater.both(destination, secondDestination)
		else
			this.navigater.main(destination)
	}

	ngOnDestroy() {
		this.navigater.enableNavigation()
	}

	private setupPages() {
		return {
			"welcome": {
				header: () => this.l.intro.welcome,
				sections: [
					new TextSection(() => this.l.intro.startWelcome),
					new NavigateSection(() => this.l.intro.next, "intro/pages"),
				],
			},

			"pages": {
				header: () => this.l.intro.layout,
				sections: [
					new TextSection(() => this.l.intro.pagesA),
					new IdunLinkSection(() => this.l.intro.openExamplePage, "intro/pages-extra"),
					new TextSection(() => this.l.intro.pagesB),
					new NavigateSection(() => this.l.intro.next, "intro/settings", "clinic-settings"),
				],
				onLoad: () => {
					this.navigater.allowNavigation((addresses) => {
						if (addresses.left && !addresses.left.includes("intro/pages"))
							return false
						if (addresses.right && !addresses.right.includes("intro/pages"))
							return false
						return true
					})
				},
			},

			"pages-extra": {
				header: () => this.l.intro.examplePage,
				sections: [
					new IdunLinkSection(() => this.l.intro.openLayoutIntro, "intro/pages"),
					new TextSection(() => this.l.intro.pagesB),
					new NavigateSection(() => this.l.intro.next, "intro/settings", "clinic-settings"),
				],
				onLoad: () => {
					this.navigater.allowNavigation((addresses) => {
						if (addresses.left && !addresses.left.includes("intro/pages"))
							return false
						if (addresses.right && !addresses.right.includes("intro/pages"))
							return false
						return true
					})
				},
			},

			"setup": {
				header: () => this.l.common.setup,
				sections: [
					new TextSection(() => this.l.intro.setupA),
					new ImageSection("/assets/images/SetupDanish.png"),
					new TextSection(() => this.l.intro.setupB),
					new NavigateSection(() => this.l.intro.next, "intro/resources", "resource/list"),
				],
			},

			"resources": {
				header: () => this.l.resource.namePlural,
				sections: [
					new TextSection(() => this.l.intro.resourcesIntro),
					new NavigateSection(() => this.l.intro.next, "intro/treatment-codes", "treatment-code/list"),
				],
			},

			"treatment-codes": {
				header: () => this.l.intro.treatmentCodesTitle,
				sections: [
					new TextSection(() => this.l.intro.treatmentCodesIntro),
					new NavigateSection(() => this.l.intro.next, "intro/procedures", "procedure/list"),
				],
			},

			"procedures": {
				header: () => this.l.procedure.namePlural,
				sections: [
					new TextSection(() => this.l.intro.proceduresIntro),
					new NavigateSection(() => this.l.intro.next, "intro/staff", "staff/list"),
				],
			},

			"staff": {
				header: () => this.l.staff.namePlural,
				sections: [
					new TextSection(() => this.l.intro.staffIntro),
					new NavigateSection(() => this.l.intro.next, "intro/prices", "partner/list"),
				],
			},

			"prices": {
				header: () => this.l.partner.namePlural,
				sections: [
					new TextSection(() => this.l.intro.setupPrices),
					new NavigateSection(() => this.l.intro.next, "intro/end-setup"),
				],
			},

			"end-setup": {
				header: () => this.l.intro.end,
				sections: [
					new TextSection(() => this.l.intro.endDialogue),
					new NavigateSection(() => this.l.intro.end, "home"),
				],
			},
		}
	}

	private templatePages() {
		return {
			"template-start": {
				header: () => this.l.journalTemplate.nameSingular,
				sections: [
					new TextSection(() => this.l.intro.templateStartA),
				],
				onLoad: () => {
					this.navigater.allowNavigation((addresses) => {
						if (!addresses.right.includes("journal-template/"))
							return false
						addresses.left = "intro/template-edit"
						return true
					})
				},
			},

			"template-edit": {
				header: () => this.l.intro.journalTemplateEdit,
				sections: [
					new TextSection(() => this.l.intro.templateEditA)
				],
				onLoad: () => {
					this.navigater.allowNavigation((addresses) => {
						if (!addresses.right.includes("journal-template/"))
							return false
						addresses.left = "intro/template-add-navigate"
						return true
					})
				},
			},

			"template-add-navigate": {
				header: () => this.l.intro.addNewTemplate,
				sections: [
					new TextSection(() => this.l.intro.templateAddNavigateA)
				],
				onLoad: () => {
					this.navigater.allowNavigation((addresses) => {
						if (!addresses.right.includes("journal-template/add"))
							return false
						addresses.left = "intro/template-add"
						return true
					})
				},
			},

			"template-add": {
				header: () => this.l.intro.addNewTemplate,
				sections: [
					new TextSection(() => this.l.intro.templateAddA),
					new NavigateSection(() => this.l.intro.next, "intro/template-use-navigate", "patient/1"),
				],
			},

			"template-use-navigate": {
				header: () => this.l.intro.howToUseTemplate,
				sections: [
					new TextSection(() => this.l.intro.templateUseNavigateA)
				],
				onLoad: () => {
					this.navigater.allowNavigation((addresses) => {
						if (!addresses.right.includes("journal-template/add"))
							return false
						addresses.left = "intro/template-use"
						return true
					})
				},
			},

			"template-use": {
				header: () => this.l.intro.howToUseTemplate,
				sections: [
					new TextSection(() => this.l.intro.templateUseA),
					new NavigateSection(() => this.l.intro.end, "home"),
				],
			},
		}
	}

	private patientPages() {
		return {
			"patient-start": {
				header: () => this.l.common.patientOverview,
				sections: [
					new TextSection(() => this.l.intro.patientStartA),
				],
				onLoad: () => {
					this.navigater.allowNavigation((addresses) => {
						if (addresses.right.includes("patient/") && !addresses.right.includes("patient/add")) {
							addresses.left = "intro/patient-patient"
							return true
						}
						return false
					})
				},
			},

			"patient-patient": {
				header: () => this.l.intro.patientInformation,
				sections: [
					new TextSection(() => this.l.intro.patientPatientA),
				],
				onLoad: () => {
					this.navigater.allowNavigation((addresses) => {
						if (!addresses.right.includes("patient-note/add"))
							return false
						addresses.left = "intro/patient-note"
						return true
					})
				},
			},

			"patient-note": {
				header: () => this.l.patientNote.namePlural,
				sections: [
					new TextSection(() => this.l.intro.patientNoteA),
					new NavigateSection(() => this.l.intro.next, "intro/patient-sidebar"),
				],
			},

			"patient-sidebar": {
				header: () => this.l.intro.theSidebar,
				sections: [
					new TextSection(() => this.l.intro.patientSidebarA),
					new TextAndImageSection("/assets/images/VectorIcons/Patient_icon.svg", () => this.l.intro.patientSidebarB),
					new TextAndImageSection("/assets/images/VectorIcons/CalendarBooking_icon.svg", () => this.l.intro.patientSidebarC),
					new TextAndImageSection("/assets/images/VectorIcons/Calendar_icon.svg", () => this.l.intro.patientSidebarD),
					new TextAndImageSection("/assets/images/VectorIcons/Help_icon.svg", () => this.l.intro.patientSidebarE),
					new TextSection(() => this.l.intro.patientSidebarF),
					new NavigateSection(() => this.l.intro.end, "home"),
				],
			},
		}
	}
}

interface Section {
	type: string
}

class TextSection implements Section {
	type = "text"
	constructor(
		public content: () => string
	) { }
}

class IdunLinkSection implements Section {
	type = "idun-link"
	constructor(
		public content: () => string,
		public destination: string,
	) { }
}

class NavigateSection implements Section {
	type = "navigate"
	constructor(
		public content: () => string,
		public destination: string,
		public secondDestination?: string,
	) { }
}

class ImageSection implements Section {
	type = "image"
	constructor(
		public imageUrl: string
	) { }
}

class TextAndImageSection implements Section {
	type = "text-image"
	constructor(
		public imageUrl: string,
		public content: () => string
	) { }
}

interface IntroPage {
	header: () => string
	sections: Section[]
	onLoad?: () => void
}
