import { ColorMode, ColorRow } from "./column-box.component"

export class ColorThing {
	colors: (SelectableColorRow | ColorRow)[] = []
	modes: ColorMode[] = []
	modeColors: { [index: string]: ( SelectableColorRow | ColorRow)[] } = {}
	currentMode = ""

	setMode(mode) {
		this.currentMode = mode
		this.colors = this.modeColors[mode]
	}

	getColor(entry: any) {
		if (!this.currentMode)
			return
		for (const colorRow of this.modeColors[this.currentMode]) {
			const hasColor = (<any>colorRow).hasColor
			if (hasColor && hasColor(entry))
				return colorRow.hue
		}
		return
	}

	addMode(
		modeName: string,
		modeTitle: () => string,
		colors: ( SelectableColorRow | ColorRow)[]
	) {
		if (!this.modeColors[modeName])
			this.modes.push({ name: modeName, title: modeTitle })
		this.modeColors[modeName] = colors
	}

	addModeFromList<T>(
		modeName: string,
		modeTitle: () => string,
		list: T[],
		callback: (e: T, i: string) => ColorRow
	) {
		const colors = []
		for (const i in list)
			colors.push(callback(list[i], i))
		if (!this.modeColors[modeName])
			this.modes.push({ name: modeName, title: modeTitle })
		this.modeColors[modeName] = colors
	}

	static randomNumber(seed: number): number {
		const x = Math.sin(seed) * 8000
		return x - Math.floor(x)
	}

	static integerToZeroToOneMap(n: number): number {
		if (n < 1) return 0
		const m = ColorThing.weirdRoundToPowerOfTwo(n)
		return (1 + 2 * (n - m / 2)) / m
	}

	static weirdRoundToPowerOfTwo(n) {
		return Math.pow(2, Math.floor(Math.log2(n * 2)))
	}
}

interface SelectableColorRow extends ColorRow {
	hasColor: (entry: any) => boolean
}
