﻿import { Status } from "idun-common/models/status"
import { TimeInterval } from "idun-common/models/time-interval"
import { ModelTestData } from "./model-test-data"
import { DateHelper } from "utility/date-helper"

const baseUrl = "api/statusrequest/"

const list = [
	new Status({
		DBID: 1,
		StaffID: 1,
		StatusTypeID: 2,
		Interval: {
			StartTime: DateHelper.newDate(new Date(), { relative: true, reset: true, date: 1, hours: 12 }),
			EndTime: DateHelper.newDate(new Date(), { relative: true, reset: true, date: 1, hours: 15 })
		}
	}),
	new Status({
		DBID: 2,
		StaffID: 7,
		StatusTypeID: 2,
		Interval: {
			StartTime: DateHelper.newDate(new Date(), { relative: true, reset: true, date: 7, hours: 12 }),
			EndTime: DateHelper.newDate(new Date(), { relative: true, reset: true, date: 7, hours: 15 })
		}
	}),
	new Status({
		DBID: 3,
		StaffID: 4,
		StatusTypeID: 2,
		Interval: {
			StartTime: DateHelper.newDate(new Date(), { relative: true, reset: true, date: 10, hours: 12 }),
			EndTime: DateHelper.newDate(new Date(), { relative: true, reset: true, date: 10, hours: 15 })
		}
	}),
	new Status({
		DBID: 4,
		StaffID: 2,
		StatusTypeID: 2,
		Interval: {
			StartTime: DateHelper.newDate(new Date(), { relative: true, reset: true, date: 14, hours: 12 }),
			EndTime: DateHelper.newDate(new Date(), { relative: true, reset: true, date: 14, hours: 15 })
		}
	})
]

ModelTestData.loadFromLocalStorage(baseUrl, list, Status)

const otherResponses = {}

otherResponses[baseUrl + "implement"] = (parameters) => {
	ModelTestData.deleteResponse(Status, list)(parameters)
	return JSON.stringify(true)
}

export function StatusRequestResponses() {
	return {
		...ModelTestData.commonLocalStorageResponses(Status, baseUrl, list),
		...otherResponses
	}
}
