﻿import { JournalTemplate } from "idun-common/models/journal-template"
import { ModelTestData } from "./model-test-data"

const baseUrl = "api/journaltemplate/"

const list = [
	new JournalTemplate({
		DBID: 1,
		Title: "Journal test",
		Content: "The patient has a headache",
		Placements: ["areas/head"]
	}),
]

ModelTestData.loadFromLocalStorage(baseUrl, list, JournalTemplate)

export function JournalTemplateResponses() {
	return {
		...ModelTestData.commonLocalStorageResponses(JournalTemplate, baseUrl, list)
	}
}
