export function hasValue(value: any) {
	return value !== undefined && value !== null
}

export function flatten<T>(listOfLists: T[][]) {
	return listOfLists.reduce((a, b) => a.concat(b), [])
}

// ------------- Iterable extensions ------------------

class ImprovedIterable<T> implements IterableIterator<T> {
	constructor(
		private source: IterableIterator<T>
	) { }

	[Symbol.iterator]() {
		return this
	}

	next() {
		return this.source.next()
	}

	filter(predicate: (item: T) => boolean): ImprovedIterable<T> {
		return new ImprovedIterable(filterIterator(this.source, predicate))
	}

	map<P>(transform: (item: T) => P): ImprovedIterable<P> {
		return new ImprovedIterable(mapIterator(this.source, transform))
	}

	some(predicate: (item: T) => boolean): boolean {
		for (const item of this.source)
			if (predicate(item))
				return true
		return false
	}

	toArray() {
		return Array.from(this)
	}
}

function filterIterator<T>(
	iterable: IterableIterator<T>,
	predicate: (item: T) => boolean
): IterableIterator<T> {
	return {
		[Symbol.iterator]: function () { return this },
		next: () => {
			let nextItem = iterable.next()
			if (nextItem.done)
				return nextItem
			while (!nextItem.done && !predicate(nextItem.value))
				nextItem = iterable.next()
			return nextItem
		}
	}
}

function mapIterator<T, P>(
	iterable: IterableIterator<T>,
	transform: (item: T) => P
): IterableIterator<P> {
	return {
		[Symbol.iterator]: function () { return this },
		next: () => {
			const nextItem = iterable.next()
			return { value: transform(nextItem.value), done: nextItem.done }
		}
	}
}

// ------------------- Array/Map/etc Extensions ---------------------

declare global {
	interface Array<T> {
		sum(): Number
		distinct(): Array<T>
		includesAny(things: Array<T>): boolean
	}

	interface Map<K, V> {
		getEntries(): ImprovedIterable<[K, V]>
		getKeys(): ImprovedIterable<K>
		getValues(): ImprovedIterable<V>
	}
}

Object.defineProperty(Array.prototype, "sum", {
	value: function () {
		const list: any[] = this
		return list.reduce((previous, current) => previous + +current, 0)
	}
})

Object.defineProperty(Array.prototype, "distinct", {
	value: function () {
		const list: any[] = this
		return list.filter((e, i) => list.indexOf(e) === i)
	}
})

Object.defineProperty(Array.prototype, "includesAny", {
	value: function (things: any[]) {
		const list: any[] = this
		return list.some(x => things.includes(x))
	}
})

Object.defineProperty(Map.prototype, "getEntries", {
	value: function () {
		const map: Map<any, any> = this
		return new ImprovedIterable(map.entries())
	}
})

Object.defineProperty(Map.prototype, "getKeys", {
	value: function () {
		const map: Map<any, any> = this
		return new ImprovedIterable(map.keys())
	}
})

Object.defineProperty(Map.prototype, "getValues", {
	value: function () {
		const map: Map<any, any> = this
		return new ImprovedIterable(map.values())
	}
})
