import { DateHelper } from "utility/date-helper"
import { LanguageService } from "idun-layout/language.service"
import { Resource } from "idun-common/models/resource"
import { CalendarEntryProvider } from "idun-calendar/calendar-entry-provider"
import { CalendarEntry } from "idun-calendar/calendar-entry"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { ProcedureEventEntry } from "./procedure-event-entry"
import { CalendarEventEntry } from "./calendar-event-entry"
import { CalendarEvent } from "idun-common/models/calendar-event"
import { ProcedureService } from "idun-main/model-services/procedure.service"

export class ResourceEventProvider implements CalendarEntryProvider {
	entries: (ProcedureEventEntry | CalendarEventEntry)[]

	constructor(
		private events: (ProcedureEvent | CalendarEvent)[],
		private l: LanguageService,
		private colorThing: any,
		private procedureService: ProcedureService,
		private getResource: () => Resource
	) {
		this.updateEntries()
	}

	updateEntries() {
		this.entries = this.events.map(e => e instanceof ProcedureEvent
			? new ProcedureEventEntry(e, this.colorThing, this.procedureService)
			: new CalendarEventEntry(e))
	}

	getEntries(date?: Date): CalendarEntry[] {
		if (!date)
			return this.entries
		return this.entries.filter(e => DateHelper.isSameDate(e.source.Times.EndTime, date))
	}

	getSubtitle(): string {
		return this.l.resource.namePlural
	}

	getTitle(): string {
		const staff = this.getResource()
		return staff ? staff.Abbreviation : ""
	}

	getHovertitle(): string {
		const staff = this.getResource()
		return staff ? staff.Title : ""
	}
}
