﻿import { SearchableIdunModel } from "./idun-model"

export class TreatmentCode extends SearchableIdunModel {
	DBID: number
	Title: string
	Code: string
	Description: string
	TreatmentCodeTypeID: number

	constructor(source?: any) {
		super()
		if (source) {
			for (const prop in source) this[prop] = source[prop]
		}
	}

	getTitle(): string {
		return this.Code + " - " + this.Title
	}

	getShortTitle(): string {
		return this.Code
	}

	searchAttributes() {
		return [this.Title, this.Code]
	}
}
