﻿import { Component, OnInit } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { Procedure } from "idun-common/models/procedure"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { Subscriber } from "utility/subscriber"
import { DisplaySetup, Group } from "idun-common/filter-list.component"
import { ProcedureTypeService } from "idun-main/model-services/procedure-type.service"
import { UserService } from "idun-main/user.service"

@Component({
	selector: "idun-clinic-overview",
	templateUrl: "clinic-overview.component.html"
})
export class ClinicOverviewComponent implements OnInit {
	subscriber = new Subscriber()
	procedures: Procedure[] = []
	listGroups: Group<any>[] = []
	listDisplaySetup = new DisplaySetup(
		() => this.l.procedureType.namePlural,
		() => this.l.procedure.namePlural
	)
	currency = "DKK"

	constructor(
		public l: LanguageService,
		private userService: UserService,
		private procedureService: ProcedureService,
		private procedureTypeService: ProcedureTypeService
	) { }

	ngOnInit(): void {
		this.userService.clinicSettings$.subscribe((settings) => {
			this.currency = settings.Currency
		})
		this.subscriber.subscribe(this.procedureService.getModels$(), procedures => {
			this.procedures = procedures
		})
		this.subscriber.subscribe(this.procedureTypeService.getModels$(), (models) => {
			this.listGroups = models.map(x => {
				return {
					title: () => x.getTitle(),
					contains: (entry) => entry.ProcedureTypeID === x.DBID
				}
			})
		})
	}

	priceString(procedureVariation) {
		return `${(+procedureVariation.Price).toFixed(2)} ${this.currency}`
	}
}
