import { IdunModel } from "./idun-model"
import { TimeInterval } from "./time-interval"
import { LanguageService } from "idun-layout/language.service"

export class WorkRequest extends IdunModel {
	DBID: number
	StaffID: number
	Type: WorkRequestType
	Description: string
	Changes: WorkRequestInterval[]

	constructor(source?: any) {
		super()
		this.Changes = []

		if (!source)
			return
		for (const prop in source) this[prop] = source[prop]
		if (source.Changes)
			this.Changes = source.Changes.map(e => new WorkRequestInterval(e))
	}

	getTitle(): string {
		throw new Error("Method should not be used")
	}
}

export class WorkRequestInterval {
	Type: WorkRequestIntervalType
	Times: TimeInterval

	constructor(source?: any) {
		this.Times = new TimeInterval()

		if (!source)
			return
		for (const prop in source) this[prop] = source[prop]
		if (source.Times)
			this.Times = new TimeInterval(source.Times)
	}
}

export enum WorkRequestIntervalType { Work, Free }

export enum WorkRequestType { Vacation, Course, Sick, Other }

export function WorkRequestTypeString(
	requestType: WorkRequestType,
	l: LanguageService
) {
	switch (requestType) {
		case WorkRequestType.Vacation:
			return l.staff.vacation
		case WorkRequestType.Course:
			return l.staff.course
		case WorkRequestType.Sick:
			return l.staff.sick
		case WorkRequestType.Other:
			return l.staff.other
	}
}
