﻿import { IdunModel } from "./idun-model"
import { CompetenceSet } from "./competence-set"
import { TimeInterval } from "./time-interval"
import { CalendarEvent } from "./calendar-event"

export class PeriodPlan extends IdunModel {
	DBID: number
	Title: string
	Weeks: number
	StaffRoles: PlanningRole[]
	Events: CalendarEvent[]

	constructor(source?: any) {
		super()
		this.StaffRoles = []
		this.Events = []

		if (!source)
			return
		for (const prop in source) this[prop] = source[prop]
		if (source.StaffRoles)
			this.StaffRoles = source.StaffRoles.map(e => new PlanningRole(e))
		if (source.Events)
			this.Events = source.Events.map(e => new CalendarEvent(e))
	}

	getTitle(): string {
		return this.Title
	}

	check(query: string): boolean {
		return true
	}
}

export class PlanningRole {
	Index: number
	Title: string
	Competences: CompetenceSet
	Intervals: TimeInterval[]

	constructor(source?: any) {
		this.Intervals = []
		this.Competences = new CompetenceSet()

		if (!source)
			return
		for (const prop in source) this[prop] = source[prop]
		if (source.Intervals)
			this.Intervals = source.Intervals.map(e => new TimeInterval(e))
		if (source.Competences)
			this.Competences = new CompetenceSet(source.Competences)
	}

	getTitle() {
		return this.Title
	}
}
