﻿import { Component, OnInit, Input, Output, EventEmitter, ElementRef, ViewChild } from "@angular/core"
import { ActivatedRoute, Router, Data } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LayoutService } from "idun-layout/layout.service"
import { LanguageService } from "idun-layout/language.service"
import { ExtendedService } from "idun-common/extended-service"
import { IdunModel } from "idun-common/models/idun-model"
import { ModelService } from "idun-common/model.service"
import { RouteParameterComponent } from "./route-parameter-component"
import { AutoFormInfo } from "idun-common/auto-form-info"
import { FormRow } from "idun-layout/form/form-row"
import { Colour } from "utility/colour"
import { DomSanitizer } from "@angular/platform-browser"

@Component({
	selector: "idun-auto-form",
	templateUrl: "./auto-form.html",
	styleUrls: ["./auto-form.component.scss"]
})
export class AutoFormComponent<Model extends IdunModel> extends RouteParameterComponent implements OnInit {
	@Input() modelType: ExtendedService<any>
	@Input() model: Model
	@Input() allowEdit = true
	@Input() allowDelete = true
	@Input() rowGroups: { getHeader?: (model) => string, headerHelp?: () => string, rows: FormRow[], hidden?: boolean }[] = []
	@Input() popup: { getHeader?: (model) => string, onClose: (component) => void, rows: FormRow[] }
	@Input() showPopup = false
	@Input() showNavigation = true
	@Input() helpText: () => string
	@Input() icon: string
	@Input() color: number | string
	@Input() showContent = true

	defaultGetHeader = (model: Model) => ""
	@Input() getHeader = this.defaultGetHeader
	defaultOnUpdate = (model: Model, component: AutoFormComponent<Model>) => this._onUpdate(model)
	@Input() onUpdate = this.defaultOnUpdate
	defaultOnSubmit = (model: Model, component: AutoFormComponent<Model>) => this._onSubmit()
	@Input() onSubmit = this.defaultOnSubmit
	defaultOnCancel = (component: AutoFormComponent<Model>) => this._onCancel()
	@Input() onCancel = this.defaultOnCancel
	defaultOnDelete = (component: AutoFormComponent<Model>) => this._onDelete()
	@Input() onDelete = this.defaultOnDelete
	@Input() onClose: (component: AutoFormComponent<Model>) => void

	@Output() update = new EventEmitter()
	@Output() submit = new EventEmitter()
	@Output() cancel = new EventEmitter()
	@Output() delete = new EventEmitter()
	@Output() load = new EventEmitter()
	@Output() displayShadower = new EventEmitter()

	navigater: NavigationHandler
	currentFormInfo: AutoFormInfo<Model>
	service: ExtendedService<Model>
	error: string
	isValid = false
	@Input() display = false
	skipLoad = false
	isDeleting = false
	isSaving = false
	saveText: string
	original: Model
	hiddenGroups: boolean[]
	showGroupHelp: boolean[] = []

	@ViewChild("modelForm") modelForm

	get isSubComponent() {
		return !!this.modelType
	}

	editButton = [
		{
			icon: "Edit",
			show: () => this.allowEdit && this.display && this.service.hasEditPermission(),
			action: () => this.switchToEdit()
		},
		{
			icon: "ArrowLeft",
			show: () => this.shouldShowArrowLinks(),
			action: () => this.navigater.left(this.service.ngUrl + "/list"),
			mouseover: () => this.layoutService.showShadowOverlays("left"),
			mouseout: () => this.layoutService.hideShadowOverlays("left")
		},
		{
			text: () => this.service ? this.service.translations().modelList : "",
			show: () => this.service && this.service.showListLink,
			action: () => this.navigater.current(this.service.ngUrl + "/list"),
			mouseover: () => this.layoutService.showShadowOverlays("middle"),
			mouseout: () => this.layoutService.hideShadowOverlays("middle"),
			groupWithPrevious: true
		},
		{
			icon: "ArrowRight",
			show: () => this.shouldShowArrowLinks(),
			action: () => this.navigater.right(this.service.ngUrl + "/list"),
			mouseover: () => this.layoutService.showShadowOverlays("right"),
			mouseout: () => this.layoutService.hideShadowOverlays("right"),
			groupWithPrevious: true
		}
	]

	private shouldShowArrowLinks() {
		return !this.layoutService.sizeController.isSinglePageLayout
			&& this.service
			&& this.service.showListLink
	}

	constructor(
		public route: ActivatedRoute,
		public router: Router,
		public myElement: ElementRef,
		public layoutService: LayoutService,
		public modelService: ModelService,
		public l: LanguageService,
		private sanitizer: DomSanitizer
	) {
		super()
		this.parameterHandlers["id"] = (val) => { this.handleIDUpdate(val) }
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit(): void {
		if (this.model !== undefined)
			this.skipLoad = true
		if (this.modelType) {
			this.setupService(this.modelType)
			this.onUpdate(this.model, this)
			this.handleRouteParameters(this.route)
		}
		else
			this.route.data.subscribe((value: Data) => {
				this.setupService(value["model"])
				this.onUpdate(this.model, this)
				this.handleRouteParameters(this.route)
			})
	}

	setupService(modelType: string | ExtendedService<Model>) {
		const service = typeof modelType === "string" ? this.modelService[modelType] : modelType || this.service
		if (!(service instanceof ExtendedService))
			return console.error("trying to use autoform with baseservice")
		this.service = service
		this.setupFrom(this.service.formInfo, service.icon, service.color)
	}

	setupFrom(formInfo: AutoFormInfo<Model>, icon?: string, color?: number | string) {
		this.currentFormInfo = formInfo
		this.rowGroups = formInfo.rowGroups
		this.hiddenGroups = []
		for (const n in this.rowGroups)
			this.hiddenGroups[n] = false
		const options = formInfo.options
		if (options)
			this.popup = options.popup
		for (const i in options.parameterHandlers)
			this.parameterHandlers[i] = options.parameterHandlers[i]
		this.finalHandler = options.finalHandler
		if (formInfo.getHeader && this.getHeader === this.defaultGetHeader)
			this.getHeader = formInfo.getHeader
		if (options.onUpdate && this.onUpdate === this.defaultOnUpdate)
			this.onUpdate = options.onUpdate
		if (options.onSubmit && this.onSubmit === this.defaultOnSubmit)
			this.onSubmit = options.onSubmit
		if (options.onCancel && this.onCancel === this.defaultOnCancel)
			this.onCancel = options.onCancel
		if (options.onDelete && this.onDelete === this.defaultOnDelete)
			this.onDelete = options.onDelete
		if (options.allowEdit !== undefined)
			this.allowEdit = options.allowEdit
		if (options.allowDelete !== undefined)
			this.allowDelete = options.allowDelete
		if (options.helpText !== undefined)
			this.helpText = options.helpText
		if (icon !== undefined)
			this.icon = icon
		if (color !== undefined)
			this.color = color
	}

	public handleIDUpdate(id: any) {
		if (!this.isID(id)) {
			if (id === "add") {
				this.model = this.service.getNewModel()
				this.original = this.service.getNewModel()
				this.update.next(this.original)
			}
			if (!this.hasID())
				this.switchToEdit()
			return
		}
		for (const i in this.rowGroups) {
			this.hiddenGroups[i] = this.rowGroups[i].hidden
		}
		if (this.skipLoad) {
			this.onUpdate(this.model, this)
			return
		}
		let firstResponse = true
		this.subscribe(this.service.getModel$(+id), (model: Model) => {
			if (firstResponse || this.display) {
				firstResponse = false
				this.switchToDisplay()
				this.onUpdate(model, this)
			}
		}, "model")
	}

	hasID() {
		return this.model && this.model.DBID
	}

	isID(id: any) {
		return id > 0
	}

	_onUpdate(model?: Model) {
		if (model) {
			this.model = new this.service.modelClass(model)
			this.original = model
		}
		else {
			this.model = this.service.getNewModel()
			this.original = this.service.getNewModel()
		}
		this.validate()
		this.update.next(this.original)
	}

	_onSubmit() {
		this.validate()
		if (!this.isValid)
			return
		this.isSaving = true
		let promise: Promise<any>
		if (this.model.DBID)
			promise = this.service.update(this.model)
		else
			promise = this.service.add(this.model)
		return promise.then((model: Model) => {
			this.onUpdate(model, this)
			this.error = undefined
			this.isSaving = false
			this.switchToDisplay()
			this.submit.next(this.model)
			if (!this.isSubComponent)
				this.navigater.current(this.service.ngUrl + "/" + model.DBID)
			return this.model
		}).catch((reason: any) => {
			this.isSaving = false
			this.error = reason["_body"]
			this.switchToEdit()
		})
	}

	_onCancel() {
		this.switchToDisplay()
		this.model = new this.service.modelClass(this.original)
		this.cancel.next(this.original)
	}

	_onDelete() {
		this.isDeleting = true
		this.service.delete(this.model.DBID).catch((reason: string) => {
			this.error = reason
		}).then((blockers: any[]) => {
			if (!blockers) {
				this.delete.next(this.model)
			}
			else {
				this.error = "This cannot be deleted, as other things in the system depend on it!"
				this.isDeleting = false
			}
		})
	}

	cancelDelete() {
		this.service.cancelDelete(this.model.DBID).catch((reason: string) => {
			this.error = reason
		}).then(() => {
			this.isDeleting = false
		})
	}

	closeDeleteBox() {
		this.navigater.current("home")
	}

	validate() {
		this.updateRowValidities()
		this.updateValidity()
	}

	updateRowValidities() {
		for (const group of this.rowGroups)
			for (const row of group.rows)
				row.validate(this.model)
	}

	updateValidity() {
		this.isValid = true
		for (const group of this.rowGroups)
			for (const row of group.rows)
				if (row.error)
					return this.isValid = false
	}

	switchToEdit(): void {
		this.model = new this.service.modelClass(this.original)
		this.display = false
		this.displayShadower.emit(this.display)
	}

	switchToDisplay(): void {
		this.model = new this.service.modelClass(this.original)
		this.display = true
		this.displayShadower.emit(this.display)
	}

	toggleGroup(i: number) {
		this.hiddenGroups[i] = !this.hiddenGroups[i]
	}

	get headerColor() {
		const color = +this.color ? +this.color : this.color
		if (typeof color === "string")
			return this.sanitizer.bypassSecurityTrustStyle(color)
		return Colour.ToRgbaString(Colour.HSLtoRGB(color === undefined ? 0.55 : color, 0.26, 0.40), 1)
	}
}
