import { Component, Input, Output, ElementRef, EventEmitter } from "@angular/core"
import { Router } from "@angular/router"
import { NavigationHandler } from "../navigation-handler"
import { LayoutService } from "../layout.service"

@Component({
	selector: "idun-list-entry",
	templateUrl: "list-entry.component.html",
	styleUrls: ["list-entry.component.scss"]
})
export class ListEntryComponent {
	navigater: NavigationHandler

	@Input() destination: string
	@Input() highlight = false
	@Input() danger = false
	@Output() click = new EventEmitter()

	side = "middle"
	hover = { right: "out", left: "out", middle: "out" }

	constructor(
		private myElement: ElementRef,
		private router: Router,
		public layoutService: LayoutService
	) {
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	onContentClick() {
		if (this.destination)
			this.navigater.main(this.destination)
		// we don't emit a click event, because it's already done automatically and would thus result in two
	}

	clickLeft() {
		if (this.destination)
			this.navigater.left(this.destination)
		this.click.emit()
	}

	clickRight() {
		if (this.destination)
			this.navigater.right(this.destination)
		this.click.emit()
	}

	mouseover(side, event) {
		if (event)
			event.preventDefault()
		this.side = side
		this.hover[side] = "over"
		this.layoutService.showShadowOverlays(side)
	}

	mouseout(side, event) {
		if (event)
			event.preventDefault()
		this.hover[side] = "out"
		this.layoutService.hideShadowOverlays(side)
	}

	get hoverclass() {
		if (this.hover["left"] === "over")
			return "left"
		if (this.hover["right"] === "over")
			return "right"
		if (this.hover["middle"] === "over")
			return "middle"
		return ""
	}

	showArrows() {
		return this.destination && !this.layoutService.sizeController.isSinglePageLayout
	}
}
