﻿import { Component, forwardRef, Input, OnInit, OnDestroy } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { Subscriber } from "utility/subscriber"
import { FormRow } from "idun-layout/form/form-row"
import { LanguageService } from "idun-layout/language.service"
import { Procedure, ProcedureVariation } from "idun-common/models/procedure"
import { ProcedureSetup, PriceVariation } from "idun-common/models/partner"
import { DisplaySetup, Group } from "idun-common/filter-list.component"
import { UserService } from "idun-main/user.service"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { ProcedureTypeService } from "idun-main/model-services/procedure-type.service"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormProcedureChangesComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormProcedureChangesComponent),
	multi: true
}

@Component({
	selector: "idun-form-procedure-changes",
	templateUrl: "form-procedure-changes.component.html",
	styleUrls: ["form-procedure-changes.component.scss"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormProcedureChangesComponent
	extends FormBasicComponent<ProcedureSetup[]>
	implements ControlValueAccessor, OnDestroy, OnInit {
	rowClass = FormProcedureChangesRow
	subscriber = new Subscriber()

	_display = false
	@Input() set display(value: boolean) {
		if (this._display !== !!value)
			this.deselectProcedure()
		this._display = value
	}
	get display() {
		return this._display
	}

	procedures: Procedure[]
	supportedProcedures: Procedure[]
	unsupportedProcedures: Procedure[]
	selectedProcedureId: number
	selectedEntry: ProcedureSetup
	currency = "DKK"

	listDisplaySetup = new DisplaySetup(
		() => this.l.procedureType.namePlural,
		() => this.l.procedure.namePlural
	)

	listGroups: Group<any>[] = []
	listGroupsForUnsupported: Group<any>[] = []

	constructor(
		public l: LanguageService,
		public userService: UserService,
		public procedureService: ProcedureService,
		public procedureTypeService: ProcedureTypeService
	) { super() }

	ngOnInit() {
		this.userService.clinicSettings$.subscribe((settings) => {
			this.currency = settings.Currency
		})
		this.subscriber.subscribe(this.procedureService.getModels$(), (models) => {
			if (!this.procedures)
				this.splitProcedures(models)
			this.procedures = models
		})
		this.subscriber.subscribe(this.procedureTypeService.getModels$(), (models) => {
			this.listGroupsForUnsupported = models.map(x => {
				return {
					title: () => x.getTitle(),
					contains: (entry) => entry.ProcedureTypeID === x.DBID
				}
			})
			this.listGroups = this.listGroupsForUnsupported.map(e => e)
			this.addPriceTypeListGroups()
		})
	}

	addPriceTypeListGroups() {
		this.listGroups.unshift({
			title: () => this.l.partner.discountPrices,
			contains: (entry) => this.usesBasePrice(entry)
		})
		this.listGroups.unshift({
			title: () => this.l.partner.changedPrices,
			contains: (entry) => this.usesChangedPrice(entry)
		})
	}

	usesBasePrice(procedure: Procedure) {
		const setup = this.value.find(e => e.ProcedureID === procedure.DBID)
		if (!setup)
			return true
		if (!setup.Supported)
			false
		return !setup.Variations.find(e => e.hasPrice())
	}

	usesChangedPrice(procedure: Procedure) {
		const setup = this.value.find(e => e.ProcedureID === procedure.DBID)
		if (!setup || !setup.Supported)
			return false
		return !!setup.Variations.find(e => e.hasPrice())
	}

	ngOnDestroy() {
		this.subscriber.unsubscribe()
	}

	onValueChange() {
		if (this.procedures)
			this.splitProcedures(this.procedures)
	}

	splitProcedures(procedures: Procedure[]) {
		if (!this.value)
			return
		this.supportedProcedures = []
		this.unsupportedProcedures = []
		const unsupportedProcedureIds = this.value
			.filter(x => !x.Supported)
			.map(x => x.ProcedureID)
		for (const procedure of procedures) {
			if (unsupportedProcedureIds.includes(procedure.DBID))
				this.unsupportedProcedures.push(procedure)
			else
				this.supportedProcedures.push(procedure)
		}
	}

	findSupportedProcedures(procedures: Procedure[]) {
		return procedures.filter(procedure => {
			const procedureSetup = this.value.find(x => x.ProcedureID === procedure.DBID)
			if (procedureSetup)
				return procedureSetup.Supported
			return true
		})
	}

	findUnsupportedProcedures(procedures: Procedure[]) {
		return procedures.filter(procedure => {
			const procedureSetup = this.value.find(x => x.ProcedureID === procedure.DBID)
			if (procedureSetup)
				return !procedureSetup.Supported
			return false
		})
	}

	selectProcedure(id: number) {
		if (this.selectedProcedureId === id)
			return this.deselectProcedure()
		this.selectedProcedureId = id
		this.selectedEntry = this.entryFor(this.selectedProcedureId)
		if (!this.selectedEntry) {
			this.selectedEntry = new ProcedureSetup({ ProcedureID: id })
			this.value.push(this.selectedEntry)
		}
	}

	deselectProcedure() {
		this.selectedProcedureId = undefined
		this.selectedEntry = undefined
	}

	changedTimeFor(model: Procedure) {
		const entry = this.entryFor(model.DBID)
		if (entry && entry.ChangedTime)
			return entry.ChangedTime
		return ""
	}

	composeListEntryTitle(model: Procedure) {
		const entry = this.entryFor(model.DBID)
		if (entry && !entry.Supported)
			return this.l.partner.notAllowedForThisAgreement
		if (entry)
			return this.pricesString(model.DBID, entry.Variations)
		return this.pricesString(model.DBID)
	}

	pricesString(procedureId: number, priceVariations: PriceVariation[] = []): string {
		const procedure = this.procedureService.getModel(procedureId)
		return procedure.Variations
			.map((variation, i) => this.priceString(variation, priceVariations[i]))
			.join(", ")
	}

	priceString(procedureVariation: ProcedureVariation, priceVariation?: PriceVariation) {
		if (!priceVariation || !priceVariation.Price)
			return `${(+procedureVariation.Price * (1 - this.model.PercentageDiscount / 100)).toFixed(2)} ${this.currency}`
		return `${(+priceVariation.Price).toFixed(2)} ${this.currency}`
	}

	entryFor(id: number): ProcedureSetup {
		if (!this.value)
			return
		return this.value.find(e => e.ProcedureID === id)
	}
}

export class FormProcedureChangesRow extends FormRow {
	cssClasses = "no-padding"
}
