import { Component, OnInit, ElementRef, Output, EventEmitter, Input } from "@angular/core"
import { Router } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LanguageService } from "idun-layout/language.service"
import { LayoutService } from "idun-layout/layout.service"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { UserService } from "idun-main/user.service"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { ProcedureVariationPair } from "idun-main/page-components/booking/select-variant-by-placement.component"
import { PartnerService } from "idun-main/model-services/partner.service"
import { TreatmentCodeService } from "idun-main/model-services/treatment-code.service"
import { ResourceService } from "idun-main/model-services/resource.service"
import { StaffService } from "idun-main/model-services/staff.service"
import { ProcedureEventAnalysis, ProcedureEventAnalysisService } from "idun-main/other-services/procedure-event-analysis.service"
import { PatientEventCreationService } from "idun-main/other-services/patient-event-creation.service"

@Component({
	selector: "idun-procedure-event-box",
	templateUrl: "procedure-event-box.component.html",
	styleUrls: ["booking-page.component.scss"]
})
export class ProcedureEventBoxComponent extends BaseSubscriber implements OnInit {
	navigater: NavigationHandler

	@Input() display = false
	_procedureEvent: ProcedureEvent
	@Input() set procedureEvent(value: ProcedureEvent) {
		if (this.procedureEvent === value)
			return
		this._procedureEvent = value
		this.updateTitle()
		this.updateAnalysis()
	}
	get procedureEvent() {
		return this._procedureEvent
	}
	@Input() showNavigation = false
	@Input() allowDelete = false
	@Input() requirementsArea: AreaState = "display"
	@Input() procedureArea: AreaState = "display"
	@Input() priceArea: AreaState = "display"
	@Output() accept = new EventEmitter()
	@Output() delete = new EventEmitter()

	title = ""
	currency = "DKK"
	analysis: ProcedureEventAnalysis

	constructor(
		router: Router,
		myElement: ElementRef,
		layoutService: LayoutService,
		private procedureEventAnalysisService: ProcedureEventAnalysisService,
		private patientEventCreationService: PatientEventCreationService,
		private userService: UserService,
		public procedureService: ProcedureService,
		public partnerService: PartnerService,
		public staffService: StaffService,
		public resourceService: ResourceService,
		public treatmentCodeService: TreatmentCodeService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit(): void {
		this.userService.clinicSettings$.subscribe((settings) => {
			this.currency = settings.Currency
		})
	}

	showArea(area: AreaState) {
		return area !== "hide"
	}

	displayFor(area: AreaState) {
		return area !== "edit" || this.display
	}

	updateAnalysis() {
		this.analysis = this.procedureEventAnalysisService.analyse(this.procedureEvent)
	}

	selectProcedureVariation(selected: ProcedureVariationPair) {
		this.procedureEvent.ProcedureId = selected.procedure.DBID
		this.procedureEvent.Variation = selected.variation.Title
		this.procedureEvent.TreatmentCodeIds = selected.variation.ModelIDs.map(e => e)
		this.procedureEvent.Price = selected.variation.Price
		this.updateTitle()
		this.updateAnalysis()
	}

	updatePartner(newPartnerId: number) {
		this.procedureEvent.PartnerId = newPartnerId
		this.patientEventCreationService.updatePriceAndCodesOf(this.procedureEvent)
		this.updateAnalysis()
	}

	private async updateTitle() {
		if (!this.procedureEvent || !this.procedureEvent.ProcedureId)
			return this.title = ""
		const procedure = await this.procedureService.model(this.procedureEvent.ProcedureId)
		this.title = procedure.Variations.length > 1
			? procedure.Title + " - " + this.procedureEvent.Variation
			: procedure.Title
	}

	acceptProcedureEvent() {
		this.accept.emit()
	}

	removeProcedureEvent() {
		this.delete.emit()
	}
}

type AreaState = "hide" | "display" | "edit"
