import { Injectable, NgZone } from "@angular/core"
import { Observable, BehaviorSubject, Subject } from "rxjs"
import { LanguageService } from "./language.service"
import { SizeController, ScreenSpace } from "./layout-size"
import { ColorController } from "./layout-color"
import { ErrorController } from "./layout-error"
import { NavigationController } from "./layout-navigation"
import { Router, NavigationEnd } from "@angular/router"
import { Title } from "@angular/platform-browser"
import { IdunLayoutComponent } from "./idun-layout.component"

const maximisableComponents = []
export function Maximisable(target) {
	maximisableComponents.push(target)
}

@Injectable()
export class LayoutService {
	sizeController = new SizeController(this)
	colorController = new ColorController()
	errorController = new ErrorController(this)
	navigationController = new NavigationController(this)

	private _maximised = false
	set maximised(value) {
		if (this._maximised === value) return
		this._maximised = value
		this.maximised$.next(value)
		this.layoutComponent.updateLayout()
	}
	get maximised() {
		return this._maximised
	}
	maximised$ = new Subject<boolean>()

	private isMaximisedViewPossible(): boolean {
		const screenSpace = this.sizeController.screenSpace
		return !this.navigationController.twoPagesOpen
			&& (screenSpace === ScreenSpace.TwoPages || screenSpace === ScreenSpace.Full)
	}

	layoutComponent: IdunLayoutComponent

	addBookmark
	removeBookmark
	set bookmarks(value: string[]) {
		this._bookmarks$.next(value)
	}
	_bookmarks$: BehaviorSubject<string[]>
	get bookmarks$(): Observable<string[]> {
		return this._bookmarks$.asObservable()
	}

	private shadowOverlays = { right: false, left: false, middle: false }

	constructor(
		public ngZone: NgZone,
		public router: Router,
		public titleService: Title,
		public l: LanguageService
	) {
		this._bookmarks$ = new BehaviorSubject<string[]>([])
		this.registerVarious()
		this.navigationController.twoPagesOpen$.subscribe(() => {
			this.updateMaximisation()
		})
		this.router.events.subscribe(event => {
			if (event instanceof NavigationEnd)
				this.updateMaximisation()
		})
	}

	updateMaximisation() {
		this.maximised = this.shouldMaximiseView()
	}

	private shouldMaximiseView(): boolean {
		const children = this.router.routerState.root.children
		return children.length === 1
			&& maximisableComponents.includes(children[0].component)
			&& this.isMaximisedViewPossible()
	}

	showMobileMenu() {
		return this.sizeController.screenSpace === ScreenSpace.Mobile
			|| this.sizeController.screenSpace === ScreenSpace.OnePage
	}

	registerVarious() {
		window["idunLayout"] = this
		window["idunLanguage"] = this.l
		window["oversæt"] = (original, translation) => { this.ngZone.run(() => this.l.dictionary[original] = translation) }
		window["downloadSprogfil"] = () => {
			let text = "{\n"
			for (const i of Object.keys(this.l.dictionary).sort())
				text += "\"" + i + "\": " + JSON.stringify(this.l.dictionary[i]) + ",\n"
			text = text.substr(0, text.length - 2) + "\n}"
			const blob = new Blob([text], { type: "text/plain;charset=utf-8" })

			const a = document.createElement("a")
			a.download = this.l.language + ".json"
			a.href = URL.createObjectURL(blob)
			document.body.appendChild(a)
			a.click()
			document.body.removeChild(a)
		}
		window["indlæsSprogfil"] = (data) => { this.ngZone.run(() => this.l.dictionary = JSON.parse(data)) }
	}

	resetShadowOverlays() {
		this.shadowOverlays.left = false
		this.shadowOverlays.middle = false
		this.shadowOverlays.right = false
	}

	showShadowOverlays(side: string) {
		this.shadowOverlays[side] = true
	}

	hideShadowOverlays(side: string) {
		this.shadowOverlays[side] = false
	}

	showOverlay(overlay: string): boolean {
		const twoPagesOpen = this.navigationController.twoPagesOpen
		switch (overlay) {
			case "outer-left":
				return this.shadowOverlays.left && !twoPagesOpen
			case "left":
				return this.shadowOverlays.left && twoPagesOpen
			case "outer-right":
				return this.shadowOverlays.right && !twoPagesOpen
			case "right":
				return (this.shadowOverlays.middle && !twoPagesOpen)
					|| (this.shadowOverlays.right && twoPagesOpen)
			case "both":
				return this.shadowOverlays.middle && twoPagesOpen
		}
		return false
	}
}
