import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { Observable } from "rxjs"
import { ExtendedService } from "idun-common/extended-service"
import { LanguageService } from "idun-layout/language.service"
import { Status } from "idun-common/models/status"

@Injectable()
export class StatusRequestService extends ExtendedService<Status> {
	constructor(
		http: Http,
		l: LanguageService
	) {
		super(http, l,
			Status,
			"api/StatusRequest",
			"status-request",
			() => l.statusRequest
		)
	}

	getStatusRequestsList(staffID: number, start: Date, end: Date): Promise<Observable<Status[]>> {
		return this.baseGetSubList("GetStatusList", { id: staffID, start: start, end: end })
	}

	implementRequest(id: number): Promise<boolean> {
		return this.baseRequest("Implement", { id: id }).then((success) => {
			if (!success)
				return success
			for (let i = 0; this.models.length; i++) {
				if (+this.models[i].DBID === +id) {
					this.models.splice(i, 1)
					this.updateObservables()
					break
				}
			}
			return success
		})
	}
}
