﻿import { Component, Input, Output, EventEmitter, TemplateRef, HostBinding } from "@angular/core"
import { search } from "utility/search-helper"
import { SearchableIdunModel } from "./models/idun-model"
import { LanguageService } from "idun-layout/language.service"
import { ExtendedService } from "./extended-service"

export class DisplaySetup {
	constructor(
		public groupNamePlural: () => string,
		public entryNamePlural: () => string,
		public groupAddLink?: () => string,
		public entryAddLink?: () => string
	) { }
}

@Component({
	selector: "idun-filter-list",
	templateUrl: "filter-list.html",
	styleUrls: ["filter-list.scss"]
})
export class FilterListComponent<Entry extends SearchableIdunModel> {
	@Input("entries") set _entries(entries: Entry[]) {
		this.entries = entries
		this.updateList()
	}
	@Input() totalEntryCount = 0
	get totalCount() {
		return this.totalEntryCount || this.entries.length
	}
	@Input() groups: Group<Entry>[]
	@Input() entryService: ExtendedService<Entry>
	@Input() showHeader = true
	@Input() showSearch = true
	@Input() useGroupBar = true
	@Input() displaySetup: DisplaySetup
	@Input() listEntryTemplate: TemplateRef<any>

	@Output() queryChange = new EventEmitter<string>()
	@Output() scroll = new EventEmitter()

	@HostBinding("style.grid-template-areas") get gridStyle() {
		if (this.useGroupBar)
			return `"groups search" "groups entries"`
		return `"search search" "entries entries" "groups groups"`
	}

	query = ""
	selectedGroup: Group<Entry>
	entries: Entry[]
	shownEntries: Entry[]

	constructor(
		public l: LanguageService
	) { }

	updateQuery(query?: string) {
		if (typeof query === "string")
			this.query = query
		this.query = this.query.trim()
	}

	updateSearch(query?: string) {
		this.updateQuery(query)
		this.queryChange.emit(this.query)
		return this.updateList()
	}

	updateList() {
		if (!this.entries)
			return this.shownEntries = undefined
		this.shownEntries = search<Entry>(this.query, <any>this.entries, true)
			.filter(e => !this.selectedGroup || this.selectedGroup.contains(e))
	}
}

export interface Group<T> {
	title(): string
	contains(entry: T): boolean
	link?(): { title: string, destination: string }
}
