﻿import { Component, Input, Output, EventEmitter } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { DateHelper, newResetDateFrom } from "utility/date-helper"
import { CalendarEntry } from "./calendar-entry"
import { CalendarEntryProvider } from "./calendar-entry-provider"

@Component({
	selector: "idun-calendar",
	templateUrl: "calendar.component.html",
	styleUrls: ["calendar.component.scss"]
})
export class CalendarComponent {
	private _date = new Date()
	@Input() set date(value: Date) {
		this._date = DateHelper.newDate(value, { reset: true })
	}
	get date(): Date {
		return this._date
	}
	@Input() mode: "day" | "week" = "day"
	@Input() entryProviders: CalendarEntryProvider[]
	@Input() title: string
	@Input() subtitle: string
	@Input() helpText = ""
	@Input() showNavigation = true
	@Input() showNavigationBar = true
	@Input() showPopup = false
	@Input() showDate = true
	@Input() hourHeight = 6
	@Input() days = 14
	@Input() icon: string
	@Input() color: number | string
	@Input() loading = false
	@Input() showMaximised = false

	@Output() navigate = new EventEmitter<Date>()
	@Output() addEntry = new EventEmitter<{ provider: CalendarEntryProvider, time: Date }>()
	@Output() selectEntry = new EventEmitter<{ provider: CalendarEntryProvider, entry: CalendarEntry }>()
	@Output() closeEntry = new EventEmitter()
	@Output() close = new EventEmitter()

	hasCloseHandler() {
		return this.close.observers.length > 0
	}

	constructor(public l: LanguageService) { }

	getTitle() {
		let title = this.l.date[DateHelper.monthNames[this.date.getMonth()]] + " - " + this.date.getFullYear()
		if (this.mode === "day")
			title = this.date.getDate() + " " + title
		return title
	}

	navigateForward() {
		this.navigate.emit(this.nextDate())
	}

	private nextDate() {
		switch (this.mode) {
			case "day":
				return newResetDateFrom({ days: 1 })
			case "week":
				return newResetDateFrom({ days: this.showMaximised ? 14 : 7 })
		}
	}

	navigateBackward() {
		this.navigate.emit(this.previousDate())
	}

	private previousDate() {
		switch (this.mode) {
			case "day":
				return newResetDateFrom({ days: -1 })
			case "week":
				return newResetDateFrom({ days: this.showMaximised ? -14 : -7 })
		}
	}
}
