import { Component, OnInit, ElementRef, HostBinding } from "@angular/core"
import { Router, ActivatedRoute } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { RouteParameterComponent } from "idun-common/route-parameter-component"
import { LanguageService } from "idun-layout/language.service"
import { LayoutService, Maximisable } from "idun-layout/layout.service"
import { TimeInterval } from "idun-common/models/time-interval"
import { PeriodPlan, PlanningRole } from "idun-common/models/period-plan"
import { PeriodPlanService } from "../../model-services/period-plan.service"
import { StaffService } from "../../model-services/staff.service"
import { ResourceService } from "../../model-services/resource.service"
import { Resource } from "idun-common/models/resource"
import { PeriodPlanStaffRoleProvider } from "./period-plan-staff-role-provider"
import { ResourceUtilisationProvider } from "./resource-utilisation-provider"
import { ColorThing } from "./color-thing.component"
import { StaffGroupService } from "../../model-services/staff-group.service"
import { ProviderGroup } from "idun-calendar/multi-calendar.component"
import { TimeIntervalSeries } from "idun-common/models/time-interval-series"
import { PeriodPlanEventsProvider, PeriodPlanEventsProviderTarget } from "./period-plan-events-provider"
import { CalendarEvent } from "idun-common/models/calendar-event"
import { BasicCalendarEntry } from "idun-calendar/basic-calendar-entry"
import { OptionInfo } from "idun-layout/form/form-row"
import { ResourceUtilisationService } from "./resource-utilisation.service"

@Maximisable
@Component({
	selector: "idun-planning-calendar",
	templateUrl: "planning-calendar.component.html",
	styleUrls: ["planning-calendar.component.scss"]
})
export class PlanningCalendarComponent extends RouteParameterComponent implements OnInit {
	navigater: NavigationHandler
	display = true

	time = new Date(1900, 0, 1)
	staffProviderGroup = new ProviderGroup(() => this.l.staff.namePlural)
	resourceProviderGroup = new ProviderGroup(() => this.l.resource.namePlural)
	providerGroups: ProviderGroup[] = []
	loading = true

	selectedStaffID: number
	original: PeriodPlan
	plan: PeriodPlan

	selectedStaff: number
	selectedStaffGroup: number
	selectRoleType: "empty" | "fromStaff" | "fromStaffGroup" = "empty"

	selectedRole: PlanningRole
	selectedEvent: CalendarEvent
	colorThing = new ColorThing()
	optionsForRoles: OptionInfo[] = []

	dayIsSelected: boolean
	@HostBinding("class.maximised") get classA() {
		return this.dayIsSelected && this.layoutService.maximised
	}
	@HostBinding("class.non-maximised") get classB() {
		return !this.dayIsSelected && this.layoutService.maximised
	}

	editButton = [
		{
			icon: "Edit",
			show: () => this.display && this.periodPlanService.hasEditPermission(),
			action: () => this.switchToEdit()
		},
		{
			icon: "ArrowLeft",
			show: () => true,
			action: () => this.navigater.left(this.periodPlanService.ngUrl + "/list")
		},
		{
			text: () => this.periodPlanService.translations().modelList,
			show: () => true,
			action: () => this.navigater.main(this.periodPlanService.ngUrl + "/list"),
			groupWithPrevious: true
		},
		{
			icon: "ArrowRight",
			show: () => true,
			action: () => this.navigater.right(this.periodPlanService.ngUrl + "/list"),
			groupWithPrevious: true
		}
	]

	constructor(
		myElement: ElementRef,
		router: Router,
		private route: ActivatedRoute,
		public layoutService: LayoutService,
		public staffService: StaffService,
		public staffGroupService: StaffGroupService,
		public resourceService: ResourceService,
		public periodPlanService: PeriodPlanService,
		private resourceUtilisationService: ResourceUtilisationService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
		this.providerGroups[0] = this.staffProviderGroup
		this.providerGroups[1] = this.resourceProviderGroup
	}

	ngOnInit(): void {
		this.parameterHandlers.id = (planId: string) => {
			if (+planId)
				this.loadForId(+planId)
			else if (planId === "add")
				this.createNewEmptyPlan()
			else if (planId === "generate")
				this.createNewGeneratedPlan()
		}
		this.handleRouteParameters(this.route)
		this.subscribe(
			this.resourceUtilisationService.resourceDataChanged$,
			ready => {
				if (!ready)
					return
				this.updateProviders()
				this.loading = false
			})
		this.colorThing.addMode("resources", () => this.l.resource.namePlural, [
			{ hue: 120, title: () => this.l.periodPlan.allProceduresAvailable },
			{ hue: 60, title: () => this.l.periodPlan.notAllProceduresAvailable },
			{ hue: 0, title: () => this.l.periodPlan.notEnoughStaffAvailable }
		])
		this.colorThing.addMode("staff roles", () => this.l.staff.staffRoles, [
			{ hue: 120, title: () => this.l.periodPlan.availableForProcedures },
			{ hue: 60, title: () => this.l.periodPlan.otherTask },
			{ hue: 0, title: () => this.l.periodPlan.notAtWork }
		])
		this.colorThing.setMode("resources")
	}

	private loadForId(planId: number) {
		this.subscribeToItem(this.periodPlanService, planId, model => {
			this.updatePlan(model)
		}, "model")
	}

	private updatePlan(model: PeriodPlan) {
		this.original = model
		this.plan = new PeriodPlan(model)
		this.updateProviders()
	}

	updateProviders() {
		this.resetProviders()
		if (!this.plan)
			return
		const resourceProviders = this.resourceUtilisationService.createResourceProviders(
			this.plan.StaffRoles,
			this.availableTimesForRole,
			this.occupiedTimesForResource,
		)
		for (const resourceId in resourceProviders)
			this.addResourceProvider(+resourceId, resourceProviders[resourceId])
		for (const role of this.plan.StaffRoles)
			this.addRoleProvider(role)
		this.optionsForRoles = this.plan.StaffRoles
			.map(e => new OptionInfo(<any>e.Index, e.Title))
	}

	private resetProviders() {
		this.staffProviderGroup.resetProviders()
		this.resourceProviderGroup.resetProviders()
	}

	private availableTimesForRole = (role: PlanningRole) => {
		const worktimes = new TimeIntervalSeries(...role.Intervals)
		const intervals = this.plan.Events
			.filter(x => x.OccupiedStaff.includes(role.Index))
			.map(x => x.Times)
		return worktimes.subtract(new TimeIntervalSeries(...intervals))
	}

	private occupiedTimesForResource = (resource: Resource) => {
		return new TimeIntervalSeries(
			...this.plan.Events
				.filter(e => e.OccupiedResources.includes(resource.DBID))
				.map(e => e.Times))
	}

	private addResourceProvider(
		resourceId: number,
		provider: ResourceUtilisationProvider<PlanningRole>
	) {
		this.resourceProviderGroup.addProvider(
			provider
		)
		this.resourceProviderGroup.addProvider(
			PeriodPlanEventsProvider.forResource(
				resourceId,
				this.plan.Events,
				() => this.display,
				() => this.updateProviders())
		)
	}

	private addRoleProvider(role: PlanningRole) {
		this.staffProviderGroup.addProvider(
			new PeriodPlanStaffRoleProvider(
				role,
				() => this.display,
				() => this.updateProviders())
		)
		this.staffProviderGroup.addProvider(
			PeriodPlanEventsProvider.forRole(
				role.Index,
				this.plan.Events,
				() => this.display,
				() => this.updateProviders())
		)
	}

	private createNewEmptyPlan() {
		this.plan = new PeriodPlan({ Title: "New Plan", Weeks: 1 })
		this.original = new PeriodPlan({ Title: "New Plan", Weeks: 1 })
		this.display = false
		this.updateProviders()
	}

	private createNewGeneratedPlan() {
		// TODO: #1859
		this.plan = new PeriodPlan({ Title: "New Plan", Weeks: 1 })
		this.original = new PeriodPlan({ Title: "New Plan", Weeks: 1 })
		this.display = false
		this.updateProviders()
	}

	addEntry(data: {
		provider: PeriodPlanStaffRoleProvider | PeriodPlanEventsProvider | ResourceUtilisationProvider<PlanningRole>,
		time: Date
	}) {
		if (this.display)
			return
		if (data.provider instanceof PeriodPlanStaffRoleProvider)
			this.addRoleEntry(data.provider.role, data.time)
		else if (data.provider instanceof PeriodPlanEventsProvider)
			this.addEvent(data.provider, data.time)
	}

	private addRoleEntry(role: PlanningRole, startTime: Date) {
		TimeIntervalSeries.mergeIntervalIntoList(
			TimeInterval.fromDuration(startTime, { hours: 1 }),
			role.Intervals)
		this.updateProviders()
	}

	private addEvent(provider: PeriodPlanEventsProvider, startTime: Date) {
		const interval = TimeInterval.fromDuration(startTime, { hours: 1 })
		const event = provider.target === PeriodPlanEventsProviderTarget.Role
			? this.newEventForRole(provider.id, interval)
			: this.newEventForResource(provider.id, interval)
		this.plan.Events.push(event)
		this.updateProviders()
	}

	private newEventForRole(index: number, interval: TimeInterval) {
		return new CalendarEvent({
			Title: "Pause",
			Times: interval,
			OccupiedStaff: [index]
		})
	}

	private newEventForResource(id: number, interval: TimeInterval) {
		return new CalendarEvent({
			Title: "Pause",
			Times: interval,
			OccupiedResources: [id]
		})
	}

	closePopups() {
		this.closeEvent()
		this.selectedRole = undefined
	}

	closeEvent() {
		this.selectedEvent = undefined
		this.updateProviders()
	}

	selectEntry(data: {
		provider: PeriodPlanStaffRoleProvider | PeriodPlanEventsProvider | ResourceUtilisationProvider<PlanningRole>,
		entry: BasicCalendarEntry<{ source: CalendarEvent, Interval: TimeInterval }>
	}) {
		if (!(data.provider instanceof PeriodPlanEventsProvider))
			return
		this.selectedEvent = data.entry.source.source
	}

	selectDate(date: Date) {
		this.dayIsSelected = !!date
	}

	submitPlan() {
		this.display = true
		if (this.plan.DBID)
			this.periodPlanService.update(this.plan)
		else
			this.periodPlanService.add(this.plan).then(plan => {
				this.updatePlan(plan)
			})
	}

	deletePlan() {
		this.display = true
		this.periodPlanService.delete(this.plan.DBID).then(() => {
			this.navigater.current(this.periodPlanService.ngUrl + "/list")
		})
	}

	addStaffRole(roleType: "empty" | "fromStaff" | "fromStaffGroup") {
		this.selectRoleType = roleType
		this.selectedStaffGroup = 0
		this.selectedStaff = 0
		this.createBasicRole()
	}

	private switchToEdit(): void {
		this.plan = new PeriodPlan(this.original)
		this.display = false
	}

	switchToDisplay(): void {
		this.plan = new PeriodPlan(this.original)
		this.display = true
	}

	private createBasicRole() {
		const title = this.l.staff.nameSingular
		let next = 0
		for (const i in this.plan.StaffRoles)
			if (this.plan.StaffRoles[i].Title.startsWith(title))
				next = Math.max(+this.plan.StaffRoles[i].Title.split(title)[1], next)
		this.selectedRole = new PlanningRole({
			Title: title + " " + (next + 1)
		})
	}

	addStaffRoleFromStaff(staffId: number) {
		if (!staffId)
			return this.createBasicRole()
		const staff = this.staffService.getModel(staffId)
		this.selectedRole = new PlanningRole({
			Title: staff.Name,
			Competences: staff.Competences,
			Intervals: staff.WorkTimes.map(e => new TimeInterval(e))
		})
	}

	addStaffRoleFromStaffGroup(staffGroupId: number) {
		if (!staffGroupId)
			return this.createBasicRole()
		const staffGroup = this.staffGroupService.getModel(staffGroupId)
		this.selectedRole = new PlanningRole({
			Title: staffGroup.Title,
			Competences: staffGroup.Competences
		})
	}

	saveStaffRole() {
		if (this.isSelectedRoleNew())
			this.plan.StaffRoles.push(this.selectedRole)
		this.updateIndexes()
		this.selectedRole = null
		this.updateProviders()
	}

	deleteStaffRole() {
		const index = this.plan.StaffRoles.findIndex((e) => e === this.selectedRole)
		this.plan.StaffRoles.splice(index, 1)
		this.updateIndexes()
		this.selectedRole = null
		this.updateProviders()
	}

	private updateIndexes() {
		for (const i in this.plan.StaffRoles)
			this.plan.StaffRoles[i].Index = +i
	}

	isSelectedRoleNew() {
		return !this.plan.StaffRoles.includes(this.selectedRole)
	}
}
