import { Injectable } from "@angular/core"
import { ResourceService } from "idun-main/model-services/resource.service"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { ProcedureTypeService } from "idun-main/model-services/procedure-type.service"
import { Resource } from "idun-common/models/resource"
import { combineLatest, BehaviorSubject } from "rxjs"
import { SharedCompetences, UtilisationSet, Competent } from "idun-common/models/resource-utilisation"
import { CompetenceSet } from "idun-common/models/competence-set"
import { TimeIntervalSeries } from "idun-common/models/time-interval-series"
import { Procedure } from "idun-common/models/procedure"
import { ResourceUtilisationProvider, Titled } from "./resource-utilisation-provider"

@Injectable()
export class ResourceUtilisationService {
	private resources: Resource[] = []
	private resourceCompetences: { [index: number]: CompetenceSet }
	private procedures: Procedure[] = []

	resourceDataChanged$ = new BehaviorSubject<boolean>(false)

	constructor(
		private resourceService: ResourceService,
		private procedureService: ProcedureService,
		private procedureTypeService: ProcedureTypeService,
	) {
		this.subscribeToResources()
	}

	private subscribeToResources() {
		combineLatest(
			this.resourceService.getModels$(),
			this.procedureService.getModels$(),
			this.procedureTypeService.getModels$()
		).subscribe(values => {
			this.resources = values[0]
			this.procedures = values[1]
			const procedureTypes = values[2]
			this.resourceCompetences = SharedCompetences.calculateResourceCompetences(
				this.resources,
				procedureTypes,
				this.procedures)
			this.resourceDataChanged$.next(true)
		})
	}

	createResourceProviders<StaffType extends Competent & Titled>(
		staff: StaffType[],
		availableTimesForStaff: (staff: StaffType) => TimeIntervalSeries,
		occupiedTimesForResource: (resource: Resource) => TimeIntervalSeries,
	): { [index: number]: ResourceUtilisationProvider<StaffType> } {
		return new ResourceProviderCreator(
			this.procedures,
			this.resources,
			this.resourceCompetences,
			staff,
			availableTimesForStaff,
			occupiedTimesForResource,
		).createResourceProviders()
	}
}

class ResourceProviderCreator<StaffType extends Competent & Titled> {
	constructor(
		private procedures: Procedure[],
		private resources: Resource[],
		private resourceCompetences: { [index: number]: CompetenceSet },
		private staff: StaffType[],
		private availableTimesForStaff: (staff: StaffType) => TimeIntervalSeries,
		private occupiedTimesForResource: (resource: Resource) => TimeIntervalSeries,
	) {

	}

	createResourceProviders(): { [index: number]: ResourceUtilisationProvider<StaffType> } {
		const providers = {}
		const resourceUtilisationSet = UtilisationSet.newFrom(
			this.availableStaffTimesMap(),
			this.resources,
			this.unavailableResourceTimesMap(),
			new SharedCompetences(
				this.staff,
				this.resources,
				role => role.Competences,
				resource => this.resourceCompetences[resource.DBID],
				this.procedures)
		)
		resourceUtilisationSet.utilisationMap.forEach((intervalSets, resource) =>
			providers[resource.DBID] = new ResourceUtilisationProvider(resource, intervalSets))
		return providers
	}

	private availableStaffTimesMap(): Map<StaffType, TimeIntervalSeries> {
		const map = new Map<StaffType, TimeIntervalSeries>()
		for (const staffMember of this.staff)
			map.set(staffMember, this.availableTimesForStaff(staffMember))
		return map
	}

	private unavailableResourceTimesMap() {
		const map = new Map<Resource, TimeIntervalSeries>()
		for (const resource of this.resources)
			map.set(resource, this.occupiedTimesForResource(resource))
		return map
	}
}
