import { TimeInterval } from "./time-interval"
import { IdunModel } from "./idun-model"

export class WorkInterval extends IdunModel {
	DBID: number
	StaffID: number
	Times: TimeInterval

	constructor(source?: any) {
		super()
		this.Times = new TimeInterval()

		if (!source)
			return
		for (const prop in source) this[prop] = source[prop]
		if (source.Times)
			this.Times = new TimeInterval(source.Times)
	}

	public getTitle(): string {
		throw new Error("Method should not be used")
	}
}
