﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { LanguageService } from "idun-layout/language.service"
import { PeriodPlan } from "idun-common/models/period-plan"
import { UserService } from "idun-main/user.service"
import { Permission } from "idun-common/models/staff-group"

@Injectable()
export class PeriodPlanService extends ExtendedService<PeriodPlan> {
	constructor(
		http: Http,
		l: LanguageService,
		userService: UserService
	) {
		super(http, l,
			PeriodPlan,
			"api/PeriodPlan",
			"period-plan",
			() => l.periodPlan
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.EditStatus)
		this.setupList({
			entryHeader: model => model.Title,
			contentList: [model => `Periode: ${model.Weeks} uger - ${model.StaffRoles.length} roller`],
			linkList: [model => {
				return {
					destination: "implement-plan/" + model.DBID,
					getText: () => l.periodPlan.implementPlan
				}
			}]
		})
		this.listInfo.searchTypes.push("Title")
	}

	implementPlan(
		planID: number,
		starttime: Date,
		endtime: Date,
		staffChoices: { StaffID: number, Choice: string }[]
	): Promise<any> {
		return this.baseRequest("ImplementPlan", {
			StartTime: starttime,
			EndTime: endtime,
			PlanID: planID,
			StaffChoices: staffChoices
		})
	}
}
