import { Component, Input, Output, EventEmitter } from "@angular/core"

@Component({
	selector: "idun-popup",
	templateUrl: "./popup.component.html",
	styleUrls: ["./popup.component.scss"]
})

export class PopupComponent {
	@Input() show = false
	@Output() close = new EventEmitter()

	hide(): void {
		this.show = false
		this.close.emit()
	}
}
