﻿import { Component, forwardRef, Input, Output, EventEmitter } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor } from "@angular/forms"
import { FormRow, FormRowOptions, FieldInput } from "idun-layout/form/form-row"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { AutoFormComponent } from "idun-common/auto-form.component"
import { LanguageService } from "idun-layout/language.service"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormFigurePlacementComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormFigurePlacementComponent),
	multi: true
}

@Component({
	selector: "idun-form-figure-placement",
	templateUrl: "./form-figure-placement.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormFigurePlacementComponent extends FormBasicComponent<string[]> implements ControlValueAccessor {
	rowClass = FormFigurePlacementRow

	get value(): string[] {
		return this.innerValue
	}
	@Input() set value(value: string[]) {
		if (value === this.innerValue)
			return
		this.innerValue = value
		this.onUpdateFromWithin()
		this.onChangeCallback(this.innerValue)
	}

	@Output() selectPlacement = new EventEmitter()
	get showSelectButton() {
		return this.selectPlacement.observers.length > 0
	}

	constructor(public l: LanguageService) {
		super()
	}
}

export class FormFigurePlacementRow extends FormRow {
	constructor(
		field: FieldInput,
		options: Options
	) {
		super(field, options)
	}
}

interface Options extends FormRowOptions {
	selectPlacement?: (model: any, value: string, component: AutoFormComponent<any>) => void
}
