﻿import { IdunModel } from "idun-common/models/idun-model"
import { FormRow } from "idun-layout/form/form-row"

export class AutoFormInfo<Model extends IdunModel> {
	constructor(
		public getHeader: (model: Model) => string,
		public rowGroups: {
			getHeader?: (model) => string,
			headerHelp?: () => string,
			rows: FormRow[],
			hidden?: boolean,
			showHeader?: (model) => boolean
		}[],
		public options: {
			parameterHandlers?: any
			finalHandler?: any
			onUpdate?: any
			onSubmit?: any
			onCancel?: any
			onDelete?: any
			allowEdit?: boolean
			allowDelete?: boolean
			helpText?: () => string
			popup?: { getHeader?: (model) => string, onClose: (component) => void, rows: FormRow[] }
		} = {}
	) { }
}
