﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { DateHelper } from "utility/date-helper"
import { ProcedureService } from "../model-services/procedure.service"
import { LanguageService } from "idun-layout/language.service"

import { BookingRequest, RequestStatus } from "idun-common/models/booking-request"

@Injectable()
export class BookingRequestService extends ExtendedService<BookingRequest> {
	constructor(
		public procedureService: ProcedureService,
		http: Http,
		l: LanguageService
	) {
		super(http, l,
			BookingRequest,
			"api/BookingRequest",
			"booking-request",
			() => l.bookingRequest
		)
		this.setupList({
			entryHeader: (model: BookingRequest) => {
				const procedure = this.procedureService.getModel(model.ProcedureID)
				return procedure ? procedure.Title : ""
			},
			contentList: [(model: BookingRequest) => {
				return DateHelper.dateToString(model.Times.StartTime) + " "
					+ DateHelper.timeIntervalToString(model.Times.StartTime, model.Times.EndTime)
			}],
			groups: [
				{
					getHeader: () => { return this.l.bookingRequest.newRequests },
					filter: (list: BookingRequest[]) => {
						return list.filter((request: BookingRequest) => { return request.Status === RequestStatus.Waiting })
					}
				},
				{
					getHeader: () => { return this.l.bookingRequest.acceptedRequests },
					filter: (list: BookingRequest[]) => {
						return list.filter((request: BookingRequest) => { return request.Status === RequestStatus.Accepted })
					}
				},
				{
					getHeader: () => { return this.l.bookingRequest.deniedRequests },
					filter: (list: BookingRequest[]) => {
						return list.filter((request: BookingRequest) => { return request.Status === RequestStatus.Denied })
					}
				}
			]
		})
	}

	getUnhandledRequests() {
		return this.baseGetSubList("GetUnhandled", null)
	}
}
