﻿import { Status, Period } from "idun-common/models/status"
import { TimeInterval } from "idun-common/models/time-interval"
import { DateHelper } from "utility/date-helper"

const baseUrl = "api/status/"
const today = DateHelper.dayInterval(new Date())


const list = [
	new Status({
		DBID: 1,
		StaffID: 1,
		StatusTypeID: 1,
		Interval: TimeInterval.from(new Date(1000, 1, 1), new Date(3000, 1, 1))
	}),
	new Status({
		DBID: 2,
		StaffID: 2,
		StatusTypeID: 1,
		Interval: TimeInterval.from(today.start, today.end)
	})
]

function generateStatuses() {
	const day = new Date()
	day.setDate(day.getDate() - 500)
	for (let i = 0; i < 1000; i++) {
		day.setDate(day.getDate() + 1)
		if (day.getDay() === 0 || day.getDay() === 6)
			continue
		newStatusForDate(day, 1)
	}
}

function newStatusForDate(date: Date, staffID: number) {
	date.setHours(8, 0, 0, 0)
	list[list.length - 1].Interval.EndTime = new Date(date)
	list.push(new Status({
		DBID: list.length + 1,
		StaffID: staffID,
		StatusTypeID: 2,
		Periods: [new Period({
			Assignment: "Break",
			Interval: TimeInterval.from(
				new Date(date.getTime() + 2 * 1000 * 60 * 60),
				new Date(date.getTime() + 3 * 1000 * 60 * 60))
		})],
		Interval: TimeInterval.from(new Date(date), new Date(date))
	}))
	date.setHours(16, 0, 0, 0)
	list[list.length - 1].Interval.EndTime = new Date(date)
	list.push(new Status({
		DBID: list.length + 1,
		StaffID: staffID,
		StatusTypeID: 1,
		Interval: TimeInterval.from(new Date(date), new Date(3000, 1, 1))
	}))
}

generateStatuses()

const otherResponses = {}

otherResponses[baseUrl + "getstatuslist"] = (parameters: { id: number, start: Date, end: Date }) => {
	const interval = TimeInterval.fromInterval(parameters)
	const result = list.filter(e => {
		if (e.StaffID !== parameters.id)
			return false
		return interval.intersects(e.Interval)
	})
	return JSON.stringify(result)
}

otherResponses[baseUrl + "getstatusnow"] = (parameters) => {
	const now = new Date()
	const status = list.find(e => {
		if (e.StaffID !== parameters.id)
			return false
		return e.Interval.containsDate(now)
	})
	return JSON.stringify(status)
}

otherResponses[baseUrl + "requestchange"] = (parameters) => {
	return JSON.stringify(true)
}

export function StatusResponses() {
	return {
		// ...ModelTestData.commonResponses(Status, baseUrl, list),
		...otherResponses
	}
}
