﻿import { StaffNotification, StaffNotificationType } from "idun-common/models/staff-notification"
import { ModelTestData } from "./model-test-data"
import { StaffNotificationReceivers } from "idun-common/models/staff-notification-receivers"

const baseUrl = "api/staffnotificationreceivers/"

const list = [
	new StaffNotificationReceivers({
		DBID: 1, TargetedStaffGroupIds: [1, 2], Type: StaffNotificationType.PatientArrival
	})
]

ModelTestData.loadFromLocalStorage(baseUrl, list, StaffNotificationReceivers)

export function StaffNotificationReceiversResponses() {
	return { ...ModelTestData.commonLocalStorageResponses(StaffNotificationReceivers, baseUrl, list) }
}
