import { CalendarEntry } from "idun-calendar/calendar-entry"
import { ProcedureEvent, CompletionStatus, PaymentStatus } from "idun-common/models/procedure-event"
import { ProcedureService } from "idun-main/model-services/procedure.service"

export class ProcedureEventEntry implements CalendarEntry {
	description: string
	hoverText: string
	length: number
	clickable = true

	constructor(
		public source: ProcedureEvent,
		public colorThing: any,
		procedureService: ProcedureService,
	) {
		this.setTitle(procedureService)
		this.setLength()
	}

	private async setTitle(procedureService: ProcedureService) {
		const procedure = await procedureService.model(this.source.ProcedureId)
		this.description = procedure.Title
		this.hoverText = procedure.Title + "\n" + this.source.Variation
	}

	private setLength() {
		this.length = this.source.Times.getLengthInMinutes()
	}

	isSelected(selection: any): boolean {
		return this.source === selection
	}

	get borderHue(): number {
		// TODO: flesh this out #1868
		switch (this.source.CompletionStatus) {
			case CompletionStatus.NotStarted:
				if (new Date().getTime() - this.source.Times.StartTime.getTime() > 0)
					return 0
				else
					return 0.53
			// case "waiting":
			// 	return 0.16
			case CompletionStatus.InProgress:
				return 0.09
			case CompletionStatus.Finished:
				return 0.35
		}
	}

	get boxHue(): number {
		return this.colorThing.getColor(this.source)
	}

	getStartMinute(baseDate: Date): number {
		return this.source.Times.getStartMinute(baseDate)
	}

	moveStartMinute(minuteChange: number) {
		const minutes = this.source.Times.StartTime.getMinutes()
		this.source.Times.StartTime.setMinutes(minutes + minuteChange)
	}

	moveEndMinute(minuteChange: number) {
		const minutes = this.source.Times.EndTime.getMinutes()
		this.source.Times.EndTime.setMinutes(minutes + minuteChange)
	}

	get statusIcon(): string {
		// TODO: flesh this out #1868
		if (new Date().getTime() < this.source.Times.StartTime.getTime())
			return null
		switch (this.source.PaymentStatus) {
			case PaymentStatus.NotStarted:
				return "payment_started_not_sent"
			case PaymentStatus.Paid:
				return "payment"
		}
	}
}
