import { IdunModel } from "./idun-model"

export class Edifact extends IdunModel {
	DBID: number
	RawMessage: string
	Unread: boolean
	RecieveDate: Date

	constructor(source?: any) {
		super()
		for (const prop in source) this[prop] = source[prop]
	}

	public getTitle(): string {
		return "E" + this.DBID
	}
}
