﻿import { Component, forwardRef, Input } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { FormRow, FieldInput, FormRowOptions } from "./form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormNumberComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormNumberComponent),
	multi: true
}

@Component({
	selector: "idun-form-number",
	templateUrl: "form-number.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormNumberComponent extends FormBasicComponent<string> {
	@Input() endLabel: string
	rowClass = FormNumberRow
}

export class FormNumberRow extends FormRow {
	constructor(
		field: FieldInput,
		options: Options
	) {
		super(field, options)
	}
}

interface Options extends FormRowOptions {
	endLabel?: () => string
}
