import { Component, OnInit, ElementRef } from "@angular/core"
import { Router, ActivatedRoute } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { RouteParameterComponent } from "idun-common/route-parameter-component"
import { DateHelper, changeDate } from "utility/date-helper"
import { LanguageService } from "idun-layout/language.service"
import { LayoutService, Maximisable } from "idun-layout/layout.service"
import { CalendarEntryProvider } from "idun-calendar/calendar-entry-provider"
import { CalendarEntry } from "idun-calendar/calendar-entry"
import { PatientEvent } from "idun-common/models/patient-event"
import { PatientEventService } from "idun-main/model-services/patient-event.service"
import { TimeInterval } from "idun-common/models/time-interval"
import { BasicCalendarEntry } from "idun-calendar/basic-calendar-entry"
import { ProcedureService } from "idun-main/model-services/procedure.service"

@Maximisable
@Component({
	selector: "idun-booking-calendar",
	templateUrl: "booking-calendar.component.html",
	styleUrls: ["../calendar/calendar-page.component.scss"]
})
export class BookingCalendarComponent extends RouteParameterComponent implements OnInit {
	navigater: NavigationHandler

	patientEvent: PatientEvent
	selectedInterval: TimeInterval
	date = new Date()
	entryProviders: BookingEventProvider[] = []
	loading = false
	subtitle = ""
	showMissingPatientEventMessage = false

	constructor(
		router: Router,
		myElement: ElementRef,
		public layoutService: LayoutService,
		private route: ActivatedRoute,
		private patientEventService: PatientEventService,
		private procedureService: ProcedureService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
		this.parameterHandlers["date"] = (date: any) => { this.date = date ? new Date(date) : new Date() }
	}

	ngOnInit(): void {
		this.handleRouteParameters(this.route, () => {
			this.updateIntervals()
		})
		this.subscribe(this.patientEventService.patientEvent$, patientEvent => {
			this.patientEvent = patientEvent
			this.updateSubtitle()
			this.updateIntervals()
		})
		this.subscribe(this.layoutService.maximised$, _ => {
			this.updateIntervals()
		})
	}

	private updateSubtitle() {
		if (!this.patientEvent)
			return this.subtitle = ""
		this.subtitle = this.patientEvent.ProcedureEvents
			.map(e => this.procedureService.getModel(e.ProcedureId))
			.map(e => e ? e.Title : "")
			.join(" / ")
	}

	async updateIntervals() {
		this.loading = true
		this.selectedInterval = undefined
		this.entryProviders = [new BookingEventProvider([], this.l)]
		if (!this.patientEvent)
			return this.loading = false
		const intervals = await this.findAvailableTimes()
		this.entryProviders[0] = new BookingEventProvider(intervals, this.l)
		this.loading = false
	}

	private findAvailableTimes() {
		const interval = DateHelper.weekInterval(this.date)
		if (this.layoutService.maximised)
			changeDate(interval.end, { days: 7 })
		return this.patientEventService.getAvailableTimes(this.patientEvent, interval)
	}

	addEntry(params: { provider: BookingEventProvider, time: Date }) {
		if (this.patientEventHasBeenSetUp())
			this.selectInterval(TimeInterval.from(params.time, params.time))
		else
			this.showMissingPatientEventMessage = true
	}

	private patientEventHasBeenSetUp() {
		const patientEvent = this.patientEventService.patientEvent$.value
		return patientEvent
			&& patientEvent.ProcedureEvents.length > 0
			&& patientEvent.ProcedureEvents[0].ProcedureId
	}

	selectInterval(interval: TimeInterval) {
		this.selectedInterval = interval
	}

	closePopup() {
		this.selectedInterval = null
		this.showMissingPatientEventMessage = false
	}

	navigate(date: Date) {
		this.navigater.current(this.getUrl(date))
	}

	getUrl(date: Date) {
		return "booking-calendar/" + date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate()
	}
}

class BookingEventProvider implements CalendarEntryProvider {
	entries: BasicCalendarEntry<any>[]

	constructor(
		public intervals: TimeInterval[],
		private l: LanguageService
	) {
		this.entries = this.intervals.map(e => this.calendarEntryFromInterval(e))
	}

	getEntries(date?: Date): CalendarEntry[] {
		if (!date)
			return this.entries
		return this.entries.filter(e => DateHelper.isSameDate(e.source.Interval.EndTime, date))
	}

	getSubtitle(): string {
		return this.l.procedure.namePlural
	}

	getTitle(): string {
		return ""
	}

	getHovertitle(): string {
		return ""
	}

	private calendarEntryFromInterval(interval: TimeInterval) {
		const text = DateHelper.timeIntervalToString(interval.StartTime, interval.EndTime)
		return new BasicCalendarEntry({ Interval: interval }, { description: text, clickable: true })
	}
}
