﻿import { Component, Input, Output, EventEmitter } from "@angular/core"

@Component({
	selector: "idun-form-model-switcher",
	template: `
		<div *ngFor="let list of switchLists" class="idun-form-row">
			<button *ngFor="let curModel of list.models; let i=index;" type="button"
			        class="{{model==curModel?'selected':''}}" (click)="switchModel(curModel)">
				{{list.getModelText(curModel)}}
			</button>
		</div>`,
	styleUrls: ["form-model-switcher.component.scss"]
})
export class FormModelSwitcherComponent {
	@Input() model
	@Input() switchLists: SwitchList[]
	@Output() switch = new EventEmitter()

	switchModel(model) {
		this.switch.emit(model)
	}
}

export class SwitchList {
	constructor(
		public models: any[],
		public getModelText) { }
}
