import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ResponseType } from "idun-common/base-service"
import { ExtendedService } from "idun-common/extended-service"
import { DateHelper } from "utility/date-helper"
import { Observable } from "rxjs"
import { LanguageService } from "idun-layout/language.service"

import { Patient, Gender } from "idun-common/models/patient"
import { IdunId } from "utility/idun-id"
import { FormIdunidRow } from "idun-common/form/form-idunid.component"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { FormDateRow } from "idun-layout/form/form-date.component"
import { FormSelectRow } from "idun-layout/form/form-select.component"
import { OptionInfo } from "idun-layout/form/form-row"
import { FormTelephoneRow } from "idun-common/form/form-telephone.component"
import { Permission } from "idun-common/models/staff-group"
import { UserService } from "idun-main/user.service"
import { FormTextareaRow } from "idun-layout/form/form-textarea.component"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"

import danishZipcodes from "../model-services/zipcodes/danish_postal_zipcodes.json"
import { LabelThing } from "idun-common/form/form-dynamic-extra.component"
import { AutoFormInfo } from "idun-common/auto-form-info"
import { AutoFormComponent } from "idun-common/auto-form.component"
import { FormTextHistoryRow } from "idun-layout/form/form-text-history.component"

@Injectable()
export class PatientService extends ExtendedService<Patient> {
	constructor(
		http: Http,
		l: LanguageService,
		userService: UserService
	) {
		super(http, l,
			Patient,
			"api/Patient",
			"patient",
			() => l.patient
		)
		userService.patientService = this
		this.hasEditPermission = () => userService.hasPermission(Permission.EditPatients)
		this.hasReadPermission = () => userService.hasPermission(Permission.ReadPatients)
		this.showListLink = false
		this.icon = "Patient"
		this.color = "var(--color-patient)"
		this.loadAll = false
		this.formInfo = new AutoFormInfo(
			(model) => {
				if (model.Name)
					return model.Name + " - " + (model.hasCprNumber() ? model.getCprNumber() : model.UniqueIdentification)
				return l.patient.nameSingular
			},
			[{
				rows: [
					new FormIdunidRow("UniqueIdentification", {
						label: () => l.common.id,
						hint: () => l.patient.uniqueIdentificationHint,
						requirements: [ValidationRequirement.ValueRequired(l)],
						change: (model, value) => {
							model.updateFromIdentification(value)
						}
					}),
					new FormTextRow("Name", {
						label: () => l.common.name,
						requirements: [ValidationRequirement.ValueRequired(l)],
					}),
					new FormDateRow("Birthdate", {
						label: () => l.common.birthdate,
						requirements: [ValidationRequirement.ValueRequired(l)],
						width: 50,
						change: this.updateIdunId
					}),
					new FormSelectRow("Gender", {
						label: () => l.common.gender,
						requirements: [ValidationRequirement.ValueRequired(l)],
						width: 50,
						cssClasses: "padding-left",
						options: [
							new OptionInfo("0", () => l.common.male),
							new OptionInfo("1", () => l.common.female)],
						change: (model) => {
							this.updateIdunId(model)
						}
					}),
				]
			},
			{
				getHeader: (model: Patient) => {
					return this.l.common.contact +
						": " + (model.getPrimaryPhone().Number || "") +
						" - " + (model.Email || "")
				},
				rows: [
					new FormTextRow("Email", {
						label: () => l.common.email,
						requirements: [
							ValidationRequirement.ValueRequired(l),
							ValidationRequirement.MustBeEmail(l)
						]
					}),
					new FormTelephoneRow("TelephoneNumbers", {
						label: () => l.common.telephone,
						hint: () => l.patient.telephoneHint,
						requirements: [ValidationRequirement.ValueRequired(l)],
					}),
					new FormTextareaRow("Address", {
						label: () => l.common.address,
						hint: () => l.patient.addressHint,
						requirements: [ValidationRequirement.ValueRequired(l)],
						change: this.updateIdunId
					}),
					new FormTextRow("Zipcode", {
						label: () => l.patient.postalCode,
						hint: () => l.patient.postalCodeHint,
						requirements: [ValidationRequirement.ValueRequired(l)],
						change: this.updateIdunId,
						things: [new LabelThing<Patient>(this.zipcodeArea)]
					})
				]
			},
			{
				getHeader: () => l.patient.cave,
				rows: [
					new FormTextHistoryRow("Cave", l, {
						label: () => l.patient.cave,
						hint: () => l.patient.caveHint,
						things: [this.signatureLabel()]
					})
				]
			}
			],
			{
				onSubmit: (model: Patient, component: AutoFormComponent<Patient>) => {
					const latestEntry = model.Cave[model.Cave.length - 1]
					const previousEntry = model.Cave.length > 1
						? model.Cave[model.Cave.length - 2]
						: null

					if (latestEntry.Title === "new")
						latestEntry.Title = this.titleForNewTextEntry(userService)

					if (previousEntry && previousEntry.Content === latestEntry.Content)
						model.Cave.pop()

					component._onSubmit().then((savedModel: Patient) => {
						component.navigater.current("patient/" + savedModel.DBID)
					})
				}
			}
		)
	}

	titleForNewTextEntry(userService: UserService) {
		return DateHelper.dateToString(new Date())
			+ " " + DateHelper.timeToString(new Date())
			+ " - " + userService.user.Name
	}

	signatureLabel() {
		return new LabelThing<Patient>(
			(model) => {
				if (model.Cave.length < 2)
					return this.l.journalEntry.notSigned
				const signatures = []
				const datedSignatures = model.Cave[model.Cave.length - 2].Title.split(", ")
				datedSignatures.forEach((signature) => {
					signatures.push(signature.split(" - ")[1])
				})
				const unique = signatures
					.filter((v, i) => signatures.indexOf(v) === i)
					.filter(e => e !== "new")
				const stringify = unique.join(", ")
				return this.l.journalEntry.signatures + ": " + stringify
			},
			() => true,
			(model) => {
				if (!model.Cave) {
					return ""
				}
				if (model.Cave.length < 2)
					return ""
				return model.Cave[model.Cave.length - 2].Title
			})
	}

	zipcodeArea = (patient: Patient) => {
		let area = ""

		if (patient.Zipcode !== "" && patient.Zipcode !== undefined && danishZipcodes) {
			danishZipcodes.postnr.forEach(zipcode => {
				if (zipcode[0].toString() === patient.Zipcode) {
					area = zipcode[1]
				}
			})
		}
		return area
	}

	updateIdunId = (patient: Patient) => {
		if (patient.DBID)
			return
		let idunid: any = new IdunId()
		idunid.loadFromPatient(patient)
		if (!idunid.isValid())
			return
		idunid = idunid.toString()
		if (patient.UniqueIdentification && patient.UniqueIdentification.startsWith(idunid.substring(0, 12)))
			return
		patient.UniqueIdentification = idunid.toString()
	}

	lastPermanentPatientPromise: Promise<Observable<Patient[]>>
	lastPermanentPatient$: Observable<Patient[]>
	lastPermanentPatientUpdate: Date = new Date(2000, 1, 1)
	getPermanentPatient$(): Promise<Observable<Patient[]>> {
		if (new Date().getTime() - this.lastPermanentPatientUpdate.getTime() > 10000) {
			this.lastPermanentPatientPromise = this.baseGetSubList("GetList", null)
			this.lastPermanentPatientUpdate = new Date()
			this.lastPermanentPatient$ = undefined
			return this.lastPermanentPatientPromise.then((param) => {
				this.lastPermanentPatient$ = param
				return param
			})
		}
		if (this.lastPermanentPatient$)
			return Promise.resolve(this.lastPermanentPatient$)
		else {
			return this.lastPermanentPatientPromise
		}
	}

	// TODO: examine this
	getUserPatient(userID: string): Promise<any> {
		return this.baseRequest("GetUserPatient", { userID: userID }, (response: any) => {
			response = response.json()
			let patient
			if (response.isRegistered) {
				patient = response.patient
				patient.Birthdate = DateHelper.dateToString(DateHelper.parseDate(patient.Birthdate))
				patient.Cpr = undefined
				patient.IsRegistered = true
			}
			else {
				patient = JSON.parse(response.patient)
				patient.DBID = 0
				patient.IsRegistered = false
			}
			return patient
		})
	}

	// TODO: examine this
	addUserPatient(userID: string, patient: Patient): Promise<any> {
		return this.baseRequest("AddUserPatient", { userID: userID, patient: patient })
	}

	getPatientFromCpr(cpr: string): Promise<Patient> {
		return this.baseModelRequest("GetFromCpr", { cpr: cpr }, ResponseType.Single)
	}

	getGenderDistribution(): Promise<any> {
		return this.baseRequest("getGenderDistribution", null)
	}

	getBirthdates(): Promise<any[]> {
		return this.baseRequest("getBirthdates", null)
	}
}
