const otherResponses = {}

otherResponses[baseUrl + "geteventsforstaff"] = (parameters: { id: number }) => {
	return JSON.stringify(procedureEventTestData.filter(e => e.OccupiedStaff.includes(+parameters.id)))
}

otherResponses[baseUrl + "geteventsforresource"] = (parameters: { id: number }) => {
	return JSON.stringify(procedureEventTestData.filter(e => e.OccupiedResources.includes(+parameters.id)))
}

export function ProcedureEventResponses() {
	return {
		...ModelTestData.commonResponses(ProcedureEvent, baseUrl, procedureEventTestData, { getpage: () => "", getlist: () => "" }),
		...otherResponses
	}
}

export function getProcedureEventTestDataForStaff(id: number, interval: TimeInterval) {
	return procedureEventTestData
		.filter(e => e.OccupiedStaff.includes(id))
		.filter(e => e.Times.intersects(interval))
}

export function getProcedureEventTestDataForResource(id: number, interval: TimeInterval) {
	return procedureEventTestData
		.filter(e => e.OccupiedResources.includes(id))
		.filter(e => e.Times.intersects(interval))
}
