﻿import { Component, forwardRef } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { DateHelper } from "utility/date-helper"
import { LanguageService } from "idun-layout/language.service"
import { FormRow } from "./form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormEndDateComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormEndDateComponent),
	multi: true
}

@Component({
	selector: "idun-form-end-date",
	template: `
			<div class="idun-form-row">
				<idun-label *ngIf="showLabel" [hint]="hint" [display]="display">
					<ng-content select="label"></ng-content>
				</idun-label>

				<select [(ngModel)]="mode" [disabled]="display">
					<option value="one">{{l.commonForm.onlyThisDay}}</option>
					<option value="several">{{l.commonForm.fromThisDayUntilAnotherDate}}</option>
				</select>

				<input type="date" [style.display]="mode === 'several' ? '' : 'none'"
					(blur)="onBlur()" [disabled]="display" [(ngModel)]="date" autocomplete="off"
					class="margin-left {{(error && isBlurred)?'invalid':''}}"/>

				<ng-content></ng-content>
			</div>`,
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormEndDateComponent extends FormBasicComponent<Date> {
	rowClass = FormEndDateRow
	mode: "one" | "several" = "one"

	constructor(public l: LanguageService) { super() }

	get date() {
		if (!this.value)
			return DateHelper.dateToString(new Date())
		return DateHelper.dateToString(this.value)
	}
	set date(value) {
		DateHelper.updateDateFromDateString(this.value, value)
	}
}

export class FormEndDateRow extends FormRow { }
