﻿import { IdunModel } from "./idun-model"
import { DateHelper } from "utility/date-helper"

export class Invoice extends IdunModel {
	DBID: number
	PatientID: number
	CompanyID: number
	DescriptionCodes: KeyValuePair[]
	Date: string
	Status: StatusCode

	constructor(source?: any) {
		super()
		this.DescriptionCodes = []
		if (source) {
			for (const prop in source) this[prop] = source[prop]
			this.Date = DateHelper.dateToString(DateHelper.parseDate(source.Date))
			this.DescriptionCodes = this.DescriptionCodes.map(e => new KeyValuePair(e))
		}
	}

	getTitle(): string {
		return "TODO" // TODO
	}
}

export class KeyValuePair {
	Key: number
	Value: string

	constructor(source?: any) {
		if (source)
			for (const prop in source) this[prop] = source[prop]
	}
}

export enum StatusCode { Sent, DelayedPayment, Paid, DebtCollections }
