import { Edifact } from "idun-common/models/edifact"
import { ExtendedService } from "idun-common/extended-service"
import { Http } from "@angular/http"
import { Injectable } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"

@Injectable()
export class EdifactService extends ExtendedService<Edifact> {
	constructor(
		http: Http,
		l: LanguageService
	) {
		super(http, l,
			Edifact,
			"api/Edifact",
			"edifact",
			() => l.edifact
		)
		this.hasEditPermission = () => false
		this.setupList({
			entryHeader: (model: Edifact) => model.getTitle(),
			contentList: [(model: Edifact) => model.RawMessage.substr(0, 100)]
		})
	}
}
