﻿import { Component, forwardRef, Input, Output, EventEmitter } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor } from "@angular/forms"
import { FormBasicComponent } from "../../idun-layout/form/form-basic-component"
import { FormRow } from "../../idun-layout/form/form-row"
import { LanguageService } from "../../idun-layout/language.service"
import { UnsavedFile, DatabaseFileReference } from "../models/database-file"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormListFileComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormListFileComponent),
	multi: true
}

@Component({
	selector: "idun-form-list-file",
	templateUrl: "form-list-file.component.html",
	styleUrls: ["form-list-file.component.scss"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormListFileComponent extends FormBasicComponent<(UnsavedFile | DatabaseFileReference)[]> {
	rowClass = FormListFileRow
	newValue = ""

	constructor(public l: LanguageService) {
		super()
	}

	handleFiles = (files) => {
		for (const file of files) {
			this.value.push(new UnsavedFile({ Name: file.name, Data: file }))
		}
	}

	clickThrough(element) {
		element.dispatchEvent(new MouseEvent("click"))
	}

	getKeys() {
		if (this.value)
			return Object.keys(this.value)
		else return []
	}

	remove(index: string): void {
		this.value.splice(+index, 1)
		this.onChangeCallback(this.value)
	}

	fileDragHover(e) {
		e.stopPropagation()
		e.preventDefault()
		e.target.className = (e.type === "dragover" ? "dragover" : "")
	}

	fileSelectHandler(callback) {
		return (e) => {
			// cancel event and hover styling
			this.fileDragHover(e)
			const files = e.target.files || e.dataTransfer.files
			callback(files)
		}
	}
}

export class FormListFileRow extends FormRow { }
