﻿import { SearchableIdunModel } from "./idun-model"

export class TreatmentCodeType extends SearchableIdunModel {
	DBID: number
	Title: string
	Description: string

	constructor(source?: any) {
		super()
		if (source)
			for (const prop in source) this[prop] = source[prop]
	}

	getTitle(): string {
		return this.Title
	}

	searchAttributes() {
		return [this.Title]
	}
}
