import { filter, map } from "rxjs/operators"
import { Type } from "@angular/core"
import { Http } from "@angular/http"
import { Observable, BehaviorSubject } from "rxjs"
import { RequestHelper } from "utility/request-helper"
import { IdunModel } from "idun-common/models/idun-model"
import { Cache } from "./cache"

export enum ResponseType { Single, SubList, List }

export abstract class BaseService<Model extends IdunModel> {
	private modelMap: { [index: number]: Model } = {}
	protected models: Model[] = []
	protected _models$: BehaviorSubject<Model[]>
	protected _count$: BehaviorSubject<number>
	cache = new Cache()
	public loadAll = true

	constructor(protected http: Http, public modelClass: Type<Model>, public baseUrl) {
		this._models$ = <BehaviorSubject<Model[]>>new BehaviorSubject(null)
		this._count$ = <BehaviorSubject<number>>new BehaviorSubject(null)
	}

	getNewModel(source?: any): Model {
		return new this.modelClass(source)
	}

	model(id: number): Promise<Model> {
		const model = this.modelMap[id]
		if (model)
			return Promise.resolve(model)
		return this.loadModel(id)
	}

	getModel(id: number): Model {
		const model = this.modelMap[id]
		if (model)
			return model
		this.loadModel(id)
		return null
	}

	getModel$(id: number): Observable<Model> {
		this.loadModel(id)
		return this.getSingle$(id)
	}

	getModels$(): Observable<Model[]> {
		this.loadModels()
		return this.getList$()
	}

	loadModel(id: number): Promise<Model> {
		return this.loadSingle(id)
	}

	loadModels(): Promise<Model[]> {
		return this.loadList()
	}

	add(item: Model): Promise<Model> {
		return this.baseRequest("Add", item, this.getResponseFunction(ResponseType.Single, true), true)
	}

	update(item: Model): Promise<any> {
		return this.baseModelRequest("Update", item, ResponseType.Single, true)
	}

	delete(id: number): Promise<any[]> {
		return this.baseRequest("MarkForDeletion", { id: id }, undefined, true).then((blockers: any[]) => {
			const index = this.models.findIndex(x => x.DBID === +id)
			if (!blockers && index >= 0) {
				this.models.splice(index, 1)
				delete this.modelMap[id]
				this.updateObservables()
			}
			return blockers
		})
	}

	cancelDelete(id: number): Promise<void> {
		return this.baseRequest("RevertDelete", { id: id }, () => { }, true)
	}

	search(query: string): Promise<Observable<Model[]>> {
		return this.baseGetSubList("Search", { query: query })
	}

	count$(): Observable<number> {
		this.getCount()
		return this._count$
	}

	private getCount(): Promise<number> {
		return this.baseRequest("GetCount", null, (response) => {
			const count = (response.json ? response.json() : JSON.parse(response)).count
			this._count$.next(count)
			return count
		})
	}

	public getSingle$(id: number): Observable<Model> {
		return this.getList$().pipe(map((models: Model[]) => {
			for (let i = 0; i < models.length; i++)
				if (+models[i].DBID === +id)
					return models[i]

		}), filter((model: Model) => {
			return !(!model)
		}))
	}

	public getSubList$(ids: number[]): Observable<Model[]> {
		return this.getList$().pipe(map((models: Model[]) => {
			const sublist = []
			for (let i = 0; i < models.length; i++)
				if (ids.indexOf(models[i].DBID) !== -1)
					sublist.push(models[i])

			return sublist
		}))
	}

	protected getList$(): Observable<Model[]> {
		return this._models$.asObservable().pipe(filter((models: Model[]) => {
			return models !== null
		}))
	}

	protected baseGetSubList(path: string, parameters: any, ignoreCache = false): Promise<Observable<Model[]>> {
		return this.baseModelRequest(path, parameters, ResponseType.SubList, ignoreCache).then((items: Model[]) => {
			return this.getSubList$(items.map(x => x.DBID))
		})
	}

	protected loadSingle(id: number): Promise<Model> {
		if (!+id)
			return Promise.resolve(null)
		return this.baseModelRequest("Get", { id: id }, ResponseType.Single)
	}

	protected loadList(): Promise<Model[]> {
		return this.baseModelRequest("GetList", null, ResponseType.List)
	}

	public loadPage(
		page: number,
		pageSize: number,
		query = null,
		searchType = null,
		filterThing = null,
		filterType = null
	): Promise<Observable<Model[]>> {
		const parameters = {
			page: page,
			pageSize: pageSize,
			query: query,
			searchType: searchType,
			filterThing: filterThing,
			filterType: filterType
		}

		if (this.loadAll) {
			this.loadList()
			return Promise.resolve(this.getPage(parameters))
		}
		return this.baseGetSubList("GetPage", parameters)
	}

	private getPage(
		parameters: {
			page: number,
			pageSize: number,
			query?: string,
			searchType?: string,
			filterThing?: string,
			filterType?: string
		}
	): Observable<Model[]> {
		return this.getList$().pipe(
			map(models => models
				.filter(m => this.modelMatches(parameters, m))
				.slice(
					parameters.page * parameters.pageSize,
					parameters.page * parameters.pageSize + parameters.pageSize)
			)
		)
	}

	protected modelMatches(
		parameters: {
			query?: string,
			searchType?: string,
			filterThing?: string,
			filterType?: string
		},
		model: Model
	): boolean {
		if (!parameters.query)
			return true
		if (parameters.searchType)
			return this.matches(model[parameters.searchType], parameters.query)
		return this.matches(model.getTitle() + " " + model.getShortTitle(), parameters.query)
	}

	private matches(text: any, query: string) {
		if (!text)
			return false
		text = (text + "").toLowerCase()
		for (const word of query.toLowerCase().split(" "))
			if (!text.includes(word))
				return false
		return true
	}

	protected baseModelRequest(path: string, parameters: any, responseType: ResponseType, ignoreCache = false): Promise<any> {
		return this.baseRequest(path, parameters, this.getResponseFunction(responseType, false), ignoreCache)
	}

	protected parse(source: any): Model {
		if (!source)
			throw new Error("Got empty reply")
		return this.getNewModel(source.json ? source.json() : JSON.parse(source))
	}

	protected parseList(source: any): Model[] {
		return this.parseListAs(source, e => this.getNewModel(e))
	}

	protected parseListAs<T>(source: any, createNew: (source: any) => T): T[] {
		const list = source.json ? source.json() : JSON.parse(source)
		return list.map(e => createNew(e))
	}

	protected getResponseFunction(responseType: ResponseType, newModel: boolean): any {
		return (response: any) => {
			response = responseType === ResponseType.Single ?
				this.parse(response) :
				this.parseList(response)

			this.updateListFromParsedResponse(response, responseType, newModel)
			this.updateObservables()
			return response
		}
	}

	private updateListFromParsedResponse(response: any, responseType: ResponseType, newModel: boolean) {
		switch (responseType) {
			case ResponseType.Single:
				this.updateListFromItem(response, newModel)
				break

			case ResponseType.SubList:
				for (let i = 0; i < response.length; i++)
					this.updateListFromItem(response[i], false)
				break

			case ResponseType.List:
				this.updateListFromList(response)
				break
		}
	}

	private updateListFromItem(item: Model, newModel: boolean) {
		if (!item)
			return
		let inserted = false
		for (let i = 0; i < this.models.length && !newModel; i++) {
			if (this.models[i].DBID === item.DBID) {
				this.models[i] = item
				inserted = true
				break
			}
		}
		if (!inserted)
			this.models.push(item)
		this.modelMap[item.DBID] = item
	}

	private updateListFromList(models: Model[]) {
		this.models = models
		this.modelMap = {}
		for (const model of models)
			this.modelMap[model.DBID] = model
	}

	protected updateObservables() {
		this._models$.next(this.models)
		if (this.loadAll)
			this._count$.next(this.models.length)
	}

	protected baseRequest(path: string, parameters: any, onResponse?: any, ignoreCache = false): Promise<any> {
		return BaseService._baseRequest(this.http, this.cache, this.baseUrl, path, parameters, onResponse, ignoreCache)
	}

	static _baseRequest(http: any, cache: Cache, baseUrl: string, path: string, parameters: any, onResponse?: any, ignoreCache = false): Promise<any> {
		let cachedResponse
		if (!ignoreCache)
			cachedResponse = cache.get(baseUrl + "/" + path + JSON.stringify(parameters))
		if (cachedResponse !== undefined) {
			// console.log("cached: " + cachedResponse.url + " - " + JSON.stringify(parameters) + " - " + cachedResponse._body.substr(0, 50))
			if (onResponse)
				return Promise.resolve(onResponse(cachedResponse))
			return Promise.resolve((cachedResponse.json ? cachedResponse.json() : JSON.parse(cachedResponse)))
		}
		const pending = cache.pending[baseUrl + "/" + path + JSON.stringify(parameters)]
		if (pending !== undefined) {
			return new Promise((resolve) => {
				pending.push((response) => {
					if (onResponse)
						return resolve(onResponse(response))
					return resolve((response.json ? response.json() : JSON.parse(response)))
				})
			})
		}
		return RequestHelper.postRequest(http, baseUrl + "/" + path, parameters, BaseService.handleResponse(cache, baseUrl, path, parameters, onResponse))
	}

	static handleResponse(cache: Cache, baseUrl: string, path: string, parameters: any, onResponse?: any) {
		cache.pending[baseUrl + "/" + path + JSON.stringify(parameters)] = []
		return (response: any) => {
			cache.push(baseUrl + "/" + path + JSON.stringify(parameters), response)
			// console.log("requested: " + response.url + " - " + JSON.stringify(parameters) + " - " + response._body.substr(0, 50))
			try {
				if (onResponse)
					return onResponse(response)
				else
					return (response.json ? response.json() : JSON.parse(response))
			} catch (e) {
				return null
				// throw new Error("Couldn't parse response")
			}
		}
	}
}
