let randomSeed = 1
export function random() {
	const x = Math.sin(randomSeed++) * 10000
	return x - Math.floor(x)
}

export function setSeed(seed: number) {
	randomSeed = seed
}

export function randomFromList<T>(list: T[], weightFunction?: (value: T) => number): T {
	if (!list.length)
		return null
	weightFunction = weightFunction || (() => 1)
	const weights = list.map(weightFunction)
	let sum = weights.reduce((previous, current) => previous + current)
	const randomNumber = random() * sum
	sum = 0
	for (const w in weights) {
		sum += weights[w]
		if (randomNumber < sum)
			return list[w]
	}
	return list[list.length - 1]
}

class Sound {
	constructor(
		public representation,
		public point,
		public manner,
		public vocalisation,
		public syllabic) { }
}

class Language {
	words = {}
	orderedWords = []

	constructor(
		public sounds,
		public weights,
		public seed) {
	}

	randomSyllable() {
		const vocals = this.sounds.filter(e => e.syllabic)
		const consonants = this.sounds.filter(e => !e.syllabic)
		let syllable = this.weightedRandomFrom(vocals, "nucleus")
		if (random() < 0.7)
			syllable = this.weightedRandomFrom(consonants, "onset") + syllable
		if (random() < 0.7)
			syllable += this.weightedRandomFrom(consonants, "coda")
		return syllable
	}

	randomWord(max?, min?) {
		max = max || 4
		min = min || 2
		let word = ""
		const syllables = Math.floor(random() * (max + 1 - min)) + min
		for (let i = 0; i < syllables; i++)
			word += this.randomSyllable()
		return word
	}

	weightedRandomFrom(list: Sound[], placement) {
		placement = placement || "any"
		return randomFromList(list, (e) => {
			let modifier = 1
			for (const i in this.weights)
				if (this.weightFits(this.weights[i], e, placement))
					modifier *= this.weights[i].weight
			return modifier
		}).representation
	}

	weightFits(weight, sound, placement) {
		if (weight.placement !== placement && weight.placement !== "any" && placement !== "any")
			return false
		for (const i in weight.selection) {
			if (sound[i] !== weight.selection[i])
				return false
		}
		return true
	}
}

