﻿import { IdunModel } from "./idun-model"

export class JournalTemplate extends IdunModel {
	DBID: number
	Title: string
	Content: string
	Placements: string[]

	constructor(source?: any) {
		super()
		this.Placements = []
		if (!source)
			return
		for (const prop in source) this[prop] = source[prop]
		if (source.Placements)
			this.Placements = source.Placements.map(e => e)
	}

	getTitle(): string {
		return this.Title
	}

	check(query: string): boolean {
		return this.getTitle().toLowerCase().indexOf(query.toLowerCase()) > -1 ||
			this.Content.indexOf(query.toLowerCase()) > -1
	}
}
