import { Component, ViewChild, OnInit, ElementRef } from "@angular/core"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { LanguageService } from "idun-layout/language.service"
import { search } from "utility/search-helper"
import { UserService, FrontendState } from "idun-main/user.service"
import { PatientSummaryService } from "idun-main/model-services/patient-summary.service"
import { PatientSummary } from "idun-common/models/patient-summary"
import { PatientService } from "idun-main/model-services/patient.service"
import { JournalEntryService } from "idun-main/model-services/journal-entry.service"
import { Router } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LayoutService } from "idun-layout/layout.service"

@Component({
	selector: "idun-patient-bar",
	templateUrl: "patient-bar.component.html",
	styleUrls: ["patient-bar.component.scss"]
})
export class PatientBarComponent extends BaseSubscriber implements OnInit {
	navigater: NavigationHandler
	@ViewChild("searchInput") input
	currentPatient: any
	showSearchResult = false
	searchResults = []
	previousPatients

	constructor(
		myElement: ElementRef,
		router: Router,
		layoutService: LayoutService,
		private userService: UserService,
		private patientSummaryService: PatientSummaryService,
		public patientService: PatientService,
		public journalEntryService: JournalEntryService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit() {
		this.subscribe(this.userService.currentPatient$, (patient) => {
			this.updatePatient(patient)
		})
		this.subscribe(this.userService.frontendState$, (data: FrontendState) => {
			if (!data)
				return
			this.previousPatients = data.previousPatients
		})
	}

	updatePatient(patient) {
		this.currentPatient = patient
	}

	updateSearch(query) {
		if (!query) {
			this.showSearchResult = false
			return
		}
		this.showSearchResult = true

		this.subscribe(this.patientSummaryService.loadPage(0, 20, query),
			(list: PatientSummary[]) => {
				this.searchResults = <any>search(query,
					list, false, 20)
				// this.updateSearch()
			}, "models")
		// TODO: recode the following in the proper way
		/*this.patientSummaryService.getModels$().subscribe((patients) => {
			this.searchResults = []
			for (const i in patients) {
				const patient = patients[i]
				if (this.fitsQuery(patient, query))
					this.searchResults.push(patient)
			}
		}).unsubscribe()*/
	}

	fitsQuery(patient: PatientSummary, query: string): boolean {
		return patient.Name.toLowerCase().indexOf(query.toLowerCase()) > -1 ||
			(patient.hasCprNumber() ? patient.getCprNumber().indexOf(query) > -1 : false) ||
			patient.UniqueIdentification.indexOf(query.toLowerCase()) > -1
	}

	selectPatient(id: number) {
		this.userService.selectPatient(id)
		this.input.nativeElement.value = ""
		this.updateSearch(null)
	}

	public navigateToDetailPage(currentPatient: number) {
		this.navigater.both("patient/" + currentPatient, "patient-journal/" + currentPatient)
	}
}
