﻿import { SearchableIdunModel } from "./idun-model"
import { TelephoneNumber, Gender } from "./patient"
import { CompetenceSet } from "./competence-set"
import { TimeInterval } from "./time-interval"
import { DatabaseFileReference, UnsavedFile } from "./database-file"

export class Staff extends SearchableIdunModel {
	DBID: number
	ID: string
	Name: string
	Abbreviation: string
	Gender: Gender

	Email: string
	TelephoneNumbers: TelephoneNumber[]
	Address: string

	SalaryBankAccount: string
	PensionInfo: string
	Files: (UnsavedFile | DatabaseFileReference)[]

	StaffRoleID: number
	Competences: CompetenceSet
	WorkPeriod: number // in weeks
	WorkTimes: TimeInterval[]

	constructor(source?: any) {
		super()
		this.Address = ""
		this.TelephoneNumbers = [new TelephoneNumber({ Primary: true })]
		this.Files = []
		this.WorkTimes = []
		this.WorkPeriod = 4
		this.Competences = new CompetenceSet()
		if (source) {
			for (const prop in source) this[prop] = source[prop]
			if (source.TelephoneNumbers) {
				this.TelephoneNumbers = source.TelephoneNumbers.map(e => new TelephoneNumber(e))
				this.TelephoneNumbers.map(e => e.Type = "")
			}
			if (source.Files)
				this.Files = source.Files.map(e => new DatabaseFileReference(e))
			if (source.WorkTimes)
				this.WorkTimes = source.WorkTimes.map(e => new TimeInterval(e))
			this.Competences = new CompetenceSet(source.Competences)
		}
	}

	getPrimaryPhone() {
		for (const i in this.TelephoneNumbers)
			if (this.TelephoneNumbers[i].Primary)
				return this.TelephoneNumbers[i]
		return this.TelephoneNumbers[0]
	}

	getTitle(): string {
		return this.Name
	}

	searchAttributes() {
		return [this.Name, this.Abbreviation]
	}

	getWorkHours() {
		let sum = 0
		for (const time of this.WorkTimes)
			sum += time.getLengthInMinutes()
		return sum / 60 / this.WorkPeriod
	}
}
