import { Component, ViewChild, OnInit, ElementRef } from "@angular/core"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { LanguageService } from "idun-layout/language.service"
import { UserService } from "idun-main/user.service"
import { Router } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LayoutService } from "idun-layout/layout.service"
import { StaffNotification, StaffNotificationType } from "idun-common/models/staff-notification"
import { StaffNotificationService } from "idun-main/model-services/staff-notification.service"
import { StaffService } from "idun-main/model-services/staff.service"
import { Staff } from "idun-common/models/staff"
import { StatusService } from "idun-main/model-services/status.service"
import { DateHelper } from "utility/date-helper"
import { StatusTypeService } from "idun-main/model-services/status-type.service"
import { combineLatest } from "rxjs"
import { Status } from "idun-common/models/status"

@Component({
	selector: "idun-notifications-bar",
	templateUrl: "staff-notification-bar.component.html",
	styleUrls: ["staff-notification-bar.component.scss"]
})
export class StaffNotificationBarComponent extends BaseSubscriber implements OnInit {
	navigater: NavigationHandler
	notifications: StaffNotification[] = []
	shownNotifications: StaffNotification[]
	staff: Staff[]
	staffStatuses: { [index: number]: Status[] } = {}
	today = DateHelper.dayInterval(new Date())
	showPrintPopup = false
	showPatientArrivals = true
	showEdifactReferral = true
	filterTypes = [
		new NotificationFilter("showRead",
			notification => this.readNotification(notification),
			() => this.l.staffNotification.showRead),
		new NotificationFilter("showPatientArrivals",
			notification => notification.Type === StaffNotificationType.PatientArrival,
			() => this.l.staffNotification.patientArrivals),
		new NotificationFilter("showEdifactReferral",
			notification => notification.Type === StaffNotificationType.EdifactReferral,
			() => this.l.patient.referrals),
	]

	constructor(
		myElement: ElementRef,
		router: Router,
		layoutService: LayoutService,
		private userService: UserService,
		private staffNotificationService: StaffNotificationService,
		private staffService: StaffService,
		private staffStatusService: StatusService,
		private statusTypeService: StatusTypeService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit() {
		this.subscribe(
			combineLatest(
				this.staffNotificationService.getModels$(),
				this.staffService.getModels$(),
				this.statusTypeService.getModels$()
			), async (values) => {
				this.notifications = values[0]
				this.staff = values[1]
				this.shownNotifications = undefined

				for (const staffMember of this.staff)
					this.loadStatusesFor(staffMember)
			})

	}

	private async loadStatusesFor(staffMember: Staff) {
		const temp = await this.staffStatusService.getStatusList(staffMember.DBID, this.today.start, this.today.end)
		temp.subscribe(statuses => {
			this.staffStatuses[staffMember.DBID] = statuses
			if (Object.keys(this.staffStatuses).length === this.staff.length && this.shownNotifications === undefined)
				this.updateTimelineEntries()
		}).unsubscribe()
	}

	updateTimelineEntries() {
		this.showNotifications()
		this.filterNotifications()
		this.shownNotifications.sort((a, b) => b.Created.getTime() - a.Created.getTime())
	}

	private filterNotifications() {
		for (const filter of this.filterTypes)
			this.shownNotifications = this.shownNotifications
				.filter(e => filter.enabled || !filter.belongs(e))
	}

	public setupDate(notification: StaffNotification) {
		return DateHelper.dateTimeToString(notification.Created)
	}

	private showNotifications() {
		this.shownNotifications = []
		for (const notification of this.notifications) {
			const staffID = this.userService.user.StaffID
			const staffForUser = this.staffService.getModel(staffID)

			const targetedStaffThroughGroup = this.staff
				.filter(e => notification.TargetedStaffGroupIds.includes(e.StaffRoleID))
				.map(e => e.DBID)
			const targetedStaff = notification.TargetedStaffIds.map(e => e).concat(targetedStaffThroughGroup)

			const shouldNotifyAdmin = this.notificationMissingRecipients(targetedStaff)

			// Admin gets it. Note it's hard coded, and in the future we might want to make a proper check.
			if (shouldNotifyAdmin && staffForUser.StaffRoleID === 1)
				targetedStaff.push(staffForUser.DBID)
			if (targetedStaff.includes(staffForUser.DBID))
				this.shownNotifications.push(notification)
		}
	}

	private notificationMissingRecipients(targetedStaff: number[]) {
		for (const staff of targetedStaff.map(id => this.staff.find(x => x.DBID === id)))
			for (const status of this.staffStatuses[staff.DBID])
				if (this.statusTypeService.getModel(status.StatusTypeID).Working)
					return false
		return true
	}

	readNotification(notification: StaffNotification) {
		return notification.ReadByStaffIds.includes(this.userService.user.StaffID)
	}

	isNotificationPatientArrival(type: StaffNotificationType) {
		return type === StaffNotificationType.PatientArrival
	}

	isNotificationEdifactReferral(type: StaffNotificationType) {
		return type === StaffNotificationType.EdifactReferral
	}

	markAsRead(notification: StaffNotification) {
		notification.ReadByStaffIds.push(this.userService.user.StaffID)
		this.staffNotificationService.update(notification)
	}
}

class NotificationFilter {
	constructor(
		public title: string,
		public belongs: (notification: StaffNotification) => boolean,
		public label: () => string,
		public enabled = true
	) { }
}
