﻿import { BookingRequest, RequestStatus } from "idun-common/models/booking-request"
import { ModelTestData } from "./model-test-data"

const baseUrl = "api/bookingrequest/"

const list = [
	new BookingRequest({
		DBID: 1,
		Requester: "TODO",
		Clinic: "TODO",
		ProcedureID: 1,
		Times: { StartTime: new Date(2000, 2, 2, 12, 30), EndTime: new Date(2000, 2, 2, 12, 45) },
		PatientData: "TODO",
		Status: RequestStatus.Accepted,
		CalendarEventID: 1
	})
]

const otherResponses = {}

otherResponses[baseUrl + "getunhandled"] = (parameters) => {
	return JSON.stringify(list)
}

export function BookingRequestResponses() {
	return {
		...ModelTestData.commonResponses(BookingRequest, baseUrl, list),
		...otherResponses
	}
}
