import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { LanguageService } from "idun-layout/language.service"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { ProcedureTypeService } from "idun-main/model-services/procedure-type.service"
import { Procedure, ProcedureVariation } from "idun-common/models/procedure"
import { Group, DisplaySetup } from "idun-common/filter-list.component"

@Component({
	selector: "idun-select-variant-by-placement",
	templateUrl: "select-variant-by-placement.component.html",
	styleUrls: ["select-variant-by-placement.component.scss"]
})
export class SelectVariantByPlacementComponent extends BaseSubscriber implements OnInit {
	_placement = ""
	@Input() set placement(value: string[]) {
		if (!value || value[0] === this._placement)
			return
		this._placement = value[0]
		this.updateShownProcedureVariations()
	}
	@Output() selectProcedureVariation = new EventEmitter<ProcedureVariationPair>()

	procedures: Procedure[] = []
	shownProcedureVariations: { procedure: Procedure, variation: ProcedureVariation }[] = []
	listGroups: Group<any>[] = []
	listDisplaySetup = new DisplaySetup(
		() => this.l.procedureType.namePlural,
		() => this.l.procedure.namePlural
	)

	constructor(
		public procedureService: ProcedureService,
		public procedureTypeService: ProcedureTypeService,
		public l: LanguageService
	) {
		super()
	}

	ngOnInit() {
		this.subscribe(this.procedureTypeService.getModels$(), (procedureTypes) => {
			this.listGroups = procedureTypes.map(x => {
				return {
					title: () => x.getTitle(),
					contains: (entry) => entry.procedure.ProcedureTypeID === x.DBID
				}
			})
		})
		this.subscribe(this.procedureService.getModels$(), (procedures) => {
			this.procedures = procedures
			this.updateShownProcedureVariations()
		})
	}

	updateShownProcedureVariations() {
		this.shownProcedureVariations = flatten(this.procedures
			.map(mapVariations))
			.filter(e => anyPlacedAt(this._placement, e.variation.Placements))
	}
}

function mapVariations(procedure: Procedure) {
	return procedure.Variations.map(variation => {
		return new ProcedureVariationPair(procedure, variation)
	})
}

function flatten<T>(listOfLists: T[][]) {
	return listOfLists.reduce((a, b) => a.concat(b), [])
}

function anyPlacedAt(placement: string, placements: string[]) {
	if (!placement)
		return true
	if (!placements.length)
		return true
	return !!placements.find(p => p.includes(placement))
}

export class ProcedureVariationPair {
	constructor(
		public procedure: Procedure,
		public variation: ProcedureVariation) { }

	search(query: string) {
		return this.procedure.search(query)
	}
}
