import { LanguageService } from "idun-layout/language.service"
import { OnInit, Component, forwardRef, OnDestroy } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { ProcedureType } from "idun-common/models/procedure-type"
import { Procedure } from "idun-common/models/procedure"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { ProcedureTypeService } from "idun-main/model-services/procedure-type.service"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { CompetenceSet } from "idun-common/models/competence-set"
import { Subscriber } from "utility/subscriber"
import { FormRow } from "idun-layout/form/form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormCompetencesComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormCompetencesComponent),
	multi: true
}

@Component({
	selector: "idun-form-competences",
	templateUrl: "form-competences.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormCompetencesComponent extends FormBasicComponent<CompetenceSet> implements OnInit, OnDestroy {
	procedures: Procedure[]
	procedureTypes: ProcedureType[]
	subscriber = new Subscriber()

	rowGroups: {
		getTitle: () => string,
		getHint: () => string,
		set: (value: boolean) => void,
		isSet: () => boolean,
		expanded: boolean,
		rows: { getTitle: () => string, getHint: () => string, set: (value: boolean) => void, isSet: () => boolean }[]
	}[] = []

	constructor(
		public procedureService: ProcedureService,
		public procedureTypeService: ProcedureTypeService,
		public l: LanguageService
	) {
		super()
	}

	ngOnInit(): void {
		this.subscriber.subscribe(this.procedureService.getModels$(), (procedures) => {
			this.procedures = procedures
			this.updateModelGroups()
		})
		this.subscriber.subscribe(this.procedureTypeService.getModels$(), (procedureTypes) => {
			this.procedureTypes = procedureTypes
			this.updateModelGroups()
		})
	}

	ngOnDestroy() {
		this.subscriber.unsubscribe()
	}

	private updateModelGroups() {
		this.rowGroups = []
		for (const i in this.procedureTypes)
			this.rowGroups.push(this.newRowGroup(this.procedureTypes[i]))
	}

	private newRowGroup(procedureType: ProcedureType) {
		return {
			getTitle: () => procedureType.getTitle(),
			getHint: () => "",
			set: (value: boolean) => {
				this.value.toggleProcedureTypeCompetence(procedureType, this.procedures)
				this.onChangeCallback(this.value)
			},
			isSet: () => {
				if (!this.value)
					return false
				return this.value.AllowedProcedureTypes.includes(procedureType.DBID)
			},
			expanded: false,
			rows: this.procedures
				.filter(e => e.ProcedureTypeID === procedureType.DBID)
				.map(procedure => this.newRow(procedure))
		}
	}

	private newRow(procedure: Procedure) {
		return {
			getTitle: () => procedure.getTitle(),
			getHint: () => "",
			set: (value: boolean) => {
				this.value.toggleProcedureCompetence(procedure)
				this.onChangeCallback(this.value)
			},
			isSet: () => {
				if (!this.value)
					return false
				return this.value.canPerform(procedure)
			}
		}
	}
}

export class FormCompetencesRow extends FormRow { }
