﻿import { Component, forwardRef } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { FormRow, OptionInfo, FormRowOptions, FieldInput } from "./form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormSelectComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormSelectComponent),
	multi: true
}

@Component({
	selector: "idun-form-select",
	template: `
<idun-form-input-container [hint]="hint" [label]="label" [display]="display"
		[showLabel]="showLabel" [error]="errorToShow()" [showAsterisk]="showAsterisk()">
	<select [(ngModel)]="value" (blur)="onBlur()" [disabled]="display">
		<ng-content select="option"></ng-content>
	</select>
	<ng-content></ng-content>
</idun-form-input-container>`,
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormSelectComponent extends FormBasicComponent<string> {
	rowClass = FormSelectRow
}

export class FormSelectRow extends FormRow {
	constructor(
		field: FieldInput,
		options: Options = { options: [] }
	) {
		super(field, options)
	}
}

interface Options extends FormRowOptions {
	options: OptionInfo[]
}
