import { Component, OnInit, ElementRef, Input, Output, EventEmitter } from "@angular/core"
import { Router } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LanguageService } from "idun-layout/language.service"
import { LayoutService } from "idun-layout/layout.service"
import { PatientEvent } from "idun-common/models/patient-event"
import { Patient } from "idun-common/models/patient"
import { PatientEventService } from "idun-main/model-services/patient-event.service"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { UserService } from "idun-main/user.service"
import { ProcedureEvent, CompletionStatus, PaymentStatus } from "idun-common/models/procedure-event"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { PartnerService } from "idun-main/model-services/partner.service"
import { TreatmentCodeService } from "idun-main/model-services/treatment-code.service"
import { StaffService } from "idun-main/model-services/staff.service"
import { ResourceService } from "idun-main/model-services/resource.service"
import { DateHelper } from "utility/date-helper"
import { Warning } from "idun-layout/components/warning-box.component"
import { ProcedureEventAnalysisService, ProcedureEventAnalysis } from "idun-main/other-services/procedure-event-analysis.service"

@Component({
	selector: "idun-patient-event-box",
	templateUrl: "patient-event-box.component.html",
	styleUrls: ["patient-event-box.component.scss"]
})
export class PatientEventBoxComponent extends BaseSubscriber implements OnInit {
	navigater: NavigationHandler

	@Input() patientEvent: PatientEvent
	@Input() patient: Patient
	@Input() warnings: { [index: string]: Warning } = {}
	_analysis = new Map<ProcedureEvent, ProcedureEventAnalysis>()
	@Input() set analysis(value: Map<ProcedureEvent, ProcedureEventAnalysis>) {
		this._analysis = value
	}
	@Input() buttons = []
	@Input() showNavigation = false
	@Output() close = new EventEmitter()
	@Output() selectProcedureEvent = new EventEmitter<ProcedureEvent>()
	currency = "DKK"

	constructor(
		router: Router,
		myElement: ElementRef,
		layoutService: LayoutService,
		private patientEventService: PatientEventService,
		private userService: UserService,
		public procedureService: ProcedureService,
		public partnerService: PartnerService,
		public staffService: StaffService,
		public resourceService: ResourceService,
		public treatmentCodeService: TreatmentCodeService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit(): void {
		this.userService.clinicSettings$.subscribe((settings) => {
			this.currency = settings.Currency
		})
	}

	hasProblems(procedureEvent: ProcedureEvent) {
		const problems = this._analysis.get(procedureEvent).hasProblems()
		return problems
	}

	titleFor(procedureEvent: ProcedureEvent) {
		const procedure = this.procedureService.getModel(procedureEvent.ProcedureId)
		if (!procedure)
			return this.l.patientEvent.missingProcedure
		if (procedure.Variations.length > 1)
			return procedure.Title + " - " + procedureEvent.Variation
		return procedure.Title
	}

	staffFor(procedureEvent: ProcedureEvent) {
		return procedureEvent.OccupiedStaff
			.map(id => this.staffService.getModel(id).Name)
			.join(", ")
	}

	timesFor(procedureEvent: ProcedureEvent) {
		return DateHelper.timeIntervalToString(
			procedureEvent.Times.StartTime,
			procedureEvent.Times.EndTime)
	}

	timeText(): string {
		if (!this.anyProcedureHasBeenSelected())
			return this.l.patientEvent.missingProcedure
		const times = DateHelper.timeIntervalToString(
			this.patientEvent.ProcedureEvents[0].Times.StartTime,
			this.patientEvent.ProcedureEvents[this.patientEvent.ProcedureEvents.length - 1].Times.EndTime)
		const duration = this.patientEvent.ProcedureEvents
			.map(e => this.durationOf(e))
			.sum()
		return `${times} (${this.l.format(this.l.date.xMinutes, duration)})`
	}

	private anyProcedureHasBeenSelected() {
		return this.patientEvent.ProcedureEvents.some(x => !!x.ProcedureId)
	}

	private durationOf(procedureEvent: ProcedureEvent) {
		const procedure = this.procedureService.getModel(procedureEvent.ProcedureId)
		return procedure ? procedure.Duration : 0
	}

	priceText() {
		if (!this.anyProcedureHasBeenSelected())
			return this.l.patientEvent.missingProcedure
		const price = this.patientEvent.ProcedureEvents
			.map(e => e.Price)
			.reduce((sum, current) => sum + (current || 0), 0)
		return "" + price + " " + this.currency
	}

	completionStatusFor(procedureEvent: ProcedureEvent) {
		switch (procedureEvent.CompletionStatus) {
			case CompletionStatus.NotStarted:
				return this.l.patientEvent.completionStatusNotStarted
			case CompletionStatus.InProgress:
				return this.l.patientEvent.completionStatusInProgress
			case CompletionStatus.Finished:
				return this.l.patientEvent.completionStatusFinished
		}
	}

	paymentStatusFor(procedureEvent: ProcedureEvent) {
		switch (procedureEvent.PaymentStatus) {
			case PaymentStatus.NotStarted:
				return this.l.patientEvent.paymentStatusNotStarted
			case PaymentStatus.Sent:
				return this.l.patientEvent.paymentStatusSent
			case PaymentStatus.Paid:
				return this.l.patientEvent.paymentStatusPaid
		}
	}

	async bookPatientEvent() {
		const newEvent = await this.patientEventService.add(this.patientEvent)
		this.navigater.current("patient-event/" + newEvent.DBID)
	}
}
