﻿import { Component, OnInit } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { StaffNotificationType } from "idun-common/models/staff-notification"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { StaffService } from "idun-main/model-services/staff.service"
import { StaffGroupService } from "idun-main/model-services/staff-group.service"
import { StaffNotificationReceiversService } from "idun-main/model-services/staff-notification-receivers.service"
import { StaffNotificationReceivers } from "idun-common/models/staff-notification-receivers"

@Component({
	selector: "idun-staff-notification-settings",
	templateUrl: "staff-notification-settings.component.html"
})
export class StaffNotificationSettingsComponent extends BaseSubscriber implements OnInit {
	staffGroups = []
	staff = []
	staffNotificationReceivers: StaffNotificationReceivers[] = []
	lastSaved: Date

	constructor(
		private staffService: StaffService,
		private staffGroupService: StaffGroupService,
		private staffNotificationReceiversService: StaffNotificationReceiversService,
		public l: LanguageService
	) { super() }

	ngOnInit(): void {
		this.subscribe(this.staffGroupService.getModels$(), (staffGroups) => {
			this.staffGroups = staffGroups
		})

		this.subscribe(this.staffService.getModels$(), (staff) => {
			this.staff = staff
		})

		this.subscribe(this.staffNotificationReceiversService.getModels$(), (receivers) => {
			this.staffNotificationReceivers = receivers
		})
	}

	receivingNotificationsAlready(staffGroupID: number, notificationType: string) {
		var receivingNotification = false
		this.staffNotificationReceivers.forEach(receiver => {
			if (receiver.TargetedStaffGroupIds.includes(staffGroupID) && receiver.Type === StaffNotificationType[notificationType])
				receivingNotification = true
		})
		return receivingNotification
	}

	markForReceptionOfNotification(staffGroupID: number, notificationType: string) {
		if (!(this.lastSaved === undefined || (this.lastSaved.getTime() - (new Date()).getTime()) > 500))
			return

		this.staffNotificationReceivers.forEach(receiver => {
			if (receiver.Type !== StaffNotificationType[notificationType])
				return

			if (receiver.TargetedStaffGroupIds.includes(staffGroupID))
				receiver.TargetedStaffGroupIds = receiver.TargetedStaffGroupIds.filter(i => i !== staffGroupID)
			else
				receiver.TargetedStaffGroupIds.push(staffGroupID)
			this.staffNotificationReceiversService.update(receiver)
		})
		this.lastSaved = new Date
	}
}
