import { Component, OnInit, ElementRef } from "@angular/core"
import { Router } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LanguageService } from "idun-layout/language.service"
import { LayoutService } from "idun-layout/layout.service"
import { PatientEvent } from "idun-common/models/patient-event"
import { Patient } from "idun-common/models/patient"
import { PatientEventService } from "idun-main/model-services/patient-event.service"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { UserService } from "idun-main/user.service"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { PartnerService } from "idun-main/model-services/partner.service"
import { TreatmentCodeService } from "idun-main/model-services/treatment-code.service"

@Component({
	selector: "idun-booking-page",
	templateUrl: "booking-page.component.html",
	styleUrls: ["booking-page.component.scss"]
})
export class BookingPageComponent extends BaseSubscriber implements OnInit {
	navigater: NavigationHandler

	patientEvent: PatientEvent
	selectedProcedureEvent: ProcedureEvent
	patient: Patient
	currency = "DKK"
	isSelectingFirstProcedure = false

	constructor(
		router: Router,
		myElement: ElementRef,
		layoutService: LayoutService,
		private patientEventService: PatientEventService,
		private userService: UserService,
		public procedureService: ProcedureService,
		public partnerService: PartnerService,
		public treatmentCodeService: TreatmentCodeService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit(): void {
		this.subscribe(this.userService.currentPatient$, patient => {
			this.patient = patient
			this.updateEvent()
		})
		this.subscribe(this.patientEventService.patientEvent$, patientEvent => {
			this.patientEvent = patientEvent
			this.updateEvent()
		})
		this.userService.clinicSettings$.subscribe((settings) => {
			this.currency = settings.Currency
		})
	}

	private updateEvent() {
		if (!this.patient || this.patientEvent === undefined)
			return
		if (!this.patientEvent || this.patientEvent.PatientId !== this.patient.DBID)
			this.setupNewEvent()
		if (!this.hasSelectedProcedure())
			this.showFirstProcedureSelection()
	}

	private setupNewEvent() {
		this.patientEvent = new PatientEvent()
		this.patientEvent.PatientId = this.patient.DBID
		this.patientEvent.ProcedureEvents.push(new ProcedureEvent())
		this.patientEventService.patientEvent$.next(this.patientEvent)
	}

	private hasSelectedProcedure() {
		return this.patientEvent.ProcedureEvents.length > 0 && this.patientEvent.ProcedureEvents[0].ProcedureId
	}

	private showFirstProcedureSelection() {
		this.selectedProcedureEvent = this.patientEvent.ProcedureEvents[0]
		this.isSelectingFirstProcedure = true
	}

	showMainBox() {
		return this.patientEvent && this.patient && !this.isSelectingFirstProcedure
	}

	selectProcedureEvent(procedureEvent: ProcedureEvent) {
		if (this.selectedProcedureEvent === procedureEvent)
			this.selectedProcedureEvent = null
		else
			this.selectedProcedureEvent = procedureEvent
	}

	addProcedureEvent() {
		this.selectedProcedureEvent = new ProcedureEvent()
		this.patientEvent.ProcedureEvents.push(this.selectedProcedureEvent)
	}

	titleFor(procedureEvent: ProcedureEvent) {
		const procedure = this.procedureService.getModel(procedureEvent.ProcedureId)
		if (!procedure)
			return ""
		if (procedure.Variations.length > 1)
			return procedure.Title + " - " + procedureEvent.Variation
		return procedure.Title
	}

	timeText(): string {
		if (!this.anyProcedureHasBeenSelected())
			return this.l.patientEvent.missingProcedure
		const duration = this.patientEvent.ProcedureEvents
			.map(e => this.durationOf(e))
			.reduce((sum, current) => sum + current, 0)
		return this.l.format(this.l.date.xMinutes, duration)
	}

	private anyProcedureHasBeenSelected() {
		for (const event of this.patientEvent.ProcedureEvents)
			if (event.ProcedureId)
				return true
		return false
	}

	private durationOf(procedureEvent: ProcedureEvent) {
		const procedure = this.procedureService.getModel(procedureEvent.ProcedureId)
		return procedure ? procedure.Duration : 0
	}

	priceText() {
		if (!this.anyProcedureHasBeenSelected())
			return this.l.patientEvent.missingProcedure
		const price = this.patientEvent.ProcedureEvents
			.map(e => e.Price)
			.reduce((sum, current) => sum + (current || 0), 0)
		return "" + price + " " + this.currency
	}

	readyToFindTimes() {
		if (!this.patientEvent || this.patientEvent.ProcedureEvents.length === 0)
			return false
		for (const event of this.patientEvent.ProcedureEvents)
			if (!event.ProcedureId)
				return false
		return true
	}

	acceptProcedureEvent() {
		this.isSelectingFirstProcedure = false
		this.selectedProcedureEvent = null
		this.emitPatientEvent()
	}

	removeProcedureEvent() {
		const index = this.patientEvent.ProcedureEvents.indexOf(this.selectedProcedureEvent)
		this.patientEvent.ProcedureEvents.splice(index, 1)
		this.selectedProcedureEvent = null
	}

	emitPatientEvent() {
		if (this.readyToFindTimes())
			this.patientEventService.patientEvent$.next(this.patientEvent)
	}
}

