import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { Observable, BehaviorSubject } from "rxjs"
import { LanguageService } from "idun-layout/language.service"
import { PatientEvent } from "idun-common/models/patient-event"
import { TimeInterval } from "idun-common/models/time-interval"
import { ProcedureService } from "./procedure.service"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { PatientSummaryService } from "./patient-summary.service"

@Injectable()
export class PatientEventService extends ExtendedService<PatientEvent> {
	public patientEvent$ = new BehaviorSubject<PatientEvent>(new PatientEvent())

	constructor(
		http: Http,
		l: LanguageService,
		private patientSummaryService: PatientSummaryService,
		private procedureService: ProcedureService
	) {
		super(http, l,
			PatientEvent,
			"api/PatientEvent",
			"patient-event",
			() => l.patientEvent
		)
		this.loadAll = false
		this.setupList({
			entryHeader: (model) => model.timeIntervalString() + " " + this.getPatientName(model.PatientId),
			contentList: [(model) => {
				return model.ProcedureEvents
					.map(e => this.getProcedureEventName(e))
					.join(", ")
			}]
		})
	}

	private getProcedureEventName(procedureEvent: ProcedureEvent) {
		const procedure = this.procedureService.getModel(procedureEvent.ProcedureId)
		if (procedure)
			return procedure.Title
		return ""
	}

	private getPatientName(patientId: number) {
		const patient = this.patientSummaryService.getModel(patientId)
		if (patient)
			return patient.Name
		return ""
	}

	getEvents(start: Date, end: Date): Promise<Observable<PatientEvent[]>> {
		return this.baseGetSubList("GetEvents", { start: start, end: end })
	}

	getEventsFor(patientId: number): Promise<Observable<PatientEvent[]>> {
		return this.baseGetSubList("GetEventsFor", { id: patientId })
	}

	getAvailableTimes(patientEvent: PatientEvent, interval: { start: Date, end: Date }): Promise<TimeInterval[]> {
		return this.baseRequest("GetAvailableTimes", { patientEvent, start: interval.start, end: interval.end },
			(source) => { return this.parseListAs(source, e => new TimeInterval(e)) })
	}
}
