﻿import { Component, forwardRef, Input } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { FormRow } from "./form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormDateTimeComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormDateTimeComponent),
	multi: true
}

@Component({
	selector: "idun-form-date-time",
	templateUrl: "form-date-time.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormDateTimeComponent extends FormBasicComponent<Date> {
	rowClass = FormDateTimeRow
	@Input() hideDate = false

	constructor() {
		super()
	}
}

export class FormDateTimeRow extends FormRow {
	basicValidityErrors(value) {
		if (!value)
			return
		if (typeof value === "string")
			return () => "unknown error"
	}
}
