﻿import { Component, forwardRef, Input, OnInit, OnDestroy } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { BaseService } from "idun-common/base-service"
import { IdunModel } from "idun-common/models/idun-model"
import { Subscriber } from "utility/subscriber"
import { FormRow, FormRowOptions, FieldInput } from "idun-layout/form/form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormListBoxModelComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormListBoxModelComponent),
	multi: true
}

@Component({
	selector: "idun-form-list-box-model",
	templateUrl: "form-list-box-model.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormListBoxModelComponent extends FormBasicComponent<number[]> implements OnInit, OnDestroy {
	rowClass = FormListBoxModelRow
	showRight = false
	@Input("showRight") set _showRight(value) {
		this.showRight = value === "false" ? false : value // yuck
	}
	@Input() service: BaseService<any>

	list: any[]
	subscriber = new Subscriber()

	rightSelection = []
	leftSelection = []

	get selectedList() {
		if (!this.list)
			return []
		return this.list.filter((e: IdunModel) => {
			for (const key in this.getKeys())
				if (+this.value[key] === +e.DBID)
					return true
			return false
		})
	}

	get unselectedList() {
		if (!this.list)
			return []
		return this.list.filter((e: IdunModel) => {
			for (const key in this.getKeys())
				if (+this.value[key] === +e.DBID)
					return false
			return true
		})
	}

	get leftList() {
		return this.showRight ? this.unselectedList : this.selectedList
	}

	get rightList() {
		return this.showRight ? this.selectedList : this.unselectedList
	}

	getKeys() {
		if (this.value)
			return Object.keys(this.value)
		else return []
	}

	ngOnInit(): void {
		this.subscriber.subscribe(this.service.getModels$(),
			(list: any) => { this.list = list }, "list")
	}

	ngOnDestroy(): void {
		this.subscriber.unsubscribe()
	}

	add(id: number): void {
		this.value.push(id)
		this.onChangeCallback(this.value)
	}

	remove(id: number): void {
		const index = this.value.findIndex(e => +e === +id)
		if (index !== undefined) {
			this.value.splice(index, 1)
			this.onChangeCallback(this.value)
		}
	}

	getTitle(id) {
		const thing = IdunModel.getModelFromList(id, this.list)
		if (thing)
			return thing.getTitle()
		return "Unknown"
	}

	selectFromRight() {
		for (const i in this.rightSelection)
			if (this.showRight)
				this.remove(this.rightSelection[i])
			else
				this.add(this.rightSelection[i])
		this.onBlur()
	}

	selectFromLeft() {
		for (const i in this.leftSelection)
			if (this.showRight)
				this.add(this.leftSelection[i])
			else
				this.remove(this.leftSelection[i])
		this.onBlur()
	}

	changeRight(options) {
		this.rightSelection = Array.apply(null, options)  // convert to real Array
			.filter(option => option.selected)
			.map(option => option.value)
	}

	changeLeft(options) {
		this.leftSelection = Array.apply(null, options)  // convert to real Array
			.filter(option => option.selected)
			.map(option => option.value)
	}
}

export class FormListBoxModelRow extends FormRow {
	constructor(
		field: FieldInput,
		options: Options
	) {
		super(field, options)
	}
}

interface Options extends FormRowOptions {
	service: BaseService<any>
}
