export function initApp(
	userService: UserService,
	modelService: ModelService,
	bookingRequestService: BookingRequestService,
	bookmarkService: BookmarkService,
	calendarEventService: CalendarEventService,
	cardReaderTokenService: CardReaderTokenService,
	treatmentCodeTypeService: TreatmentCodeTypeService,
	edifactService: EdifactService,
	partnerService: PartnerService,
	invoiceService: InvoiceService,
	journalEntryService: JournalEntryService,
	journalTemplateService: JournalTemplateService,
	staffNotificationService: StaffNotificationService,
	patientService: PatientService,
	patientEventService: PatientEventService,
	patientSummaryService: PatientSummaryService,
	patientNoteService: PatientNoteService,
	periodPlanService: PeriodPlanService,
	procedureService: ProcedureService,
	procedureEventService: ProcedureEventService,
	procedureTypeService: ProcedureTypeService,
	resourceService: ResourceService,
	staffService: StaffService,
	staffGroupService: StaffGroupService,
	staffStatusService: StatusService,
	staffStatusRequestService: StatusRequestService,
	statusTypeService: StatusTypeService,
	treatmentCodeService: TreatmentCodeService,
) {
	return () => {
		const addService = (service: ExtendedService<IdunModel>, serviceName: string) => {
			modelService[service.ngUrl] = service
			modelService[serviceName] = service
		}
		const addBaseService = (service: BaseService<IdunModel>, serviceName: string, thing: string) => {
			modelService[thing] = service
			modelService[serviceName] = service
		}
		addService(bookingRequestService, "bookingRequestService")
		addBaseService(bookmarkService, "bookmarkService", "bookmark")
		addService(calendarEventService, "calendarEventService")
		addBaseService(cardReaderTokenService, "cardReaderTokenService", "card-reader-token")
		addService(treatmentCodeTypeService, "treatmentCodeTypeService")
		addBaseService(edifactService, "edifactService", "edifact")
		addService(partnerService, "partnerService")
		addService(invoiceService, "invoiceService")
		addService(journalEntryService, "journalEntryService")
		addService(journalTemplateService, "journalTemplateService")
		addBaseService(staffNotificationService, "staffNotificationService", "staff-notification")
		addService(patientService, "patientService")
		addService(patientEventService, "patientEventService")
		addBaseService(patientSummaryService, "patientSummaryService", "patient-summary")
		addService(patientNoteService, "patientNoteService")
		addService(periodPlanService, "periodPlanService")
		addService(procedureService, "procedureService")
		addService(procedureEventService, "procedureEventService")
		addService(procedureTypeService, "procedureTypeService")
		addService(resourceService, "resourceService")
		addService(staffService, "staffService")
		addService(staffGroupService, "staffGroupService")
		addBaseService(staffStatusService, "staffStatusService", "staff-status")
		addService(staffStatusRequestService, "staffStatusRequestService")
		addService(statusTypeService, "statusTypeService")
		addService(treatmentCodeService, "treatmentCodeService")
		userService.loadUser()
		userService.loadClinicName()
	}
}

