﻿import { staffTestData } from "./staff-test-data"

const baseUrl = "api/user/"

var user = {
	Name: "MISSING_NAME",
	FrontendState: JSON.stringify({
		previousPatients: [],
		zoomLevel: 14
	}),
	Settings: JSON.stringify({
		backgroundImage: "assets/images/Backgrounds/2.jpg"
	}),
	StaffID: 1
}

if (localStorage[baseUrl])
	user = JSON.parse(localStorage[baseUrl])

const otherResponses = {}

otherResponses[baseUrl + "get"] = (parameters) => {
	user.Name = staffTestData.find(x => x.DBID === user.StaffID).Name
	return JSON.stringify(user)
}

otherResponses[baseUrl + "setfrontendstate"] = (parameters) => {
	user.FrontendState = parameters.state
	localStorage[baseUrl] = JSON.stringify(user)
	return JSON.stringify("")
}

otherResponses[baseUrl + "setsettings"] = (parameters) => {
	user.Settings = parameters.state
	localStorage[baseUrl] = JSON.stringify(user)
	return JSON.stringify("")
}

otherResponses[baseUrl + "updatepassword"] = (parameters) => {
	return JSON.stringify("success")
}

export function UserResponses() {
	return {
		...otherResponses
	}
}
