import { Component } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { Language } from "idun-layout/language/language"

@Component({
	selector: "idun-translation",
	templateUrl: "translation.component.html"
})
export class TranslationComponent {
	possibleLanguages = ["dansk", "english"]
	languages: Language[] = []
	categories: string[]
	shownCategory = "common"
	translationKeys: { [category: string]: string[] }

	constructor(
		public l: LanguageService
	) {
		this.exctractCategoriesAndKeys()
		l.hasLoaded$.subscribe(() => {
			this.languages[0] = l.language
		})
	}

	private exctractCategoriesAndKeys() {
		this.categories = this.exctractCategories()
		this.translationKeys = this.exctractTranslationKeys()
	}

	private exctractCategories() {
		return Object.keys(new Language()).filter(e => e !== "language")
	}

	private exctractTranslationKeys() {
		const language = new Language()
		const translationKeys = {}
		for (const category of this.categories)
			translationKeys[category] = Object.keys(language[category])
		return translationKeys
	}

	load(language: string) {
		if (this.l.language.language === language)
			return
		this.l.loadLanguage(language).then((response) => {
			this.languages.push(response)
			if (!response.language)
				response.language = language
		})
	}

	unload(language: string) {
		const index = this.languages.findIndex(e => e.language === language)
		this.languages.splice(index, 1)
	}

	addLanguage(languageName: string) {
		const newLanguage = new Language({ language: languageName })
		for (const category of this.categories)
			for (const translationKey of this.translationKeys[category])
				newLanguage[category][translationKey] = ""
		this.languages.push(newLanguage)
		if (!this.possibleLanguages.includes(languageName))
			this.possibleLanguages.push(languageName)
	}

	download(language: string) {
		const languageObject = this.languages.find(e => e.language === language)
		const blob = new Blob([JSON.stringify(languageObject, null, "\t")], { type: "text/plain;charset=utf-8" })
		const a = document.createElement("a")
		a.download = language + ".json"
		a.href = URL.createObjectURL(blob)
		document.body.appendChild(a)
		a.click()
		document.body.removeChild(a)
	}

	isShown(language: string): boolean {
		return !!this.languages.find(e => e.language === language)
	}
}
