import { Procedure } from "idun-common/models/procedure"
import { TimeInterval } from "idun-common/models/time-interval"
import { Staff } from "idun-common/models/staff"
import { Resource } from "idun-common/models/resource"
import { TimeIntervalSeries } from "idun-common/models/time-interval-series"
import { getWorkIntervalTestDataFor } from "./test-data/work-interval-test-data"
import { getCalendarEventTestDataForStaff, getCalendarEventTestDataForResource } from "./test-data/calendar-event-test-data"
import { getProcedureEventTestDataForStaff, getProcedureEventTestDataForResource } from "./test-data/procedure-event-test-data"
import { testResourceCompetences } from "./test-data/resource-test-data"

export class BookingTimeService {
	private procedure: Procedure
	private interval: TimeInterval

	constructor(
		private staff: Staff[],
		private resources: Resource[]
	) { }

	findAvailableTimesFor(procedure: Procedure, interval: TimeInterval) {
		this.procedure = procedure
		this.interval = interval
		const staffTimes = this.findAvailableStaffTimes()
		const resourceTimes = this.findAvailableResourceTimes()
		const availableTimes = staffTimes.intersection(resourceTimes)
		return this.createProcedureIntervals(availableTimes)
	}

	private findAvailableStaffTimes() {
		let interval = new TimeIntervalSeries()
		for (const staff of this.staffThatCanPerformProcedure())
			interval = interval.add(this.availableTimesForStaff(staff))
		return interval
	}

	private staffThatCanPerformProcedure() {
		return this.staff.filter(e => e.Competences.canPerform(this.procedure))
	}

	private availableTimesForStaff(staff: Staff) {
		return this.workTimesFor(staff)
			.subtract(this.eventTimesForStaff(staff))
	}

	private workTimesFor(staff: Staff) {
		return new TimeIntervalSeries(
			...getWorkIntervalTestDataFor(staff.DBID, this.interval)
				.map(e => e.Times)
		)
	}

	private eventTimesForStaff(staff: Staff) {
		return new TimeIntervalSeries(
			...getCalendarEventTestDataForStaff(staff.DBID, this.interval)
				.map(e => e.Times),
			...getProcedureEventTestDataForStaff(staff.DBID, this.interval)
				.map(e => e.Times)
		)
	}

	private findAvailableResourceTimes() {
		let interval = new TimeIntervalSeries()
		for (const resource of this.resourcesUsableForProcedure())
			interval = interval.add(this.availableTimesForResource(resource))
		return interval
	}

	private resourcesUsableForProcedure() {
		return this.resources
			.filter(e => testResourceCompetences[e.DBID].canPerform(this.procedure))
	}

	private availableTimesForResource(resource: Resource) {
		return new TimeIntervalSeries(this.interval)
			.subtract(this.eventTimesForResource(resource))
	}

	private eventTimesForResource(resource: Resource) {
		return new TimeIntervalSeries(
			...getCalendarEventTestDataForResource(resource.DBID, this.interval)
				.map(e => e.Times),
			...getProcedureEventTestDataForResource(resource.DBID, this.interval)
				.map(e => e.Times)
		)
	}

	private createProcedureIntervals(availableTimes: TimeIntervalSeries) {
		let intervals = []
		for (const interval of availableTimes.Intervals)
			intervals = intervals.concat(...this.createProcedureIntervalsWithin(interval))
		return intervals
	}

	private createProcedureIntervalsWithin(interval: TimeInterval) {
		const intervals = []
		let currentInterval = TimeInterval.fromDuration(
			interval.StartTime,
			{ minutes: this.procedure.Duration })
		while (currentInterval.EndTime.getTime() < interval.EndTime.getTime()) {
			intervals.push(currentInterval)
			currentInterval = TimeInterval.fromDuration(
				currentInterval.EndTime,
				{ minutes: this.procedure.Duration })
		}
		return intervals
	}
}
