﻿import { Component, OnInit, Input, Output, EventEmitter, ElementRef } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { DateHelper } from "utility/date-helper"
import { CalendarEntry } from "./calendar-entry"
import { CalendarEntryProvider } from "./calendar-entry-provider"
import { CalendarColumn } from "./calendar-column-display.component"

@Component({
	selector: "idun-calendar-day",
	templateUrl: "calendar-day.component.html"
})
export class CalendarDayComponent {
	@Input() date: Date = new Date()
	@Input() entryProviders: CalendarEntryProvider[]
	@Input() hourHeight = 6

	@Output() addEntry = new EventEmitter<{ provider: CalendarEntryProvider, time: Date }>()
	@Output() selectEntry = new EventEmitter<{ provider: CalendarEntryProvider, entry: CalendarEntry }>()

	constructor(public l: LanguageService) { }

	get columns(): CalendarColumn[] {
		if (!this.entryProviders)
			return []
		return this.entryProviders.map(e => {
			return {
				date: () => this.date,
				getEntries: () => e ? e.getEntries() : [],
				title: () => e ? e.getTitle() : "",
				subtitle: () => e ? e.getSubtitle() : "",
				hovertitle: () => e ? e.getHovertitle() : "",
				addEntry: (time: Date) => { this.addEntry.emit({ provider: e, time: time }) },
				selectEntry: (entry: CalendarEntry) => { this.selectEntry.emit({ provider: e, entry: entry }) },
				hideByDefault: () => false
			}
		})
	}
}
