﻿import { Injectable } from "@angular/core"

declare let google: any

@Injectable()
export class StatisticsService {
	distributionPieChart(element: any, distribution: any[], type: string): void {
		const content = [
			[type, "Amount"]
		]

		for (const key in distribution) {
			content.push([key, distribution[key]])
		}

		const data = google.visualization.arrayToDataTable(content)

		const options = {
			title: "Distribution",
			is3D: true,
		}

		const chart = new google.visualization.PieChart(element)
		chart.draw(data, options)
	}

	distributionBarChart(element: any, distribution: any[], type: string): void {
		const content = [
			[type, "Amount"]
		]

		for (let i = 0; i < distribution.length; i++) {
			content.push(distribution[i])
		}

		const data = google.visualization.arrayToDataTable(content)

		const options = {
			title: "Distribution"
		}

		const chart = new google.visualization.BarChart(element)
		chart.draw(data, options)
	}
}
