import { OnDestroy } from "@angular/core"
import { Observable } from "rxjs"
import { Subscriber } from "utility/subscriber"

export class BaseSubscriber implements OnDestroy {
	protected subscriber = new Subscriber()

	ngOnDestroy(): void {
		this.subscriber.unsubscribe()
	}

	subscribe<T>(observable: Observable<T> | Promise<Observable<T>>, onGet: ((value: T) => any), subscriptionName?: string) {
		this.subscriber.subscribe(observable, onGet, subscriptionName)
	}
}
