﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { ExtendedService } from "idun-common/extended-service"
import { LanguageService } from "idun-layout/language.service"
import { TreatmentCodeTypeService } from "./treatment-code-type.service"

import { TreatmentCode } from "idun-common/models/treatment-code"
import { FormTextRow } from "idun-layout/form/form-text.component"
import { FormTextareaRow } from "idun-layout/form/form-textarea.component"
import { FormSelectModelRow } from "idun-common/form/form-select-model.component"
import { UserService } from "idun-main/user.service"
import { Permission } from "idun-common/models/staff-group"
import { ValidationRequirement } from "idun-layout/validation/validation-requirement"

@Injectable()
export class TreatmentCodeService extends ExtendedService<TreatmentCode> {
	constructor(
		http: Http,
		treatmentCodeTypeService: TreatmentCodeTypeService,
		l: LanguageService,
		userService: UserService
	) {
		super(http, l,
			TreatmentCode,
			"api/TreatmentCode",
			"treatment-code",
			() => l.treatmentCode
		)
		this.hasEditPermission = () => userService.hasPermission(Permission.EditTreatmentCodes)
		this.icon = "Fundamental"
		this.color = "var(--color-fundamental)"
		this.loadAll = false

		this.setupForm({
			getHeader: (model) => { return this.translations().nameSingular + (model.Title ? ": " + model.Title : "") },
			rows: [
				new FormTextRow("Title", {
					label: () => l.common.title,
					requirements: [ValidationRequirement.ValueRequired(l)],
					width: 60,
					cssClasses: "padding-right"
				}),
				new FormTextRow("Code", {
					label: () => l.common.code,
					hint: () => l.treatmentCode.codeHint,
					requirements: [
						ValidationRequirement.ValueRequired(l),
						new ValidationRequirement((value, model) => {
							if (this.models.find(x =>
								x.TreatmentCodeTypeID === model.TreatmentCodeTypeID
								&& x.Code === value
								&& x.DBID !== model.DBID))
								return () => l.treatmentCode.codeMustBeUniqueForGroup
						})
					],
					width: 40
				}),
				new FormSelectModelRow("TreatmentCodeTypeID", {
					label: () => l.treatmentCode.treatmentCodeType,
					hint: () => l.treatmentCode.treatmentCodeTypeHint,
					requirements: [ValidationRequirement.ValueRequired(l)],
					service: treatmentCodeTypeService
				}),
				new FormTextareaRow("Description", {
					label: () => l.common.description
				})
			]
		})
		this.setupList({
			entryHeader: (model) => model.Code,
			contentList: [(model: TreatmentCode) => {
				const group = treatmentCodeTypeService.getModel(model.TreatmentCodeTypeID)
				if (group)
					return group.Title + " - " + model.Title
				return "? - " + model.Title
			}]
		})
		this.listInfo.searchTypes.push("Code")
		this.listInfo.searchTypes.push("Description")
	}
}
