import { Component, forwardRef, Input, OnInit, OnDestroy } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { BaseService } from "idun-common/base-service"
import { ExtendedService } from "idun-common/extended-service"
import { IdunModel } from "idun-common/models/idun-model"
import { Subscriber } from "utility/subscriber"
import { Observable } from "rxjs"
import { FormRow, FormRowOptions } from "idun-layout/form/form-row"
import { LanguageService } from "idun-layout/language.service"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => SimpleSelectComponent),
	multi: true
}

@Component({
	selector: "idun-simple-select",
	templateUrl: "simple-select.component.html",
	styles: [":host {flex-grow: 1; display: flex;}"],
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class SimpleSelectComponent extends FormBasicComponent<string> implements ControlValueAccessor, OnInit, OnDestroy {
	@Input() allowEmpty = true
	@Input() service: BaseService<any>
	@Input() getter: Function
	@Input() emptyText = ""
	@Input() emptyValue = "0"

	list: any[]
	selected: IdunModel
	subscriber = new Subscriber()

	constructor(public l: LanguageService) {
		super()
	}

	serviceHasComponent() {
		return this.service instanceof ExtendedService
	}

	ngOnInit(): void {
		if (!this.value && this.allowEmpty)
			this.value = this.emptyValue
		if (this.getter)
			this.getter(this.service).then((observable) => {
				this.subscriber.subscribe(observable,
					(list: any) => { this.list = list })
			})
		else
			this.subscriber.subscribe(this.service.getModels$(),
				(list: any) => {
					this.list = list
					if (!this.allowEmpty)
						this.value = list[0].DBID
				}, "list")
	}

	ngOnDestroy(): void {
		this.subscriber.unsubscribe()
	}
}
