import { Observable } from "rxjs"
import { BaseService } from "./base-service"
import { IdunModel } from "./models/idun-model"
import { BaseSubscriber } from "./base-subscriber"

export class BaseModelSubscriber extends BaseSubscriber {
	subscribeToList<T extends IdunModel>(service: BaseService<T>, onGet: (list: T[]) => void, subscriptionName?: string) {
		this.subscribe(service.getModels$(), onGet, subscriptionName)
	}

	subscribeToPage<T extends IdunModel>(
		service: BaseService<T>,
		onGet: (list: T[]) => void,
		page: number = 0,
		pageSize: number = 20,
		query: string = null) {
		this.subscribe(service.loadPage(page, pageSize, query), onGet, "page")
	}

	subscribeToItem<T extends IdunModel>(service: BaseService<T>, id: number, onGet: (model: T) => void, subscriptionName?: string) {
		this.subscribe(service.getModel$(id), onGet, subscriptionName)
	}

	updateNamedSubscription<T extends IdunModel>(
		currentList: IdunModel[],
		newItem: IdunModel,
		service: BaseService<T>,
		onGet: any,
		subscriptionName: string) {
		const list = currentList.map(x => x.DBID)
		list.push(newItem.DBID)
		this.subscribe(service.getSubList$(list),
			onGet,
			subscriptionName)
	}
}
