﻿import { Component, OnInit } from "@angular/core"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { UserService, Settings } from "idun-main/user.service"
import { IdunUser } from "idun-common/models/idun-user"
import { PatientSummaryService } from "idun-main/model-services/patient-summary.service"
import { StaffService } from "idun-main/model-services/staff.service"
import { LanguageService } from "idun-layout/language.service"

@Component({
	selector: "idun-settings",
	templateUrl: "settings.component.html",
	styleUrls: ["settings.component.scss"]
})
export class SettingsComponent extends BaseSubscriber implements OnInit {
	user: IdunUser
	oldPassword: string
	newPassword: string
	repeatPassword: string
	newWebsite: string

	settings: Settings
	passwordChangeState: "" | "success" | "failure" = ""
	currentBackground = 0

	backgrounds = [
		"assets/images/Backgrounds/main_background.png",
		"assets/images/Backgrounds/1.jpg",
		"assets/images/Backgrounds/2.jpg",
		"assets/images/Backgrounds/3.jpg",
		"assets/images/Backgrounds/4.jpg",
		"assets/images/Backgrounds/5.jpg",
		"assets/images/Backgrounds/6.jpg",
		"assets/images/Backgrounds/7.jpg",
		"assets/images/Backgrounds/8.jpg",
		"assets/images/Backgrounds/9.jpg",
		"assets/images/Backgrounds/10.jpg",
		"assets/images/Backgrounds/11.jpg",
		"assets/images/Backgrounds/12.jpg",
		"assets/images/Backgrounds/13.jpg",
		"assets/images/Backgrounds/14.jpg",
		"assets/images/Backgrounds/15.jpg",
		"assets/images/Backgrounds/16.jpg",
		"assets/images/Backgrounds/17.jpg",
		"assets/images/Backgrounds/18.jpg",
		"assets/images/Backgrounds/19.jpg",
		"assets/images/Backgrounds/background_1.png",
		"assets/images/Backgrounds/background_2.png",
		"assets/images/Backgrounds/background_3.png",
		"assets/images/Backgrounds/background_4.png"
	]

	constructor(
		private userService: UserService,
		public patientSummaryService: PatientSummaryService,
		public staffService: StaffService,
		public l: LanguageService
	) {
		super()
	}

	ngOnInit(): void {
		this.subscribe(this.userService.settings$, (settings: Settings) => {
			this.settings = settings
			if (settings)
				this.currentBackground = this.backgrounds.indexOf(settings.backgroundImage)
		})
		this.subscribe(this.userService.user$, (user) => { this.user = user })
	}

	changeWebsite(): void {
		this.userService.updateSettings("website", this.newWebsite)
	}

	async changePasswords() {
		const promise = this.userService.changePasswords(this.oldPassword, this.newPassword)
		this.oldPassword = ""
		this.newPassword = ""
		this.repeatPassword = ""
		this.passwordChangeState = await promise
			? "success"
			: "failure"
	}

	changeBackground(newBackground): void {
		this.userService.updateSettings("backgroundImage", newBackground)
	}

	changeBackgroundPreview(n: number) {
		this.currentBackground += n

		if (this.currentBackground < 0)
			this.currentBackground = this.backgrounds.length - 1
		else if (this.currentBackground >= this.backgrounds.length)
			this.currentBackground = 0

		this.userService.updateSettings("backgroundImage", this.backgrounds[this.currentBackground])
		this.changeBackground(this.backgrounds[this.currentBackground])
	}

	get image() {
		if (this.settings)
			return this.settings.backgroundImage
		return this.backgrounds[0]
	}

	confirmPasswordIsReady() {
		if (!this.oldPassword)
			return false
		if (!this.newPassword)
			return false
		return this.newPassword === this.repeatPassword
	}
}
