﻿import { Component, Input, Output, EventEmitter, ViewChild, OnInit } from "@angular/core"
import { BaseModelSubscriber } from "idun-common/base-model-subscriber"
import { FigureComponent } from "idun-figure/figure.component"
import { FormRow, FormRowOptions, FieldInput } from "idun-layout/form/form-row"
import { BaseService } from "../base-service"
import { IdunModel } from "../models/idun-model"
import { LanguageService } from "idun-layout/language.service"
import { JournalTemplate } from "idun-common/models/journal-template"
import { JournalTemplateService } from "idun-main/model-services/journal-template.service"

@Component({
	selector: "idun-form-template-chooser",
	templateUrl: "./form-template-chooser.component.html",
	styleUrls: ["./form-template-chooser.component.scss"]
})
export class FormTemplateChooserComponent extends BaseModelSubscriber implements OnInit {
	rowClass = FormTemplateChooserRow

	@Input() display = false
	@Output() change = new EventEmitter()

	_selectedParts: string[] = []
	set selectedParts(value) {
		this._selectedParts = value
		this.updateCurrentTemplates()
	}
	get selectedParts(): string[] {
		return this._selectedParts
	}

	@Input() templates: JournalTemplate[]
	currentTemplates = []
	updateCurrentTemplates() {
		this.currentTemplates = this.templates
			.filter(template =>
				this.selectedParts.find(path =>
					template.Placements.includes(path)))
	}

	constructor(
		public l: LanguageService,
		public journalTemplateService: JournalTemplateService
	) {
		super()
	}

	ngOnInit() {
		this.subscribeToList(this.journalTemplateService,
			(models) => {
				this.templates = models
				this.currentTemplates = this.templates
			})
	}

	insertTemplate(index: string) {
		if (!(Number(index) > -1))
			return
		this.change.emit(this.getCurrentSelection(index))
	}

	getCurrentSelection(index: string) {
		if (!index && "" + index !== "0")
			return
		const template = this.currentTemplates[index]
		let description = template.Content ? template.Content : ""
		const title = template.Title
		const path = this.selectedParts[0]
		var side = ""
		if (path.includes("left")) {
			description = description.replace("[side]", "left")
			side = "left"
		}
		else if (path.includes("right")) {
			description = description.replace("[side]", "right")
			side = "right"
		}
		else {
			description = description.replace("[side]", "")
			side = "undefined"
		}
		return { id: template.DBID, description: description, title: title, side: side }
	}
}

export class FormTemplateChooserRow extends FormRow { }
