﻿import { Component, ElementRef, ViewChild, OnInit } from "@angular/core"
import { Router } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LayoutService } from "idun-layout/layout.service"
import { IdunLayoutComponent } from "idun-layout/idun-layout.component"
import { BaseModelSubscriber } from "idun-common/base-model-subscriber"
import { LanguageService } from "idun-layout/language.service"

import { Bookmark } from "idun-common/models/bookmark"
import { BookmarkService } from "./model-services/bookmark.service"
import { UserService } from "./user.service"
import { Title } from "@angular/platform-browser"

@Component({
	selector: "idun-app",
	templateUrl: "idun.component.html",
	styleUrls: ["idun.component.scss"]
})
export class IdunComponent extends BaseModelSubscriber implements OnInit {
	@ViewChild(IdunLayoutComponent) layoutComponent: IdunLayoutComponent
	navigater: NavigationHandler

	@ViewChild("zoomBox") zoomBox: ElementRef
	zoomBoxHover = false

	settings: any
	frontendState
	get backgroundImage() {
		if (this.settings && this.settings.backgroundImage)
			return this.settings.backgroundImage
		return "assets/images/Backgrounds/3.jpg"
	}
	bookmarks: Bookmark[]
	bookmarkedPaths: string[]
	currentFontSize = 14
	finishedLoading = false

	constructor(
		private myElement: ElementRef,
		private router: Router,
		public layoutService: LayoutService,
		private bookmarkService: BookmarkService,
		private userService: UserService,
		public l: LanguageService,
		titleService: Title
	) {
		super()
		titleService.setTitle("IdunHealth")
		this.navigater = new NavigationHandler(router, myElement, layoutService)
		this.l.hasLoaded$.subscribe((loaded) => {
			if (loaded)
				this.finishedLoading = true
		})
		this.layoutService.addBookmark = (p) => this.addBookmark(p)
		this.layoutService.removeBookmark = (p) => this.removeBookmark(p)
		this.subscribe(this.userService.settings$, (settings) => {
			this.settings = settings
		})
		this.subscribe(this.userService.frontendState$, (state) => {
			this.frontendState = state
			if (this.frontendState) {
				layoutService.sizeController.setFontSize(this.frontendState.zoomLevel || 14)
				this.currentFontSize = this.frontendState.zoomLevel
			}
		})
	}

	ngOnInit() {
		this.subscribeToList(this.bookmarkService,
			(bookmarks: Bookmark[]) => {
				this.bookmarks = bookmarks
				this.bookmarkedPaths = []
				for (const i in bookmarks)
					this.bookmarkedPaths.push(bookmarks[i].IdunRoutePath)
				this.layoutService.bookmarks = this.bookmarkedPaths
			})
	}

	addBookmark(path) {
		const bookmark = new Bookmark()
		bookmark.IdunRoutePath = path
		bookmark.IconPath = "Settings"
		bookmark.MouseoverText = "New bookmark"
		this.bookmarkedPaths.push(path)
		this.layoutService.bookmarks = this.bookmarkedPaths
		this.bookmarkService.add(bookmark)
	}

	removeBookmark(path: string) {
		for (const i in this.bookmarks) {
			if (this.bookmarks[i].IdunRoutePath === path) {
				this.bookmarkService.delete(this.bookmarks[i].DBID)
				break
			}
		}
		for (const i in this.bookmarkedPaths) {
			if (this.bookmarkedPaths[i] === path) {
				this.bookmarkedPaths.splice(+i, 1)
				this.layoutService.bookmarks = this.bookmarkedPaths
				break
			}
		}
	}

	zoomIn() {
		this.userService.updateFrontendState("zoomLevel", this.currentFontSize = this.layoutComponent.zoomIn())
	}

	zoomOut() {
		this.userService.updateFrontendState("zoomLevel", this.currentFontSize = this.layoutComponent.zoomOut())
	}

	resetZoom() {
		this.userService.updateFrontendState("zoomLevel", this.currentFontSize = this.layoutComponent.resetZoom())
	}

	getZoomPercentage() {
		return Math.floor(100 * this.currentFontSize / 14) + "%"
	}

	zoomBoxMouseEnter() {
		const zoomPercentage = this.getZoomPercentage()
		if (zoomPercentage === "67%") {
			// height = 2.1rem;
		}
		else if (zoomPercentage === "71%") {
			// height = 2.3rem;
		}
		else if (zoomPercentage === "85%") {
			// height = 2.1rem;
		}
		else if (zoomPercentage === "100%") {
			// height = 2.2rem;
		}
		else if ( zoomPercentage <= "157%") { // and 171%
			// height = 2.17rem;
		}

		const iconElements = document.getElementsByClassName("zoom-box-icon")
		for (let i = 0; i < iconElements.length; i++) {
			const iconElement = iconElements.item(i) as HTMLElement
			iconElement.style.width = this.currentFontSize + "px"
			iconElement.style.height = this.currentFontSize + "px"
			iconElement.style.padding = (0.5 * this.currentFontSize) + "px"
		}
		const aElement = document.getElementsByClassName("zoom-box-a")[0] as HTMLElement
		aElement.style.width = (4 * this.currentFontSize) + "px"
		aElement.style.fontSize = (1.2 * this.currentFontSize) + "px"
		aElement.style.padding = (0.5 * this.currentFontSize) + "px"
		this.zoomBox.nativeElement.style.padding = "0 " + this.currentFontSize + "px"
		this.zoomBox.nativeElement.style.display = ""
		this.zoomBoxHover = true
	}

	zoomBoxMouseLeave() {
		this.zoomBoxHover = false
		this.zoomBox.nativeElement.style.display = "none"
	}
}
