﻿import { Component, OnInit, ElementRef, HostBinding } from "@angular/core"
import { Router, ActivatedRoute } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { RouteParameterComponent } from "idun-common/route-parameter-component"
import { DateHelper, newDateFrom } from "utility/date-helper"
import { LanguageService } from "idun-layout/language.service"
import { Staff } from "idun-common/models/staff"
import { StaffService } from "idun-main/model-services/staff.service"
import { LayoutService, Maximisable } from "idun-layout/layout.service"
import { ProviderGroup } from "idun-calendar/multi-calendar.component"
import { Resource } from "idun-common/models/resource"
import { WorkIntervalService } from "idun-main/model-services/work-interval.service"
import { WorkRequestService } from "idun-main/model-services/work-request.service"
import { WorkIntervalProvider } from "./overview-calendar-providers"
import { WorkRequest, WorkRequestType, WorkRequestTypeString, WorkRequestInterval, WorkRequestIntervalType } from "idun-common/models/work-request"
import { WorkInterval } from "idun-common/models/work-interval"
import { ResourceUtilisationService } from "./resource-utilisation.service"
import { TimeIntervalSeries } from "idun-common/models/time-interval-series"
import { ResourceUtilisationProvider } from "./resource-utilisation-provider"
import { flatten } from "utility/common"

@Maximisable
@Component({
	selector: "idun-request-calendar",
	templateUrl: "request-calendar.component.html",
	styleUrls: ["request-calendar.component.scss"]
})
export class RequestCalendarComponent extends RouteParameterComponent implements OnInit {
	private navigater: NavigationHandler
	private requestEntryCreator: RequestEntryCreator

	date: Date
	private staffProviderGroup = new ProviderGroup(() => this.l.staff.namePlural)
	private resourceProviderGroup = new ProviderGroup(() => this.l.resource.namePlural)
	providerGroups: ProviderGroup[] = []
	loading = true
	numberOfDays = 14
	private staff: Staff[] = []
	private workIntervals: WorkInterval[] = []
	requestEntries: RequestEntry[] = []
	selectedRequest: RequestEntry

	dayIsSelected: boolean
	@HostBinding("class.maximised") get classA() {
		return this.dayIsSelected && this.layoutService.maximised
	}

	modeOptions = [
		{
			text: () => this.l.date.twoWeeks,
			selected: () => this.numberOfDays === 14,
			click: () => {
				this.numberOfDays = 14
				this.loadWorkIntervals()
			}
		},
		{
			text: () => this.l.date.fourWeeks,
			selected: () => this.numberOfDays === 28,
			click: () => {
				this.numberOfDays = 28
				this.loadWorkIntervals()
			}
		},
	]

	constructor(
		myElement: ElementRef,
		router: Router,
		private layoutService: LayoutService,
		private route: ActivatedRoute,
		private workIntervalService: WorkIntervalService,
		private workRequestService: WorkRequestService,
		private staffService: StaffService,
		private resourceUtilisationService: ResourceUtilisationService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
		this.requestEntryCreator = new RequestEntryCreator(staffService, l)
		this.parameterHandlers["date"] = (date: string) => { this.handleDateParameter(date) }
		this.providerGroups[0] = this.staffProviderGroup
		this.providerGroups[1] = this.resourceProviderGroup
	}

	ngOnInit(): void {
		this.handleRouteParameters(this.route, () => this.loadWorkIntervals())
		this.subscribeToList(this.staffService, list => {
			this.staff = list
			this.updateProviders()
		})
		this.subscribe(
			this.resourceUtilisationService.resourceDataChanged$,
			ready => {
				if (ready)
					this.updateProviders()
			})
		this.subscribeToList(this.workRequestService, workRequests => {
			this.requestEntries = workRequests
				.map(e => this.requestEntryCreator.createFor(e))
		})
	}

	private loadWorkIntervals() {
		const { start } = DateHelper.weekInterval(this.date)
		const end = newDateFrom({ days: this.numberOfDays }, start)
		this.subscribe(
			this.workIntervalService.getAllWorkIntervals(start, end),
			intervals => {
				this.workIntervals = intervals
				this.updateProviders()
				this.loading = false
			},
			"workIntervals")
	}

	private handleDateParameter(date: any) {
		if (date === undefined)
			this.date = new Date()
		else
			this.date = new Date(date)
	}

	private updateProviders() {
		this.updateWorkIntervals()
		this.updateResourceIntervals()
	}

	private updateWorkIntervals() {
		this.staffProviderGroup.resetProviders()
		for (const staffMember of this.staff)
			this.updateIntervalsForStaff(staffMember)
	}

	private updateIntervalsForStaff(staffMember: Staff) {
		const times = this.availableTimesForStaff(staffMember)
		this.staffProviderGroup.addProvider(
			this.newProvider(staffMember).withIntervals(times.Intervals))
	}

	private availableTimesForStaff = (staffMember: Staff) => {
		let times = this.workTimesFor(staffMember.DBID)
		for (const change of this.changesFor(staffMember.DBID))
			times = this.implementChange(change, times)
		return times
	}

	private workTimesFor(staffId: number) {
		const intervals = this.workIntervals
			.filter(x => x.StaffID === staffId)
			.map(x => x.Times)
		return new TimeIntervalSeries(...intervals)
	}

	private changesFor(staffId: number) {
		const enabledChanges = this.requestEntries
			.filter(x => x.selected && x.request.StaffID === staffId)
			.map(x => x.request.Changes)
		return flatten(enabledChanges)
	}

	private implementChange(change: WorkRequestInterval, times: TimeIntervalSeries) {
		if (change.Type === WorkRequestIntervalType.Free)
			return times.subtract(change.Times)
		return times.add(change.Times)
	}

	private newProvider(staffMember: Staff) {
		return new WorkIntervalProvider(
			staffMember,
			this.navigater,
			false)
	}

	private updateResourceIntervals() {
		this.resourceProviderGroup.resetProviders()
		const resourceProviders = this.resourceUtilisationService.createResourceProviders(
			this.staff,
			this.availableTimesForStaff,
			this.occupiedTimesForResource,
		)
		for (const resourceId in resourceProviders)
			this.updateStatusesForResource(+resourceId, resourceProviders[resourceId])
	}

	private occupiedTimesForResource = (resource: Resource) => {
		return TimeIntervalSeries.empty()
	}

	private updateStatusesForResource(
		resourceId: number,
		provider: ResourceUtilisationProvider<Staff>
	) {
		provider.onTitleClick = (date: Date) =>
			this.navigater.current(`calendar/week/${DateHelper.dateToString(date)}/0/${resourceId}`)
		this.resourceProviderGroup.addProvider(provider)
	}

	async acceptRequest() {
		const result = await this.workRequestService
			.acceptRequest(this.selectedRequest.request.DBID)
		// TODO: issue 1933 + 1934
		this.updatePage()
	}

	async rejectRequest() {
		const result = await this.workRequestService
			.rejectRequest(this.selectedRequest.request.DBID)
		// TODO: issue 1933 + 1934
		this.updatePage()
	}

	private updatePage() {
		this.closePopup()
		this.workRequestService.loadModels()
		this.navigate(this.date)
	}

	selectDate(date: Date) {
		this.dayIsSelected = !!date
	}

	navigate(date: Date) {
		this.navigater.current(this.getUrl(date))
	}

	private getUrl(date: Date) {
		return "calendar-request/" + DateHelper.dateToString(date)
	}

	closePopup() {
		this.selectedRequest = undefined
	}
}

class RequestEntry {
	constructor(
		public request: WorkRequest,
		public staffName: string,
		private _title: () => string,
		public startTime: string,
		public endTime: string,
		public selected: boolean = false
	) { }

	get title() {
		return this._title()
	}
}

class RequestEntryCreator {
	constructor(
		private staffService: StaffService,
		private l: LanguageService
	) { }

	createFor(request: WorkRequest) {
		return new RequestEntry(
			request,
			this.getStaffName(request.DBID),
			this.getTitle(request),
			this.getStartTime(request.Changes),
			this.getEndTime(request.Changes),
		)
	}

	private getStaffName(staffID: number) {
		const staff = this.staffService.getModel(staffID)
		return staff ? staff.Name : ""
	}

	private getTitle(request: WorkRequest) {
		return request.Type === WorkRequestType.Other
			? () => request.Description
			: () => WorkRequestTypeString(request.Type, this.l)
	}

	private getStartTime(changes: WorkRequestInterval[]) {
		return DateHelper.dateTimeToString(changes[0].Times.StartTime)
	}

	private getEndTime(changes: WorkRequestInterval[]) {
		return DateHelper.dateTimeToString(changes[changes.length - 1].Times.EndTime)
	}
}
