﻿import { SearchableIdunModel } from "idun-common/models/idun-model"

export function search<Model extends SearchableIdunModel>(
	query: string,
	models: Model[],
	noQueryReturnsAll: boolean = true,
	maxEntries: number = 0
): Model[] {
	let list = []
	if (!query) {
		if (maxEntries === 0 || models.length <= maxEntries) {
			return noQueryReturnsAll ? models : []
		} else {
			list = models.slice(0, maxEntries)
			return noQueryReturnsAll ? list : []
		}
	}

	for (const i in models)
		if (models[i].search(query))
			if (!maxEntries || list.length < maxEntries)
				list.push(models[i])
	return list
}
