﻿import { Input, Output, EventEmitter } from "@angular/core"
import { ControlValueAccessor } from "@angular/forms"

export class FormBasicComponent<T> implements ControlValueAccessor {
	@Input() display = false
	@Input() showLabel = true
	@Input() hint = ""
	@Input() label = "PLACEHOLDER"
	@Input() model = null
	@Input() error: () => string
	public isBlurred = false
	// The internal data model
	protected innerValue: T

	// Placeholders for the callbacks which are later providesd
	// by the Control Value Accessor
	protected onTouchedCallback: () => void = function () { }
	// protected onChangeCallback: (_: any) => void = function () { }
	@Output() change: EventEmitter<any> = new EventEmitter()
	protected onChangeCallback(value) {
		this.change.next(value)
	}

	// get accessor
	get value(): T {
		return this.innerValue
	}

	// set accessor including call the onchange callback
	@Input() set value(value: T) {
		if (value !== this.innerValue) {
			this.innerValue = value
			this.triggerChange()
		}
	}

	triggerChange() {
		this.onUpdateFromWithin()
		this.onChangeCallback(this.value)
	}

	// Set touched on blur
	onBlur() {
		this.onTouchedCallback()
		this.isBlurred = true
	}

	protected onValueChange() { }
	protected onUpdateFromWithin() {
		this.onValueChange()
	}
	protected onUpdateFromWithout() {
		this.onValueChange()
	}

	// From ControlValueAccessor interface
	writeValue(value: T) {
		if (value !== this.innerValue) {
			this.innerValue = value
			this.onUpdateFromWithout()
		}
	}

	// From ControlValueAccessor interface
	registerOnChange(fn: any) {
		this.change.subscribe(fn)
	}

	// From ControlValueAccessor interface
	registerOnTouched(fn: any) {
		this.onTouchedCallback = fn
	}

	validate() {
		return null
	}

	errorToShow() {
		if (this.isBlurred && this.error)
			return this.error()
	}

	showAsterisk() {
		return !this.isBlurred && this.error
	}
}
