import { RouteParameterComponent } from "./route-parameter-component"
import { BaseService } from "./base-service"
import { IdunModel } from "./models/idun-model"

export abstract class FormComponent<Model extends IdunModel> extends RouteParameterComponent {
	error: string
	display = false
	original: any
	skipLoad = false

	constructor(
		public service: BaseService<Model>) {
		super()
		this.parameterHandlers["id"] = (val) => { this.handleIDUpdate(val) }
	}

	abstract onModelUpdate(model: Model)

	public handleIDUpdate(id: any) {
		if (+id === 0 || id === "add" || id === undefined) {
			this.display = false
		} else if (!this.skipLoad) {
			let firstResponse = true
			this.subscribe(this.service.getModel$(+id), (model: Model) => {
				if (firstResponse || this.display) {
					firstResponse = false
					this.display = true
					this.onModelUpdate(model)
				}
			})
		}
	}

	protected baseOnSubmit(item: Model): Promise<any> {
		let promise: Promise<any>
		if (item.DBID) {
			promise = this.service.update(item).then((response: any) => {
				this.error = undefined
				this.display = true
				return response
			})
		} else {
			promise = this.service.add(item).then((response: any) => {
				this.onModelUpdate(response)
				this.error = undefined
				this.display = true
				return response
			})
		}
		return promise.catch((reason: any) => {
			this.error = reason
			this.display = false
		})
	}

	switchToEdit(): void {
		this.display = false
	}

	switchToDisplay(): void {
		this.display = true
	}

	cancel(callback): any {
		this.switchToDisplay()
		callback()
		return JSON.parse(JSON.stringify(this.original))
	}
}
