﻿import { Component, OnInit, Input } from "@angular/core"
import { ActivatedRoute, Data } from "@angular/router"
import { search } from "utility/search-helper"
import { RouteParameterComponent } from "./route-parameter-component"
import { AutoListInfo } from "./auto-list-info"
import { IdunModel } from "./models/idun-model"
import { ExtendedService } from "idun-common/extended-service"
import { ModelService } from "idun-common/model.service"
import { LanguageService } from "idun-layout/language.service"

@Component({
	selector: "idun-auto-list",
	templateUrl: "auto-list.html",
	styleUrls: ["auto-list.component.scss"]
})
export class AutoListComponent<Model extends IdunModel> extends RouteParameterComponent implements OnInit {
	@Input() showAllWhenQueryIsEmpty = true
	@Input() showWhenEmpty = true
	@Input() showSearch = true

	@Input() pageSize = 20
	@Input() page = 0
	scrollTimestamp = 0

	@Input() modelType: string
	@Input() contentTextAlign = "left"
	@Input() addText: string

	useInputList = false
	@Input("models") set InputModels(value) {
		if (value)
			this.useInputList = true
		this.models = value
	}
	@Input("query") set inputQuery(value: string) {
		this.query = value
		if (this.models)
			this.updateSearch()
	}
	query: string
	searchType = "_"
	@Input() param

	service: ExtendedService<Model>
	list: Model[]
	models: Model[]
	modelCount = 0
	info: AutoListInfo<Model>

	constructor(
		protected route: ActivatedRoute,
		public modelService: ModelService,
		public l: LanguageService
	) {
		super()
		this.parameterHandlers["query"] = (query: any) => {
			this.inputQuery = query
		}
		this.parameterHandlers["page"] = (page: any) => {
			page = +page
			if (page > -1) {
				this.page = page
			}
		}
		this.parameterHandlers["param"] = (value: any) => {
			if (value)
				this.param = value
		}
	}

	ngOnInit(): void {
		if (this.modelType)
			this.updateService(this.modelType)
		else
			this.route.data.subscribe((value: Data) => {
				this.updateService(value["model"])
			})
	}

	updateService(model: string) {
		const service = this.modelService[model]
		if (!(service instanceof ExtendedService)) {
			console.error("trying to use autolist with baseservice")
			return
		}
		this.service = service
		this.info = this.service.listInfo
		if (this.pageSize === undefined)
			this.pageSize = this.info.pageSize

		this.handleRouteParameters(this.route, (params) => {
			this.updateSubscription()
		})
	}

	updateQuery(query?: string) {
		this.query = query === undefined ? this.query : query
		if (this.query)
			this.query = this.query.trim()
		else
			this.query = ""
		return this.query
	}

	updateSearch(query?: string) {
		this.updateQuery(query)
		if (this.query)
			return this.updateSubscription()

		if (this.showAllWhenQueryIsEmpty) {
			this.list = this.models
			this.updateSubscription()
		}
		else {
			this.list = []
			this.subscriber.namedSubs["models"].unsubscribe()
		}
	}

	updateSearchType(type: string) {
		this.searchType = type
		this.updateSearch()
	}

	loadPageFromScroll(event) {
		if (!this.scrollReachedEnd(event))
			return
		if (Date.now() - this.scrollTimestamp < 1000)
			return

		this.scrollTimestamp = Date.now()
		this.pageSize += 20
		this.loadPage()
	}

	scrollReachedEnd(event: any) {
		const limit = event.target.scrollHeight - event.target.clientHeight - 50
		return event.target.scrollTop > limit
	}

	loadPage() {
		if (this.pageSize > 0) {
			this.subscribe(this.service.loadPage(
				this.page,
				this.pageSize,
				this.query,
				this.searchType === "_" ? undefined : this.searchType),
				(list: Model[]) => {
					this.models = list
					this.updateList()
				}, "models")
			this.subscribe(this.service.count$(), count => {
				this.modelCount = count
			})
		}
		else {
			this.subscribeToList(this.service,
				(list: Model[]) => {
					this.models = list
					this.modelCount = list.length
					this.updateList()
				}, "models")
		}
	}



	updateSubscription() {
		if (this.useInputList)
			return

		if (this.info && this.info.subscriberFunction) {
			this.info.subscriberFunction(this, (list: Model[]) => {
				this.models = list
				this.modelCount = list.length
				this.updateList()
			}, this.param)
		}
		else {
			this.loadPage()
		}
	}

	updateList() {
		this.list = <any>search(this.query, <any>this.models, this.showAllWhenQueryIsEmpty)
	}

	getGroupList(group: any) {
		return group.filter(this.list)
	}
}
