import { CalendarEntry } from "./calendar-entry"
import { CalendarEntryProvider } from "./calendar-entry-provider"

export class BasicCalendarProvider implements CalendarEntryProvider {
	isHeader
	onTitleClick
	onDayTitleClick

	constructor(
		public options: any = {}
	) {
		if (this.options.isHeader) {
			this.isHeader = this.options.isHeader
		}
		if (this.options.onTitleClick) {
			this.onTitleClick = this.options.onTitleClick
		}
		if (this.options.onDayTitleClick) {
			this.onDayTitleClick = this.options.onDayTitleClick
		}
	}

	getEntries(date?: Date): CalendarEntry[] {
		return callOrReturn(this.options.entries, date) || []
	}

	getTitle(): string {
		return callOrReturn(this.options.title) || ""
	}

	getSubtitle(): string {
		return callOrReturn(this.options.subtitle) || ""
	}

	getHovertitle(): string {
		return callOrReturn(this.options.hovertitle) || ""
	}

}

function callOrReturn(thing, args?) {
	if (typeof thing === "function")
		return thing(args)
	return thing
}
