﻿import { JournalEntry } from "idun-common/models/journal-entry"
import { ModelTestData } from "./model-test-data"
import * as english from "../../assets/languages/english.json"

const baseUrl = "api/settings/"

const otherResponses = {}

otherResponses[baseUrl + "getlanguage"] = (parameters) => {
	return JSON.stringify(english)
}

otherResponses[baseUrl + "updatelanguage"] = (parameters) => {
	return JSON.stringify(parameters)
}

export function LanguageResponses() {
	return {
		...otherResponses
	}
}
