﻿import { Component, Input, HostBinding } from "@angular/core"

@Component({
	selector: "idun-switch",
	template: `
		<button *ngFor="let option of options; let f=first; let l=last;" type="button"
		class="{{option.selected(argsToPassAlong) ? 'neutral-button' : 'dark-button'}}"
		[disabled]="disabled"
		[style.borderTopLeftRadius]="f?'':'0'"
		[style.borderBottomLeftRadius]="vertical?(l?'':'0'):(f?'':'0')"
		[style.borderTopRightRadius]="vertical?(f?'':'0'):(l?'':'0')"
		[style.borderBottomRightRadius]="l?'':'0'"
		[style.width]="vertical?'100%':''"
		(click)="option.click(argsToPassAlong)"
		style="flex-grow: 1; flex-basis: 0;">
			{{option.text(argsToPassAlong)}}
		</button>`
})
export class SwitchComponent {
	@Input() options: { text: (args) => string, selected: (args) => boolean, click: (args) => void }[]
	@Input() disabled: boolean
	@Input() argsToPassAlong: any
	@Input() vertical = false
	@HostBinding("style.display") get direction() {
		return this.vertical ? "inline-block" : "flex"
	}
	@HostBinding("style.flex-grow") get grow() {
		return this.vertical ? "" : "1"
	}
}
