import { Subject, Observable } from "rxjs"
import { LayoutService } from "./layout.service"

export class NavigationController {
	leftUrl: string
	leftHistory: string[] = []
	rightUrl: string
	rightHistory: string[] = []

	allowNavigation: (addresses: { left: string, right: string }) => boolean = () => true

	get twoPagesOpen() {
		return this.leftUrl !== ""
	}
	_twoPagesOpen$: Subject<boolean>
	get twoPagesOpen$(): Observable<boolean> {
		return this._twoPagesOpen$.asObservable()
	}

	constructor(private layoutService: LayoutService) {
		this._twoPagesOpen$ = <Subject<boolean>>new Subject()
		const addresses = this.addressesFromUrl(this.layoutService.router.url)
		this.leftUrl = addresses.left
		this.rightUrl = addresses.right
	}

	updatePages(url: string) {
		const urls: string[] = url.split("(left-content:")
		if (urls.length === 1) {
			this.rightUrl = urls[0]
			this.leftUrl = ""
		} else {
			this.rightUrl = urls[0].substr(1)
			this.leftUrl = urls[1].substr(0, urls[1].length - 1)
		}
		this._twoPagesOpen$.next(this.twoPagesOpen)
	}

	private updateUrls(addresses: { right: string, left: string }) {
		addresses.right = this.stripStartSlash(addresses.right)
		addresses.left = this.stripStartSlash(addresses.left)
		if (addresses.right && !addresses.left)
			return this.updateOnePageUrl(addresses)
		if (this.leftUrl && this.leftUrl !== this.leftHistory[this.leftHistory.length - 1])
			this.leftHistory.push(this.leftUrl)
		this.leftUrl = addresses.left
		if (this.rightUrl && this.rightUrl !== this.rightHistory[this.rightHistory.length - 1])
			this.rightHistory.push(this.rightUrl)
		this.rightUrl = addresses.right
	}

	private stripStartSlash(address: string) {
		if (address[0] === "/")
			return address.substring(1)
		return address
	}

	private updateOnePageUrl(addresses) {
		if (this.isClosingRightPage(addresses.right))
			this.closeRightPage()
		else if (this.isClosingLeftPage(addresses.right))
			this.closeLeftPage()
		else {
			if (this.rightUrl !== this.rightHistory[this.rightHistory.length - 1])
				this.rightHistory.push(this.rightUrl)
			this.rightUrl = addresses.right
			this.closeLeftPage()
		}
	}

	private isClosingRightPage(address: string) {
		return this.leftUrl === address
	}

	private isClosingLeftPage(address: string) {
		return this.rightUrl === address && this.leftUrl
	}

	private closeRightPage() {
		this.rightHistory = this.leftHistory
		this.rightUrl = this.leftUrl
		this.closeLeftPage()
	}

	private closeLeftPage() {
		this.leftHistory = []
		this.leftUrl = ""
	}

	navigateByAddresses(addresses) {
		if (!this.allowNavigation(addresses))
			return
		this.updateUrls(addresses)
		this.doNavigate()
	}

	navigate(url: string) {
		const addresses = this.addressesFromUrl(url)
		if (!this.allowNavigation(addresses))
			return
		this.updateUrls(addresses)
		this.doNavigate()
	}

	navigateBackLeft() {
		if (!this.leftHistory.length)
			return
		while (this.leftUrl === this.leftHistory[this.leftHistory.length - 1])
			this.leftHistory.pop()
		if (!this.allowNavigation({ left: this.leftHistory[0], right: this.rightUrl }))
			return
		this.leftUrl = this.leftHistory.pop()
		this.doNavigate()
	}

	navigateBackRight() {
		if (!this.rightHistory.length)
			return
		while (this.rightUrl === this.rightHistory[this.rightHistory.length - 1])
			this.rightHistory.pop()
		if (!this.allowNavigation({ left: this.leftUrl, right: this.rightHistory[0] }))
			return
		this.rightUrl = this.rightHistory.pop()
		this.doNavigate()
	}

	private doNavigate() {
		this.updateTitle()
		this.layoutService.router.navigateByUrl(this.getUrl())
		this.layoutService.resetShadowOverlays()
	}

	private updateTitle() {
		const leftText = this.extractMainAddressPart(this.leftUrl)
		const rightText = this.extractMainAddressPart(this.rightUrl)
		const title = `Idunhealth - ${this.join(leftText, rightText, " / ")}`
		this.layoutService.titleService.setTitle(title)
	}

	private extractMainAddressPart(address: string) {
		address = this.stripStartSlash(address)
		return address.split("/")[0]
	}

	private getUrl() {
		return "/" + this.join(this.rightUrl, this.leftUrl, "(left-content:", ")")
	}

	private join(left: string, right: string, separator: string, end = "") {
		if (left && right)
			return left + separator + right + end
		return left || right
	}

	urlFromAddresses(addresses: { left: string, right: string }): string {
		if (addresses.left && addresses.right)
			return "/" + addresses.right + "(left-content:" + addresses.left + ")"
		if (!addresses.right)
			return "/" + addresses.left
		return "/" + addresses.right
	}

	addressesFromUrl(url: string): { left: string, right: string } {
		const addresses = { left: "", right: "" }
		const urls = url.split("(left-content:")
		if (urls.length === 1) {
			addresses.right = urls[0]
			addresses.left = ""
		}
		else {
			addresses.right = urls[0]
			addresses.left = "/" + urls[1].substr(0, urls[1].length - 1)
		}
		return addresses
	}
}
