﻿import { LayoutService } from "./layout.service"

export class ErrorController {
	message = ""

	constructor(layoutService: LayoutService) {
		layoutService.ngZone.onError.subscribe((error) => layoutService.ngZone.run(() => {
			this.onError(error)
		}))
	}

	onError(error) {
		this.message = "An error has occurred. Consider reloading the page to ensure everything works correctly."
	}
}
