﻿import { hasValue } from "./common"

export class DateHelper {

	static convertUTCDateToLocalDate(date: Date) {
		const newDate = new Date(date.getTime() + date.getTimezoneOffset() * 60 * 1000)

		const offset = date.getTimezoneOffset() / 60
		const hours = date.getHours()

		newDate.setHours(hours - offset)
		return newDate
	}

	static parseDate(dateString: string): Date {
		return new Date(dateString)
	}

	static parseTimeString(time: string, allowSingleValue = true): { hours: number, minutes: number, error: any } {
		const result = { hours: 0, minutes: 0, error: undefined }
		const elements = time.split(":")
		if (elements.length === 1 && allowSingleValue) {
			result.hours = Math.floor((+elements[0]) / 60)
			result.minutes = (+elements[0]) % 60
		}
		else if (elements.length === 2) {
			result.hours = +elements[0]
			result.minutes = +elements[1]
		}
		else {
			result.error = "couldn't parse the time " + time
			console.error(result.error)
		}
		return result
	}

	static updateDateFromTimeString(date: Date, newTime: string) {
		const times = DateHelper.parseTimeString(newTime)
		if (!times.error)
			date.setHours(times.hours, times.minutes, 0, 0)
	}

	static parseDateString(date: string): { year: number, month: number, date: number, error: any } {
		const result = { year: 0, month: 0, date: 0, error: undefined }
		const elements = date.split("-")
		if (elements.length === 3) {
			result.year = +elements[0]
			result.month = +elements[1]
			result.date = +elements[2]
		}
		else {
			result.error = "couldn't parse the date " + date
			console.error(result.error)
		}
		return result
	}

	static updateDateFromDateString(date: Date, newDate: string) {
		const times = DateHelper.parseDateString(newDate)
		if (!times.error)
			date.setFullYear(times.year, times.month - 1, times.date)
	}

	static dateTimeToString(date: Date) {
		return this.dateToString(date) + " " + this.timeToString(date)
	}

	static dateToString(date: Date) {
		const year = date.getFullYear()
		let month = (date.getMonth() + 1).toString()
		let day = (date.getDate()).toString()
		if (+month < 10) {
			month = "0" + month
		}
		if (+day < 10) {
			day = "0" + day
		}
		return year + "-" + month + "-" + day
	}

	static timeToString(date: Date): string {
		return DateHelper.minutesToString(date.getHours() * 60 + date.getMinutes())
	}

	static minutesToString(minutes: number): string {
		const hours = Math.floor(minutes / 60)
		minutes = minutes % 60

		let result = ""
		if (hours < 10)
			result = "0" + hours
		else
			result = "" + hours
		result += ":"
		if (minutes < 10)
			result += "0" + minutes
		else
			result += "" + minutes

		return result
	}

	static ageFromDate(dateString: string): number {
		const date = new Date(dateString)

		const birthYear = date.getFullYear()
		const birthMonth = date.getMonth()
		const birthDay = date.getDate()

		const currentDate = new Date
		const currentYear = currentDate.getFullYear()
		const currentMonth = currentDate.getMonth()
		const currentDay = currentDate.getDate()

		if (currentMonth === birthMonth) {
			if (currentDay < birthDay) {
				return currentYear - birthYear - 1
			}
			else {
				return currentYear - birthYear
			}
		}
		else if (currentMonth < birthMonth) {
			return currentYear - birthYear - 1
		}
		else {
			return currentYear - birthYear
		}
	}

	static timeIntervalToString(start: Date, end: Date): string {
		return this.timeToString(start) + " - " + this.timeToString(end)
	}

	static minuteIntervalToString(start: number, end: number): string {
		return this.minutesToString(start) + " - " + this.minutesToString(end)
	}

	static dayInterval(baseDate: Date): { start: Date, end: Date } {
		const interval = { start: new Date(baseDate), end: null }
		interval.start.setHours(0, 0, 0)
		interval.end = new Date(interval.start)
		interval.end.setHours(23, 59, 59)
		return interval
	}

	static threeDayInterval(baseDate: Date): { start: Date, end: Date } {
		const yesterday = new Date(baseDate)
		yesterday.setDate(yesterday.getDate() - 1)
		const tomorrow = new Date(baseDate)
		tomorrow.setDate(tomorrow.getDate() + 1)

		const interval = { start: yesterday, end: tomorrow }
		interval.start.setHours(0, 0, 0)
		interval.end.setHours(23, 59, 59)
		return interval
	}

	static weekInterval(baseDate: Date): { start: Date, end: Date } {
		const interval = { start: new Date(baseDate), end: null }
		interval.start.setHours(0, 0, 0)

		let weekday: number = interval.start.getDay() - 1
		if (weekday === -1)
			weekday = 6
		interval.start.setDate(interval.start.getDate() - weekday)

		interval.end = new Date(interval.start)
		interval.end.setDate(interval.end.getDate() + 7)
		return interval
	}

	static yearInterval(baseDate: Date): { start: Date, end: Date } {
		const interval = { start: new Date(baseDate), end: null }
		interval.start.setHours(0, 0, 0)
		interval.start.setMonth(0, 1)
		interval.end = new Date(interval.start)
		interval.start.setHours(23, 59, 59)
		interval.end.setMonth(11, 31)
		return interval
	}

	static forDayInWeek(startDate: Date, callback: (day: { index: number, start: Date, end: Date }) => void) {
		const interval = DateHelper.weekInterval(startDate)
		for (let i = 0; i < 7; i++) {
			const baseDay = new Date(interval.start)
			baseDay.setDate(baseDay.getDate() + i)
			const day = { index: i, start: new Date(baseDay), end: new Date(baseDay) }
			day.start.setHours(0, 0, 0, 0)
			day.end.setHours(23, 59, 59, 999)
			callback(day)
		}
	}

	static dayDistance(startDate: Date, endDate: Date): number {
		const oneDay = 24 * 60 * 60 * 1000
		const timeDifference = Math.abs(startDate.getTime() - endDate.getTime())
		return Math.floor(timeDifference / oneDay)
	}

	static isSameDate(dateA: Date, dateB: Date): boolean {
		return dateA.getFullYear() === dateB.getFullYear() &&
			dateA.getMonth() === dateB.getMonth() &&
			dateA.getDate() === dateB.getDate()
	}

	static isSameWeek(dateA: Date, dateB: Date) {
		const { start, end } = DateHelper.weekInterval(dateA)
		return start.getTime() <= dateB.getTime() && dateB.getTime() < end.getTime()
	}

	static weekdayNames = ["monday", "tuesday", "wednesday", "thursday", "friday", "saturday", "sunday"]
	static monthNames = ["january", "february", "march", "april", "may", "june", "july", "august", "september", "october", "november", "december"]

	static newDate(oldDate: Date,
		options?: {
			reset?: boolean, relative?: boolean,
			year?: number, month?: number, date?: number,
			hours?: number, minutes?: number, seconds?: number
		}): Date {
		const _options = { reset: true, relative: false }
		for (const i in options)
			_options[i] = options[i]
		const date = new Date(oldDate.getTime())
		if (_options.reset)
			date.setHours(0, 0, 0, 0)
		for (const n in _options) {
			switch (n) {
				case "year":
					date.setFullYear(_options.relative ? date.getFullYear() + _options[n] : _options[n])
					break
				case "month":
					date.setMonth(_options.relative ? date.getMonth() + _options[n] : _options[n])
					break
				case "date":
					date.setDate(_options.relative ? date.getDate() + _options[n] : _options[n])
					break
				case "hours":
					date.setHours(_options.relative ? date.getHours() + _options[n] : _options[n])
					break
				case "minutes":
					date.setMinutes(_options.relative ? date.getMinutes() + _options[n] : _options[n])
					break
				case "seconds":
					date.setSeconds(_options.relative ? date.getSeconds() + _options[n] : _options[n])
					break
			}
		}
		return date
	}

	static getWeekNumber(date: Date) {
		date = new Date(Date.UTC(date.getFullYear(), date.getMonth(), date.getDate()))
		// Set to nearest Thursday: current date + 4 - current day number
		// Make Sunday's day number 7
		date.setUTCDate(date.getUTCDate() + 4 - (date.getUTCDay() || 7))
		// Get first day of year
		const yearStart = new Date(Date.UTC(date.getUTCFullYear(), 0, 1))
		// Calculate full weeks to nearest Thursday
		const weekNo = Math.ceil((((date.getTime() - yearStart.getTime()) / 86400000) + 1) / 7)
		// Return array of year and week number
		return weekNo
	}
}

export function forDayInInterval(start: Date, end: Date, callback: (date: Date) => void) {
	const date = new Date(start)
	while (date.getTime() < end.getTime()) {
		callback(date)
		date.setDate(date.getDate() + 1)
	}
}

export function newResetDateFrom(change: TimeChange, origin: Date = new Date()) {
	const date = new Date(origin.getTime())
	date.setHours(0, 0, 0, 0)
	changeDate(date, change)
	return date
}

export function newDateFrom(change: TimeChange, origin: Date = new Date()) {
	const date = new Date(origin.getTime())
	changeDate(date, change)
	return date
}

export function changeDate(date: Date, change: TimeChange) {
	if (hasValue(change.years))
		date.setFullYear(date.getFullYear() + change.years)
	if (hasValue(change.months))
		date.setMonth(date.getMonth() + change.months)
	if (hasValue(change.days))
		date.setDate(date.getDate() + change.days)
	if (hasValue(change.hours))
		date.setHours(date.getHours() + change.hours)
	if (hasValue(change.minutes))
		date.setMinutes(date.getMinutes() + change.minutes)
	if (hasValue(change.seconds))
		date.setSeconds(date.getSeconds() + change.seconds)
	return date
}

export interface TimeChange {
	years?: number,
	months?: number,
	days?: number,
	hours?: number,
	minutes?: number,
	seconds?: number
}

export function isWeekend(date: Date) {
	return date.getDay() === 0 || date.getDay() === 6
}
