import { Component, OnInit, Input } from "@angular/core"
import { UserService } from "idun-main/user.service"
import { IdunUser } from "idun-common/models/idun-user"
import { LanguageService } from "idun-layout/language.service"
import { ProcedureService } from "../../model-services/procedure.service"
import { ProcedureTypeService } from "../../model-services/procedure-type.service"
import { Procedure } from "idun-common/models/procedure"
import { ProcedureType } from "idun-common/models/procedure-type"
import { Staff } from "idun-common/models/staff"
import { StaffService } from "../../model-services/staff.service"
import { RouteParameterComponent } from "idun-common/route-parameter-component"

@Component({
	selector: "idun-competences",
	templateUrl: "competences.component.html",
	styleUrls: ["competences.component.scss"]
})
export class CompetencesComponent extends RouteParameterComponent implements OnInit {
	@Input() allowHide = false
	@Input() open = true

	user: IdunUser

	staff: Staff[]
	procedures: Procedure[]
	procedureTypes: ProcedureType[]

	rowGroups: {
		getTitle: () => string,
		toggle: (target: Staff) => void,
		get: (target: Staff) => boolean,
		expanded: boolean,
		rows: { getTitle: () => string, toggle: (target: Staff) => void, get: (target: Staff) => boolean }[]
	}[] = []
	columns: { getTitle: () => string, getHoverTitle: () => string, source: Staff }[] = []

	isCurrentUserAdmin = true // TODO: A proper check needs to be implemented

	constructor(
		private userService: UserService,
		public staffService: StaffService,
		public procedureService: ProcedureService,
		public procedureTypeService: ProcedureTypeService,
		public l: LanguageService
	) {
		super()
	}

	ngOnInit(): void {
		this.subscribe(this.userService.user$, (user) => { this.user = user })
		this.subscribeToList(this.staffService, (staff) => {
			this.staff = staff
			this.updateColumns()
		})
		this.subscribeToList(this.procedureService, (procedures) => {
			this.procedures = procedures
			this.updateModelGroups()
		})
		this.subscribeToList(this.procedureTypeService, (procedureTypes) => {
			this.procedureTypes = procedureTypes
			this.updateModelGroups()
		})
	}

	private updateModelGroups() {
		this.rowGroups = []
		for (const i in this.procedureTypes)
			this.rowGroups.push(this.newRowGroup(this.procedureTypes[i]))
	}

	private newRowGroup(procedureType: ProcedureType) {
		return {
			getTitle: () => procedureType.getTitle(),
			toggle: (target: Staff) => {
				target.Competences.toggleProcedureTypeCompetence(procedureType, this.procedures)
				this.staffService.update(target)
			},
			get: (target: Staff) => target.Competences.AllowedProcedureTypes.includes(procedureType.DBID),
			expanded: false,
			rows: this.procedures
				.filter(e => e.ProcedureTypeID === procedureType.DBID)
				.map(procedure => this.newRow(procedure))
		}
	}

	private newRow(procedure: Procedure) {
		return {
			getTitle: () => procedure.getTitle(),
			toggle: (target: Staff) => {
				target.Competences.toggleProcedureCompetence(procedure)
				this.staffService.update(target)
			},
			get: (target: Staff) => target.Competences.canPerform(procedure),
		}
	}

	private updateColumns() {
		this.columns = this.staff.map(staff => {
			return {
				getTitle: () => staff.Abbreviation,
				getHoverTitle: () => staff.Name,
				source: staff
			}
		})
	}
}
