﻿import { CalendarEntryProvider } from "idun-calendar/calendar-entry-provider"
import { BasicCalendarEntry } from "idun-calendar/basic-calendar-entry"
import { Resource } from "idun-common/models/resource"
import { CalendarEntry } from "idun-calendar/calendar-entry"
import { DateHelper } from "utility/date-helper"
import { TimeInterval } from "idun-common/models/time-interval"
import { IntervalSet, ModelCombination } from "idun-common/models/resource-utilisation"
import { Fractional } from "utility/math"

export class ResourceUtilisationProvider<StaffType extends Titled> implements CalendarEntryProvider {
	entries: BasicCalendarEntry<any>[]
	onTitleClick: (date: Date) => void

	constructor(
		public resource: Resource,
		public intervalSets: IntervalSet<StaffType, Resource>[]
	) {
		this.updateEntries()
	}

	getEntries(date?: Date): CalendarEntry[] {
		const interval = TimeInterval.fromInterval(DateHelper.dayInterval(date))
		return this.entries.filter(e => e.interval.intersects(interval))
	}
	getTitle() {
		return this.resource.getTitle()
	}
	getSubtitle() {
		return ""
	}
	getHovertitle() {
		return this.resource.getTitle()
	}

	updateEntries() {
		this.entries = []
		for (const set of this.intervalSets)
			this.entries.push(...this.getEntriesFrom(set))
	}

	private getEntriesFrom(set: IntervalSet<StaffType, Resource>) {
		const hue = this.hueFrom(set.combinations)
		const hoverText = this.hoverTextFrom(set)
		return set.intervals.Intervals.map(interval => {
			return new BasicCalendarEntry<any>(
				{ Interval: interval, source: set },
				{ boxHue: hue.boxHue, boxSaturation: hue.boxSaturation, clickable: true, hoverText: hoverText }
			)
		})
	}

	private hoverTextFrom(set: IntervalSet<StaffType, Resource>) {
		let text = "Staff that can perform the procedures related to this:\n"
		for (const combo of set.combinations)
			text += combo.modelA.getTitle() + " (" + combo.shared + ")\n"
		return text
	}

	private hueFrom(combinations: ModelCombination<StaffType, Resource>[]) {
		if (combinations.find(e => e.modelA.getTitle() === "disabled"))
			return { boxHue: 360, boxSaturation: 0 }
		let shared = "some"
		let availability = new Fractional(0, 1)
		for (const combination of combinations) {
			if (combination.shared === "all" || combination.shared === "more")
				shared = "all"
			availability = availability.add(combination.availability)
		}
		if (shared === "some") {
			if (availability.toNumber() >= 1)
				return { boxHue: 60 }
			return { boxHue: 0 }
		}
		if (availability.toNumber() >= 1)
			return { boxHue: 120 }
		return { boxHue: 0 }
	}
}

export interface Titled {
	getTitle(): string
}
