﻿import { Component, Input, Output, EventEmitter } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { DateHelper } from "utility/date-helper"
import { CalendarEntry } from "./calendar-entry"
import { CalendarEntryProvider } from "./calendar-entry-provider"

@Component({
	selector: "idun-multi-calendar",
	templateUrl: "multi-calendar.component.html",
	styleUrls: ["calendar.component.scss"]
})
export class MultiCalendarComponent {
	private _date: Date
	@Input() set date(value: Date) {
		const date = DateHelper.weekInterval(value).start
		if (date.getTime() === (this._date && this._date.getTime()))
			return
		this._date = date
		this.updateDaysInPeriod()
	}
	get date(): Date {
		return this._date
	}

	_days: number
	@Input() set days(value: number) {
		if (this._days === value)
			return
		this._days = value
		this.updateDaysInPeriod()
	}
	get days() {
		return this._days
	}

	@Input() providerGroups: ProviderGroup[]
	@Input() title: string
	@Input() subtitle: string
	@Input() helpText = ""
	@Input() showNavigation = true
	@Input() showNavigationBar = true
	@Input() showPopup = false
	@Input() icon: string
	@Input() color: number | string
	@Input() loading = false
	@Input() showDates = true

	@Output() navigate = new EventEmitter<Date>()
	@Output() addEntry = new EventEmitter<{ provider: CalendarEntryProvider, time: Date }>()
	@Output() selectEntry = new EventEmitter<{ provider: CalendarEntryProvider, entry: CalendarEntry }>()
	@Output() closeEntry = new EventEmitter()
	@Output() selectDate = new EventEmitter<Date>()

	_selectedDate: Date
	set selectedDate(value: Date) {
		if (value === this._selectedDate)
			return
		this._selectedDate = value
		this.selectDate.emit(value)
	}
	get selectedDate(): Date {
		return this._selectedDate
	}
	shownDays: any[] = []
	periods: any[] = []
	providerGroupToBeFiltered: ProviderGroup

	constructor(public l: LanguageService) { }

	getTitle() {
		return this.l.date[DateHelper.monthNames[this.date.getMonth()]] + " - " + this.date.getFullYear()
	}

	private updateDaysInPeriod() {
		this.shownDays = []
		this.periods = []
		for (let i = 0; i < this.days; i++) {
			const date = DateHelper.newDate(this.date, { relative: true, date: i })
			this.shownDays.push({
				title: () => this.dateText(date),
				date: date
			})
			if (i % 7 === 0)
				this.periods.push({
					title: () => this.periodText(date),
					length: this.days - i < 7 ? this.days - i : 7
				})
		}
	}

	private dateText(date: Date) {
		if (this.showDates)
			return `${date.getDate()}/${date.getMonth() + 1} ${this.l.abbreviatedDayName(date)}`
		return this.l.abbreviatedDayName(date)
	}

	private periodText(date: Date) {
		if (this.showDates)
			return this.l.date[DateHelper.monthNames[date.getMonth()]] + " - " + this.l.date.week + " " + DateHelper.getWeekNumber(date)
		return this.l.date.week + " " + DateHelper.getWeekNumber(date)
	}

	navigateForward() {
		this.navigate.emit(DateHelper.newDate(this.date, { relative: true, date: this.days }))
	}

	navigateBackward() {
		this.navigate.emit(DateHelper.newDate(this.date, { relative: true, date: -this.days }))
	}

	getDateString() {
		if (this.showDates)
			return this.l.dayName(this.selectedDate) + ` - ${this.selectedDate.getDate()}/${this.selectedDate.getMonth() + 1}`
		return this.l.dayName(this.selectedDate) + " - " + this.l.date.week + " " + DateHelper.getWeekNumber(this.selectedDate)
	}
}

export class ProviderGroup {
	constructor(
		public title: () => string,
		public hiddenWeek = false,
		public hiddenDay = false,
		public entryProviders: CalendarEntryProvider[] = [],
		public shownProviders: CalendarEntryProvider[] = [],
		public hiddenProviders: boolean[] = []
	) { }

	resetProviders() {
		this.entryProviders = []
		this.shownProviders = []
	}

	addProvider(entryProvider: CalendarEntryProvider, index?: number) {
		if (index === undefined)
			index = this.entryProviders.length
		this.entryProviders[index] = entryProvider
		if (!this.hiddenProviders[index])
			this.shownProviders[index] = entryProvider
	}

	toggleProvider(index: number) {
		this.hiddenProviders[index] = !this.hiddenProviders[index]
		this.shownProviders = this.entryProviders.filter((e, i) => {
			return !this.hiddenProviders[i]
		})
	}

	selectAll() {
		this.hiddenProviders = []
		this.shownProviders = this.entryProviders.map(e => e)
	}

	selectNone() {
		this.hiddenProviders = this.entryProviders.map(e => true)
		this.shownProviders = []
	}
}
