﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { BaseService } from "idun-common/base-service"
import { StaffNotification } from "idun-common/models/staff-notification"
import { BehaviorSubject } from "rxjs"
import { UserService } from "idun-main/user.service"

@Injectable()
export class StaffNotificationService extends BaseService<StaffNotification> {
	unreadNotificationCount$: BehaviorSubject<number>

	constructor(
		http: Http,
		userService: UserService
	) {
		super(http,
			StaffNotification,
			"api/StaffNotification",
		)
		this.loadAll = true
		this.unreadNotificationCount$ = new BehaviorSubject(0)
		this._models$.subscribe(_ => {
			if (!userService.user)
				return
			this.unreadNotificationCount$.next(
				this.numberOfUnreadNotificationsFor(userService.user.StaffID)
			)
		})
	}

	private numberOfUnreadNotificationsFor(staffId: number) {
		let unreadNotifications = 0
		for (const notification of this.models) {
			if (notification.ReadByStaffIds.includes(staffId))
				continue
			unreadNotifications++
		}
		return unreadNotifications
	}
}
