import { Component, OnInit, ElementRef, Input } from "@angular/core"
import { Router, ActivatedRoute } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LanguageService } from "idun-layout/language.service"
import { LayoutService } from "idun-layout/layout.service"
import { PatientEvent } from "idun-common/models/patient-event"
import { Patient } from "idun-common/models/patient"
import { PatientEventService } from "idun-main/model-services/patient-event.service"
import { ProcedureEvent } from "idun-common/models/procedure-event"
import { ProcedureService } from "idun-main/model-services/procedure.service"
import { PartnerService } from "idun-main/model-services/partner.service"
import { TreatmentCodeService } from "idun-main/model-services/treatment-code.service"
import { StaffService } from "idun-main/model-services/staff.service"
import { ResourceService } from "idun-main/model-services/resource.service"
import { RouteParameterComponent } from "idun-common/route-parameter-component"
import { PatientService } from "idun-main/model-services/patient.service"
import { ProcedureEventAnalysis, ProcedureEventAnalysisService } from "idun-main/other-services/procedure-event-analysis.service"

@Component({
	selector: "idun-patient-event",
	templateUrl: "patient-event.component.html",
	styleUrls: ["patient-event.component.scss"]
})
export class PatientEventComponent extends RouteParameterComponent implements OnInit {
	navigater: NavigationHandler
	display = true

	@Input() set patientEventId(id: number) {
		this.loadForId(id)
	}
	patientEvent: PatientEvent
	original: PatientEvent
	selectedProcedureEvent: ProcedureEvent
	patient: Patient
	analysis = new Map<ProcedureEvent, ProcedureEventAnalysis>()
	isSaving = false
	editButton = [
		{ icon: "Edit", show: () => this.display, action: () => this.switchToEdit() }
	]

	constructor(
		router: Router,
		myElement: ElementRef,
		layoutService: LayoutService,
		private route: ActivatedRoute,
		private patientService: PatientService,
		private patientEventService: PatientEventService,
		private procedureEventAnalysisService: ProcedureEventAnalysisService,
		public procedureService: ProcedureService,
		public partnerService: PartnerService,
		public staffService: StaffService,
		public resourceService: ResourceService,
		public treatmentCodeService: TreatmentCodeService,
		public l: LanguageService
	) {
		super()
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	ngOnInit(): void {
		this.parameterHandlers.id = (patientEventId: string) => {
			if (+patientEventId)
				this.loadForId(+patientEventId)
		}
		this.handleRouteParameters(this.route)
	}

	private loadForId(patientEventId: number) {
		this.subscribeToItem(this.patientEventService, patientEventId, model => {
			this.original = model
			this.patientEvent = new PatientEvent(model)
			this.analyseProcedureEvents()

			this.subscribe(this.patientService.getModel$(model.PatientId), patient => {
				this.patient = patient
			}, "patient")
		}, "model")
	}

	private analyseProcedureEvents() {
		this.analysis = this.procedureEventAnalysisService.analyseIn(this.patientEvent)
	}

	private switchToEdit(): void {
		this.showOriginalModel()
		this.display = false
	}

	switchToDisplay(): void {
		this.showOriginalModel()
		this.display = true
	}

	private showOriginalModel() {
		const index = this.patientEvent.ProcedureEvents.indexOf(this.selectedProcedureEvent)
		this.patientEvent = new PatientEvent(this.original)
		this.selectedProcedureEvent = this.patientEvent.ProcedureEvents[index]
		this.analyseProcedureEvents()
	}

	async submit() {
		this.isSaving = false
		const updatedEvent = await this.patientEventService.update(this.patientEvent)
		this.original = updatedEvent
		this.isSaving = true
		this.switchToDisplay()
	}

	allowDelete() {
		return true
	}

	async delete() {
		const patientId = this.patientEvent.PatientId
		await this.patientEventService.delete(this.patientEvent.DBID)
		this.navigater.current("patient/" + patientId)
	}

	selectProcedureEvent(procedureEvent: ProcedureEvent) {
		if (this.selectedProcedureEvent === procedureEvent)
			this.selectedProcedureEvent = null
		else
			this.selectedProcedureEvent = procedureEvent
	}

	addProcedureEvent() {
		this.selectedProcedureEvent = new ProcedureEvent()
		this.patientEvent.ProcedureEvents.push(this.selectedProcedureEvent)
		this.analyseProcedureEvents()
	}

	acceptProcedureEvent() {
		this.selectedProcedureEvent = null
		this.analyseProcedureEvents()
	}

	removeProcedureEvent() {
		const index = this.patientEvent.ProcedureEvents.indexOf(this.selectedProcedureEvent)
		this.patientEvent.ProcedureEvents.splice(index, 1)
	}
}

