﻿import { Component, ViewChild, OnDestroy } from "@angular/core"
import { AutoFormComponent } from "idun-common/auto-form.component"

@Component({
	selector: "idun-staff-page",
	templateUrl: "staff-page.component.html"
})
export class StaffPageComponent implements OnDestroy {
	@ViewChild(AutoFormComponent) autoform

	ngOnDestroy() {
		delete (<any>this.autoform).currentPopup
	}

	closePopup() {
		delete (<any>this.autoform).currentPopup
	}
}
