﻿import { IdunModel } from "./idun-model"
import { Wordifier } from "utility/number"

export class CardReaderToken extends IdunModel {
	DBID: number
	Token: string

	constructor(source?: any) {
		super()
		for (const prop in source) this[prop] = source[prop]
	}

	getTitle(): string {
		return this.Token
	}

	static generateNew() {
		const array = new Uint32Array(2)
		window.crypto.getRandomValues(array)
		const token = new Wordifier().wordify(array[0] + "" + array[1])
		return new CardReaderToken({ Token: token })
	}
}
