﻿import { Injectable } from "@angular/core"
import { Http } from "@angular/http"
import { BaseService } from "idun-common/base-service"

import { Bookmark } from "idun-common/models/bookmark"

@Injectable()
export class BookmarkService extends BaseService<Bookmark> {
	constructor(http: Http) {
		super(http, Bookmark, "api/Bookmark")
	}

	generaliseUrl(path: string) {
		const pathParts: string[] = path.split("(")
		for (let i = 0; i < pathParts.length; i++)
			if (this.pathIsPatientRelated(pathParts[i]))
				pathParts[i] = this.replacePatientId(pathParts[i])
		return pathParts.join("(")
	}

	private pathIsPatientRelated(path: string) {
		return path.includes("patient/") || path.includes("journal-entry/add/")
	}

	private replacePatientId(path: string) {
		const smallerParts = path.split("/")
		const last = smallerParts.length - 1
		if (this.isNumber(smallerParts[last])) {
			smallerParts[last] = smallerParts[last].includes(")")
				? ":id)"
				: ":id"
		}
		return smallerParts.join("/")
	}

	private isNumber(text: string) {
		return !isNaN(parseInt(text, 10))
	}
}
