﻿export class ColorController {
	colors = {}

	registerColors() {
		this.colors = {}
		for (const i in document.styleSheets) {
			for (const j in (<any>document.styleSheets[i]).cssRules) {
				const rule = (<any>document.styleSheets[i]).cssRules[j]
				if (rule.style && rule.style.backgroundColor) {
					const color = this.getNamedColor(rule.style.backgroundColor) || rule.style.backgroundColor
					if (!this.colors[color]) {
						this.colors[color] = []
					}
					this.colors[color].push({ i: i, j: j })
				}
				if (rule.style && rule.style.color) {
					if (!this.colors["text"]) {
						this.colors["text"] = []
					}
					this.colors["text"].push({ i: i, j: j })
				}
			}
		}
	}

	setColor(color, newColor) {
		for (const i in this.colors[color]) {
			const set = this.colors[color][i];
			(<any>document.styleSheets[set.i]).cssRules[set.j].style[color === "text" ? "color" : "backgroundColor"] = newColor
		}
	}

	getNamedColor(color) {
		const namedColors = {
			"primary": "rgb(26, 162, 230)",
			"primary-active": "rgb(26, 162, 230)",
			"positive": "rgb(26, 162, 230)",
			"positive-active": "rgb(26, 162, 230)",
			"negative": "rgb(26, 162, 230)",
			"negative-active": "rgb(26, 162, 230)",
			"nav-bar": "rgb(0, 102, 153)",
			"nav-bar-active": "rgb(26, 162, 230)",
			"background": "rgba(51, 85, 153, 0.85)",
			"background-overlay": "rgba(8, 28, 69, 0.6)",
			"background-sidebar": "rgba(0, 34, 102, 0.6)",
		}
		for (const name in namedColors) {
			if (color === namedColors[name])
				return name
		}
	}
}
