﻿import { TimeInterval } from "idun-common/models/time-interval"
import { CalendarEntry } from "./calendar-entry"

export class BasicCalendarEntry<Model extends { Interval: TimeInterval }> implements CalendarEntry {
	constructor(
		public source: Model,
		public options: any = {}
	) {
		this.source = source
	}

	isSelected(selection: Model): boolean {
		return this.source === selection
	}

	get interval() {
		return this.source.Interval
	}

	get borderHue(): number {
		return this.options.borderHue
	}

	get boxHue(): number {
		return this.options.boxHue
	}

	get boxSaturation(): number {
		return this.options.boxSaturation
	}

	get length(): number {
		return this.interval.getLengthInMinutes()
	}

	get clickable(): boolean {
		return this.options.clickable
	}

	get ghost(): boolean {
		return this.options.ghost
	}

	getStartMinute(baseDate: Date): number {
		return this.interval.getStartMinute(baseDate)
	}

	get allowResize(): boolean {
		return this.options.allowResize
	}

	get moveStartMinute() {
		return this.options.moveStartMinute
	}

	get moveEndMinute() {
		return this.options.moveEndMinute
	}

	get delete() {
		return this.options.delete
	}

	get statusIcon(): string {
		return this.options.statusIcon || null
	}

	get description(): string {
		return this.options.description || ""
	}

	get hoverText(): string {
		return this.options.hoverText || ""
	}
}
