import { LanguageService } from "idun-layout/language.service"

export class ValidationRequirement<V, M> {
	constructor(
		public findError: (value: V, model: M) => (() => string)
	) { }

	public static ValueRequired(l: LanguageService) {
		return new ValidationRequirement((value) => {
			if (value === undefined
				|| value === null
				|| value === "")
				return () => l.commonForm.fieldMustHaveValue
		})
	}

	public static MustBeEmail(l: LanguageService) {
		return new ValidationRequirement((value) => {
			if (!value)
				return
			if (typeof value !== "string")
				return () => l.commonForm.fieldMustBeEmail
			const index = value.indexOf("@")
			if (!(index > -1 && value[index - 1] && value[index + 1]))
				return () => l.commonForm.fieldMustBeEmail
		})
	}
}
