import { Component, OnInit } from "@angular/core"
import { BaseSubscriber } from "idun-common/base-subscriber"
import { LanguageService } from "idun-layout/language.service"

import { CalendarEvent } from "idun-common/models/calendar-event"
import { CalendarEventService } from "idun-main/model-services/calendar-event.service"
import { DateHelper } from "utility/date-helper"

@Component({
	selector: "idun-event-list-bar",
	templateUrl: "event-list-bar.component.html",
	styleUrls: ["event-list-bar.component.scss"]
})
export class EventListBarComponent extends BaseSubscriber implements OnInit {
	events: CalendarEvent[]

	constructor(
		private calendarEventService: CalendarEventService,
		public l: LanguageService
		) {
		super()
	}

	ngOnInit() {
		const interval = DateHelper.dayInterval(new Date)
		this.subscribe(
			this.calendarEventService.getPersonalEvents(interval.start, interval.end),
			(val) => { this.events = val },
		"events")
	}

	getTimeInterval(start: Date, end: Date): string {
		return DateHelper.timeIntervalToString(start, end)
	}
}
