﻿import { Routes } from "@angular/router"
import { AutoListBoxComponent } from "idun-common/auto-list-box.component"
import { AutoFormComponent } from "idun-common/auto-form.component"

import { BookingRequestComponent } from "./booking-request.component"
import { BookmarkComponent } from "./bookmark.component"
import { BookmarkListComponent } from "./bookmark-list.component"
import { PatientJournalComponent } from "./patient-journal.component"
import { StaffNotificationComponent } from "./staff-notification.component"
import { PatientPageComponent } from "./patient-page.component"
import { StaffPageComponent } from "./staff-page.component"
import { StaffWorkHoursComponent } from "./staff-work-hours.component"
import { ProcedureListComponent } from "./procedure-list.component"
import { TreatmentCodeListComponent } from "./treatment-code-list.component"
import { StaffListComponent } from "./staff-list.component"
import { EdifactComponent } from "./edifact.component"
import { StaffNotificationListComponent } from "./staff-notification-list.component"
import { PatientEventComponent } from "./patient-event.component"
import { PlanningCalendarComponent } from "idun-main/page-components/calendar/planning-calendar.component"
import { PeriodPlanListComponent } from "./period-plan-list.component"

export enum AutoType { Form, List }

export function standardRoutes(model: string) {
	return [
		{ path: model + "/search/:query", component: AutoListBoxComponent, data: { model: model }, outlet: "left-content" },
		{ path: model + "/search/:query", component: AutoListBoxComponent, data: { model: model } },
		{ path: model + "/list/:page", component: AutoListBoxComponent, data: { model: model }, outlet: "left-content" },
		{ path: model + "/list/:page", component: AutoListBoxComponent, data: { model: model } },
		{ path: model + "/list", component: AutoListBoxComponent, data: { model: model }, outlet: "left-content" },
		{ path: model + "/list", component: AutoListBoxComponent, data: { model: model } },
		{ path: model + "/:id", component: AutoFormComponent, data: { model: model }, outlet: "left-content" },
		{ path: model + "/:id", component: AutoFormComponent, data: { model: model } }
	]
}

export function autoFormRoutes(model: string, subPath: string, data?) {
	return [
		{ path: model + subPath, component: AutoFormComponent, data: data || { model: model }, outlet: "left-content" },
		{ path: model + subPath, component: AutoFormComponent, data: data || { model: model } }
	]
}

export function autoListRoutes(model: string, subPath: string, data?) {
	return [
		{ path: model + subPath, component: AutoListBoxComponent, data: data || { model: model }, outlet: "left-content" },
		{ path: model + subPath, component: AutoListBoxComponent, data: data || { model: model } }
	]
}

export function componentRoutes<T>(model: string, subPath: string, component: T, data?) {
	return [
		{ path: model + subPath, component: component, data: data, outlet: "left-content" },
		{ path: model + subPath, component: component, data: data }
	]
}

export const modelRoutes: Routes = [
	...autoListRoutes("booking-request", "/list"),
	...componentRoutes("booking-request", "/:id", BookingRequestComponent),
	...componentRoutes("bookmark", "/list", BookmarkListComponent),
	...componentRoutes("bookmark", "/:id", BookmarkComponent),
	...autoFormRoutes("calendar-event", "/:id"),
	...autoListRoutes("edifact", "/list"),
	...componentRoutes("edifact", "/:id", EdifactComponent),
	...autoListRoutes("journal-entry", "/list/:patient"),
	...componentRoutes("journal-entry", "/list", PatientJournalComponent),
	...autoFormRoutes("journal-entry", "/add/:patient/:template/:side"),
	...autoFormRoutes("journal-entry", "/add/:patient"),
	...autoFormRoutes("journal-entry", "/add"),
	...autoFormRoutes("journal-entry", "/:id"),
	...autoFormRoutes("patient-note", "/add/:patient"),
	...autoFormRoutes("patient-note", "/add"),
	...autoFormRoutes("patient-note", "/:id"),
	...componentRoutes("staff-notification", "/:id", StaffNotificationComponent),
	...componentRoutes("staff-notification", "/list", StaffNotificationListComponent),
	...autoListRoutes("patient", "/search/:query", { model: "patient-summary" }),
	...autoListRoutes("patient", "/list/:page", { model: "patient-summary" }),
	...autoListRoutes("patient", "/list", { model: "patient-summary" }),
	...componentRoutes("patient", "/:id", PatientPageComponent),
	...componentRoutes("patient-journal", "/:id", PatientJournalComponent),
	...componentRoutes("staff", "/list", StaffListComponent),
	...componentRoutes("staff", "/:id", StaffPageComponent),
	...componentRoutes("staff-notification", "/:id", StaffNotificationComponent),
	...componentRoutes("staff-work-hours", "/:id", StaffWorkHoursComponent),
	...standardRoutes("staff-group"),
	...componentRoutes("patient-event", "/:id", PatientEventComponent),
	...componentRoutes("procedure", "/list", ProcedureListComponent),
	...autoFormRoutes("procedure", "/:id"),
	...standardRoutes("procedure-type"),
	...standardRoutes("partner"),
	...standardRoutes("invoice"),
	...componentRoutes("treatment-code", "/list", TreatmentCodeListComponent),
	...autoFormRoutes("treatment-code", "/:id"),
	...standardRoutes("treatment-code"),
	...standardRoutes("treatment-code-type"),
	...standardRoutes("journal-template"),
	...standardRoutes("resource"),
	...standardRoutes("status-type"),
	...componentRoutes("period-plan", "/list", PeriodPlanListComponent),
	...componentRoutes("period-plan", "/:id", PlanningCalendarComponent),
]
