﻿import { Component, forwardRef, Input } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS } from "@angular/forms"
import { FormBasicComponent } from "./form-basic-component"
import { FormRow, OptionInfo, FieldInput, FormRowOptions } from "./form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormListSelectComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormListSelectComponent),
	multi: true
}

@Component({
	selector: "idun-form-list-select",
	templateUrl: "form-list-select.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormListSelectComponent extends FormBasicComponent<string[]> {
	@Input() options?: OptionInfo[]
	rowClass = FormListSelectRow
	newValue = ""

	add(entry: string): void {
		if (!entry)
			return
		const option = this.options.find(e => "" + e.value === "" + entry)
		if (option)
			entry = option.value
		this.value.push(entry)
		this.newValue = ""
		this.onChangeCallback(this.value)
	}

	remove(index: string): void {
		this.value.splice(+index, 1)
		this.onChangeCallback(this.value)
	}

	textFor(key: string) {
		const option = this.options.find(e => e.value === key)
		if (option)
			return option.title
		return ""
	}
}

export class FormListSelectRow extends FormRow {
	constructor(
		field: FieldInput,
		options: Options = {}
	) {
		super(field, options)
	}
}

interface Options extends FormRowOptions {
	options?: OptionInfo[]
}
