﻿import { Component, Input, Output, EventEmitter } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { DateHelper, changeDate } from "utility/date-helper"
import { CalendarEntry } from "./calendar-entry"
import { CalendarEntryProvider } from "./calendar-entry-provider"
import { CalendarColumn } from "./calendar-column-display.component"

@Component({
	selector: "idun-calendar-week",
	templateUrl: "calendar-week.component.html"
})
export class CalendarWeekComponent {
	@Input() date: Date = new Date()
	@Input() entryProvider: CalendarEntryProvider
	@Input() display = false
	@Input() hourHeight = 6
	@Input() showDate = true
	@Input() set showMaximised(value: boolean) {
		this.daysToShow = value
			? 14
			: 7
		this.createColumns()
	}

	@Output() addEntry = new EventEmitter<{ provider: CalendarEntryProvider, time: Date }>()
	@Output() selectEntry = new EventEmitter<{ provider: CalendarEntryProvider, entry: CalendarEntry }>()

	columns: CalendarColumn[]
	private daysToShow = 7

	constructor(public l: LanguageService) {
		this.createColumns()
	}

	private createColumns() {
		this.columns = []
		for (let i = 0; i < this.daysToShow; i++)
			this.addColumnForDay(i)
	}

	private addColumnForDay(day: number) {
		this.columns.push({
			date: () => this.dateFrom(day),
			getEntries: () => this.entriesFor(day),
			title: () => this.l.dayName(day % 7),
			subtitle: () => this.showDate ? this.dateText(this.dateFrom(day)) : "",
			hovertitle: () => "",
			addEntry: time => this.addEntry.emit({ provider: this.entryProvider, time: time }),
			selectEntry: entry => this.selectEntry.emit({ provider: this.entryProvider, entry: entry }),
			hideByDefault: () => this.shouldHideByDefault(day),
			isWeekend: [5, 6, 12, 13].includes(day)
		})
	}

	private entriesFor(day: number): CalendarEntry[] {
		return this.entryProvider ? this.entryProvider.getEntries(this.dateFrom(day)) : []
	}

	private dateFrom(day: number) {
		return changeDate(DateHelper.weekInterval(this.date).start, { days: day })
	}

	private dateText(date: Date) {
		return date.getDate() + "/" + (date.getMonth() + 1)
	}

	private shouldHideByDefault(day: number) {
		if (this.daysToShow > 7)
			return false
		return day > 4 && !this.hasWeekendEntries()
	}

	private hasWeekendEntries() {
		return this.entriesFor(5).length || this.entriesFor(6).length
	}
}
