import { Component, ElementRef, ViewChild, Input } from "@angular/core"

@Component({
	selector: "idun-colorbox-grid",
	templateUrl: "colorbox-grid.component.html",
	styleUrls: ["colorbox-grid.component.scss"]
})
export class ColorboxGridComponent<T> {
	@Input() title = ""

	@Input() rows: {
		getTitle: () => string,
		getHint?: () => string,
		getColor?: (target: T) => string,
		onClick?: (target: T) => void,
		source: T
	}[] = []

	columnCategories = {}

	private _columns: {
		getTitle: () => string,
		getHoverTitle: () => string,
		getColumnCategory: () => string,
		tiebreaker: () => number,
		source: T
	}[] = []

	@Input() set columns(value: {
		getTitle: () => string,
		getHoverTitle: () => string,
		getColumnCategory: () => string,
		tiebreaker: () => number,
		source: T
	}[]) {
		this._columns = value
		this.sortColumns()
		this.updateColumnCategories()
	}

	get columns(): {
		getTitle: () => string,
		getHoverTitle: () => string,
		getColumnCategory: () => string,
		tiebreaker: () => number,
		source: T
	}[] {
		return this._columns
	}

	@Input() display = false
	@Input() width = ""

	isDisplay() {
		return this.display ? "" : null
	}

	private sortColumns() {
		this._columns = this._columns.sort((a, b) => {
			const m = a.getColumnCategory()
			const n = b.getColumnCategory()
			if (m > n)
				return 1
			else if (m < n)
				return -1
			const c = a.tiebreaker()
			const d = b.tiebreaker()
			if (c > d)
				return -1
			else if (c < d)
				return 1
			return 0
		})
	}

	private updateColumnCategories() {
		this.columnCategories = {}
		for (const i of this._columns)
			this.columnCategories[i.getColumnCategory()] = this.columnCategories[i.getColumnCategory()] ? this.columnCategories[i.getColumnCategory()] + 1 : 1
		this.columnCategories = Object.keys(this.columnCategories).map((k) => {
			return { title: k, size: this.columnCategories[k] }
		})
	}
}
