﻿import { Component, forwardRef, Input } from "@angular/core"
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor } from "@angular/forms"
import { FormBasicComponent } from "idun-layout/form/form-basic-component"
import { LanguageService } from "idun-layout/language.service"
import { IdunId } from "utility/idun-id"
import { FormRow } from "idun-layout/form/form-row"

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
	provide: NG_VALUE_ACCESSOR,
	useExisting: forwardRef(() => FormIdunidComponent),
	multi: true
}
export const CUSTOM_INPUT_VALIDATE: any = {
	provide: NG_VALIDATORS,
	useExisting: forwardRef(() => FormIdunidComponent),
	multi: true
}

@Component({
	selector: "idun-form-idunid",
	templateUrl: "form-idunid.component.html",
	providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR, CUSTOM_INPUT_VALIDATE]
})
export class FormIdunidComponent extends FormBasicComponent<string> {
	rowClass = FormIdunidRow
	shownType = "idun"
	private _shownValue: any

	constructor(public l: LanguageService) {
		super()
		if (l.language.language === "dansk")
			this.shownType = "cpr"
	}

	get shownValue(): string {
		return this._shownValue
	}

	set shownValue(val: string) {
		if (!this._shownValue)
			this._shownValue = ""
		if (this.shownType === "cpr") {
			if (this._shownValue.length === 5 && val.length === 6)
				this._shownValue = val + "-"
			else
				this._shownValue = val
			if (val.length === 11)
				this.onBlur()
		} else
			this._shownValue = val
	}

	updateShownType(newType: string): void {
		if (newType === "cpr")
			this.shownValue = new IdunId(this.innerValue).toCpr()
		else
			this.shownValue = this.innerValue
	}

	onBlur() {
		let idunid: IdunId
		if (this.shownType === "cpr") {
			idunid = new IdunId()
			idunid.loadFromCpr(this._shownValue)
		} else {
			idunid = new IdunId(this._shownValue)
		}
		this.value = idunid.toString()
		this.isBlurred = true
		this.onTouchedCallback()
	}

	onValueChange() {
		if (!this._shownValue || this.shownType !== "cpr") {
			const idunid: IdunId = new IdunId(this.value)
			if (idunid.idType === "c") {
				this._shownValue = idunid.toCpr()
				this.shownType = "cpr"
			} else if (this.value) {
				this._shownValue = this.value
				this.shownType = "idun"
			}
		}
		if (!this.value)
			this._shownValue = ""
	}
}

export class FormIdunidRow extends FormRow { }
