﻿import { Component } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { environment } from "environments/environment"

@Component({
	selector: "idun-language",
	templateUrl: "language.component.html",
	styles: [`
		.language-entry {
			margin-top:1rem;
			display: flex;
			height: 4rem;
			width: 22.5rem;
			cursor: pointer;
			background-color: var(--color-neutral);
			transition: background-color 0.3s ease;
			border-radius: 0.5rem;
		}
		.language-entry:hover{
			background-color: var(--color-neutral-active);
			transition: background-color 0.3s ease;
		}
		.language-entry h3 {
			font-size:2rem;
			text-align:center;
			margin-top:0.5rem;
			text-transform: capitalize;
		}
		img {
			height:4rem;
			border-bottom-left-radius: 0.5rem;
			border-top-left-radius: 0.5rem;
		}
`]
})
export class LanguageComponent {
	languages = ["dansk", "english"]
	constructor(public l: LanguageService) { }

	setLanguage(language: string) {
		this.l.updateLanguage(language)
	}

	showTranslationPageLink() {
		return !environment.production
	}

	clearLocalStorage() {
		localStorage.clear()
		location.reload()
	}
}
