﻿import { Component, ElementRef, Input } from "@angular/core"
import { Router } from "@angular/router"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { LayoutService } from "idun-layout/layout.service"

@Component({
	selector: "idun-collapsed-menu",
	templateUrl: "collapsed-menu.component.html",
	styleUrls: ["collapsed-menu.component.scss"]
})
export class CollapsedMenuComponent {
	navigater: NavigationHandler
	@Input() menuInfoList: any

	menuSize = [
		"",
		"dropdown-width-two",
		"dropdown-width-three",
		"dropdown-width-four"
	]

	constructor(
		private myElement: ElementRef,
		private router: Router,
		private layoutService: LayoutService
	) {
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	toggleElement(element, setValue) {
		if (setValue === true)
			element.style.display = "block"
		else if (setValue === false)
			element.style.display = "none"
		else if (element.style.display === "none")
			element.style.display = "block"
		else
			element.style.display = "none"
	}
}
