﻿export abstract class IdunModel {
	DBID: number

	public abstract getTitle(): string

	public getShortTitle(): string {
		return this.getTitle().length <= 15 ? this.getTitle() : this.getTitle().substring(0, 15)
	}

	public static getModelFromList(dbid: number, list: IdunModel[]): any {
		if (!list)
			return undefined
		for (let i = 0; i < list.length; i++)
			if (+list[i].DBID === +dbid)
				return list[i]
		return undefined
	}
}

export abstract class SearchableIdunModel extends IdunModel {
	public abstract searchAttributes(): string[]

	public search(query: string): boolean {
		const searchedWords = query.toLowerCase().split(/\s/g)
		const existingWords = this.searchAttributes().join(" ").toLowerCase().split(/\s/g)
		for (const word of searchedWords)
			if (!this.wordExists(word, existingWords))
				return false
		return true
	}

	private wordExists(word: string, words: string[]): boolean {
		return words.findIndex(x => x.startsWith(word)) > -1
	}
}
