import { Component, Input, Output, EventEmitter, ElementRef, ViewChild, OnDestroy } from "@angular/core"
import { Router } from "@angular/router"
import { Colour } from "utility/colour"
import { LayoutService } from "../layout.service"
import { NavigationHandler } from "../navigation-handler"
import { Subscriber } from "utility/subscriber"
import { LanguageService } from "../language.service"
import { DomSanitizer } from "@angular/platform-browser"

@Component({
	selector: "idun-content-box",
	templateUrl: "content-box.component.html",
	styleUrls: ["content-box.component.scss"]
})
export class ContentBoxComponent implements OnDestroy {
	navigater: NavigationHandler
	subscriber = new Subscriber()
	@Input() noPadding = false
	@Input() showNavigation = false
	@Input() color: number | string
	@Input() icon: string
	@Input() hideClose = false // for when you want an observer on close, but don't want the close icon
	@Output() close = new EventEmitter()

	@Input() showContent = true
	showOptions = false
	showHelpText = false
	bookmarks: string[] = []
	get bookmarkImage() {
		if (this.isBookmarked(this.navigater.currentAddress))
			return "SingleBookmark"
		else
			return "SingleBookmarkNF"
	}

	@Input() buttons: OptionsBarButton[] = []
	buttonsRight: OptionsBarButton[] = []

	@Input() helpText = ""

	constructor(
		myElement: ElementRef,
		router: Router,
		public l: LanguageService,
		private layoutService: LayoutService,
		private sanitizer: DomSanitizer
	) {
		this.navigater = new NavigationHandler(router, myElement, layoutService)
		this.subscriber.subscribe(this.layoutService.bookmarks$, list => {
			this.bookmarks = list
		})
		const component = this
		this.buttonsRight = [
			{
				icon: "Help",
				action: () => this.showHelpText = !this.showHelpText,
				show: () => !!this.helpText,
				title: () => this.l.common.help
			},
			{
				get icon() { return component.bookmarkImage },
				action: () => {
					const url = this.generaliseUrl(this.navigater.currentAddress)
					if (this.isBookmarked(url))
						this.layoutService.removeBookmark(url)
					else
						this.layoutService.addBookmark(url)
				},
				show: () => this.showNavigation,
				title: () => this.l.bookmark.bookmarkPage
			},
			{
				icon: "Delete",
				action: () => {
					switch (this.navigater.currentSide) {
						case "main":
							return this.navigater.main("home")
						default:
							return this.navigater.current("empty")
					}
				},
				show: () => this.showNavigation,
				title: () => this.l.common.closePage
			}
		]
	}

	ngOnDestroy() {
		this.subscriber.unsubscribe()
	}

	extrasHasContent() {
		for (const button of this.buttons)
			if (button.show())
				return true
		for (const button of this.buttonsRight)
			if (button.show())
				return true
		return false
	}

	hasCloseHandler() {
		if (this.hideClose)
			return false
		return this.close.observers.length > 0
	}

	toggleShowOptions() {
		if (this.showOptions)
			this.hideOptions()
		else
			this.doShowOptions()
	}

	doShowOptions() {
		this.showOptions = true
		if (!this.showContent)
			this.showContent = true
		if (!this.extrasHasContent())
			this.showOptions = false
	}

	hideOptions() {
		this.showOptions = false
		if (this.showHelpText)
			this.showHelpText = false
	}

	showBackArrow() {
		return this.showNavigation && this.navigater.hasNavigationHistory()
	}

	showExtraBox(extrasElement: any) {
		return this.showOptions && (this.showHelpText || (extrasElement && extrasElement.childNodes.length))
	}

	get headerColor() {
		const color = +this.color || this.color
		if (typeof color === "string")
			return this.sanitizer.bypassSecurityTrustStyle(color)
		return Colour.ToRgbaString(Colour.HSLtoRGB(color === undefined ? 0.55 : color, 0.26, 0.40), 1)
	}

	get optionsColor() {
		const color = +this.color || this.color
		if (typeof color === "string")
			return this.sanitizer.bypassSecurityTrustStyle(color.substring(0, color.length - 1) + "-dark)")
		return Colour.ToRgbaString(Colour.HSLtoRGB(color === undefined ? 0.55 : color, 0.26, 0.3), 1)
	}

	private isBookmarked(route: string) {
		return this.bookmarks.some(e => e === route)
	}

	generaliseUrl(path: string) {
		const pathParts: string[] = path.split("(")
		for (let i = 0; i < pathParts.length; i++) {
			if (pathParts[i].includes("patient/") || pathParts[i].includes("journal-entry/add/")) {
				const smallerParts = pathParts[i].split("/")
				if (!isNaN(parseInt(smallerParts[smallerParts.length - 1], 10))) {
					if (smallerParts[smallerParts.length - 1].includes(")")) {
						smallerParts[smallerParts.length - 1] = ":id)"
					} else {
						smallerParts[smallerParts.length - 1] = ":id"
					}
				}
				pathParts[i] = smallerParts.join("/")
			}
		}
		return pathParts.join("(")
	}
}

export interface OptionsBarButton {
	icon?: string,
	text?: () => string,
	action: () => void,
	show: () => boolean,
	title?: () => string,
	mouseover?: () => void,
	mouseout?: () => void,
	groupWithPrevious?: boolean
}
