﻿import { Component, ElementRef } from "@angular/core"
import { LanguageService } from "idun-layout/language.service"
import { NavigationHandler } from "idun-layout/navigation-handler"
import { Router } from "@angular/router"
import { LayoutService } from "idun-layout/layout.service"

@Component({
	selector: "idun-empty",
	template: `
<h2 style="border-radius: 0.5rem; display: flex; padding: 0.5rem 1rem; width: 14rem; float: right;">
	<span style="line-height: 2rem;">{{l.common.closePage}}</span>
	<idun-icon icon='Delete' style="display: inline-block; margin-left: auto;" (click)='click()'></idun-icon>
</h2>
	`
})
export class EmptyComponent {
	navigater: NavigationHandler

	constructor(
		myElement: ElementRef,
		router: Router,
		public l: LanguageService,
		private layoutService: LayoutService
	) {
		this.navigater = new NavigationHandler(router, myElement, layoutService)
	}

	click() {
		switch (this.navigater.currentSide) {
			case "main":
				return this.navigater.main("home")
			case "right":
				return this.navigater.main(this.layoutService.navigationController.leftUrl)
			case "left":
				return this.navigater.main(this.layoutService.navigationController.rightUrl)
		}
	}
}
