import { IdunModel } from "./idun-model"
import { ProcedureEvent } from "./procedure-event"
import { DateHelper } from "utility/date-helper"

export class PatientEvent extends IdunModel {
	DBID: number
	PatientId: number
	ProcedureEvents: ProcedureEvent[] // these are stored in their own table
	Pregnant: Pregnant

	constructor(source?: any) {
		super()
		this.ProcedureEvents = []
		this.Pregnant = Pregnant.Maybe

		if (!source)
			return
		for (const prop in source) this[prop] = source[prop]
		if (source.ProcedureEvents)
			this.ProcedureEvents = source.ProcedureEvents.map(e => new ProcedureEvent(e))
	}

	getTitle(): string {
		throw new Error("Method should not be used")
	}

	duration() {
		return this.ProcedureEvents
			.map(e => e.Times.getLengthInMinutes())
			.reduce((previous, current) => previous + current)
	}

	timeIntervalString() {
		return DateHelper.timeIntervalToString(
			this.ProcedureEvents[0].Times.StartTime,
			this.ProcedureEvents[this.ProcedureEvents.length - 1].Times.EndTime)
	}
}

export enum Pregnant { Yes, Maybe, No }
